<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col my-1">
            <h6 class="mb-0">
              {{ $route.query.pr_type }} {{ $route.query.product }}
            </h6>
          </div>
          <div class="col my-1"></div>
          <div class="col my-1">
            <input
              type="search"
              :placeholder="$t('search')"
              class="form-control form-control-sm"
              v-model="search"
              @keyup="
                get(1, limit);
                $store.dispatch('setloading', false);
              "
            />
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-md-4 col-lg-4 col-sm-6 my-2"
            v-for="(plan, i) in plans"
            :key="plan"
          >
            <div class="card h-100 shadow-sm border">
              <div
                class="card-body pb-2"
                style="display: flex; flex-direction: column"
              >
                <h6 class="mb-0">
                  <b> {{ i + 1 }}.</b>
                </h6>
                <hr class="my-1" />
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-box"></i>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{ plan.product?.name }}
                  </h6>
                </div>
                <div class="d-flex" v-if="plan.material?.name">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-box"></i>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{ $t("plan.material") }}: {{ plan.material?.name }}
                  </h6>
                </div>
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-calendar-check"></i>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{ format_date(plan.to_date).slice(0, 10) }}
                  </h6>
                </div>
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <div class="fas fa-box"></div>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{ $t("plan.value") }}: {{ plan.value }}
                    {{ plan.product?.olchov?.name }}
                  </h6>
                </div>
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <div class="fas fa-box"></div>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{ $t("plan.rest_value") }}: {{ plan.rest_value }}
                    {{ plan.product?.olchov?.name }}
                  </h6>
                </div>
                <div class="d-flex" v-if="plan.comment">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <div class="fas fa-comment"></div>
                  </div>
                  <h6 class="ms-2 mb-0">{{ plan.comment }}</h6>
                </div>
                <div class="row mt-auto">
                  <div class="col my-1">
                    <button
                      class="btn btn-sm bg-gradient-warning mb-0 w-100"
                      data-bs-toggle="modal"
                      data-bs-target="#update-plan"
                      @click="
                        $refs.plan.body.product_type_id =
                          plan.product?.product_type?.id;
                        $refs.plan.item = {
                          id: plan.id,
                          product_id: plan.product_id,
                          value: plan.value,
                          to_date: plan.to_date,
                          comment: plan.comment,
                          size1: plan.size1,
                          size2: plan.size2,
                          disabled: false,
                          product_type_name: plan.product?.product_type?.name,
                          product_name: plan.product?.name,
                          olchov: plan.product?.olchov?.name,
                          hasSize: plan.product.product_type.hasSize || false,
                          material_name:
                            plan.material?.name || $t('plan.material'),
                          material_id: plan?.material?.id || 0,
                          unit: plan.product?.unit || false,
                        };
                      "
                    >
                      <i class="fa-solid fa-pen"></i>
                    </button>
                  </div>
                  <div class="col my-1">
                    <button
                      class="btn btn-sm bg-gradient-danger mb-0 w-100"
                      @click="
                        $refs.plan.item = {
                          id: plan.id,
                          product_id: plan.product_id,
                          value: plan.value,
                          to_date: plan.to_date,
                          comment: plan.comment,
                          size1: plan.size1,
                          size2: plan.size2,
                          disabled: true,
                          product_type_name: plan.product?.product_type?.name,
                          product_name: plan.product?.name,
                          olchov: plan.product?.olchov?.name,
                          hasSize: plan.product.product_type.hasSize || false,
                          material_name:
                            plan.material?.name || $t('plan.material'),
                          material_id: plan?.material?.id || 0,
                          unit: plan.product?.unit || false,
                        };
                        $refs.plan.update();
                      "
                    >
                      <div class="fas fa-trash"></div>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="plans" />
            <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
          </div>
        </div>
      </div>
    </div>
  </div>
  <plan-modals ref="plan" />
</template>

<script>
import * as api from "@/server/api";
import PlanModals from "@/components/modals/PlanModals.vue";
import DataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";

export default {
  components: { PlanModals, DataNotfound, Pagination },
  name: "PLan",
  data() {
    return {
      plans: [],
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      format_date: api.format_date,
    };
  },
  methods: {
    get(page, limit) {
      api
        .get_plans(page, limit, this.search, false, this.$route.query.id)
        .then((res) => {
          this.pages = res.data.page_count;
          this.page = res.data.current_page;
          this.limit = res.data.page_limit;
          this.plans = res.data.data;
        });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>
