<template>
  <div
    class="modal fade"
    id="add-material-type"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-material-type-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-material-type-label">
            {{ $t("material-types.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('add', body)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("material-types.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="body.name"
                    :placeholder="$t('material-types.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="olchov">{{ $t("product-types.olchov") }}</label>
                  <select
                    id="olchov"
                    class="form-select"
                    v-model="body.olchov_id"
                    required
                  >
                    <option
                      :value="olchov.id"
                      v-for="olchov in olchovs"
                      :key="olchov"
                    >
                      {{ olchov.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-12">
                <div class="form-group">
                  <label for="image">{{ $t("material-types.image") }}</label>
                  <input
                    id="image"
                    type="file"
                    class="form-control"
                    @change="body.image = $event.target.files[0]"
                    :placeholder="$t('material-types.image')"
                    required
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="update-material-type"
    tabindex="-1"
    role="dialog"
    aria-labelledby="update-material-type-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="update-material-type-label">
            {{ $t("material-types.update") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('put', data)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("material-types.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="data.name"
                    :placeholder="$t('material-types.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="olchov">{{ $t("product-types.olchov") }}</label>
                  <select
                    id="olchov"
                    class="form-select"
                    v-model="data.olchov_id"
                    required
                  >
                    <option
                      :value="olchov.id"
                      v-for="olchov in olchovs"
                      :key="olchov"
                    >
                      {{ olchov.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-12">
                <div class="form-group">
                  <label for="image">{{ $t("material-types.image") }}</label>
                  <input
                    id="image"
                    type="file"
                    class="form-control"
                    @change="data.image = $event.target.files[0]"
                    :placeholder="$t('material-types.image')"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-put
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("update") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "add-material-type",
  props: ["olchovs", "item"],
  emits: ["add", "put"],
  data() {
    return {
      body: {
        name: "",
        image: "",
        olchov_id: null,
      },
    };
  },
  computed: {
    data() {
      return this.item;
    },
  },
};
</script>
