<template>
  <div class="py-4 container-fluid">
    <div class="card h-100 h-auto">
      <div class="card-header pb-0">
        <div class="row mb-4">
          <div class="col-md-4 my-1 align-items-center d-flex">
            <h6 class="mb-0">
              {{ $t("norm.products") }}
            </h6>
          </div>
          <div class="col-md-4 my-1 align-items-center d-flex">
            <button
              class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
              data-bs-toggle="modal"
              data-bs-target="#add-plan"
              @click="
                $refs.plan.body.product_type_id = selectedCategory?.id || 0;
                $refs.plan.body.product_type_name =
                  selectedCategory?.name || '';
              "
            >
              {{ $t("plan.add-button") }}
            </button>
          </div>
          <div class="col-md-4 my-1 align-items-center d-flex">
            <input
              type="search"
              :placeholder="$t('search')"
              class="form-control form-control-sm"
              v-model="search"
              @keyup="
                getProducts(1, 10);
                $store.dispatch('setloading', false);
              "
            />
          </div>
        </div>
      </div>
      <div
        class="card-body pt-0 overflow-auto"
        style="min-height: 50vh; max-height: 50vh"
      >
        <table class="table table-bordered">
          <thead>
            <tr>
              <th>{{ $t("product-store.table.image") }}</th>
              <th>{{ $t("product-store.table.name") }}</th>
              <th>{{ $t("plan.cycle_value") }}</th>
              <th>{{ $t("plan.plan_value") }}</th>
              <th>{{ $t("plan.rest_value") }}</th>
              <th class="w-custom-40"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in products" :key="item">
              <td>
                <img
                  :src="`${image}/products/${item.image}`"
                  class="avatar avatar-sm"
                  alt="product"
                />
              </td>
              <td>{{ item.name }}</td>
              <td>
                {{ Intl.NumberFormat().format(item.cycle_value) }}
                {{ selectedCategory?.olchov?.name || "" }}
              </td>
              <td>
                {{ Intl.NumberFormat().format(item.plan_value) }}
                {{ selectedCategory?.olchov?.name || "" }}
              </td>
              <td>
                {{ Intl.NumberFormat().format(item.rest_value) }}
                {{ selectedCategory?.olchov?.name || "" }}
              </td>
              <td>
                <button
                  @click="
                    $router.push(
                      `/plan?id=${item.id}&pr_type=${
                        selectedCategory?.name || ''
                      }&product=${item.name}`
                    )
                  "
                  class="btn bg-gradient-primary mb-0 position-relative btn-sm px-3"
                  type="button"
                >
                  <i class="fa-solid fa-eye"></i>
                </button>
              </td>
            </tr>
          </tbody>
          <tfoot v-if="!products.length">
            <tr>
              <td colspan="6">
                <data-notfound :data="products" />
              </td>
            </tr>
          </tfoot>
          <tfoot v-if="pages">
            <tr>
              <td colspan="6">
                <pagination
                  :page="page"
                  :pages="pages"
                  :limit="limit"
                  @get="getProducts"
                />
              </td>
            </tr>
          </tfoot>
        </table>
      </div>
      <div class="card-footer">
        <div class="row flex-nowrap overflow-auto">
          <div
            class="col-md-2"
            v-for="product_type in product_types"
            :key="product_type"
          >
            <div
              class="card shadow"
              @click="
                selectedCategory = product_type;
                getProducts(1, 10);
              "
            >
              <img
                :src="`${image}/product_types/${product_type.image}`"
                class="card-img-top"
                style="
                  max-inline-size: 100%;
                  block-size: auto;
                  aspect-ratio: 2/1;
                  object-fit: contain;
                  height: 70px;
                "
                alt="..."
              />
              <div class="card-body">
                <span
                  class="badge badge-pill bg-primary text-capitalize d-flex justify-content-center"
                  style="text-wrap: balance"
                >
                  {{ product_type.name }}
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <plan-modals ref="plan" />
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";
import PlanModals from "@/components/modals/PlanModals.vue";
export default {
  components: {
    dataNotfound,
    Pagination,
    PlanModals,
  },
  name: "plan-products",
  data() {
    return {
      products: [],
      product_types: [],
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      image: api.image,
      selectedCategory: null,
    };
  },
  methods: {
    get() {
      api.get_product_types(1, 1000, "", "", "plan").then((res) => {
        this.product_types = res.data.data;

        if (res.data.data.length) {
          this.selectedCategory = res.data.data[0];
          this.getProducts(this.page, this.limit);
        }
      });
    },
    getProducts(page = this.page, limit = this.limit) {
      api
        .get_plan_products(page, limit, this.search, this.selectedCategory.id)
        .then((res) => {
          this.pages = res.data.pages;
          this.page = res.data.current_page;
          this.products = res.data.data;
        });
    },
  },
  created() {
    this.get();
  },
};
</script>
