<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-4 my-1">
            <h6 class="mb-0">{{ $t("users.title") }}</h6>
          </div>
          <div class="col-md-8">
            <div class="row">
              <div class="col-md-4 my-1">
                <button
                  v-if="!$route.query.market_id"
                  :class="
                    !disabled ? 'bg-gradient-danger' : 'bg-gradient-success'
                  "
                  class="btn btn-sm float-end mb-0 w-100"
                  @click="
                    page = 1;
                    disabled ? (disabled = false) : (disabled = true);
                    get(page, limit);
                  "
                >
                  {{ !disabled ? $t("users.disabled") : $t("users.active") }}
                </button>
              </div>
              <div class="col-md-4 my-1">
                <button
                  v-if="!$route.query.market_id"
                  class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
                  data-bs-toggle="modal"
                  data-bs-target="#add-user"
                  @click="$refs.add_user.get_markets()"
                >
                  {{ $t("users.add-button") }}
                </button>
              </div>
              <div class="col-md-4 my-1">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    get(1, limit);
                    $store.dispatch('setloading', false);
                  "
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body py-2">
        <div class="table-responsive p-0 mb-0" style="zoom: 85%">
          <table class="table table-bordered align-items-center mb-0">
            <thead>
              <tr>
                <th class="w-custom-40">{{ $t("users.table.id") }}</th>
                <th>{{ $t("users.table.name") }}</th>
                <th>{{ $t("users.table.birth-date") }}</th>
                <th>{{ $t("users.table.specialty") }}</th>
                <th>{{ $t("users.table.role") }}</th>
                <th>{{ $t("users.table.nation") }}</th>
                <th>{{ $t("users.address") }}</th>
                <th>{{ $t("users.table.username") }}</th>
                <th>{{ $t("users.table.balance") }}</th>
                <th>{{ $t("users.market") }}</th>
                <th class="w-custom-40"></th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="user in users"
                :key="user"
                class="text-secondary user-table"
              >
                <td>{{ user.id }}</td>
                <td>
                  <div class="d-flex px-2">
                    <div
                      class="cursor-pointer me-3"
                      @click="image_view(`${image}/passports/${user.passport}`)"
                    >
                      <img
                        :src="`${image}/passports/${user.passport}`"
                        class="avatar avatar-sm"
                        alt="Passport"
                      />
                    </div>
                    <div class="d-flex flex-column justify-content-center">
                      {{ user.name }}
                    </div>
                  </div>
                </td>
                <td>{{ new Date(user.birthDate).toLocaleDateString() }}</td>
                <td>
                  {{ user.specialty }}
                </td>
                <td>
                  {{
                    user.role == "admin"
                      ? $t("roles.admin")
                      : user.role == "accountant"
                      ? $t("roles.accountant")
                      : user.role == "analyst"
                      ? $t("roles.analyst")
                      : user.role == "seller"
                      ? $t("roles.seller")
                      : user.role == "worker"
                      ? $t("roles.worker")
                      : user.role == "plant_admin"
                      ? $t("roles.plant_admin")
                      : user.role == "seller_admin"
                      ? $t("roles.seller_admin")
                      : user.role == "founder"
                      ? $t("roles.founder")
                      : user.role
                  }}
                </td>
                <td>
                  {{ user.nation }}
                </td>
                <td>
                  {{ user.address }}
                </td>
                <td>
                  {{ user.username }}
                </td>
                <td>
                  {{
                    Intl.NumberFormat().format(user.salary) +
                    " " +
                    $t("currency")
                  }}
                </td>
                <td>{{ user.market?.name }}</td>
                <td>
                  <button
                    class="btn btn-sm px-3 bg-gradient-success mx-1 mb-0"
                    v-if="disabled"
                    @click="
                      unlock({
                        id: user.id,
                        name: user.name,
                        nation: user.nation,
                        birthdate: user.birthDate,
                        specialty: user.specialty,
                        passport: '',
                        workbegindate: user.workBeginDate,
                        agreement_expiration: user.agreement_expiration,
                        username: user.username,
                        password: '',
                        disabled: false,
                        phones: user.phones,
                        role: user.role,
                      })
                    "
                  >
                    <i class="fas fa-unlock"></i>
                  </button>
                  <button
                    class="btn btn-sm px-3 bg-gradient-secondary mx-1 mb-0"
                    data-bs-toggle="modal"
                    data-bs-target="#give-money"
                    v-if="!disabled && role == 'admin'"
                    @click="
                      body.source = user.id;
                      get_cashregs();
                    "
                  >
                    <i class="fas fa-hand-holding-dollar"></i>
                  </button>
                  <router-link
                    :to="
                      !$route.query.market_id
                        ? {
                            name: 'Profile',
                            query: {
                              id: user.id,
                            },
                          }
                        : {
                            name: 'Profile',
                            query: {
                              id: user.id,
                              market_id: $route.query.market_id
                                ? $route.query.market_id
                                : 0,
                            },
                          }
                    "
                    class="btn btn-sm px-3 bg-gradient-primary mb-0"
                  >
                    <i class="fa-solid fa-eye"></i>
                  </router-link>
                </td>
              </tr>
            </tbody>
            <tfoot v-if="!users.length">
              <tr>
                <td colspan="11">
                  <data-notfound :data="users" />
                </td>
              </tr>
            </tfoot>
          </table>
        </div>
        <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
      </div>
    </div>
  </div>
  <div id="image-viewer">
    <span class="close" @click="image_view()">&times;</span>
    <img class="modal-content" id="full-image" />
  </div>
  <add-user @add="add" ref="add_user" />

  <div
    class="modal fade"
    id="give-money"
    tabindex="-1"
    role="dialog"
    aria-labelledby="give-money-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="give-money-label">
            {{ $t("users.give-money") }}
          </h5>
        </div>
        <form @submit.prevent="give_money">
          <div class="modal-body">
            <div class="form-group">
              <label for="kassa">{{ $t("cashier.title") }}</label>
              <div class="input-group">
                <select
                  class="form-select"
                  v-model="body.kassa_id"
                  @change="
                    body.cashreg_balance = cashregs.find(
                      (e) => e.id == body.kassa_id
                    ).balance
                  "
                  required
                >
                  <option
                    :value="cashreg.id"
                    v-for="cashreg in cashregs"
                    :key="cashreg"
                  >
                    {{ cashreg.name }}
                  </option>
                </select>
                <span class="input-group-text border border-1">
                  <b>
                    {{
                      `${$t("supplier.balance")}: ${Intl.NumberFormat().format(
                        body.cashreg_balance
                      )} ${$t("currency")}`
                    }}
                  </b>
                </span>
              </div>
            </div>
            <div class="form-group">
              <label for="money">{{ $t("cashier.money") }}</label>
              <div class="input-group">
                <input
                  id="money"
                  type="number"
                  class="form-control"
                  step="any"
                  min="1"
                  :max="body.cashreg_balance"
                  v-model="body.money"
                  @focus="body.money == 0 ? (body.money = null) : body.money"
                  @focusout="body.money == null ? (body.money = 0) : body.money"
                  :placeholder="$t('cashier.money')"
                  required
                />
                <span class="input-group-text">{{ $t("currency") }}</span>
              </div>
            </div>
            <div class="form-group">
              <label for="comment">{{ $t("cashier.comment") }}</label>
              <textarea
                id="comment"
                class="form-control"
                v-model="body.comment"
                :placeholder="$t('cashier.comment')"
                required
              />
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-give-money
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("users.give-money") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import pagination from "@/components/pagination.vue";
import DataNotfound from "@/components/dataNotfound.vue";
import AddUser from "@/components/modals/AddUser.vue";
export default {
  components: { pagination, DataNotfound, AddUser },
  name: "users",
  data() {
    return {
      users: [],
      pages: 0,
      page: 1,
      limit: 10,
      disabled: false,
      search: "",
      image: api.image,
      body: {
        money: 0,
        type: "user_salary",
        source: 0,
        comment: "",
        kassa_id: 0,
        cashreg_balance: null,
      },
      cashregs: [],
      role: localStorage["role"],
    };
  },
  methods: {
    get(page, limit) {
      const market_id = this.$route.query.market_id
        ? this.$route.query.market_id
        : 0;
      api
        .get_users(
          page,
          limit,
          this.search,
          this.disabled,
          "",
          false,
          market_id
        )
        .then((res) => {
          this.pages = res.data.page_count;
          this.page = res.data.current_page;
          this.limit = res.data.page_limit;
          this.users = res.data.data;
        });
    },
    image_view(src) {
      if (src) {
        document.getElementById("full-image").src = src;
        document.getElementById("image-viewer").style.display = "block";
      } else {
        document.getElementById("image-viewer").style.display = "none";
      }
    },
    add(body) {
      let data = this.form_data(body);
      api.create_user(data).then(() => {
        body.name = "";
        body.address = "";
        (body.market_id = 0), (body.nation = "");
        body.birthdate = "";
        body.specialty = "";
        body.passport = "";
        body.workbegindate = "";
        body.agreement_expiration = "";
        body.username = "";
        body.password = "";
        body.disabled = false;
        body.phones = [{ number: "", dial_code: "+7" }];
        body.role = "";
        body.salary = 0;
        body.shift_id = 0;
        api.success("close");
        this.get(this.page, this.limit);
      });
    },
    unlock(body) {
      var phones = body.phones
        .map(function (phone) {
          return phone.number;
        })
        .join(";");
      let data = new FormData();
      data.append("name", body.name);
      data.append("nation", body.nation);
      data.append("birthdate", body.birthdate);
      data.append("specialty", body.specialty);
      if (body.passport) {
        data.append("passport", body.passport);
      }
      data.append("workbegindate", body.workbegindate);
      data.append("agreement_expiration", body.agreement_expiration);
      data.append("username", body.username);
      data.append("password", body.password);
      data.append("disabled", body.disabled);
      data.append("phones", phones);
      data.append("role", body.role);
      api.put_user(data, body.id).then(() => {
        api.success();
        this.get(this.page, this.limit);
      });
    },
    form_data(body) {
      var phones = body.phones
        .map(function (phone) {
          return phone.dial_code + "" + phone.value;
        })
        .join(";");
      let data = new FormData();
      data.append("name", body.name);
      data.append("address", body.address);
      if (body.market_id) {
        data.append("market_id", body.market_id);
      }
      data.append("nation", body.nation);
      data.append("birthdate", body.birthdate);
      data.append("specialty", body.specialty);
      data.append("passport", body.passport);
      data.append("workbegindate", body.workbegindate);
      data.append("agreement_expiration", body.agreement_expiration);
      data.append("username", body.username);
      data.append("password", body.password);
      data.append("disabled", body.disabled);
      data.append("phones", phones);
      data.append("role", body.role);
      data.append("salary", body.salary);
      data.append("shift_id", body.shift_id);
      return data;
    },
    give_money() {
      api.pay_for_expense(this.body).then(() => {
        this.body.comment = "";
        this.body.kassa_id = 0;
        this.body.money = 0;
        this.body.source = 0;
        api.success("close-give-money");
        this.get(this.page, this.limit);
      });
    },
    get_cashregs() {
      api.kassa("", 0, 0).then((res) => {
        this.cashregs = res.data;
      });
    },
    wage_period(period) {
      let pr = "";
      if (period == "hourly") {
        pr = this.$t("plant.hourly");
      } else if (period == "daily") {
        pr = this.$t("plant.daily");
      } else if (period == "weekly") {
        pr = this.$t("plant.weekly");
      } else {
        pr = this.$t("plant.monthly");
      }
      return pr;
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>
