<template>
  <div
    class="modal fade"
    id="add-market"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-market-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-market-label">
            {{ $t("market.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="post()">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("market.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="body.name"
                    :placeholder="$t('market.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="address">{{ $t("market.address") }}</label>
                  <input
                    id="address"
                    type="text"
                    class="form-control"
                    v-model="body.address"
                    :placeholder="$t('market.address')"
                    required
                  />
                </div>
              </div>
              <div
                class="col-md-12"
                v-for="(phone, i) in body.phones"
                :key="phone"
              >
                <div class="form-group">
                  <label :for="`phone${i}`"
                    >{{ $t("market.phone") }}
                    {{ body.phones.length > 1 ? i + 1 : "" }}</label
                  >
                  <div class="input-group">
                    <select
                      class="form-control w-custom-40"
                      v-model="phone.dial_code"
                      required
                    >
                      <option
                        :value="code.dial_code"
                        v-for="code in phone_codes"
                        :key="code"
                      >
                        {{ code.dial_code }}
                      </option>
                    </select>
                    <input
                      :id="`phone${i}`"
                      type="number"
                      class="form-control"
                      :class="body.phones.length > 1 ? 'w-50' : 'w-75'"
                      v-model="phone.phone"
                      :placeholder="$t('market.phone')"
                      min="1"
                      required
                    />
                    <button
                      class="btn bg-gradient-danger mb-0"
                      type="button"
                      v-if="i || (!i && body.phones.length > 1)"
                      @click="body.phones.splice(i, 1)"
                    >
                      <i class="fa-solid fa-circle-xmark"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              class="btn bg-gradient-primary"
              type="button"
              @click="body.phones.push({ phone: '', dial_code: '+7' })"
            >
              <i class="fa-solid fa-phone"></i>
            </button>
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="put-market"
    tabindex="-1"
    role="dialog"
    aria-labelledby="put-market-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="put-market-label">
            {{ $t("market.update") }}
          </h5>
        </div>
        <form @submit.prevent="put()">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("market.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="data.name"
                    :placeholder="$t('market.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="address">{{ $t("market.address") }}</label>
                  <input
                    id="address"
                    type="text"
                    class="form-control"
                    v-model="data.address"
                    :placeholder="$t('market.address')"
                    required
                  />
                </div>
              </div>
              <div
                class="col-md-12"
                v-for="(phone, i) in data.phones"
                :key="phone"
              >
                <div class="form-group">
                  <label :for="`phone${i}`"
                    >{{ $t("market.phone") }}
                    {{ data.phones.length > 1 ? i + 1 : "" }}</label
                  >
                  <div class="input-group">
                    <select
                      class="form-control w-custom-40"
                      v-model="phone.dial_code"
                      required
                    >
                      <option
                        :value="code.dial_code"
                        v-for="code in phone_codes"
                        :key="code"
                      >
                        {{ code.dial_code }}
                      </option>
                    </select>
                    <input
                      :id="`phone${i}`"
                      type="number"
                      class="form-control"
                      :class="data.phones.length > 1 ? 'w-50' : 'w-75'"
                      v-model="phone.value"
                      :placeholder="$t('market.phone')"
                      min="1"
                      required
                    />
                    <button
                      class="btn bg-gradient-danger mb-0"
                      type="button"
                      v-if="i || (!i && data.phones.length > 1)"
                      @click="data.phones.splice(i, 1)"
                    >
                      <i class="fa-solid fa-circle-xmark"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              class="btn bg-gradient-primary"
              type="button"
              @click="data.phones.push({ value: '', id: 0, dial_code: '+7' })"
            >
              <i class="fa-solid fa-phone"></i>
            </button>
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-put
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("update") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import codes from "@/language/CountryPhoneCode.json";
import * as api from "@/server/api";
export default {
  name: "customer-modal",
  data() {
    return {
      body: {
        name: "",
        address: "",
        phones: [{ phone: "", dial_code: "+7" }],
      },
      phone_codes: codes,
      data: {
        balance: 0,
        name: "string",
        user_id: 1,
        id: 1,
        address: "string",
        phones: [
          {
            id: 259,
            owner_id: 1,
            number: "+65366566565",
            ownerType: "market",
          },
          {
            id: 258,
            owner_id: 1,
            number: "+8956658985",
            ownerType: "market",
          },
        ],
        user: {
          id: 1,
          kpi: null,
          market_id: 0,
          name: " ",
          username: "abdulxamid",
          address: "",
          nation: "o`zbek",
          passwordHash:
            "$2y$13$z9EjtYSyZEO1/yVT0ZjicezVOTYtVccXzuW8uuzQl1sQQdWvIdlvG",
          birthDate: "1976-06-04",
          disabled: false,
          specialty: " ",
          role: "admin",
          passport:
            "4d2d51bd-946c-4367-83ea-0d158e3da4ab__logo-f9.23527fab.png",
          agreement_expiration: "2030-01-01",
          workBeginDate: "2018-12-25",
          balance: 2378537.9,
          salary: 0,
          phones: [
            {
              id: 253,
              owner_id: 1,
              number: "+77012571500",
              ownerType: "user",
            },
          ],
        },
      },
    };
  },
  methods: {
    setData(data) {
      this.data = data;
      this.get_property({
        id: this.data.id,
        name: this.data.name,
        address: this.data.address,
        phones: this.data.phones,
      });
    },
    get_property(body) {
      body.phones.forEach((element) => {
        element.id = 0;
        if (element.number.slice(0, 4) == "+998") {
          element.dial_code = "+998";
          let num = element.number.split("+998");
          element.value = num[1];
        } else if (element.number.slice(0, 4) == "+992") {
          element.dial_code = "+992";
          let num = element.number.split("+992");
          element.value = num[1];
        } else if (element.number.slice(0, 4) == "+996") {
          element.dial_code = "+996";
          let num = element.number.split("+996");
          element.value = num[1];
        } else if (element.number.slice(0, 4) == "+993") {
          element.dial_code = "+993";
          let num = element.number.split("+993");
          element.value = num[1];
        } else if (element.number.slice(0, 2) == "+7") {
          element.dial_code = "+7";
          let num = element.number.split("+7");
          element.value = num[1];
        } else {
          element.dial_code = "";
          element.value = element.number;
        }
      });
      this.data = body;
    },
    post() {
      this.body.phones.forEach((element) => {
        element.phone = `${element.dial_code}${element.phone}`;
      });
      api.create_market(this.body).then(() => {
        this.body.name = "";
        this.body.type = "";
        this.body.phones = [{ phone: "", dial_code: "+7" }];
        api.success("close").then(() => {
          this.$parent.getMarkets(1, 25);
        });
      });
    },
    put() {
      this.data.phones.forEach((element) => {
        element.phone = element.dial_code + "" + element.value;
      });
      api.update_market(this.data).then(() => {
        api.success("close-put").then(() => {
          this.$parent.getMarkets(1, 25);
        });
      });
    },
  },
};
</script>
