<template>
  <div class="container-fluid py-4">
    <div class="card">
      <div class="card-header">
        <h6 class="m-0">{{ $t("return.title") }}</h6>
      </div>
      <div class="card-body">
        <div class="table-responsive">
          <table class="table table-sm table-hover">
            <thead>
              <tr>
                <th>{{ $t("return.table.image") }}</th>
                <th>{{ $t("return.table.product") }}</th>
                <th>{{ $t("return.table.value") }}</th>
                <th>{{ $t("return.table.payment") }}</th>
                <th>{{ $t("return.table.date") }}</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in returned_products.data" :key="item">
                <td>
                  <img
                    :src="
                      image +
                      '/products/' +
                      item.Returned_products.trade.product_store.product.image
                    "
                    :alt="
                      item.Returned_products.trade.product_store.product.image
                    "
                  />
                </td>
                <td>
                  {{ item.Returned_products.trade.product_store.product.name }}
                </td>
                <td>
                  {{
                    item.sum_quantity +
                    " " +
                    item.Returned_products.trade.product_store.product.olchov
                      .name
                  }}
                </td>
                <td>
                  {{ item.Returned_products.price + " " + $t("currency") }}
                </td>
                <td>{{ item.Returned_products.time }}</td>
                <td>
                  <button
                    class="btn btn-sm bg-gradient-primary m-0 px-2 py-1"
                    @click="
                      $refs.orderModal.start(
                        item.Returned_products.trade.order_id
                      )
                    "
                  >
                    <i class="fa fa-eye"></i>
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
  <orderModal ref="orderModal" />
</template>

<script>
import * as api from "@/server/api.js";
import orderModal from "@/components/modals/orderModal.vue";
export default {
  name: "return",
  components: { orderModal },
  data() {
    return {
      image: api.image,
      returned_products: {
        current_page: 1,
        pages: 1,
        limit: 25,
        data: [],
      },
    };
  },
  created() {
    this.getReturnedProducts(1, 25);
  },
  methods: {
    getReturnedProducts(page = 1, limit = 25) {
      api
        .returned_products(0, this.$route.query.market_id, page, limit)
        .then((res) => {
          this.returned_products = res.data;
        });
    },
  },
};
</script>

<style scoped>
img {
  width: 40px;
  height: 40px;
  border-radius: 10px;
  object-fit: cover;
}
</style>
