<template>
  <div
    class="modal fade"
    id="proportions"
    tabindex="-1"
    role="dialog"
    aria-labelledby="proportions-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="proportions-label">
            {{ $t("olchov.proportions.title") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row d-flex justify-content-center card-zoom">
            <div class="col-lg-12">
              <form @submit.prevent="add()">
                <div class="form-group">
                  <div class="input-group">
                    <span class="input-group-text"
                      >1 {{ olchov?.name }} <span class="ms-2">=</span></span
                    >
                    <input
                      type="number"
                      :placeholder="$t('olchov.proportions.value')"
                      class="form-control"
                      v-model="body.value"
                      @focus="
                        body.value == 0 ? (body.value = null) : body.value
                      "
                      @focusout="
                        body.value == null ? (body.value = 0) : body.value
                      "
                      required
                    />
                    <select
                      class="form-select"
                      @change="body.rel_olchov_id = Number($event.target.value)"
                      id="rel_olchov_id"
                      required
                    >
                      <option selected disabled>
                        {{ $t("olchov.proportions.olchov") }}
                      </option>
                      <option
                        :value="olchov.id"
                        v-for="olchov in olchovs_filter"
                        :key="olchov"
                      >
                        {{ olchov.name }}
                      </option>
                    </select>
                    <button class="btn bg-gradient-primary mb-0" type="submit">
                      <i class="fas fa-circle-plus"></i>
                    </button>
                  </div>
                </div>
              </form>
            </div>
            <div class="col-lg-12 mt-1">
              <ul class="list-group">
                <li
                  class="list-group-item list-group-item-action"
                  v-for="proportion in proportions"
                  :key="proportion"
                >
                  <div v-if="proportion_id != proportion.olchov_proportion_id">
                    1 {{ olchov?.name }} =
                    {{ proportion.value + " " + proportion.olchov_name }}
                    <p
                      class="mb-0 text-sm cursor-pointer text-warning float-end"
                      @click="proportion_id = proportion.olchov_proportion_id"
                    >
                      <b><i class="fas fa-pen"></i> {{ $t("update") }}</b>
                    </p>
                  </div>
                  <div v-else>
                    <form @submit.prevent="put(proportion)">
                      <div class="form-group mb-0">
                        <div class="input-group">
                          <span class="input-group-text"
                            >1 {{ olchov?.name }}
                            <span class="ms-2">=</span></span
                          >
                          <input
                            type="number"
                            :placeholder="$t('olchov.proportions.value')"
                            class="form-control"
                            v-model="proportion.put_value"
                            required
                          />
                          <select
                            class="form-select"
                            v-model="proportion.put_rel_olchov_id"
                            required
                          >
                            <option
                              :value="olchov.id"
                              v-for="olchov in olchovs_filter"
                              :key="olchov"
                            >
                              {{ olchov.name }}
                            </option>
                          </select>
                          <button
                            class="btn bg-gradient-success mb-0"
                            type="submit"
                          >
                            <i class="fas fa-circle-check"></i>
                          </button>
                          <button
                            class="btn bg-gradient-danger mb-0"
                            type="button"
                            @click="proportion_id = null"
                          >
                            <i class="fas fa-circle-xmark"></i>
                          </button>
                        </div>
                      </div>
                    </form>
                  </div>
                </li>
                <li class="list-group-item list-group-item-action p-0">
                  <data-notfound :data="proportions" />
                  <pagination
                    :page="page"
                    :pages="pages"
                    :limit="limit"
                    @get="get"
                  />
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import Pagination from "@/components/pagination.vue";
import DataNotfound from "@/components/dataNotfound.vue";

export default {
  name: "olchov-proportion",
  props: ["olchovs", "id"],
  data() {
    return {
      pages: 0,
      page: 1,
      limit: 10,
      proportions: [],
      body: {
        abs_olchov_id: null,
        rel_olchov_id: null,
        value: 0,
      },
      proportion_id: null,
    };
  },
  watch: {
    id(e) {
      this.body.abs_olchov_id = e;
      this.get(this.page, this.limit);
    },
  },
  methods: {
    get(page, limit) {
      api.get_olchov_proportions(page, limit, this.id).then((res) => {
        res.data.data.forEach((element) => {
          element.put_value = element.value;
          element.put_rel_olchov_id = element.olchov_id;
        });
        this.pages = res.data.page_count;
        this.page = res.data.current_page;
        this.limit = res.data.page_limit;
        this.proportions = res.data.data;
      });
    },
    add() {
      api.create_olchov_proportion(this.body).then(() => {
        this.body.value = 0;
        this.body.rel_olchov_id = null;
        document.getElementById("rel_olchov_id").value = this.$t(
          "olchov.proportions.olchov"
        );
        api.success();
        this.get(this.page, this.limit);
      });
    },
    put(data) {
      let body = {
        id: data.olchov_proportion_id,
        abs_olchov_id: this.id,
        rel_olchov_id: data.put_rel_olchov_id,
        value: data.put_value,
      };
      api.put_olchov_proportion(body).then(() => {
        api.success();
        this.proportion_id = null;
        this.get(this.page, this.limit);
      });
    },
  },
  computed: {
    olchovs_filter() {
      return this.olchovs.filter((item) => item.id !== this.id);
    },
    olchov() {
      return this.olchovs.find((item) => item.id == this.id);
    },
  },
  components: { Pagination, DataNotfound },
};
</script>
