<template>
  <div class="fixed-plugin">
    <a
      class="px-3 py-2 fixed-plugin-button text-dark position-fixed"
      @click="toggle"
    >
      <i class="py-2 fa fa-cog"></i>
    </a>
    <div class="shadow-lg card">
      <div class="pt-3 pb-0 bg-transparent card-header">
        <div class="float-start">
          <h5 class="mt-3 mb-0">{{ $t("configurator.title") }}</h5>
          <p>{{ $t("configurator.description") }}</p>
        </div>
        <div class="mt-4 float-end" @click="toggle">
          <i
            class="top-0 p-3 cursor-pointer fas fa-times text-secondary opacity-5"
            aria-hidden="true"
          ></i>
        </div>
        <!-- End Toggle Button -->
      </div>
      <hr class="my-1 horizontal dark" />
      <div class="pt-0 card-body pt-sm-3">
        <!-- Sidebar Backgrounds -->
        <div>
          <h6 class="mb-0">{{ $t("configurator.sidebar_color") }}</h6>
        </div>
        <a href="#" class="switch-trigger background-color">
          <div class="my-2 badge-colors text-start">
            <span
              class="badge filter bg-gradient-primary"
              :class="mcolor == 'primary' ? 'active' : ''"
              data-color="primary"
              @click="sidebarColor('primary')"
            ></span>
            <span
              class="badge filter bg-gradient-dark"
              :class="mcolor == 'dark' ? 'active' : ''"
              data-color="dark"
              @click="sidebarColor('dark')"
            ></span>
            <span
              class="badge filter bg-gradient-info"
              :class="mcolor == 'info' ? 'active' : ''"
              data-color="info"
              @click="sidebarColor('info')"
            ></span>
            <span
              class="badge filter bg-gradient-success"
              :class="mcolor == 'success' ? 'active' : ''"
              data-color="success"
              @click="sidebarColor('success')"
            ></span>
            <span
              class="badge filter bg-gradient-warning"
              :class="mcolor == 'warning' ? 'active' : ''"
              data-color="warning"
              @click="sidebarColor('warning')"
            ></span>
            <span
              class="badge filter bg-gradient-danger"
              :class="mcolor == 'danger' ? 'active' : ''"
              data-color="danger"
              @click="sidebarColor('danger')"
            ></span>
          </div>
        </a>
        <hr class="horizontal dark my-4" />
        <div class="mt-2 d-flex">
          <h6 class="mb-0">{{ $t("configurator.mode") }}</h6>
          <div class="form-check form-switch ps-0 ms-auto my-auto">
            <input
              class="form-check-input mt-1 ms-auto"
              type="checkbox"
              :checked="this.$store.state.darkMode"
              @click="setDarkMode"
            />
          </div>
        </div>
        <hr class="horizontal dark my-4" />
        <div class="mt-2 mb-5">
          <switch-language />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import { activateDarkMode, deactivateDarkMode } from "@/assets/js/dark-mode";
import switchLanguage from "../components/switchLanguage.vue";
export default {
  components: { switchLanguage },
  name: "configurator",
  props: ["toggle"],
  data() {
    return {
      mcolor: localStorage.getItem("mcolor"),
    };
  },
  methods: {
    ...mapMutations(["navbarMinimize", "sidebarType", "navbarFixed"]),
    sidebarColor(color = "success") {
      this.mcolor = color;
      localStorage.setItem("mcolor", color);
      document
        .querySelector("#sidenav-main")
        .setAttribute("data-color", localStorage.getItem("mcolor"));
      this.$store.state.mcolor = `card-background-mask-${localStorage.getItem(
        "mcolor"
      )}`;
    },
    sidebarType(type) {
      this.$store.state.sidebarType = type;
    },
    setNavbarFixed() {
      if (
        this.$route.name !== "Profile" ||
        this.$route.name !== "All Projects"
      ) {
        this.$store.state.isNavFixed = !this.$store.state.isNavFixed;
      }
    },
    setDarkMode() {
      if (this.$store.state.darkMode) {
        localStorage.setItem("darkmode", false);
        localStorage.setItem("sidebartype", "bg-white");
        this.$store.state.darkMode = false;
        this.$store.state.sidebarType = "bg-white";
        deactivateDarkMode();
        return;
      } else {
        localStorage.setItem("darkmode", true);
        localStorage.setItem("sidebartype", "bg-default");
        this.$store.state.darkMode = true;
        this.$store.state.sidebarType = "bg-default";
        activateDarkMode();
      }
    },
    close_outside(event) {
      const offcanvas = document.querySelector(".fixed-plugin");
      if (event.target.tagName != "I") {
        if (
          event.target.closest(".fixed-plugin") != offcanvas &&
          offcanvas.classList[1] == "show"
        ) {
          this.$store.state.showConfig = !this.$store.state.showConfig;
        }
      }
    },
  },
  beforeMount() {
    this.$store.state.isTransparent = "bg-transparent";
    window.addEventListener("resize", this.sidenavTypeOnResize);
    window.addEventListener("load", this.sidenavTypeOnResize);
  },
  mounted() {
    document.addEventListener("click", this.close_outside);
  },
};
</script>
