<template>
  <div class="container-fluid py-4">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-2 my-1">
            <h6 class="mb-0">{{ $t("trade.title") }}</h6>
          </div>
          <div :class="role != 'analyst' ? 'col-md-3' : 'col-md-6'"></div>
          <div class="col-md my-1" v-if="role != 'analyst'">
            <button
              class="btn btn-sm bg-gradient-success m-0 w-100"
              data-bs-toggle="modal"
              data-bs-target="#add-market"
            >
              {{ $t("trade.add-shop") }}
            </button>
          </div>
          <div class="col-md my-1">
            <input
              type="search"
              class="form-control form-control-sm"
              :placeholder="$t('search')"
              v-model="search"
              @keyup="
                getMarkets(1, 25);
                $store.dispatch('setloading', false);
              "
            />
          </div>
        </div>
      </div>
      <div class="card-body">
        <div class="row">
          <div class="col-md-3 my-2" v-for="item in markets.data" :key="item">
            <div class="card h-100">
              <div class="card-body">
                <p><i class="fa fa-bars p-1"></i>{{ item.name }}</p>
                <p><i class="fa fa-location-dot p-1"></i>{{ item.address }}</p>
              </div>
              <div class="card-footer pt-0">
                <div class="row">
                  <div class="col my-1">
                    <router-link
                      :to="{ path: 'market', query: { id: item.id } }"
                      class="btn btn-sm bg-gradient-primary m-0 w-100"
                    >
                      <i class="fa fa-eye"></i>
                    </router-link>
                  </div>
                  <div class="col my-1" v-if="role != 'analyst'">
                    <button
                      class="btn btn-sm bg-gradient-warning m-0 w-100"
                      data-bs-toggle="modal"
                      data-bs-target="#put-market"
                      @click="$refs.marketModals.setData(item)"
                    >
                      <i class="fa fa-edit"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-12">
            <pagination
              :page="markets.current_page"
              :pages="markets.pages"
              :limit="markets.limit"
              @get="getMarkets"
            />
          </div>
        </div>
      </div>
    </div>
  </div>

  <marketModals ref="marketModals" />
</template>

<script>
import * as api from "../../server/api";
import pagination from "@/components/pagination.vue";
import marketModals from "@/components/modals/MarketModals.vue";
export default {
  name: "mainSection",
  components: { pagination, marketModals },
  data() {
    return {
      search: "",
      markets: {
        current_page: 1,
        pages: 1,
        limit: 25,
        data: [],
      },
      role: localStorage["role"],
    };
  },
  created() {
    this.getMarkets(1, 25);
  },
  methods: {
    getMarkets(page, limit) {
      api.markets(this.search, 0, page, limit).then((res) => {
        this.markets = res.data;
      });
    },
  },
};
</script>

<style scoped>
p {
  margin: 0;
}
</style>
