<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">{{ $route.query.name }}</h6>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col-md-6 my-1">
                <button
                  class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
                  data-bs-toggle="modal"
                  data-bs-target="#add-material"
                  @click="get_olchovs()"
                >
                  {{ $t("material.add-button") }}
                </button>
              </div>
              <div class="col-md-6 my-1">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    get(1, limit);
                    $store.dispatch('setloading', false);
                  "
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-lg-4 col-md-6 col-sm-12 my-2"
            v-for="material in materials"
            :key="material"
          >
            <div class="card card-zoom h-100 shadow-sm">
              <div class="card-body">
                <h5 class="card-title text-muted mb-0">
                  {{ material.name + " (" + material.olchov + ")" }}
                </h5>
                <hr class="my-1" />
                <p class="mb-0 text-sm">
                  <b class="text-secondary mr-2">
                    {{ $t("material.price") }}:
                    <span class="text-nowrap ms-1"
                      >{{ Intl.NumberFormat().format(material.price) }}
                      {{ $t("currency") }}</span
                    ></b
                  >
                </p>
                <p class="mb-0 text-sm">
                  <b class="text-secondary mr-2">
                    {{ $t("material.unit.title") }}:
                    <span class="text-nowrap ms-1">{{
                      material.unit
                        ? $t("material.unit.true")
                        : $t("material.unit.false")
                    }}</span></b
                  >
                </p>
                <p class="mb-0 text-sm">
                  <b class="text-secondary mr-2">
                    {{ $t("material.quantity.title") }}:
                    <span class="text-nowrap ms-1">{{
                      material.has_volume
                        ? $t("material.quantity.true")
                        : $t("material.quantity.false")
                    }}</span></b
                  >
                </p>
                <div class="row mt-1">
                  <div class="col-6">
                    <button
                      class="btn btn-sm mb-0 bg-gradient-warning w-100"
                      @click="
                        get_olchovs();
                        item = {
                          id: material.id,
                          name: material.name,
                          price: material.price,
                          olchov_id: material.olchov_id,
                          unit: material.unit,
                          has_volume: material.has_volume,
                          alarm_value: material.alarm_value,
                          material_type_id: $route.query.id,
                        };
                      "
                      data-bs-toggle="modal"
                      data-bs-target="#update-material"
                    >
                      <i class="fas fa-pen"></i>
                    </button>
                  </div>
                  <div class="col-6">
                    <button
                      class="btn btn-sm mb-0 bg-gradient-primary w-100"
                      data-bs-toggle="modal"
                      data-bs-target="#products"
                      @click="
                        $refs.product_material.body.id = material.id;
                        $refs.product_material.get(1, 10);
                      "
                    >
                      <i class="fas fa-box"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="materials" />
            <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
          </div>
        </div>
      </div>
    </div>
  </div>
  <div id="image-viewer">
    <span class="close" @click="image_view()">&times;</span>
    <img class="modal-content" id="full-image" />
  </div>
  <material-modals :olchovs="olchovs" @add="add" :item="item" @put="put" />
  <product-material ref="product_material" />
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";
import MaterialModals from "@/components/modals/MaterialModals.vue";
import ProductMaterial from "@/components/modals/ProductMaterial.vue";

export default {
  components: { dataNotfound, Pagination, MaterialModals, ProductMaterial },
  name: "Material",
  data() {
    return {
      materials: [],
      olchovs: [],
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      disabled: false,
      item: {},
      material_type_id: this.$route.query.id,
    };
  },
  methods: {
    get(page, limit) {
      api
        .get_materials(
          page,
          limit,
          this.search,
          this.material_type_id,
          this.disabled
        )
        .then((res) => {
          this.pages = res.data.page_count;
          this.page = res.data.current_page;
          this.limit = res.data.page_limit;
          this.materials = res.data.data;
        });
    },
    get_olchovs() {
      api.get_olchovs(1, 1000, "").then((res) => {
        this.olchovs = res.data.data;
      });
    },
    add(body) {
      api.create_material(body).then(() => {
        body.name = "";
        body.olchov_id = null;
        body.price = 0;
        body.unit = true;
        body.has_volume = true;
        api.success("close");
        this.get(this.page, this.limit);
      });
    },
    put(body) {
      api.put_material(body, body.id).then(() => {
        api.success("close-put");
        this.get(this.page, this.limit);
      });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>
