<template>
  <div
    class="modal fade"
    id="filter"
    tabindex="-1"
    role="dialog"
    aria-labelledby="filter-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title" id="filter-label">
            {{ $t("filter") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-6">
              <div class="form-group">
                <label for="from_date">{{ $t("from_date") }}</label>
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="filter_body.from_date"
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("to_date") }}</label>
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="filter_body.to_date"
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="plant">{{ $t("supply.plant") }}</label>
                <select
                  id="plant"
                  class="form-select"
                  v-model="filter_body.plant_id"
                >
                  <option
                    :value="plant.id"
                    v-for="plant in plants"
                    :key="plant"
                  >
                    {{ plant.name }}
                  </option>
                </select>
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label>{{ $t("supply.supplier") }}</label>
                <div class="dropdown">
                  <button
                    class="form-control border-secondary-custom shadow-none mb-0 w-100"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                    @click="
                      supplier.search = '';
                      get_supplier(false);
                    "
                  >
                    {{ filter_body.supplier_name }}
                  </button>
                  <ul
                    class="dropdown-menu w-100"
                    aria-labelledby="dropdownMenuButton1"
                  >
                    <input
                      type="text"
                      class="form-control"
                      :placeholder="$t('search')"
                      v-model="supplier.search"
                      @keyup="
                        get_materials(false);
                        $store.dispatch('setloading', false);
                      "
                    />
                    <div
                      class="scroll-25 supplier"
                      @scroll="
                        scroll_supplier();
                        $store.dispatch('setloading', false);
                      "
                    >
                      <li
                        @click="
                          filter_body.supplier_id = item.id;
                          filter_body.supplier_name = supplier.suppliers.find(
                            (e) => filter_body.supplier_id == e.id
                          ).name;
                        "
                        v-for="item in supplier.suppliers"
                        :key="item"
                      >
                        <a class="dropdown-item">
                          {{ item.name }}
                        </a>
                      </li>
                    </div>
                    <data-notfound :data="supplier.suppliers" class="text-lg" />
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-sm bg-gradient-danger"
            @click="
              filter_body = {
                supplier_id: 0,
                supplier_name: $t('supply.supplier'),
                plant_id: 0,
                from_date: '',
                to_date: '',
              };
              $emit('filter', filter_body, false);
            "
          >
            <i class="fas fa-trash"></i>
          </button>
          <button
            class="btn btn-sm bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-filter
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn btn-sm bg-gradient-primary"
            @click="$emit('filter', filter_body, true)"
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="filter-transfer"
    tabindex="-1"
    role="dialog"
    aria-labelledby="filter-transfer-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title" id="filter-transfer-label">
            {{ $t("filter") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-6">
              <div class="form-group">
                <label for="from_date">{{ $t("from_date") }}</label>
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="filter_transfer.from_date"
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("to_date") }}</label>
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="filter_transfer.to_date"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-sm bg-gradient-danger"
            @click="
              filter_transfer = {
                from_date: '',
                to_date: '',
              };
              $emit('transfer-filter', filter_transfer, false);
            "
          >
            <i class="fas fa-trash"></i>
          </button>
          <button
            class="btn btn-sm bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-filter-transfer
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn btn-sm bg-gradient-primary"
            @click="$emit('transfer-filter', filter_transfer, true)"
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="supply-update"
    tabindex="-1"
    role="dialog"
    aria-labelledby="supply-update-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title" id="supply-update-label">
            {{ $t("supply.update") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('supply-update', data)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-12">
                <div class="form-group">
                  <label for="price">{{ $t("supply.price") }}</label>
                  <div class="input-group">
                    <input
                      id="price"
                      type="number"
                      class="form-control"
                      v-model="data.price"
                      step="any"
                      :placeholder="$t('supply.price')"
                      required
                    />
                    <span class="input-group-text">{{ $t("currency") }}</span>
                  </div>
                </div>
              </div>
              <div class="col-md-12" :class="data.has_volume ? 'my-1' : ''">
                <div class="d-flex justify-content-end">
                  <div class="popover-custom" v-if="data.has_volume">
                    <div class="row">
                      <div class="col">
                        <div class="input-group input-group-sm">
                          <input
                            type="number"
                            class="form-control"
                            v-model="diametr"
                            @keyup="formula()"
                            :placeholder="$t('supply.diametr')"
                          />
                          <span class="input-group-text">{{
                            $t("supply.sm")
                          }}</span>
                        </div>
                      </div>
                      <div class="col">
                        <div class="input-group input-group-sm">
                          <input
                            type="number"
                            class="form-control"
                            v-model="length"
                            @keyup="formula()"
                            :placeholder="$t('supply.length')"
                          />
                          <span class="input-group-text">{{
                            $t("supply.meter")
                          }}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="value">{{ $t("supply.value") }}</label>
                  <div class="input-group">
                    <input
                      id="value"
                      type="number"
                      class="form-control"
                      v-model="data.value"
                      step="any"
                      :placeholder="$t('supply.value')"
                      required
                    />
                    <span class="input-group-text" v-if="data.olchov">{{
                      data.olchov
                    }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              class="btn btn-sm bg-gradient-secondary"
              data-bs-dismiss="modal"
              type="button"
              close-put
            >
              {{ $t("close") }}
            </button>
            <button class="btn btn-sm bg-gradient-primary" type="submit">
              {{ $t("update") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "../dataNotfound.vue";

export default {
  components: { dataNotfound },
  name: "supply-history",
  props: ["item"],
  emits: ["filter", "transfer-filter", "supply-update"],
  data() {
    return {
      supplier: {
        pages: 0,
        page: 1,
        search: "",
        suppliers: [],
        name: this.$t("supply.supplier"),
      },
      filter_body: {
        supplier_id: 0,
        supplier_name: this.$t("supply.supplier"),
        plant_id: 0,
        from_date: "",
        to_date: "",
      },
      filter_transfer: {
        from_date: "",
        to_date: "",
      },
      plants: [],
      diametr: null,
      length: null,
    };
  },
  methods: {
    // supplier
    get_supplier(scroll) {
      this.supplier.page = scroll ? this.supplier.page : 1;
      api
        .get_suppliers(
          scroll ? this.supplier.page + 1 : this.supplier.page,
          10,
          this.supplier.search
        )
        .then((res) => {
          this.supplier.pages = res.data.page_count;
          this.supplier.page = res.data.current_page;
          this.supplier.suppliers = scroll
            ? this.supplier.suppliers.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll_supplier() {
      let div = document.querySelector(".supplier");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.supplier.page < this.supplier.pages) {
          this.get_supplier(true);
        }
      }
    },
    get_plants() {
      api.get_plants(1, 1000, "").then((res) => {
        this.plants = res.data.data;
      });
    },
    formula() {
      var number = this.diametr * this.length * 0.9;
      this.data.value = api.number_format(number);
    },
  },
  computed: {
    data() {
      return this.item;
    },
  },
};
</script>
