<template>
  <div
    class="modal fade"
    id="customer-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="customer-modal-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-order-modal-label">
            {{ $t("order.customer") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row gap-1">
            <div class="col-12">
              <div class="dropdown">
                <button
                  id="customerDropdown"
                  class="btn btn-sm w-100 mb-0 dropdown-toggle"
                  data-bs-toggle="dropdown"
                  @click="getCustomers()"
                >
                  {{ customer ? customer?.name : $t("order.customer") }}
                </button>
                <div
                  class="dropdown-menu w-100 p-1"
                  aria-labelledby="customerDropdown"
                >
                  <input
                    type="text"
                    class="form-control form-control-sm my-1"
                    :placeholder="$t('search')"
                    v-model="search_customer"
                    @keyup="getCustomers()"
                  />
                  <ul
                    class="list-group scroll-25 p-1"
                    @scroll="scrollCustomers($event)"
                  >
                    <li
                      class="list-group-item"
                      v-for="item in customers?.data"
                      :key="item"
                      @click="customer = item"
                    >
                      {{ item.name }}
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-customer-order-modal
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn bg-gradient-primary"
            :disabled="!customer"
            @click="putOrderCustomer()"
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>

  <div
    class="modal fade"
    id="date-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="date-modal-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-order-modal-label">
            {{ $t("pre-order.table.date") }}
          </h5>
        </div>
        <form @submit.prevent="putOrderDate()">
          <div class="modal-body">
            <div class="row gap-1">
              <div class="col-12">
                <input
                  type="date"
                  class="form-control"
                  required
                  v-model="date"
                />
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-date-order-modal
            >
              {{ $t("close") }}
            </button>
            <button class="btn bg-gradient-primary">
              {{ $t("save") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>

  <div
    class="modal fade"
    id="image-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="image-modal-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-order-modal-label">
            {{ $t("pre-order.image") }}
          </h5>
        </div>
        <form @submit.prevent="postOrderImage()">
          <div class="modal-body">
            <div class="row gap-1">
              <div class="col-12">
                <input
                  type="file"
                  accept=".png, .jpeg"
                  class="form-control"
                  required
                  file
                />
              </div>
              <div class="col-12">
                <textarea
                  class="form-control"
                  :placeholder="$t('pre-order.placeholders.comment')"
                  required
                  v-model="image.comment"
                ></textarea>
              </div>
              <div class="col-12">
                <button class="btn bg-gradient-primary float-end">
                  {{ $t("add") }}
                </button>
              </div>
              <div class="col-12">
                <div class="row">
                  <div
                    class="col-4 py-2 image"
                    v-for="item in images"
                    :key="item"
                  >
                    <div class="delete" @click="deleteOrderImage(item.id)">
                      <i class="fa fa-trash fa-lg"></i>
                    </div>
                    <img
                      :src="image_url + '/order_files/' + item.file"
                      :alt="item.file"
                      @click="
                        image_view(image_url + '/order_files/' + item.file)
                      "
                    />
                    <p class="m-0">{{ item.comment }}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-image-order-modal
            >
              {{ $t("close") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>

  <div
    class="modal fade"
    id="pay-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="pay-modal-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-order-modal-label">
            {{ $t("pre-order.pay") }}
          </h5>
        </div>
        <form @submit.prevent="postOrderPay()">
          <div class="modal-body">
            <div class="row gap-1">
              <div class="col-12">
                <div class="input-group">
                  <input
                    type="number"
                    min="0"
                    step="any"
                    class="form-control"
                    @focus="pay.money == 0 ? (pay.money = null) : pay.money"
                    @focusout="pay.money == null ? (pay.money = 0) : pay.money"
                    :placeholder="$t('expense.money')"
                    required
                    v-model="pay.money"
                  />
                  <div class="input-group-text">{{ $t("currency") }}</div>
                </div>
              </div>
              <div class="col-12">
                <textarea
                  class="form-control"
                  :placeholder="$t('expense.comment')"
                  v-model="pay.comment"
                />
              </div>
              <div class="col-12">
                <button class="btn bg-gradient-primary float-end">
                  {{ $t("pre-order.pay") }}
                </button>
              </div>
            </div>
            <div class="table-responsive" style="max-height: 40vh">
              <table class="table table-sm table-hover">
                <thead>
                  <tr>
                    <th>{{ $t("pre-order.table.sum") }}</th>
                    <th>{{ $t("pre-order.table.comment") }}</th>
                    <th>{{ $t("pre-order.table.date") }}</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="item in pay_history.data" :key="item">
                    <td>
                      {{
                        Intl.NumberFormat().format(item.money) +
                        " " +
                        $t("currency")
                      }}
                    </td>
                    <td>{{ item.comment }}</td>
                    <td>{{ format_date(item.time) }}</td>
                  </tr>
                </tbody>
                <tfoot>
                  <tr>
                    <td colspan="3">
                      <pagination
                        :page="pay_history.current_page"
                        :pages="pay_history.pages"
                        :limit="pay_history.limit"
                        @get="getPayHistory"
                      />
                    </td>
                  </tr>
                </tfoot>
              </table>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-pay-order-modal
            >
              {{ $t("close") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>

  <div
    class="modal fade"
    id="expense-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="expense-modal-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-order-modal-label">
            {{ $t("pre-order.expense") }}
          </h5>
        </div>
        <form @submit.prevent="postExpense()">
          <div class="modal-body">
            <div class="row gap-1">
              <div class="col-12">
                <div class="input-group">
                  <input
                    type="number"
                    min="0"
                    step="any"
                    class="form-control"
                    :placeholder="$t('expense.money')"
                    required
                    @focus="
                      expense.money == 0
                        ? (expense.money = null)
                        : expense.money
                    "
                    @focusout="
                      expense.money == null
                        ? (expense.money = 0)
                        : expense.money
                    "
                    v-model="expense.money"
                  />
                  <div class="input-group-text">{{ $t("currency") }}</div>
                </div>
              </div>
              <div class="col-12">
                <textarea
                  class="form-control"
                  :placeholder="$t('expense.comment')"
                  v-model="expense.comment"
                />
              </div>
              <div class="col-12">
                <button class="btn bg-gradient-primary float-end">
                  {{ $t("pre-order.pay") }}
                </button>
              </div>
            </div>
            <div class="table-responsive" style="max-height: 40vh">
              <table class="table table-sm table-hover">
                <thead>
                  <tr>
                    <th>{{ $t("pre-order.table.sum") }}</th>
                    <th>{{ $t("pre-order.table.comment") }}</th>
                    <th>{{ $t("pre-order.table.date") }}</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="item in expense_history.data" :key="item">
                    <td>
                      {{
                        Intl.NumberFormat().format(item.money) +
                        " " +
                        $t("currency")
                      }}
                    </td>
                    <td>{{ item.comment }}</td>
                    <td>{{ format_date(item.time) }}</td>
                  </tr>
                </tbody>
                <tfoot>
                  <tr>
                    <td colspan="3">
                      <pagination
                        :page="expense_history.current_page"
                        :pages="expense_history.pages"
                        :limit="expense_history.limit"
                        @get="getPayHistory"
                      />
                    </td>
                  </tr>
                </tfoot>
              </table>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-expense-order-modal
            >
              {{ $t("close") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>

  <div id="image-viewer">
    <span class="close" @click="image_view()">&times;</span>
    <img class="modal-content" id="full-image" />
  </div>
</template>

<script>
import * as api from "../../server/api";
import pagination from "../../components/pagination.vue";
import setNavPills from "@/assets/js/nav-pills.js";
export default {
  name: "preOrderModals",
  components: { pagination },
  data() {
    return {
      image_url: api.image,
      search_customer: "",
      customers: null,
      customer: null,
      date: "",
      images: [],
      image: {
        comment: "",
        order_id: this.$route.query.id,
      },
      pay_history: {
        current_page: 1,
        pages: 1,
        limit: 25,
        data: [],
      },
      pay: {
        money: 0,
        source: this.$route.query.id,
        comment: "",
      },
      expense_history: {
        current_page: 1,
        pages: 1,
        limit: 25,
        data: [],
      },
      expense: {
        money: 0,
        type: "order_expense",
        source: this.$route.query.id,
        comment: "",
        kassa_id: 0,
      },
      format_date: api.format_date,
    };
  },
  created() {
    this.getCustomers();
    this.getOrderImages();
  },
  mounted() {
    setNavPills();
  },
  methods: {
    getCustomers() {
      api
        .customers(this.search_customer, 0, this.$route.query.market_id, 1, 25)
        .then((res) => {
          this.customers = res.data;
        });
    },
    scrollCustomers(event) {
      const element = event.target;
      if (element.scrollTop + element.clientHeight >= element.scrollHeight) {
        if (this.customers.current_page < this.customers.pages) {
          api
            .customers(
              this.search_customer,
              0,
              this.$route.query.market_id,
              this.customers.current_page,
              25
            )
            .then((res) => {
              this.customers.current_page = res.data.current_page;
              this.customers.pages = res.data.pages;
              this.customers.data = this.customers.data.concat(res.data.data);
            });
        }
      }
    },
    putOrderCustomer() {
      api
        .attach_customer_to_order({
          id: this.$route.query.id,
          customer_id: this.customer.id,
        })
        .then(() => {
          api.success("close-customer-order-modal").then(() => {
            this.customer = null;
            this.$parent.getOrder();
          });
        });
    },
    putOrderDate() {
      api
        .update_delivery_date_for_pre_order({
          id: this.$parent.order.id,
          delivery_date: this.date,
        })
        .then(() => {
          api.success("close-date-order-modal").then(() => {
            this.$parent.getOrder();
          });
        });
    },
    getOrderImages() {
      api.order_files(this.$route.query.id).then((res) => {
        this.images = res.data;
      });
    },
    postOrderImage() {
      let data = new FormData();
      data.append("file", document.querySelector("[file]").files[0]);
      data.append("comment", this.image.comment);
      data.append("order_id", this.image.order_id);
      api.create_order_file(data).then(
        () => {
          api.success().then(() => {
            document.querySelector("[file]").type = "text";
            document.querySelector("[file]").type = "file";
            this.image = {
              comment: "",
              order_id: this.$route.query.id,
            };
            this.getOrderImages();
          });
        },
        () => {
          document.querySelector("[file]").type = "text";
          document.querySelector("[file]").type = "file";
          this.image = {
            comment: "",
            order_id: this.$route.query.id,
          };
        }
      );
    },
    deleteOrderImage(id) {
      api.delete_order_file(id).then(() => {
        api.success().then(() => {
          this.getOrderImages();
        });
      });
    },
    getPayHistory(page, limit) {
      api
        .get_incomes(
          page,
          limit,
          "",
          "",
          this.$route.query.id,
          0,
          0,
          0,
          "order",
          this.$route.query.market_id
        )
        .then((res) => {
          this.pay_history = res.data;
        });
    },
    postOrderPay() {
      api.take_money_from_order(this.pay).then(() => {
        api.success().then(() => {
          this.pay = {
            money: 0,
            source: this.$route.query.id,
            comment: "",
          };
          this.getPayHistory(1, 25);
        });
      });
    },
    getExpenseHistory(page, limit) {
      api
        .get_expenses(
          page,
          limit,
          "",
          "",
          0,
          "order_expense",
          this.$route.query.id,
          this.$route.query.market_id
        )
        .then((res) => {
          this.expense_history = res.data;
        });
    },
    postExpense() {
      api.pay_for_expense(this.expense).then(() => {
        api.success().then(() => {
          this.expense = {
            money: 0,
            type: "order_expense",
            source: this.$route.query.id,
            comment: "",
            kassa_id: 0,
          };
          this.getExpenseHistory(1, 25);
        });
      });
    },
    image_view(src) {
      if (src) {
        document.getElementById("full-image").src = src;
        document.getElementById("image-viewer").style.display = "block";
      } else {
        document.getElementById("image-viewer").style.display = "none";
      }
    },
  },
};
</script>

<style scoped>
.image {
  position: relative;
  height: 150px;
  overflow: hidden;
}

.image:hover .delete {
  top: 0;
  right: 0;
}

.image p {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -100%;
  padding: 10px;
  color: whitesmoke;
  background-color: rgb(0, 0, 0, 0.5);
  transition: all 0.2s;
}

.image:hover p {
  bottom: 0;
}

.delete {
  position: absolute;
  top: -100%;
  right: -100%;
  color: red;
  cursor: pointer;
  transition: all 0.2s;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.2s;
}
</style>
