<template>
  <div id="order-table" v-show="false">
    <table class="table table-bordered text-center">
      <thead>
        <tr>
          <th>
            <img
              src="
data:image/png;base64,




"
              alt="logo"
            />
            <br />
            <span v-for="item in market?.phones" :key="item">
              {{ item.number }}
              <br />
            </span>
          </th>
          <th>
            <h5 class="m-0">
                 {{ order?.order?.ordinal_number }}
            </h5>
          </th>
          <th>
            <h6 class="m-0">    </h6>
          </th>
        </tr>
        <tr>
          <th colspan="3">{{ market?.address }}</th>
        </tr>
        <tr>
          <th>
            
            <br />
            {{ order?.order?.customer ? order?.order?.customer.name : "" }}
          </th>
          <th>
            
            <br />
            <span
              v-for="item in order?.order?.customer?.phones || []"
              :key="item"
            >
              {{ item.number }}
              <br />
            </span>
          </th>
          <th>
             
            <br />
            {{ order?.order?.time?.substring(0, 10) }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in trades?.data" :key="item">
          <td colspan="3" class="">
            <div class="d-flex align-items-center justify-content-between">
              <span>
                {{
                  item.Trades.product_store
                    ? item.Trades.product_store?.product?.name
                    : item.Trades.product?.name
                }}
              </span>
              <span>
                {{
                  item.sum_quantity +
                  " " +
                  (item.Trades.product_store
                    ? item.Trades.product_store?.product?.olchov.name
                    : item.Trades.product?.olchov.name)
                }}
              </span>
              <span v-if="order_type == 'pre-order'">
                {{
                  currency.format(
                    trades?.data2
                      ? trades?.data2.find(
                          (item1) => item1.trade_id == item.Trades.id
                        ).sum_price
                      : 0
                  ) +
                  " " +
                  $t("currency")
                }}
              </span>
              <span v-if="order_type == 'order'">
                {{
                  currency.format(item.Trades.price * item.sum_quantity) +
                  " " +
                  $t("currency")
                }}
                <br />
                <span v-if="item.Trades.discount">
                  {{
                    "- " +
                    currency.format(item.Trades.discount * item.sum_quantity) +
                    " " +
                    $t("currency")
                  }}
                  <br />
                  {{
                    currency.format(
                      (item.Trades.price - item.Trades.discount) *
                        item.sum_quantity
                    ) +
                    " " +
                    $t("currency")
                  }}
                </span>
              </span>
            </div>
            <div v-if="order_type == 'pre-order'">
              <ul class="list-group mt-2">
                <li
                  class="list-group-item p-1 d-flex align-items-center justify-content-between"
                  v-for="item1 in item.Trades.trade_items"
                  :key="item1"
                >
                  <span class="p-2">
                    <p class="m-0 text-start">
                      {{
                        item1.component_item.product
                          ? item1.component_item.product?.name
                          : item1.component_item.material?.name
                      }}
                    </p>
                    <p class="m-0 text-start">
                      {{
                        item1.component_item.product
                          ? item1.trade_items_size.length
                            ? (item1.trade_items_size[0].size1
                                ? `${$t("pre-order.placeholders.width")}: ${
                                    item1.trade_items_size[0].size1
                                  }cm`
                                : ``) +
                              (item1.trade_items_size[0].size2
                                ? `, ${$t("pre-order.placeholders.height")}: ${
                                    item1.trade_items_size[0].size2
                                  }cm`
                                : ``) +
                              (item1.trade_items_size[0].comment
                                ? `, ${$t("pre-order.placeholders.comment")}: ${
                                    item1.trade_items_size[0].comment
                                  }`
                                : ``)
                            : ``
                          : item1.trade_items_size.length
                          ? (item1.trade_items_size[0].size1
                              ? `${$t("pre-order.placeholders.width")}: ${
                                  item1.trade_items_size[0].size1
                                }cm`
                              : ``) +
                            (item1.trade_items_size[0].size2
                              ? `, ${$t("pre-order.placeholders.height")}: ${
                                  item1.trade_items_size[0].size2
                                }cm`
                              : ``) +
                            (item1.trade_items_size[0].comment
                              ? `, ${$t("pre-order.placeholders.comment")}: ${
                                  item1.trade_items_size[0].comment
                                }`
                              : ``)
                          : ``
                      }}
                    </p>
                  </span>
                  <span>
                    {{
                      item1.quantity +
                      " " +
                      (item1.component_item.product
                        ? item1.component_item.product?.olchov.name
                        : item1.component_item.material?.olchov.name) +
                      " x " +
                      Intl.NumberFormat().format(item1.price) +
                      " " +
                      $t("currency")
                    }}
                  </span>
                </li>
              </ul>
            </div>
          </td>
        </tr>
      </tbody>
      <tfoot>
        <tr>
          <td>  </td>
          <td colspan="2">
            {{ currency.format(balance) + " " + $t("currency") }}
          </td>
        </tr>
        <tr>
          <td> </td>
          <td colspan="2">
            {{ currency.format(order?.sum_income) + " " + $t("currency") }}
          </td>
        </tr>
        <tr>
          <td> </td>
          <td colspan="2">
            {{
              currency.format(balance - order?.sum_income) +
              " " +
              $t("currency")
            }}
          </td>
        </tr>
      </tfoot>
    </table>
    <div class="p-3 text-end">
      <p class="m-0">
         
        <span class="fs-3">____________________</span>
      </p>
      <p class="m-0">
        
        <span class="fs-3 position-relative"
          >____________________
          <img
            class="position-absolute start-0"
            style="width: 200px !important; height: 70px; top: -50%"
            src="data:image/png;base64,"
          />
        </span>
      </p>
      <p class="m-0">
         
        <span class="fs-3">____________________</span>
      </p>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api.js";
export default {
  name: "orderCheck",
  props: {
    marketId: { required: true },
    orderId: { required: true },
  },
  data() {
    return {
      currency: Intl.NumberFormat(),
      image: api.image,
      market: null,
      order: null,
      order_type: "",
      balance: null,
      trades: { data: [], data2: [] },
    };
  },
  computed: {
    market_id() {
      return this.$props.marketId;
    },
    order_id() {
      return this.$props.orderId;
    },
    window_link() {
      return `
        <link
          rel="stylesheet"
          href="https://cdnjs.cloudflare.com/ajax/libs/bootstrap/5.0.1/css/bootstrap.min.css"
          integrity="sha512-Ez0cGzNzHR1tYAv56860NLspgUGuQw16GiOOp/I2LuTmpSK9xDXlgJz3XN4cnpXWDmkNBKXR/VDMTCnAaEooxA=="
          crossorigin="anonymous"
          referrerpolicy="no-referrer"
        />`;
    },
    window_style() {
      return `
        <style>
          img {
            width: 50px;
            height: 50px;
            object-fit: contain;
          }
        </style>`;
    },
    window_body() {
      return document.getElementById("order-table").innerHTML;
    },
  },
  created() {},
  methods: {
    start() {
      this.getMarket();
      this.getOrder();
    },
    getMarket() {
      api.markets("", this.market_id, 1, 1).then((res) => {
        this.market = res.data;
      });
    },
    getOrder() {
      api
        .orders("", "", 0, 0, "", this.order_id, this.market_id, 1, 1)
        .then((res) => {
          this.order = res.data;
          this.order_type =
            this.order.order.delivery_date == "0000-00-00"
              ? "order"
              : "pre-order";
          this.getBalance();
        });
    },
    getBalance() {
      api.trades(this.order.order.id, false, 1, 1, 25).then((res) => {
        this.balance = res.data;
        this.getTrades();
      });
    },
    getTrades() {
      const related_number = this.order_type == "order" ? true : false;
      api
        .trades(this.order.order.id, related_number, 0, 1, 1000)
        .then((res) => {
          this.trades = res.data;
          this.printOrder();
        });
    },
    printOrder() {
      setTimeout(() => {
        const win_print = window.open();
        win_print.document.head.innerHTML =
          this.window_link + this.window_style;
        win_print.document.body.innerHTML = this.window_body;
        setTimeout(() => {
          win_print.print();
          win_print.close();
        }, 100);
      }, 100);
    },
  },
};
</script>

<style scoped></style>
