<template>
  <div
    class="modal fade"
    id="take-money"
    tabindex="-1"
    role="dialog"
    aria-labelledby="take-money-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="take-money-label">
            {{ $t("cashier.take-money") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('take', body)">
          <div class="modal-body">
            <div class="form-group">
              <label for="money">{{ $t("cashier.money") }}</label>
              <div class="input-group">
                <input
                  id="money"
                  type="number"
                  class="form-control"
                  step="any"
                  v-model="body.money"
                  @focus="body.money == 0 ? (body.money = null) : body.money"
                  @focusout="body.money == null ? (body.money = 0) : body.money"
                  :placeholder="$t('cashier.money')"
                  :max="max"
                  required
                />
                <span class="input-group-text">{{ $t("currency") }}</span>
              </div>
            </div>
            <div class="form-group">
              <label for="kassa">{{ $t("cashier.title") }}</label>
              <select class="form-select" v-model="body.kassa_id" required>
                <option
                  :value="cashier.id"
                  v-for="cashier in cashiers"
                  :key="cashier"
                >
                  {{ cashier.name }}
                </option>
              </select>
            </div>
            <div class="form-group">
              <label for="comment">{{ $t("cashier.comment") }}</label>
              <textarea
                id="comment"
                class="form-control"
                v-model="body.comment"
                :placeholder="$t('cashier.comment')"
                required
              />
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-take-money
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("save") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="give-money"
    tabindex="-1"
    role="dialog"
    aria-labelledby="give-money-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="give-money-label">
            {{ $t("expense.pay") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('pay', pay)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="kassa">{{ $t("expense.kassa") }}</label>
                  <select
                    id="kassa"
                    class="form-control"
                    v-model="pay.kassa_id"
                    required
                  >
                    <option
                      :value="cashreg.id"
                      v-for="cashreg in cashiers"
                      :key="cashreg"
                    >
                      {{ cashreg.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="money">{{ $t("expense.money") }}</label>
                  <div class="input-group">
                    <input
                      id="money"
                      type="number"
                      class="form-control"
                      step="any"
                      v-model="pay.money"
                      @focus="pay.money == 0 ? (pay.money = null) : pay.money"
                      @focusout="
                        pay.money == null ? (pay.money = 0) : pay.money
                      "
                      :placeholder="$t('expense.money')"
                      required
                    />
                    <span class="input-group-text">{{ $t("currency") }}</span>
                  </div>
                </div>
              </div>
              <div class="col-12">
                <div class="form-group">
                  <label for="comment">{{ $t("expense.comment") }}</label>
                  <textarea
                    id="comment"
                    class="form-control"
                    v-model="pay.comment"
                    :placeholder="$t('expense.comment')"
                    required
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-pay
            >
              {{ $t("close") }}
            </button>
            <button
              type="submit"
              class="btn bg-gradient-primary"
              :disabled="!pay.money"
            >
              {{ $t("save") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "take-money-from-user",
  props: ["cashiers"],
  emits: ["take", "pay"],
  data() {
    return {
      body: {
        money: 0,
        user_id: null,
        kassa_id: null,
        comment: "",
      },
      max: null,
      pay: {
        money: 0,
        type: "user_expense",
        source: null,
        comment: "",
        kassa_id: null,
      },
    };
  },
};
</script>
