<template>
  <div
    class="modal fade"
    id="add-plant-user"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-plant-user-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-plant-user-label">
            {{ $t("plant.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('add', body)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="admin">{{ $t("plant.role.title") }}</label>
                  <select
                    id="admin"
                    class="form-select"
                    v-model="body.admin"
                    @change="get(false)"
                  >
                    <option value="true">
                      {{ $t("plant.role.admin") }}
                    </option>
                    <option value="false">
                      {{ $t("plant.role.worker") }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <label for="kpi">{{ $t("plant.name") }}</label>
                <div class="dropdown d-flex">
                  <button
                    class="form-control border-secondary-custom shadow-none mb-0 w-100"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                    :disabled="!body.admin"
                  >
                    {{ body.name }}
                  </button>
                  <ul
                    class="dropdown-menu w-100"
                    aria-labelledby="dropdownMenuButton1"
                  >
                    <input
                      type="text"
                      class="form-control"
                      :placeholder="$t('search')"
                      v-model="search"
                      @keyup="
                        get(false);
                        $store.dispatch('setloading', false);
                      "
                    />
                    <div
                      class="scroll-25"
                      @scroll="
                        scroll();
                        $store.dispatch('setloading', false);
                      "
                    >
                      <li
                        @click="
                          body.user_id = user.id;
                          body.name = users.find(
                            (e) => body.user_id == e.id
                          ).name;
                        "
                        v-for="user in users"
                        :key="user"
                      >
                        <a class="dropdown-item"> {{ user.name }} </a>
                      </li>
                    </div>
                    <data-notfound :data="users" class="text-lg" />
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button
              type="submit"
              class="btn bg-gradient-primary"
              :disabled="!body.admin || !body.user_id"
            >
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";

export default {
  components: { dataNotfound },
  props: ["shifts"],
  emits: ["add"],
  data() {
    return {
      body: {
        plant_id: this.$route.query.id,
        user_id: null,
        admin: "",
        disabled: false,
        name: this.$t("plant.user"),
      },
      pages: 0,
      page: 1,
      search: "",
      users: [],
    };
  },
  methods: {
    get(scroll) {
      this.page = scroll ? this.page : 1;
      api
        .get_users(
          scroll ? this.page + 1 : this.page,
          10,
          this.search,
          false,
          this.body.admin == "true" ? "plant_admin" : "worker",
          false,
          0
        )
        .then((res) => {
          this.pages = res.data.page_count;
          this.page = res.data.current_page;
          this.users = scroll
            ? this.users.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll() {
      let div = document.querySelector(".scroll-25");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.page < this.pages) {
          this.get(true);
        }
      }
    },
  },
};
</script>
