<template>
  <div class="container-fluid py-3 mt-4" v-if="orders.length">
    <div class="row flex-nowrap">
      <div
        class="col-1 d-flex align-items-center justify-content-center border border-dark position-relative"
        style="height: 120px; width: 140px"
      >
        <div style="position: absolute; right: 3%; top: 3%">
          <button
            class="btn btn-sm bg-gradient-primary mb-0 px-3"
            data-bs-toggle="modal"
            data-bs-target="#add-plant"
            @click="select_removed_plants()"
          >
            <i class="fas fa-plus"></i>
          </button>
          <button
            :class="`btn btn-sm bg-gradient-${
              edit ? 'success' : 'warning'
            } mb-0 px-3 ms-1`"
            @click="edit = !edit"
          >
            <i :class="`fas ${edit ? 'fa-check' : 'fa-pen'}`"></i>
          </button>
        </div>
        <h5 class="mb-0">#</h5>
      </div>
      <div
        class="col-1 d-flex align-items-center justify-content-center border border-dark"
        style="height: 120px; width: 140px"
      >
        <h5 class="mb-0">
          <i class="fa-regular fa-clock"></i>
        </h5>
      </div>
      <div
        class="col-1 d-flex align-items-center justify-content-center border border-dark"
        style="height: 120px; width: 140px"
        v-for="(plant, i) in orders[0]?.row"
        :key="plant"
      >
        <div
          class="d-flex"
          style="position: absolute; top: 0"
          role="group"
          v-if="edit"
        >
          <span
            class="badge bg-primary cursor-pointer"
            @click="changePostion(i, i - 1, 'left')"
            v-if="i != 0"
          >
            <i class="fa-solid fa-angle-left"></i>
          </span>
          <span class="badge bg-danger cursor-pointer" @click="remove(i)">
            <i class="fas fa-circle-xmark"></i>
          </span>
          <span
            class="badge bg-primary cursor-pointer"
            @click="changePostion(i, i + 1, 'right')"
            v-if="orders[0]?.row.length - 1 != i"
          >
            <i class="fa-solid fa-angle-right"></i>
          </span>
        </div>
        <h6 class="mb-0 text-center">
          {{ plant.plant }}
        </h6>
      </div>
    </div>
    <div class="row flex-nowrap" v-for="order in orders" :key="order">
      <div
        class="col-1 d-flex align-items-center justify-content-center border border-dark"
        style="height: 120px; width: 140px"
      >
        <h5 class="mb-0">{{ order.row[0].order_id }}</h5>
      </div>
      <div
        class="col-1 d-flex align-items-center justify-content-center border border-dark"
        style="height: 120px; width: 140px"
        :class="
          count_date(
            new Date().toLocaleDateString(),
            order.row[0].delivery_date
          ) > 10
            ? 'bg-success'
            : count_date(
                new Date().toLocaleDateString(),
                order.row[0].delivery_date
              ) <= 10 &&
              count_date(
                new Date().toLocaleDateString(),
                order.row[0].delivery_date
              ) > 5
            ? 'bg-warning'
            : 'bg-danger'
        "
      >
        <h6 class="mb-0 text-white">
          {{ new Date(order.row[0].delivery_date).toLocaleDateString("en-GB") }}
        </h6>
      </div>
      <div
        class="col-1 d-flex align-items-center justify-content-center border border-dark"
        style="height: 120px; width: 140px"
        v-for="plant in order.row"
        :key="plant"
        :class="
          plant.percentage >= 0 && plant.percentage <= 50
            ? 'bg-danger'
            : plant.percentage > 50 && plant.percentage <= 80
            ? 'bg-warning'
            : plant.percentage > 80
            ? 'bg-success'
            : 'bg-white'
        "
      >
        <h5 class="mb-0 text-white" v-if="plant.percentage >= 0">
          {{ plant.order_id }}
        </h5>
        <h5 class="mb-0 text-white" v-else></h5>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="add-plant"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-plant-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-plant-label">
            {{ $t("plant.add-button") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="list-group mt-2 overflow-auto" style="max-height: 72vh">
            <div
              class="list-group-item"
              v-for="plant in removed_plants"
              :key="plant"
            >
              <label class="d-flex align-items-center">
                <div class="form-check">
                  <input
                    class="form-check-input me-1"
                    type="checkbox"
                    :value="plant"
                    v-model="body"
                  />
                </div>
                <b>
                  {{ plant.name }}
                </b>
              </label>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-utilization
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn bg-gradient-primary"
            @click="add()"
            :disabled="!body.length"
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";

export default {
  name: "orders",
  data() {
    return {
      plants: [],
      orders: [],
      removed_plants: [],
      active_plants: JSON.parse(localStorage.getItem("plants")),
      body: [],
      count_date: api.countDays,
      edit: false,
    };
  },
  methods: {
    get_plants() {
      api.get_plants(1, 1000, "").then((res) => {
        this.plants = res.data.data.reverse();
        this.get_order_status();
      });
      this.$store.dispatch("setloading", false);
    },
    get_order_status() {
      var plants = this.active_plants || this.plants;
      var ids = plants
        .map(function (plant) {
          return plant.id;
        })
        .join(";");
      api.orders_status_table(ids).then((res) => {
        this.orders = res.data;
      });
      this.$store.dispatch("setloading", false);
    },
    remove(i) {
      this.active_plants.splice(i, 1);
      localStorage.setItem("plants", JSON.stringify(this.active_plants));
      this.get_order_status();
    },
    add() {
      this.body.forEach((element) => {
        this.active_plants.unshift(element);
      });
      localStorage.setItem("plants", JSON.stringify(this.active_plants));
      this.get_order_status();
      this.select_removed_plants();
      api.success("close-utilization");
      this.body = [];
    },
    changePostion(from, to) {
      var myArray = this.active_plants || this.plants;
      myArray.splice(to, 0, myArray.splice(from, 1)[0]);
      localStorage.setItem("plants", JSON.stringify(myArray));
      this.get_order_status();
    },
    select_removed_plants() {
      var plants = this.active_plants;
      this.removed_plants = this.plants.filter(
        (plant) => !plants.some((plant2) => plant.id === plant2.id)
      );
    },
  },
  created() {
    this.get_plants();
    setTimeout(() => {
      this.get_order_status();
    }, 1800000);
  },
  beforeMount() {
    this.$store.state.showSidenav = false;
    this.$store.state.hideConfigButton = true;
    this.$store.state.showNavbar = false;
    this.$store.state.showFooter = false;
  },
  beforeUnmount() {
    this.$store.state.showSidenav = true;
    this.$store.state.hideConfigButton = false;
    this.$store.state.showNavbar = true;
    this.$store.state.showFooter = true;
  },
};
</script>

<style>
main.main-content {
  height: 0 !important;
  max-height: 0 !important;
}
</style>
