<template>
  <div class="collapse navbar-collapse w-auto" id="sidenav-collapse-main">
    <ul class="navbar-nav">
      <li
        class="nav-item"
        v-for="route in routers.main.filter((item) => {
          return item.role.find((roleItem) => {
            return roleItem == role;
          });
        })"
        :key="route"
        @click="toggleSidebar"
      >
        <sidenav-item
          :url="`/${route.path}`"
          :class="
            getRoute() === route.path || $route.meta.last_path === route.path
              ? 'active'
              : ''
          "
          :navText="$t(route.name)"
        >
          <template v-slot:icon>
            <i
              :class="route.icon"
              class="text-secondary text-sm opacity-10 my-1"
            ></i>
          </template>
        </sidenav-item>
      </li>
      <li class="nav-item">
        <a
          type="button"
          class="nav-link"
          data-bs-toggle="collapse"
          data-bs-target="#settings"
          :aria-expanded="
            routers.settings.find(
              (e) => getRoute() === e.path || $route.meta.last_path === e.path
            )
              ? true
              : false
          "
        >
          <div
            class="icon icon-shape icon-sm text-center d-flex align-items-center justify-content-center"
          >
            <i
              class="ni ni-settings-gear-65 text-secondary text-sm opacity-10"
            ></i>
          </div>
          <span class="nav-link-text">{{ $t("routers.settings") }}</span>
        </a>
        <div
          class="collapse"
          :class="
            routers.settings.find(
              (e) => getRoute() === e.path || $route.meta.last_path === e.path
            )
              ? 'show'
              : ''
          "
          id="settings"
        >
          <ul class="navbar-nav">
            <li
              class="nav-item"
              v-for="route in routers.settings"
              :key="route"
              @click="toggleSidebar"
            >
              <sidenav-item
                :url="`/${route.path}`"
                :class="
                  getRoute() === route.path ||
                  route.path === $route.meta.last_path
                    ? 'active'
                    : ''
                "
                :navText="$t(route.name)"
              >
                <template v-slot:icon>
                  <i
                    :class="route.icon"
                    class="text-secondary text-sm opacity-10"
                  ></i>
                </template>
              </sidenav-item>
            </li>
          </ul>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
import { mapActions, mapMutations } from "vuex";
import SidenavItem from "./SidenavItem.vue";

export default {
  name: "SidenavList",
  props: {
    cardBg: String,
  },
  data() {
    return {
      title: "Dashboard",
      controls: "dashboardsExamples",
      isActive: "active",
      role: localStorage["role"],
      routers: {
        main: [
          {
            name: "routers.statistika",
            icon: "fas fa-chart-pie",
            path: "admin-statistic",
            role: ["admin", "founder", "accountant"],
          },
          {
            name: "routers.order-view",
            icon: "fas fa-box",
            path: "orders-view",
            role: [localStorage["role"]],
          },
          {
            name: "routers.users",
            icon: "fas fa-users",
            path: "users",
            role: ["admin", "founder", "accountant", "analyst"],
          },
          {
            name: "routers.attandance",
            icon: "ni ni-calendar-grid-58",
            path: "attandance",
            role: ["admin", "founder", "accountant", "analyst", "plant_admin"],
          },
          {
            name: "routers.cashier",
            icon: "ni ni-box-2",
            path: "cashreg",
            role: ["admin", "founder", "accountant", "seller", "seller_admin"],
          },
          {
            name: "routers.expense",
            icon: "ni ni-box-2",
            path: "expense?market_id=0",
            role: ["admin", "founder", "accountant", "seller_admin"],
          },
          {
            name: "routers.supplier",
            icon: "ni ni-box-2",
            path: "suppliers",
            role: ["admin", "founder", "accountant", "analyst"],
          },
          {
            name: "routers.warehouse",
            icon: "ni ni-box-2",
            path: "material-stores",
            role: ["admin", "founder", "accountant", "analyst"],
          },
          {
            name: "routers.product-store",
            icon: "ni ni-box-2",
            path: "product-stores",
            role: ["admin", "founder", "accountant", "analyst", "plant_admin"],
          },
          {
            name: "routers.production",
            icon: "ni ni-box-2",
            path: "production",
            role: ["admin", "founder", "accountant", "analyst", "plant_admin"],
          },
          {
            name: "routers.trade",
            icon: "fa fa-handshake",
            path: "main-trade",
            role: ["admin", "founder", "accountant", "analyst"],
          },
          {
            name: "routers.plan",
            icon: "ni ni-box-2",
            path: "plan-products",
            role: ["admin", "analyst"],
          },
        ],
        settings: [
          {
            name: "routers.shift",
            icon: "ni ni-time-alarm",
            path: "shift",
            role: ["admin", "founder", "accountant", "analyst"],
          },
          {
            name: "routers.product-type",
            icon: "ni ni-box-2",
            path: "product-type",
            role: ["admin", "founder", "accountant", "analyst"],
          },
          {
            name: "routers.material-type",
            icon: "ni ni-box-2",
            path: "material-type",
            role: ["admin", "founder", "accountant", "analyst"],
          },
          {
            name: "routers.plant",
            icon: "ni ni-box-2",
            path: "plant",
            role: ["admin", "founder", "accountant", "analyst"],
          },
          {
            name: "routers.olchov",
            icon: "ni ni-box-2",
            path: "measure",
            role: ["admin", "founder", "accountant", "analyst"],
          },
          // {
          //   name: "routers.building",
          //   icon: "ni ni-box-2",
          //   path: "building",
          // },
          {
            name: "routers.spare",
            icon: "ni ni-box-2",
            path: "spare-part",
            role: ["admin", "founder", "accountant"],
          },
        ],
      },
    };
  },
  components: {
    SidenavItem,
  },
  methods: {
    ...mapMutations(["navbarMinimize", "toggleConfigurator"]),
    ...mapActions(["toggleSidebarColor"]),
    toggleSidebar() {
      let width = document.documentElement.clientWidth;
      if (width < 1200) {
        this.toggleSidebarColor(!this.$store.state.darkMode ? "bg-white" : "");
        this.navbarMinimize();
      }
    },
    getRoute() {
      const routeArr = this.$route.path.split("/");
      return routeArr[1];
    },
  },
};
</script>
