<template>
  <button
    data-bs-toggle="modal"
    data-bs-target="#order"
    v-show="false"
  ></button>
  <div class="modal fade" id="order">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title">{{ order.order?.ordinal_number }}</h5>
          <div>
            <p>{{ order.order?.time?.substring(0, 10) }}</p>
            <p v-if="type == 'pre-order'">{{ order.order?.delivery_date }}</p>
          </div>
        </div>
        <div class="modal-body">
          <div class="row text-center">
            <div class="col-md-4">
              <p>{{ $t("order-modal.sum") }}</p>
              {{ currency.format(balance) + " " + $t("currency") }}
            </div>
            <div class="col-md-4">
              <p>{{ $t("order-modal.discount") }}</p>
              {{
                currency.format(order.order?.discount) + " " + $t("currency")
              }}
            </div>
            <div class="col-md-4">
              <p>{{ $t("order-modal.customer") }}</p>
              {{ order.order?.customer ? order.order?.customer.name : "" }}
            </div>
          </div>
          <div class="table-responsive" style="max-height: 60vh">
            <table class="table table-sm table-hover">
              <thead>
                <tr>
                  <th>{{ $t("trades.table.image") }}</th>
                  <th>{{ $t("trades.table.product") }}</th>
                  <th>{{ $t("trades.table.value") }}</th>
                  <th>{{ $t("trades.table.return") }}</th>
                  <th>{{ $t("trades.table.price") }}</th>
                  <th v-if="type == 'order'">
                    {{ $t("trades.table.discount") }}
                  </th>
                  <th v-if="type == 'order'">
                    {{ $t("trades.table.payment") }}
                  </th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item in trades.data" :key="item">
                  <td>
                    <div class="img">
                      <img
                        :src="
                          image +
                          '/products/' +
                          (item.Trades.product_store
                            ? item.Trades.product_store?.product?.image
                            : item.Trades.product?.image)
                        "
                        :alt="
                          item.Trades.product_store
                            ? item.Trades.product_store?.product?.image
                            : item.Trades.product?.image
                        "
                      />
                    </div>
                  </td>
                  <td>
                    {{
                      item.Trades.product_store
                        ? item.Trades.product_store?.product?.name
                        : item.Trades.product?.name
                    }}
                  </td>
                  <td>
                    {{
                      item.sum_quantity +
                      " " +
                      (item.Trades.product_store
                        ? item.Trades.product_store?.product?.olchov.name
                        : item.Trades.product?.olchov.name)
                    }}
                  </td>
                  <td>
                    {{
                      (findReturnedQuantity(item)
                        ? findReturnedQuantity(item).sum_quantity
                        : 0) +
                      " " +
                      (item.Trades.product_store
                        ? item.Trades.product_store?.product?.olchov.name
                        : item.Trades.product?.olchov.name)
                    }}
                  </td>
                  <td>
                    {{
                      (type == "order"
                        ? currency.format(item.Trades.price)
                        : currency.format(
                            trades?.data2
                              ? trades?.data2.find(
                                  (item1) => item1.trade_id == item.Trades.id
                                ).sum_price
                              : 0
                          )) +
                      " " +
                      $t("currency")
                    }}
                  </td>
                  <td v-if="type == 'order'">
                    {{
                      currency.format(item.Trades.discount) +
                      " " +
                      $t("currency")
                    }}
                  </td>
                  <td v-if="type == 'order'">
                    {{
                      currency.format(
                        (item.Trades.price - item.Trades.discount) *
                          item.sum_quantity
                      ) +
                      " " +
                      $t("currency")
                    }}
                  </td>
                  <td>
                    <button
                      v-if="type == 'pre-order'"
                      class="btn btn-sm m-0 px-2 py-1 bg-gradient-primary"
                      data-bs-toggle="modal"
                      data-bs-target="#component-modal"
                      @click="
                        trade = item;
                        getTradeItems();
                      "
                    >
                      <i class="fa fa-eye"></i>
                    </button>
                    <button
                      v-if="type == 'order'"
                      class="btn btn-sm m-0 px-2 py-1 bg-gradient-info"
                      data-bs-toggle="modal"
                      data-bs-target="#return"
                      @click="$refs.returnProductModal.set(item)"
                    >
                      <i class="fa fa-undo"></i>
                    </button>
                  </td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <td colspan="7">
                    <pagination
                      :page="trades.current_page"
                      :pages="trades.pages"
                      :limit="trades.limit"
                      @get="getTrades"
                    />
                  </td>
                </tr>
              </tfoot>
            </table>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn m-0 mx-1 bg-gradient-info"
            @click="$refs.orderCheck.start()"
          >
            <i class="fa fa-print"></i>
          </button>
          <button class="btn m-0 bg-gradient-secondary" data-bs-dismiss="modal">
            {{ $t("close") }}
          </button>
        </div>
      </div>
    </div>
  </div>

  <div
    class="modal fade"
    id="component-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="component-modal-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-order-modal-label">
            {{ trade?.Trades?.product?.name }}
          </h5>
        </div>
        <form @submit.prevent="postTradeItem()">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-12">
                <div class="table-responsive" style="max-height: 50vh">
                  <ul class="list-group">
                    <li
                      class="list-group-item px-2"
                      v-for="item in trade_items"
                      :key="item"
                    >
                      <!-- {{ view(item) }} -->
                      <div class="d-flex align-items-center">
                        <div class="img" v-if="item.component_item.product">
                          <img
                            :src="
                              image +
                              '/products/' +
                              item.component_item.product.image
                            "
                            :alt="item.component_item.product.image"
                          />
                        </div>
                        <div class="ps-2">
                          <strong>
                            {{
                              item.component_item.product
                                ? item.component_item.product.name
                                : item.component_item.material.name
                            }}
                          </strong>
                          <p class="m-0">
                            {{
                              Intl.NumberFormat().format(item.price) +
                              " " +
                              $t("currency")
                            }}
                          </p>
                          <p class="m-0" v-if="item.component_item.product">
                            {{
                              `${$t("pre-order.placeholders.value")}: ${
                                item.quantity
                              } ${item.component_item.product.olchov.name}` +
                              (item.trade_items_size.length
                                ? (item.trade_items_size[0].size1
                                    ? `, ${$t(
                                        "pre-order.placeholders.width"
                                      )}: ${item.trade_items_size[0].size1}cm`
                                    : ``) +
                                  (item.trade_items_size[0].size2
                                    ? `, ${$t(
                                        "pre-order.placeholders.height"
                                      )}: ${item.trade_items_size[0].size2}cm`
                                    : ``) +
                                  (item.trade_items_size[0].comment
                                    ? `, ${$t(
                                        "pre-order.placeholders.comment"
                                      )}: ${item.trade_items_size[0].comment}`
                                    : ``)
                                : ``)
                            }}
                          </p>
                          <p class="m-0" v-if="item.component_item.material">
                            {{
                              `${$t("pre-order.placeholders.value")}: ${
                                item.quantity
                              } ${item.component_item.material.olchov.name}` +
                              (item.trade_items_size.length
                                ? (item.trade_items_size[0].size1
                                    ? `, ${$t(
                                        "pre-order.placeholders.width"
                                      )}: ${item.trade_items_size[0].size1}cm`
                                    : ``) +
                                  (item.trade_items_size[0].size2
                                    ? `, ${$t(
                                        "pre-order.placeholders.height"
                                      )}: ${item.trade_items_size[0].size2}cm`
                                    : ``) +
                                  (item.trade_items_size[0].comment
                                    ? `, ${$t(
                                        "pre-order.placeholders.comment"
                                      )}: ${item.trade_items_size[0].comment}`
                                    : ``)
                                : ``)
                            }}
                          </p>
                        </div>
                      </div>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-component-modal
            >
              {{ $t("close") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>

  <returnProductModal ref="returnProductModal" />

  <orderCheck
    :market-id="$route.query.market_id"
    :order-id="order.order.id || 0"
    ref="orderCheck"
  />
</template>

<script>
import * as api from "../../server/api";
import returnProductModal from "./returnProductModal.vue";
import orderCheck from "@/views/trade/orderCheck.vue";
import pagination from "../pagination.vue";
export default {
  name: "orderModal",
  components: { pagination, returnProductModal, orderCheck },
  data() {
    return {
      image: api.image,
      currency: Intl.NumberFormat(),
      type: "",
      order: {
        order: {
          time: "",
          id: 0,
          discount: 0,
          customer_id: 0,
          ordinal_number: 0,
          delivery_date: "0000-00-00",
          status: "",
          user_id: 0,
          customer: {
            name: "",
            user_id: 0,
            id: 0,
            type: "",
            phones: [
              {
                id: 0,
                owner_id: 0,
                number: "",
                ownerType: "",
              },
            ],
          },
          files: [],
          user: {
            salary: null,
            balance: 0,
            id: 1,
            kpi: null,
            name: "",
            username: "",
            nation: "",
            passwordHash: "",
            birthDate: "",
            disabled: false,
            specialty: "",
            role: "",
            passport: "",
            agreement_expiration: "",
            workBeginDate: "",
          },
        },
        sum_income: 0,
      },
      balance: null,
      trades: {
        current_page: 1,
        pages: 1,
        limit: 25,
        data: [],
        data2: [],
      },
      trade: null,
      returned_products: {
        current_page: 1,
        pages: 1,
        limit: 25,
        data: [],
      },
      trade_items: [],
    };
  },
  methods: {
    start(order_id) {
      this.getOrder(order_id);
    },
    getOrder(id) {
      api
        .orders("", "", 0, 0, "", id, this.$route.query.market_id, 1, 1)
        .then((res) => {
          this.order = res.data;
          if (this.order.order.delivery_date == "0000-00-00")
            this.type = "order";
          else this.type = "pre-order";
          this.getBalance();
        });
    },
    getBalance() {
      api.trades(this.order.order.id, false, 1, 1, 25).then((res) => {
        this.balance = res.data;
        this.getTrades(1, 25);
      });
    },
    getTrades(page, limit) {
      const related_number = this.type == "order" ? true : false;
      api
        .trades(this.order.order.id, related_number, 0, page, limit)
        .then((res) => {
          this.trades = res.data;
          this.getReturnedProducts();
        });
    },
    getReturnedProducts() {
      api
        .returned_products(
          this.order.order.id,
          this.$route.query.market_id,
          1,
          this.trades.data.length
        )
        .then((res) => {
          this.returned_products = res.data;
          document.querySelector(`[data-bs-target="#order"]`).click();
        });
    },
    findReturnedQuantity(trade) {
      return this.returned_products.data.find((item) => {
        return item.Returned_products.trade_id == trade.Trades.id;
      });
    },
    getTradeItems() {
      api.trade_items(this.trade.Trades.id).then((res) => {
        this.trade_items = res.data;
        this.trade_items.sort(
          (a, b) => b.component_item.product - a.component_item.product
        );
      });
    },
  },
};
</script>

<style scoped>
p {
  margin: 0;
}

.img {
  text-align: center;
}

.img img {
  width: 40px;
  height: 40px;
  border-radius: 10px;
  object-fit: contain;
}
</style>
