<template>
  <div
    class="modal fade"
    id="add-supplier"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-supplier-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-supplier-label">
            {{ $t("supplier.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="add()">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("supplier.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="body.name"
                    :placeholder="$t('supplier.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="balance">{{ $t("supplier.balance") }}</label>
                  <div class="input-group">
                    <input
                      id="balance"
                      type="number"
                      class="form-control"
                      v-model="body.balance"
                      @focus="
                        body.balance == 0 ? (body.balance = null) : body.balance
                      "
                      @focusout="
                        body.balance == null ? (body.balance = 0) : body.balance
                      "
                      step="any"
                      :placeholder="$t('supplier.balance')"
                      required
                    />
                    <span class="input-group-text">{{ $t("currency") }}</span>
                  </div>
                </div>
              </div>
              <div
                class="col-md-6"
                v-for="(phone, i) in body.phones"
                :key="phone"
              >
                <div class="form-group">
                  <label :for="`phone${i}`"
                    >{{ $t("supplier.phone") }}
                    {{ body.phones.length > 1 ? i + 1 : "" }}</label
                  >
                  <div class="input-group">
                    <select
                      class="form-control w-25"
                      v-model="phone.dial_code"
                      required
                    >
                      <option
                        :value="code.dial_code"
                        v-for="code in phone_codes"
                        :key="code"
                      >
                        {{ code.dial_code }}
                      </option>
                    </select>
                    <input
                      :id="`phone${i}`"
                      type="number"
                      class="form-control"
                      :class="body.phones.length > 1 ? 'w-50' : 'w-75'"
                      v-model="phone.value"
                      :placeholder="$t('supplier.phone')"
                      min="1"
                      required
                    />
                    <button
                      class="btn bg-gradient-danger mb-0"
                      type="button"
                      v-if="i || (!i && body.phones.length > 1)"
                      @click="body.phones.splice(i, 1)"
                    >
                      <i class="fa-solid fa-circle-xmark"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              class="btn bg-gradient-primary"
              type="button"
              @click="
                body.phones.push({ value: '', number: '', dial_code: '+7' })
              "
            >
              <i class="fa-solid fa-phone"></i>
            </button>
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-supplier
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="update-supplier"
    tabindex="-1"
    role="dialog"
    aria-labelledby="update-supplier-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="update-supplier-label">
            {{ $t("supplier.update") }}
          </h5>
        </div>
        <form @submit.prevent="put()">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("supplier.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="data.name"
                    :placeholder="$t('supplier.name')"
                    required
                  />
                </div>
              </div>
              <div
                class="col-md-6"
                v-for="(phone, i) in data.phones"
                :key="phone"
              >
                <div class="form-group">
                  <label :for="`phone${i}`"
                    >{{ $t("supplier.phone") }}
                    {{ data.phones.length > 1 ? i + 1 : "" }}</label
                  >
                  <div class="input-group">
                    <select
                      class="form-control w-25"
                      v-model="phone.dial_code"
                      required
                    >
                      <option
                        :value="code.dial_code"
                        v-for="code in phone_codes"
                        :key="code"
                      >
                        {{ code.dial_code }}
                      </option>
                    </select>
                    <input
                      :id="`phone${i}`"
                      type="number"
                      class="form-control"
                      :class="data.phones.length > 1 ? 'w-50' : 'w-75'"
                      v-model="phone.value"
                      :placeholder="$t('supplier.phone')"
                      min="1"
                      required
                    />
                    <button
                      class="btn bg-gradient-danger mb-0"
                      type="button"
                      v-if="i || (!i && data.phones.length > 1)"
                      @click="data.phones.splice(i, 1)"
                    >
                      <i class="fa-solid fa-circle-xmark"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              class="btn bg-gradient-primary"
              type="button"
              @click="
                data.phones.push({ value: '', number: '', dial_code: '+7' })
              "
            >
              <i class="fa-solid fa-phone"></i>
            </button>
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-put
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("update") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import codes from "@/language/CountryPhoneCode.json";

export default {
  name: "supplier-modal",
  props: ["item"],
  emits: ["add", "put"],
  data() {
    return {
      phone_codes: codes,
      body: {
        name: "",
        balance: 0,
        phones: [
          {
            value: "",
            number: "",
            dial_code: "+998",
          },
        ],
        materials: [],
      },
    };
  },
  methods: {
    add() {
      this.body.phones.forEach((element) => {
        element.number = element.dial_code + element.value;
      });
      this.$emit("add", this.body);
    },
    put() {
      this.data.phones.forEach((element) => {
        element.number = element.dial_code + element.value;
      });
      this.$emit("put", this.data);
    },
  },
  computed: {
    data() {
      this.item.phones?.forEach((element) => {
        if (element.number.slice(0, 4) == "+998") {
          element.dial_code = "+998";
          let num = element.number.split("+998");
          element.value = num[1];
        } else if (element.number.slice(0, 4) == "+992") {
          element.dial_code = "+992";
          let num = element.number.split("+992");
          element.value = num[1];
        } else if (element.number.slice(0, 4) == "+996") {
          element.dial_code = "+996";
          let num = element.number.split("+996");
          element.value = num[1];
        } else if (element.number.slice(0, 4) == "+993") {
          element.dial_code = "+993";
          let num = element.number.split("+993");
          element.value = num[1];
        } else if (element.number.slice(0, 2) == "+7") {
          element.dial_code = "+7";
          let num = element.number.split("+7");
          element.value = num[1];
        } else {
          element.dial_code = "";
          element.value = element.number;
        }
      });
      return this.item;
    },
  },
};
</script>
