<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col my-1">
            <h6 class="mb-0">{{ $t("expense.history") }}</h6>
          </div>
          <div class="col-auto my-1">
            <button
              class="btn btn-sm bg-gradient-primary mb-0 px-3 float-end"
              data-bs-toggle="modal"
              data-bs-target="#filter"
            >
              <i class="fas fa-filter"></i>
            </button>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="table-responsive p-0 mt-2">
          <table class="table table-bordered align-items-center mb-0">
            <thead>
              <tr>
                <th class="w-custom-40">#</th>
                <th>{{ $t("expense.name") }}</th>
                <th>{{ $t("expense.expense") }}</th>
                <th>{{ $t("expense.money") }}</th>
                <th>{{ $t("expense.kassa") }}</th>
                <th>{{ $t("expense.date") }}</th>
                <th>{{ $t("expense.user") }}</th>
                <th>{{ $t("expense.comment") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(expense, i) in expenses" :key="expense">
                <td>{{ i + 1 }}</td>
                <td :colspan="expense.fixed_expense ? 1 : 2">
                  {{
                    expense.type == "fixed_expense"
                      ? $t("expense.fixed")
                      : expense.type == "order_expense"
                      ? $t("expense.order")
                      : expense.type == "returned_product"
                      ? $t("expense.return")
                      : $t("expense.variable")
                  }}
                </td>
                <td v-if="expense.fixed_expense">
                  {{ expense.fixed_expense?.name }}
                </td>
                <td>
                  {{
                    `${Intl.NumberFormat().format(expense.money)} ${$t(
                      "currency"
                    )}`
                  }}
                </td>
                <td :class="expense.kassa ? '' : 'text-danger'">
                  {{ expense.kassa?.name ? expense.kassa?.name : "-" }}
                </td>
                <td>{{ format_date(expense.time) }}</td>
                <td>{{ expense.user.name }}</td>
                <td>{{ expense.comment }}</td>
              </tr>
            </tbody>
            <tfoot v-if="!expenses.length">
              <tr>
                <td colspan="8">
                  <data-notfound :data="expenses" />
                </td>
              </tr>
            </tfoot>
          </table>
        </div>
        <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
      </div>
    </div>
  </div>
  <!-- filter -->
  <div class="modal fade" id="filter">
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title">
            {{ $t("filter") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-6">
              <div class="form-group">
                <label for="from_date">{{ $t("from_date") }}</label>
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="from_date"
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("to_date") }}</label>
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="to_date"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-sm bg-gradient-danger"
            data-bs-dismiss="modal"
            @click="
              from_date = '';
              to_date = '';
              get(page, limit, true);
            "
          >
            <i class="fas fa-trash"></i>
          </button>
          <button
            class="btn btn-sm bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-filter
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn btn-sm bg-gradient-primary"
            data-bs-dismiss="modal"
            @click="get(page, limit, true)"
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";

export default {
  components: { dataNotfound, Pagination },
  name: "expense-history",
  data() {
    return {
      expenses: [],
      pages: 0,
      page: 1,
      limit: 10,
      from_date: "",
      to_date: "",
      format_date: api.format_date,
    };
  },
  methods: {
    get(page, limit, bool) {
      api
        .get_expenses(
          page,
          limit,
          this.from_date,
          this.to_date,
          0,
          "",
          0,
          this.$route.query.market_id
        )
        .then((res) => {
          if (bool) {
            api.success();
          }
          this.pages = res.data.page;
          this.page = res.data.current_page;
          this.limit = res.data.limit;
          this.expenses = res.data.data;
        });
    },
  },
  created() {
    this.get(this.page, this.limit, false);
  },
};
</script>
