<template>
  <div class="row">
    <div
      class="col-md-4 col-lg-3 col-sm-6 my-2"
      v-for="item in trades?.data"
      :key="item"
    >
      <div class="card card-stats shadow-sm">
        <div class="card-body">
          <div class="row">
            <div class="col">
              <h6 class="text-primary">
                {{ item.Trades.product_store?.product?.name }}
              </h6>
              <h6>
                {{ $t("trades.table.value") }}:
                {{
                  item.sum_quantity +
                  " " +
                  item.Trades.product_store?.product?.olchov?.name
                }}
              </h6>
              <h6>
                {{ $t("trades.table.price") }}:
                {{
                  Intl.NumberFormat().format(item.Trades.price) +
                  " " +
                  $t("currency")
                }}
              </h6>
              <h6>
                {{ $t("trades.table.discount") }}:
                {{
                  Intl.NumberFormat().format(item.Trades.discount) +
                  " " +
                  $t("currency")
                }}
              </h6>
              <h6>
                {{ $t("trades.table.payment") }}:
                {{
                  Intl.NumberFormat().format(
                    item.sum_quantity *
                      (item.Trades.price - item.Trades.discount)
                  ) +
                  " " +
                  $t("currency")
                }}
              </h6>
            </div>
            <div
              class="col-auto d-flex justify-content-center align-items-center"
            >
              <div
                class="avatar avatar-xl cursor-pointer"
                @click="
                  image_view(
                    image +
                      '/products/' +
                      item.Trades.product_store?.product?.image
                  )
                "
              >
                <img
                  height="80"
                  width="80"
                  style="object-fit: cover"
                  :src="
                    image +
                    '/products/' +
                    item.Trades.product_store?.product?.image
                  "
                  class="text-white rounded-2"
                  :alt="item.Trades.product_store?.product?.image"
                />
              </div>
            </div>
          </div>
          <button
            class="btn bg-gradient-danger m-0 py-2 px-3 w-100 mt-1"
            @click="deleteTrade(item.Trades.id)"
          >
            <i class="fa fa-trash"></i>
          </button>
        </div>
      </div>
    </div>
    <div class="col-md-12 mt-1" v-if="trades?.data?.length">
      <pagination
        :page="trades?.current_page"
        :pages="trades?.pages"
        :limit="trades?.limit"
        @get="getTrades"
      />
    </div>
  </div>

  <div
    class="modal fade"
    id="products"
    tabindex="-1"
    role="dialog"
    aria-labelledby="products-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="products-label">
            {{ $t("trades.modal-title") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row gap-1">
            <div class="col-12">
              <input
                type="text"
                class="form-control"
                :placeholder="$t('search')"
                v-model="search_product"
                @keyup="getProducts(1, 25)"
              />
            </div>
            <div class="col-12">
              <ul class="list-group">
                <li
                  class="list-group-item"
                  v-for="item in products?.data"
                  :key="item"
                  data-bs-toggle="modal"
                  data-bs-target="#trade-modal"
                  @click="
                    product = item;
                    new_trade.price = item.Product_Store.price;
                    new_trade.size1 = item.Product_Store.size1;
                    new_trade.size2 = item.Product_Store.size2;
                  "
                >
                  <div class="item-img">
                    <img
                      :src="
                        image +
                        '/products/' +
                        item.Product_Store?.product?.image
                      "
                      :alt="item.Product_Store?.product?.image"
                    />
                  </div>
                  <div class="p-2">
                    <p class="m-0">
                      {{ item.Product_Store?.product?.name }}
                    </p>
                    <p class="m-0">
                      {{
                        (item.Product_Store?.material
                          ? item.Product_Store?.material?.name + " "
                          : "") +
                        item.sum_quantity +
                        " " +
                        item.Product_Store.product?.olchov?.name +
                        " " +
                        item.Product_Store?.size1 +
                        " cm " +
                        item.Product_Store?.size2 +
                        " cm"
                      }}
                    </p>
                  </div>
                </li>
              </ul>
            </div>
            <div class="col-12">
              <pagination
                :page="products?.current_page"
                :pages="products?.pages"
                :limit="products?.limit"
                @get="getProducts"
              />
            </div>
          </div>
        </div>
        <div class="modal-footer"></div>
      </div>
    </div>
  </div>

  <div
    class="modal fade"
    id="trade-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="trade-modal-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="trade-modal-label">
            {{ $t("trades.modal-title") }}
          </h5>
        </div>
        <form @submit.prevent="postTrade(new_trade)">
          <div class="modal-body pt-0">
            <h6 class="m-0">
              {{ product?.Product_Store?.product?.name }}
            </h6>
            <h6 class="m-0">
              {{
                product?.sum_quantity +
                " " +
                product?.Product_Store?.product?.olchov?.name +
                " "
              }}
              {{
                product?.Product_Store?.product?.product_type?.hasSize
                  ? product?.Product_Store.size1 +
                    $t("supply.sm") +
                    product?.Product_Store.size2 +
                    $t("supply.sm")
                  : ""
              }}
            </h6>
            <div class="row gap-1">
              <div class="col-12">
                <div class="input-group">
                  <input
                    type="number"
                    :min="product?.Product_Store?.product?.unit ? 0.1 : 1"
                    :step="product?.Product_Store?.product?.unit ? 'any' : 1"
                    class="form-control"
                    :placeholder="$t('trades.placeholders.value')"
                    required
                    v-model="new_trade.quantity"
                    @focus="
                      new_trade.quantity == 0
                        ? (new_trade.quantity = null)
                        : new_trade.quantity
                    "
                    @focusout="
                      new_trade.quantity == null
                        ? (new_trade.quantity = 0)
                        : new_trade.quantity
                    "
                  />
                  <div class="input-group-text">
                    {{ product?.Product_Store?.product?.olchov?.name }}
                  </div>
                </div>
              </div>
              <div class="col-12">
                <div class="input-group">
                  <input
                    type="number"
                    min="1"
                    step="any"
                    class="form-control"
                    :placeholder="$t('trades.placeholders.price')"
                    required
                    v-model="new_trade.price"
                    @focus="
                      new_trade.price == 0
                        ? (new_trade.price = null)
                        : new_trade.price
                    "
                    @focusout="
                      new_trade.price == null
                        ? (new_trade.price = 0)
                        : new_trade.price
                    "
                  />
                  <div class="input-group-text">{{ $t("currency") }}</div>
                </div>
              </div>
              <div class="col-12">
                <div class="input-group">
                  <input
                    type="number"
                    min="0"
                    step="any"
                    class="form-control"
                    :placeholder="$t('trades.placeholders.discount')"
                    v-model="new_trade.discount"
                    @focus="
                      new_trade.discount == 0
                        ? (new_trade.discount = null)
                        : new_trade.discount
                    "
                    @focusout="
                      new_trade.discount == null
                        ? (new_trade.discount = 0)
                        : new_trade.discount
                    "
                  />
                  <div class="input-group-text">{{ $t("currency") }}</div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-trade-modal
            >
              {{ $t("close") }}
            </button>
            <button class="btn bg-gradient-primary">
              {{ $t("order.confirm-button") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div id="image-viewer">
    <span class="close" @click="image_view()">&times;</span>
    <img class="modal-content" id="full-image" />
  </div>
</template>

<script>
import * as api from "../../server/api";
import pagination from "@/components/pagination.vue";
export default {
  name: "trades",
  components: { pagination },
  data() {
    return {
      image: api.image,
      order: null,
      search_product: "",
      products: {
        current_page: 1,
        pages: 1,
        limit: 25,
        data: [],
      },
      product: null,
      new_trade: {
        product_store_pr_id: 0,
        plant_id: 0,
        quantity: null,
        price: null,
        discount: null,
        order_id: 0,
        size1: 0,
        size2: 0,
        material_id: 0,
      },
      trades: null,
    };
  },
  methods: {
    start(order) {
      this.order = order;
      this.getTrades(1, 25);
    },
    getTrades(page, limit) {
      api.trades(this.order.id, true, 0, page, limit).then((res) => {
        this.trades = res.data;
      });
    },
    postTrade() {
      this.new_trade.material_id = this.product.Product_Store.material_id;
      this.new_trade.product_store_pr_id = this.product.Product_Store.product.id;
      this.new_trade.order_id = this.order?.id;
      !this.new_trade.discount ? (this.new_trade.discount = 0) : false;
      api.sell_product(this.new_trade).then(() => {
        api.success("close-trade-modal").then(() => {
          this.new_trade = {
            product_store_pr_id: 0,
            plant_id: 0,
            quantity: 0,
            price: 0,
            discount: 0,
            order_id: 0,
            material_id: 0,
          };
          this.getTrades(1, 25);
        });
      });
    },
    deleteTrade(id) {
      api.delete_trade(id).then(() => {
        api.success().then(() => {
          this.getTrades(1, 25);
        });
      });
    },
    getProducts(page, limit) {
      const search = this.search_product ? this.search_product : "";
      api
        .products_store_trade(
          search,
          0,
          0,
          this.$route.query.market_id,
          page,
          limit
        )
        .then((res) => {
          this.products = res.data;
        });
    },
    scrollProducts(event) {
      const element = event.target;
      const search = this.search_product ? this.search_product : "";
      if (
        element.scrollTop + element.clientHeight >=
        element.scrollHeight - 1
      ) {
        if (this.products.current_page < this.products.pages) {
          api
            .products_store_trade(
              search,
              0,
              0,
              this.$route.query.market_id,
              this.products.current_page + 1,
              25
            )
            .then((res) => {
              this.products.current_page = res.data.current_page;
              this.products.pages = res.data.pages;
              this.products.data = this.products.data.concat(res.data.data);
            });
        }
      }
    },
    countOrdinalNumber(index, page, limit) {
      let start = null,
        numbers = [];
      start = (page - 1) * limit;
      for (let i = 0; i < limit; i++) {
        numbers.push(start);
        start++;
      }
      return numbers[index] + 1;
    },
    image_view(src) {
      if (src) {
        document.getElementById("full-image").src = src;
        document.getElementById("image-viewer").style.display = "block";
      } else {
        document.getElementById("image-viewer").style.display = "none";
      }
    },
  },
};
</script>

<style scoped>
h6 {
  margin: 0 0;
}
.list-group {
  padding: 0;
}

.list-group-item {
  padding: 0;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.item-img {
  width: 50px;
  height: 50px;
  border-radius: inherit;
}

.item-img img {
  width: 100%;
  height: 100%;
  border-radius: inherit;
  object-fit: cover;
}

.td-img {
  height: 40px;
}
.td-img img {
  height: 100%;
  border-radius: inherit;
  object-fit: cover;
}
</style>
