      </p>
      <p class="lead fw-bold">{{ $t("page-not-found.description") }}</p>
      <button @click="$router.go(-1)" class="btn bg-gradient-primary">
        <i class="fa-solid fa-left-long"></i> {{ $t("page-not-found.back") }}
      </button>
    </div>
  </div>
</template>
<script>
export default {
  name: "NotFound",
  beforeMount() {
    this.$store.state.imageLayout = "profile-overview";
