<template>
  <div
    class="modal fade"
    id="add-material"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-material-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-material-label">
            {{ $t("material.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('add', body)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("material.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="body.name"
                    :placeholder="$t('material.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="price">{{ $t("material.price") }}</label>
                  <div class="input-group">
                    <input
                      id="price"
                      type="number"
                      class="form-control"
                      v-model="body.price"
                      @focus="
                        body.price == 0 ? (body.price = null) : body.price
                      "
                      @focusout="
                        body.price == null ? (body.price = 0) : body.price
                      "
                      :placeholder="$t('material.price')"
                      required
                    />
                    <span class="input-group-text">{{ $t("currency") }}</span>
                  </div>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="olchov">{{ $t("material.olchov") }}</label>
                  <select
                    id="olchov"
                    class="form-select"
                    v-model="body.olchov_id"
                    required
                  >
                    <option
                      :value="olchov.id"
                      v-for="olchov in olchovs"
                      :key="olchov"
                    >
                      {{ olchov.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="unit">{{ $t("material.unit.title") }}</label>
                  <select
                    id="unit"
                    class="form-select"
                    v-model="body.unit"
                    required
                  >
                    <option value="true">{{ $t("material.unit.true") }}</option>
                    <option value="false">
                      {{ $t("material.unit.false") }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="quantity">{{
                    $t("material.quantity.title")
                  }}</label>
                  <select
                    id="quantity"
                    class="form-select"
                    v-model="body.has_volume"
                    required
                  >
                    <option value="true">
                      {{ $t("material.quantity.true") }}
                    </option>
                    <option value="false">
                      {{ $t("material.quantity.false") }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="alarm">{{ $t("material.alarm") }}</label>
                  <input
                    id="alarm"
                    type="number"
                    min="0"
                    @focus="
                      body.alarm_value == 0
                        ? (body.alarm_value = null)
                        : body.alarm_value
                    "
                    @focusout="
                      body.alarm_value == null
                        ? (body.alarm_value = 0)
                        : body.alarm_value
                    "
                    class="form-control"
                    v-model="body.alarm_value"
                    :placeholder="$t('material.alarm')"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="update-material"
    tabindex="-1"
    role="dialog"
    aria-labelledby="update-material-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="update-material-label">
            {{ $t("material.update") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('put', data)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("material.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="data.name"
                    :placeholder="$t('material.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="price">{{ $t("material.price") }}</label>
                  <div class="input-group">
                    <input
                      id="price"
                      type="number"
                      class="form-control"
                      v-model="data.price"
                      :placeholder="$t('material.price')"
                      required
                    />
                    <span class="input-group-text">{{ $t("currency") }}</span>
                  </div>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="olchov">{{ $t("material.olchov") }}</label>
                  <select
                    id="olchov"
                    class="form-select"
                    v-model="data.olchov_id"
                    required
                  >
                    <option
                      :value="olchov.id"
                      v-for="olchov in olchovs"
                      :key="olchov"
                    >
                      {{ olchov.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="unit">{{ $t("material.unit.title") }}</label>
                  <select
                    id="unit"
                    class="form-select"
                    v-model="data.unit"
                    required
                  >
                    <option value="true">{{ $t("material.unit.true") }}</option>
                    <option value="false">
                      {{ $t("material.unit.false") }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="quantity">{{
                    $t("material.quantity.title")
                  }}</label>
                  <select
                    id="quantity"
                    class="form-select"
                    v-model="data.has_volume"
                    required
                  >
                    <option value="true">
                      {{ $t("material.quantity.true") }}
                    </option>
                    <option value="false">
                      {{ $t("material.quantity.false") }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="alarm">{{ $t("material.alarm") }}</label>
                  <input
                    id="alarm"
                    type="number"
                    min="0"
                    class="form-control"
                    v-model="data.alarm_value"
                    :placeholder="$t('material.alarm')"
                    required
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-put
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("update") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "add-material",
  props: ["olchovs", "item"],
  emits: ["add", "put"],
  data() {
    return {
      body: {
        material_type_id: this.$route.query.id,
        name: "",
        price: 0,
        olchov_id: null,
        unit: true,
        has_volume: true,
        alarm_value: 0,
      },
    };
  },
  computed: {
    data() {
      return this.item;
    },
  },
};
</script>
