<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-4">
            <h5 class="mb-0">{{ $t("indicator.need_material") }}</h5>
          </div>
          <div class="col-md-4"></div>
          <div class="col-md-4">
            <div class="row">
              <div class="col">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    get(page, limit);
                    $store.dispatch('setloading', false);
                  "
                />
              </div>
              <div class="col-auto">
                <button
                  class="btn btn-sm bg-gradient-primary mx-2 mb-0 px-3"
                  data-bs-toggle="modal"
                  data-bs-target="#filter"
                >
                  <i class="fas fa-filter"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body py-2">
        <div class="table-responsive p-0 mb-0">
          <table class="table table-bordered align-items-center mb-0">
            <thead>
              <tr>
                <th>{{ $t("return.table.product") }}</th>
                <th>{{ $t("cycle.title") }}</th>
                <th>{{ $t("supply.value") }}</th>
                <th>{{ $t("product-store.date") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
            </tbody>
          </table>
        </div>
        <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
      </div>
    </div>
  </div>
  <div class="modal fade" id="filter">
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title">
            {{ $t("filter") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-6">
              <div class="form-group">
                <label for="from_date">{{ $t("from_date") }}</label>
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="from_date"
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("to_date") }}</label>
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="to_date"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-sm bg-gradient-danger"
            data-bs-dismiss="modal"
            @click="
              from_date = new Date(
                new Date().getFullYear(),
                new Date().getMonth(),
                1
              )
                .toISOString()
                .substring(0, 10);
              to_date = new Date(
                new Date().getFullYear(),
                new Date().getMonth(),
                new Date().getDate() + 1
              )
                .toISOString()
                .substring(0, 10);
              get(page, limit);
            "
          >
            <i class="fas fa-trash"></i>
          </button>
          <button
            class="btn btn-sm bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-filter
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn btn-sm bg-gradient-primary"
            data-bs-dismiss="modal"
            @click="get(page, limit)"
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Pagination from "@/components/pagination.vue";
export default {
  components: { Pagination },
  data() {
    return {
      page: 1,
      pages: 0,
      limit: 10,
    };
  },
};
</script>

<style lang="scss" scoped></style>
