<template>
  <div class="dropdown d-flex">
    <h6 class="mb-0">{{ $t("sign-in.language") }} -</h6>
    <a
      type="button"
      class="dropdown-toggle ms-1"
      data-bs-toggle="dropdown"
      id="navbarDropdownMenuLink2"
    >
      <img
        :src="
          require(`../assets/img/icons/flags/${lang == 'kk' ? 'uz' : lang}.svg`)
        "
        width="20"
      />
    </a>
    <ul class="dropdown-menu" aria-labelledby="navbarDropdownMenuLink2">
      <li>
        <button
          class="dropdown-item"
          type="button"
          :class="lang == 'ru' ? 'active' : ''"
          @click="switch_language('ru')"
        >
          <img src="../assets/img/icons/flags/ru.svg" width="20" />
          
        </button>
      </li>
      <li>
        <button
          class="dropdown-item"
          type="button"
          :class="lang == 'uz' ? 'active' : ''"
          @click="switch_language('uz')"
        >
          <img src="../assets/img/icons/flags/uz.svg" width="20" />
          ()
        </button>
      </li>
      <li>
        <button
          class="dropdown-item"
          type="button"
          :class="lang == 'kk' ? 'active' : ''"
          @click="switch_language('kk')"
        >
          <img src="../assets/img/icons/flags/uz.svg" width="20" />
          ()
        </button>
      </li>
      <li>
        <button
          class="dropdown-item"
          type="button"
          :class="lang == 'kz' ? 'active' : ''"
          @click="switch_language('kz')"
        >
          <img src="../assets/img/icons/flags/kz.svg" width="20" />
          
        </button>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "switch-language",
  data() {
    return {
      lang: localStorage["lang"],
    };
  },
  methods: {
    switch_language(e) {
      localStorage["lang"] = e;
      window.location.reload();
    },
  },
};
</script>
