<template>
  <div class="py-4 container-fluid">
    <current-date />

    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">{{ $t("attandance.title") }}</h6>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col-md-6"></div>
              <div class="col-md-6">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    get();
                    $store.dispatch('setloading', false);
                  "
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body">
        <div class="table-responsive p-0 mb-2">
          <table class="table table-bordered align-items-center mb-0">
            <thead>
              <tr>
                <th style="width: 40px">
                  <div class="form-check">
                    <input
                      class="form-check-input mb-0"
                      type="checkbox"
                      v-model="checkAll"
                      :disabled="!attandance_users.length"
                    />
                  </div>
                </th>
                <th>{{ $t("attandance.user") }}</th>
                <th>{{ $t("attandance.type.title") }}</th>
                <th class="w-250">{{ $t("attandance.plant") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="attandance in attandance_users" :key="attandance">
                <td>
                  <div class="d-flex justify-content-center">
                    <div class="form-check">
                      <input
                        class="form-check-input mb-0"
                        type="checkbox"
                        :value="{ id: attandance.id }"
                        v-model="users"
                      />
                    </div>
                  </div>
                </td>
                <td>
                  <router-link
                    :to="{ name: 'Profile', query: { id: attandance.id } }"
                  >
                    {{ attandance.name }}
                  </router-link>
                </td>
                <td>
                  <span
                    class="text-success"
                    v-if="attandance.last_attandance?.type == 'entry'"
                    >{{ $t("attandance.type.entry") }}-<span class="text-dark"
                      >({{ attandance.last_attandance?.authorizator }})</span
                    >
                    <p class="m-0 text-sm">
                      {{ format_date(attandance.last_attandance?.datetime) }}
                    </p>
                  </span>
                  <span
                    class="text-danger"
                    v-else-if="attandance.last_attandance?.type == 'exit'"
                    >{{ $t("attandance.type.exit") }}-<span class="text-dark"
                      >({{ attandance.last_attandance?.authorizator }})</span
                    >
                    <p class="m-0 text-sm">
                      {{ format_date(attandance.last_attandance?.datetime) }}
                    </p></span
                  >
                  <span class="text-danger" v-else>{{
                    $t("attandance.type.did-not-come")
                  }}</span>
                </td>
                <td>
                  <select
                    v-if="attandance.plant_users.length > 1"
                    class="form-select form-select-sm w-250"
                    v-model="attandance.plant_id"
                    required
                  >
                    <option
                      :value="plant.plant?.id"
                      v-for="plant in attandance.plant_users"
                      :key="plant"
                    >
                      {{ plant.plant?.name }}
                    </option>
                  </select>
                  <span v-else>
                    {{ attandance.plant_users[0]?.plant?.name }}
                  </span>
                </td>
              </tr>
            </tbody>
            <tfoot v-if="!attandance_users.length">
              <tr>
                <td colspan="4">
                  <data-notfound :data="attandance_users" />
                </td>
              </tr>
            </tfoot>
          </table>
        </div>
        <div class="row">
          <div class="col-6">
            <button
              class="btn bg-gradient-success mb-0 w-100 btn-sm py-1"
              :disabled="!users.length || !attandance_users.length"
              @click="attandance('entry')"
            >
              {{ $t("attandance.type.entry") }}
            </button>
          </div>
          <div class="col-6">
            <button
              class="btn bg-gradient-danger mb-0 w-100 btn-sm py-1"
              :disabled="!users.length || !attandance_users.length"
              @click="attandance('exit')"
            >
              {{ $t("attandance.type.exit") }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import currentDate from "@/components/current-date.vue";
import DataNotfound from "@/components/dataNotfound.vue";

export default {
  components: { currentDate, DataNotfound },
  name: "Attandance",
  data() {
    return {
      attandance_users: [],
      search: "",
      users: [],
      format_date: api.format_date,
    };
  },
  methods: {
    get() {
      api.get_attandance_users(this.search).then((res) => {
        res.data.forEach((element) => {
          element.plant_id = !element?.last_attandance
            ? element.plant_users[0]?.plant?.id
            : element.last_attandance?.plant_id;
        });
        this.attandance_users = res.data;
      });
    },
    attandance(type) {
      let body = new Array();
      this.users.forEach((element) => {
        let data = this.attandance_users.find((e) => element.id == e.id);
        body.push({ plant_id: data.plant_id || 0, user_id: data.id });
      });

      api.create_attandance(body, type).then(() => {
        body = null;
        this.users = [];
        api.success();
        this.get();
      });
    },
  },
  created() {
    this.get();
  },
  computed: {
    checkAll: {
      get: function () {
        return this.attandance_users
          ? this.users.length == this.attandance_users.length
          : false;
      },
      set: function (value) {
        var users = [];
        if (value) {
          this.attandance_users.forEach(function (attandance_user) {
            users.push({ id: attandance_user.id });
          });
        }
        this.users = users;
      },
    },
  },
};
</script>
