<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6">
            <div class="row">
              <div class="col-md-6 my-1">
                <h6 class="mb-0">{{ $route.query.name }}</h6>
              </div>
              <div class="col-md-6 my-1">
                <button
                  class="btn btn-sm bg-gradient-secondary float-end mb-0 w-100"
                  @click="
                    search = '';
                    content == 'product'
                      ? ((content = 'cycle'), get_product_type())
                      : ((content = 'product'), get(page, limit));
                  "
                >
                  {{ $t(`product.${content == "cycle" ? "title" : "cycle"}`) }}
                </button>
              </div>
            </div>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col-md-6 my-1">
                <button
                  class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
                  data-bs-toggle="modal"
                  :data-bs-target="`#add-${
                    content == 'product' ? 'product' : 'cycle'
                  }`"
                  @click="content == 'product' ? get_olchovs() : ''"
                >
                  {{
                    $t(
                      `product.${
                        content == "product" ? "add-button" : "add-cycle"
                      }`
                    )
                  }}
                </button>
              </div>
              <div class="col-md-6 my-1">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    content == 'product'
                      ? (get(1, limit), $store.dispatch('setloading', false))
                      : ''
                  "
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row" v-if="content == 'product'">
          <div
            class="col-lg-4 col-md-6 col-sm-12 my-2"
            v-for="product in products"
            :key="product"
          >
            <div class="card card-zoom h-100 shadow-sm">
              <div class="card-body">
                <div class="row">
                  <div class="col">
                    <h5 class="card-title text-muted mb-0">
                      {{ product.name }}
                    </h5>
                    <hr class="my-1" />
                    <p class="mt-2 mb-0 text-sm">
                      <b class="text-secondary mr-2">
                        {{ $t("product.unit.title") }}:
                        <span class="text-nowrap ms-1">{{
                          product.unit
                            ? $t("product.unit.true")
                            : $t("product.unit.false")
                        }}</span></b
                      >
                    </p>
                    <div class="d-flex mt-2">
                      <button
                        class="btn btn-sm mb-0 bg-gradient-warning px-3"
                        @click="
                          get_olchovs();
                          product_id = product.id;
                          item = {
                            name: product.name,
                            image: '',
                            olchov_id: product.olchov_id,
                            disabled: product.disabled,
                            product_type_id: product.product_type_id,
                            unit: product.unit,
                            alarm_value: product.alarm_value,
                            trade_price: product.trade_price,
                            plant_price: product.plant_price,
                          };
                        "
                        data-bs-toggle="modal"
                        data-bs-target="#update-product"
                      >
                        <i class="fas fa-pen"></i>
                      </button>
                      <button
                        class="btn btn-sm mb-0 bg-gradient-success px-3 ms-1"
                        @click="
                          product_id = product.id;
                          item = {
                            id: product.id,
                            name: product.name,
                            image: '',
                            olchov_id: product.olchov_id,
                            disabled: product.disabled,
                            product_type_id: product.product_type_id,
                            unit: product.unit,
                            alarm_value: product.alarm_value,
                            trade_price: product.trade_price,
                            plant_price: product.plant_price,
                          };
                        "
                        data-bs-toggle="modal"
                        data-bs-target="#clone-product"
                      >
                        <i class="fas fa-clone"></i>
                      </button>
                      <button
                        class="btn btn-sm mb-0 bg-gradient-danger px-3 ms-1"
                        @click="
                          product_id = product.id;
                          item = {
                            name: product.name,
                            image: '',
                            olchov_id: product.olchov_id,
                            disabled: true,
                            product_type_id: product.product_type_id,
                            unit: product.unit,
                            alarm_value: product.alarm_value,
                            trade_price: product.trade_price,
                            plant_price: product.plant_price,
                          };
                          remove(item);
                        "
                      >
                        <i class="fas fa-trash"></i>
                      </button>
                      <router-link
                        :to="{
                          name: 'Cycle',
                          query: {
                            id: product.id,
                            product_type_id: $route.query.id,
                            name: product.name,
                          },
                        }"
                        class="btn btn-sm px-3 mb-0 bg-gradient-primary ms-1"
                      >
                        <i class="fas fa-eye"></i>
                      </router-link>
                      <button
                        class="btn btn-sm px-3 mb-0 bg-gradient-secondary ms-1"
                        @click="reload_product(product.id)"
                      >
                        <i class="fa-solid fa-rotate-right"></i>
                      </button>
                    </div>
                  </div>
                  <div
                    class="col-auto d-flex justify-content-center align-items-center"
                  >
                    <div
                      class="avatar avatar-xl cursor-pointer"
                      @click="image_view(`${image}/products/${product.image}`)"
                    >
                      <img
                        height="80"
                        width="80"
                        style="object-fit: cover"
                        :src="`${image}/products/${product.image}`"
                        class="text-white rounded-2"
                        alt="Product Type"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="products" />
            <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
          </div>
        </div>
        <div v-else>
          <div
            class="row my-2 cycle"
            v-for="cycle in filteredData"
            :key="cycle"
          >
            <div class="col-12">
              <div class="card shadow-sm">
                <div class="card-body py-3 cycle">
                  <div class="row d-flex align-items-center">
                    <div class="col-md context">
                      <h6 class="card-title mb-0">
                        <b class="text-primary"> {{ cycle.name }} </b> ({{
                          $t("cycle.kpi")
                        }}:
                        {{ Intl.NumberFormat().format(cycle.kpi) }}
                        {{ $t("currency") }})
                      </h6>
                      <h6 class="mb-0" style="font-size: 14px">
                        {{ $t("cycle.plan_value") }}:
                        {{ Intl.NumberFormat().format(cycle.plan_value) }}
                        {{ product_type?.olchov?.name }}
                      </h6>
                      <h6 class="mb-0" style="font-size: 14px">
                        {{ $t("cycle.plan_day") }}:
                        {{ Intl.NumberFormat().format(cycle.plan_day) }}
                        {{ $t("cycle.day") }}
                      </h6>
                      <h6 class="mb-0" style="font-size: 14px">
                        {{ $t("cycle.spending_minut") }}:
                        {{ Intl.NumberFormat().format(cycle.spending_minut) }}
                        {{ $t("cycle.minut") }}
                      </h6>
                    </div>
                    <div class="col-md-auto">
                      <div class="row my-1">
                        <div class="col p-0 px-1">
                          <button
                            class="btn btn-sm bg-gradient-secondary mb-0 px-3 w-100"
                            data-bs-toggle="modal"
                            data-bs-target="#plant-cycle"
                            @click="$refs.plant_cycle.get(1, 10, cycle.id)"
                          >
                            <i class="fas fa-box"></i>
                          </button>
                        </div>
                        <div class="col p-0 px-1">
                          <button
                            class="btn btn-sm bg-gradient-primary mb-0 px-3 w-100"
                            data-bs-toggle="modal"
                            data-bs-target="#cycle-works"
                            @click="
                              $refs.cycle_work.body.cycle_id = cycle.id;
                              $refs.cycle_work.get(1, 10);
                            "
                          >
                            <i class="ni ni-collection"></i>
                          </button>
                        </div>
                        <div class="col p-0 px-1">
                          <button
                            class="btn btn-sm bg-gradient-primary mb-0 px-3 w-100"
                            data-bs-toggle="modal"
                            data-bs-target="#user-kpi"
                            @click="
                              $refs.cycle_kpi.body.value = cycle.kpi;
                              $refs.cycle_kpi.body.cycle_id = cycle.id;
                              $refs.cycle_kpi.get(1, 10);
                            "
                          >
                            <i class="fas fa-users"></i>
                          </button>
                        </div>
                        <div class="col p-0 px-1">
                          <button
                            class="btn btn-sm bg-gradient-primary mb-0 px-3 w-100"
                            data-bs-toggle="modal"
                            data-bs-target="#component"
                            @click="$refs.component.get(1, 10, cycle.id)"
                          >
                            <i class="fas fa-eye"></i>
                          </button>
                        </div>
                        <div class="col p-0 px-1">
                          <button
                            class="btn btn-sm bg-gradient-warning mb-0 px-3 w-100"
                            @click="
                              item_cycle = {
                                id: cycle.id,
                                name: cycle.name,
                                plan_day: cycle.plan_day,
                                plan_value: cycle.plan_value,
                                spending_minut: cycle.spending_minut,
                                kpi: cycle.kpi,
                                disabled: cycle.disabled,
                                special: cycle.special,
                                hasPeriod: cycle.hasPeriod,
                              }
                            "
                            data-bs-toggle="modal"
                            data-bs-target="#update-cycle"
                          >
                            <i class="fas fa-pen"></i>
                          </button>
                        </div>
                        <div class="col p-0 px-1">
                          <button
                            class="btn btn-sm bg-gradient-danger mb-0 px-3 w-100"
                            @click="
                              item_cycle = {
                                id: cycle.id,
                                name: cycle.name,
                                plan_day: cycle.plan_day,
                                plan_value: cycle.plan_value,
                                spending_minut: cycle.spending_minut,
                                kpi: cycle.kpi,
                                disabled: true,
                                special: cycle.special,
                                hasPeriod: cycle.hasPeriod,
                              };
                              put_cycle(item_cycle);
                            "
                          >
                            <i class="fas fa-trash"></i>
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12">
              <data-notfound
                :data="product_type.cycles != null ? product_type.cycles : ''"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div id="image-viewer">
    <span class="close" @click="image_view()">&times;</span>
    <img class="modal-content" id="full-image" />
  </div>
  <product-modals
    :olchovs="olchovs"
    @add="add"
    :item="item"
    @put="put"
    @clone="clone"
  />
  <cycle-modals @add="add_cycle" :item="item_cycle" @put="put_cycle" />
  <component-modals ref="component" />
  <plant-cycle ref="plant_cycle" />
  <cycle-kpi ref="cycle_kpi" />
  <work ref="cycle_work" />
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";
import ProductModals from "@/components/modals/ProductModals.vue";
import Swal from "sweetalert2";
import CycleModals from "@/components/modals/CycleModals.vue";
import ComponentModals from "@/components/modals/ComponentModals.vue";
import PlantCycle from "@/components/modals/PlantCycle.vue";
import CycleKpi from "@/components/modals/CycleKpi.vue";
import Work from "../work/work.vue";

export default {
  components: {
    dataNotfound,
    Pagination,
    ProductModals,
    CycleModals,
    ComponentModals,
    PlantCycle,
    CycleKpi,
    Work,
  },
  name: "Product",
  data() {
    return {
      products: [],
      olchovs: [],
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      disabled: false,
      image: api.image,
      item: {},
      product_type_id: this.$route.query.id,
      product_id: null,
      content: "product",
      item_cycle: "",
      product_type: {},
    };
  },
  methods: {
    get(page, limit) {
      api
        .get_products(
          page,
          limit,
          this.search,
          this.product_type_id,
          this.disabled
        )
        .then((res) => {
          this.pages = res.data.page_count;
          this.page = res.data.current_page;
          this.limit = res.data.page_limit;
          this.products = res.data.data;
        });
    },
    get_olchovs() {
      api.get_olchovs(1, 1000, "").then((res) => {
        this.olchovs = res.data.data;
      });
    },
    image_view(src) {
      if (src) {
        document.getElementById("full-image").src = src;
        document.getElementById("image-viewer").style.display = "block";
      } else {
        document.getElementById("image-viewer").style.display = "none";
      }
    },
    add(body) {
      api.create_product(api.getFormData(body)).then(() => {
        body.name = "";
        body.image = "";
        body.olchov_id = this.$route.query.olchov_id;
        body.unit = "";
        body.alarm_value = 0;
        body.trade_price = 0;
        body.plant_price = 0;
        api.success("close");
        this.get(this.page, this.limit);
      });
    },
    put(body) {
      let data = api.getFormData(body);
      if (!body.image) {
        data.delete("image");
      }
      api.put_product(data, this.product_id).then(() => {
        api.success("close-put");
        this.get(this.page, this.limit);
      });
    },
    clone(id, name, data, file) {
      console.log(file);
      api.product_clonize(id, name, data).then(() => {
        api.success("close-clone").then(() => {
          this.get(this.page, this.limit);
        });
      });
    },
    remove(body) {
      Swal.fire({
        title: this.$t("delete_text"),
        showCancelButton: true,
        confirmButtonText: this.$t("confirm"),
        cancelButtonText: this.$t("cancel"),
      }).then((result) => {
        if (result.isConfirmed) {
          this.put(body);
        }
      });
    },
    add_cycle(body) {
      api.create_cycle(body).then(() => {
        body.name = "";
        body.kpi = 0;
        body.plan_value = 0;
        body.plan_day = 0;
        body.spending_minut = 0;
        body.special = false;
        body.components = [];
        api.success("close-cycle");
        this.get_product_type();
      });
    },
    put_cycle(body) {
      api.put_cycle(body).then(() => {
        api.success("close-cycle-put");
        this.get_product_type();
      });
    },
    get_product_type() {
      api.get_product_type(this.product_type_id).then((res) => {
        this.product_type = res.data;
      });
    },
    reload_product(id) {
      api.make_tree(id).then(() => {
        api.success();
        this.get(this.page, this.limit);
      });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
  computed: {
    filteredData() {
      return this.product_type?.cycles?.filter(({ name }) =>
        [name].some((val) =>
          val.toLowerCase().includes(this.search.toLowerCase())
        )
      );
    },
  },
};
</script>
