<template>
  <div
    class="modal fade"
    id="add-expense"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-expense-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-expense-label">
            {{ $t("expense.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('add', body)">
          <div class="modal-body">
            <div class="row">
              <div class="col-12">
                <div class="form-group">
                  <label for="name">{{ $t("expense.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="body.name"
                    :placeholder="$t('expense.name')"
                    required
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="update-expense"
    tabindex="-1"
    role="dialog"
    aria-labelledby="update-expense-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="update-expense-label">
            {{ $t("expense.update") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('put', data)">
          <div class="modal-body">
            <div class="row">
              <div class="col-12">
                <div class="form-group">
                  <label for="name">{{ $t("expense.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="data.name"
                    :placeholder="$t('expense.name')"
                    required
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-put
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("update") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="pay-for-expense"
    tabindex="-1"
    role="dialog"
    aria-labelledby="pay-for-expense-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="pay-for-expense-label">
            {{ $t("expense.pay") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('pay', pay_body)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="kassa">{{ $t("expense.kassa") }}</label>
                  <select
                    id="kassa"
                    class="form-control"
                    v-model="pay_body.kassa_id"
                    required
                  >
                    <option
                      :value="cashreg.id"
                      v-for="cashreg in cashregs"
                      :key="cashreg"
                    >
                      {{ cashreg.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="money">{{ $t("expense.money") }}</label>
                  <div class="input-group">
                    <input
                      id="money"
                      type="number"
                      class="form-control"
                      step="any"
                      v-model="pay_body.money"
                      @focus="
                        pay_body.money == 0
                          ? (pay_body.money = null)
                          : pay_body.money
                      "
                      @focusout="
                        pay_body.money == null
                          ? (pay_body.money = 0)
                          : pay_body.money
                      "
                      :placeholder="$t('expense.money')"
                      required
                    />
                    <span class="input-group-text">{{ $t("currency") }}</span>
                  </div>
                </div>
              </div>
              <div class="col-12">
                <div class="form-group">
                  <label for="comment">{{ $t("expense.comment") }}</label>
                  <textarea
                    id="comment"
                    class="form-control"
                    v-model="pay_body.comment"
                    :placeholder="$t('expense.comment')"
                    required
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-expense
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("save") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";

export default {
  name: "expense-modal",
  props: ["item"],
  emits: ["add", "put", "pay"],
  data() {
    return {
      body: {
        name: "",
      },
      pay_body: {
        money: 0,
        type: "",
        source: null,
        comment: "",
        kassa_id: null,
      },
      cashregs: [],
    };
  },
  methods: {
    get_cashregs() {
      api.kassa("", 0, this.$route.query.market_id).then((res) => {
        this.cashregs = res.data;
      });
    },
  },
  computed: {
    data() {
      return this.item;
    },
  },
};
</script>
