<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">{{ $t("plant.title") }}</h6>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col-md-6 my-1">
                <button
                  class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
                  data-bs-toggle="modal"
                  data-bs-target="#add-plant"
                  @click="get_shifts()"
                >
                  {{ $t("plant.add-button") }}
                </button>
              </div>
              <div class="col-md-6 my-1">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    get(1, limit);
                    $store.dispatch('setloading', false);
                  "
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-lg-4 col-md-6 col-sm-12 my-2"
            v-for="plant in plants"
            :key="plant"
          >
            <div class="card card-zoom h-100 shadow-sm">
              <div class="card-body py-2">
                <h5 class="card-title text-muted mb-0">
                  {{ plant.name }}
                </h5>
                <h6 class="text-muted mb-0">
                  {{ $t("cycle.kpi") }}: {{ plant.kpi }}
                  {{ $t("currency") }}
                </h6>
                <div class="row my-1">
                  <div class="col">
                    <button
                      class="btn btn-sm w-100 bg-gradient-warning mb-0 px-3"
                      @click="
                        item = {
                          id: plant.id,
                          name: plant.name,
                          kpi: plant.kpi,
                          disabled: plant.disabled,
                        }
                      "
                      data-bs-toggle="modal"
                      data-bs-target="#update-plant"
                    >
                      <i class="fas fa-pen"></i>
                    </button>
                  </div>
                  <div class="col">
                    <router-link
                      :to="{
                        name: 'Norm',
                        query: { id: plant.id, name: plant.name },
                      }"
                      class="btn btn-sm w-100 bg-gradient-primary mb-0 px-3"
                    >
                      <i class="fas fa-box"></i>
                    </router-link>
                  </div>
                  <div class="col">
                    <router-link
                      :to="{
                        name: 'PlantView',
                        query: { id: plant.id, name: plant.name },
                      }"
                      class="btn btn-sm w-100 bg-gradient-secondary mb-0 px-3"
                    >
                      <i class="fas fa-eye"></i>
                    </router-link>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="plants" />
            <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
          </div>
        </div>
      </div>
    </div>
  </div>
  <plant-modals @add="add" :item="item" @put="put" :shifts="shifts" />
</template>

<script>
import * as api from "@/server/api";
import PlantModals from "@/components/modals/PlantModals.vue";
import Pagination from "@/components/pagination.vue";
import DataNotfound from "@/components/dataNotfound.vue";

export default {
  components: { PlantModals, Pagination, DataNotfound },
  name: "PLant",
  data() {
    return {
      plants: [],
      shifts: [],
      pages: 0,
      page: 1,
      limit: 10,
      disabled: false,
      search: "",
      item: {},
    };
  },
  methods: {
    get(page, limit) {
      api.get_plants(page, limit, this.search).then((res) => {
        this.pages = res.data.page_count;
        this.page = res.data.current_page;
        this.limit = res.data.page_limit;
        this.plants = res.data.data;
      });
    },
    get_shifts() {
      api.get_shifts(1, 100).then((res) => {
        this.shifts = res.data.data;
      });
    },
    add(body) {
      api.create_plant(body).then(() => {
        body.name = "";
        body.kpi = 0;
        body.plant_users = [];
        api.success("close");
        this.get(this.page, this.limit);
      });
    },
    put(body) {
      api.put_plant(body).then(() => {
        api.success("close-put");
        this.get(this.page, this.limit);
      });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>
