<template>
  <div
    class="modal fade"
    :id="`production-details${index}`"
    tabindex="-1"
    role="dialog"
    aria-labelledby="take-supply-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header d-block">
          <h6 class="modal-title mb-1">
            {{ $t("production.order.name") }}: {{ detail.product_name }}
          </h6>
          <h6 class="modal-title mb-1">
            {{ $t("plan.material") }}: {{ detail.material }}
          </h6>
          <h6 class="modal-title mb-1">
            {{ $t("production.order.progress") }}: {{ detail.cycle_name }}
          </h6>
          <h6 class="modal-title mb-1" v-if="detail.izoh">
            {{ $t("production.order.description") }}: {{ detail.izoh }}
          </h6>
        </div>
        <form @submit.prevent="post">
          <div class="modal-body pt-0">
            <div>
              <div class="row mb-2" v-if="details?.before_cycle">
                <div class="col">
                  <div class="card shadow-none border border-secondary-custom">
                    <div class="card-body">
                      <span
                        >{{ $t("production.material_name") }}:
                        {{ details?.before_cycle?.Cycle?.name }}</span
                      >
                      <br />
                      <div
                        v-if="details?.before_cycle?.Cycle?.hasPeriod"
                        class="row"
                      >
                        <div class="col-auto">
                          {{ $t("production.warehouse") }}:
                        </div>
                        <div class="col-8">
                          <table class="table table-sm table-bordered">
                            <thead>
                              <tr>
                                <th class="py-1"></th>
                                <th class="py-1">
                                  {{ $t("product-store.table.quantity") }}
                                </th>
                                <th class="py-1">
                                  {{ $t("product-store.date") }}
                                </th>
                              </tr>
                            </thead>
                            <tbody>
                              <tr
                                v-for="(warehouse, i) in details?.before_cycle
                                  ?.warehouse"
                                :key="warehouse"
                              >
                                <td>{{ i + 1 }}</td>
                                <td>
                                  {{ warehouse.value }} {{ detail.olchov_name }}
                                </td>
                                <td
                                  :class="
                                    count_date(
                                      new Date().toISOString(),
                                      warehouse.period
                                    ) > 10
                                      ? 'text-success'
                                      : count_date(
                                          new Date().toISOString(),
                                          warehouse.period
                                        ) <= 10 &&
                                        count_date(
                                          new Date().toISOString(),
                                          warehouse.period
                                        ) > 5
                                      ? 'text-warning'
                                      : 'text-danger'
                                  "
                                >
                                  {{
                                    format_date(warehouse.period).slice(0, 10)
                                  }}
                                </td>
                              </tr>
                            </tbody>
                          </table>
                        </div>
                      </div>
                      <div v-else>
                        {{ $t("production.warehouse") }}:
                        {{ details?.before_cycle?.warehouse }}
                        {{ detail.olchov_name }}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row mb-2" v-if="detail.hasSize">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="size1">{{ $t("production.size") }} 1</label>
                  <input
                    id="size1"
                    type="number"
                    class="form-control text-uppercase"
                    v-model="body.size1"
                    min="0"
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="size2">{{ $t("production.size") }} 2</label>
                  <input
                    id="size2"
                    type="number"
                    class="form-control text-uppercase"
                    v-model="body.size2"
                    min="0"
                  />
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col" v-if="details?.next_plants?.length > 1">
                <div class="form-group">
                  <label for="plant">{{ $t("production.plant") }}</label>
                  <select
                    id="plant"
                    class="form-select"
                    v-model="selectedPlantId"
                    :disabled="!details?.next_plants"
                    required
                  >
                    <option
                      v-for="plant in details?.next_plants"
                      :key="plant"
                      :value="plant.id"
                    >
                      {{ plant.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="norm">{{ $t("production.norm") }}</label>
                  <div class="input-group">
                    <input
                      id="norm"
                      type="number"
                      class="form-control text-uppercase"
                      v-model="body.norm"
                      step="any"
                      min="0"
                    />
                  </div>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="value">{{ $t("production.value") }}</label>
                  <div class="input-group">
                    <input
                      id="value"
                      type="number"
                      class="form-control"
                      v-model="body.value"
                      step="any"
                      :placeholder="detail.olchov_name"
                      min="0"
                      required
                    />
                    <span class="input-group-text">{{
                      detail.olchov_name
                    }}</span>
                    <button
                      :disabled="body.value <= 0"
                      class="btn bg-gradient-success mb-0 input-group-text"
                      @click="$emit('get-details', body.value)"
                      type="button"
                    >
                      <i class="fas fa-check"></i>
                    </button>
                  </div>
                </div>
              </div>
              <div class="col-md-6" v-if="details?.this_cycle?.hasPeriod">
                <label for="period">{{ $t("production.period") }}</label>
                <input
                  id="period"
                  type="date"
                  class="form-control text-uppercase"
                  v-model="period"
                  :min="new Date().toISOString().slice(0, 10)"
                />
              </div>
            </div>
            <table
              class="table table-bordered mt-2"
              v-if="details?.components?.length"
            >
              <thead>
                <tr>
                  <th>{{ $t("production.material_name") }}</th>
                  <th>{{ $t("production.needed_value") }}</th>
                  <th>{{ $t("production.warehouse") }}</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="detail in details?.components"
                  :key="detail"
                  :class="
                    detail.needy_value > detail.warehouse ? 'table-danger' : ''
                  "
                >
                  <td v-if="detail.Component_Item.material">
                    {{ detail.Component_Item.material.name }}
                  </td>
                  <td v-else>{{ detail.Component_Item.product.name }}</td>
                  <td v-if="detail.Component_Item.material">
                    {{ detail.needy_value }}
                    {{ detail.Component_Item.material.olchov.name }}
                  </td>
                  <td v-else>
                    {{ detail.needy_value }}
                    {{ detail.Component_Item.product.olchov.name }}
                  </td>
                  <td v-if="detail.Component_Item.material">
                    {{ detail.warehouse }}
                    {{ detail.Component_Item.material.olchov.name }}
                  </td>
                  <td v-else>
                    {{ detail.warehouse }}
                    {{ detail.Component_Item.product.olchov.name }}
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              :class="`btn bg-gradient-secondary close${index}`"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button
              type="submit"
              class="btn bg-gradient-primary"
              :disabled="
                details?.before_cycle?.warehouse < body.value ||
                body.value <= 0 ||
                details?.next_plants?.length > 1
                  ? !selectedPlantId
                  : false
              "
            >
              {{ $t("production.doit") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
export default {
  name: "production-modals",
  props: ["details", "detail", "content", "index"],
  emits: ["get-details", "get-orders", "get-orders-production"],
  data() {
    return {
      body: {
        value: 0,
        size1: this.detail.size1,
        size2: this.detail.size2,
        norm: 0,
      },
      period: "",
      selectedPlantId: "",
      selectedPlantName: null,
      count_date: api.countDays,
      format_date: api.format_date,
    };
  },
  methods: {
    post() {
      api
        .production_create(
          this.detail.product_cycle_id,
          this.detail.material_id,
          this.detail.plan_id,
          this.$store.state.user,
          this.details?.next_plants?.length
            ? this.details?.next_plants?.length === 1
              ? this.details?.next_plants[0]?.id
              : this.$store.state.user
            : 0,
          Number(this.body.value),
          Number(this.body.size1),
          Number(this.body.size2),
          Number(this.body.norm),
          this.detail.trade_id ? this.detail.trade_id : 0,
          this.detail.trade_item_id ? this.detail.trade_item_id : 0,
          this.period
          // this.content === "orders" ? this.detail.detail : true
        )
        .then(() => {
          api.success("close");
          document.querySelector(`.close${this.index}`).click();
          this.body.value = 1;
          if (this.content === "production") {
            this.$emit("get-orders-production", false);
          } else {
            this.$emit("get-orders", false);
          }
        });
    },
  },
};
</script>
