<template>
  <div class="table-responsive">
    <table class="table table-sm table-hover table-bordered">
      <thead>
        <tr>
          <th></th>
          <th>{{ $t("trades.table.image") }}</th>
          <th>{{ $t("trades.table.product") }}</th>
          <th>{{ $t("trades.table.value") }}</th>
          <th>{{ $t("trades.table.price") }}</th>
          <th class="w-custom-40"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in trades?.data" :key="item">
          <td>
            <!-- {{
                countOrdinalNumber(
                  index,
                  trades?.current_page + 1,
                  trades?.page_limit
                )
              }} -->
            {{ index + 1 }}
          </td>
          <td>
            <div class="img">
              <img
                :src="image + '/products/' + item.Trades.product.image"
                :alt="item.Trades.product.image"
                @click="
                  image_view(image + '/products/' + item.Trades.product.image)
                "
              />
            </div>
          </td>
          <td>
            {{ item.Trades.product.name }}
          </td>
          <td>
            {{ item.sum_quantity + " " + item.Trades.product.olchov.name }}
          </td>
          <td>
            {{
              Intl.NumberFormat().format(
                trades?.data2.find((item1) => item1.trade_id == item.Trades.id)
                  .sum_price
              ) +
              " " +
              $t("currency")
            }}
          </td>
          <td>
            <div class="btn-group">
              <button
                class="btn btn-sm bg-gradient-primary m-0 py-2 px-3"
                data-bs-toggle="modal"
                data-bs-target="#component-modal"
                @click="
                  product = item;
                  product_component = null;
                  getProduct();
                  getTradeItems();
                "
              >
                <i class="fa fa-eye"></i>
              </button>
              <button
                class="btn btn-sm bg-gradient-danger m-0 py-2 px-3"
                @click="deleteTrade(item.Trades.id)"
              >
                <i class="fa fa-trash"></i>
              </button>
            </div>
          </td>
        </tr>
      </tbody>
      <tfoot v-if="trades?.data?.length">
        <tr>
          <td colspan="8">
            <pagination
              :page="trades?.current_page"
              :pages="trades?.pages"
              :limit="trades?.limit"
              @get="getTrades"
            />
          </td>
        </tr>
      </tfoot>
    </table>
  </div>

  <div
    class="modal fade"
    id="product-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="product-modal-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-order-modal-label">
            {{ $t("trades.modal-title") }}
          </h5>
        </div>
        <form @submit.prevent>
          <div class="modal-body">
            <div class="row gap-1">
              <div class="col-12">
                <input
                  type="text"
                  class="form-control"
                  :placeholder="$t('search')"
                  v-model="search_product_type"
                  @keyup="getProductTypes(1, 25)"
                />
              </div>
              <div
                class="col-12 border border-primary rounded"
                v-for="(item, i) in product_types?.data.filter(
                  (item) => item.detail
                )"
                :key="item"
              >
                <div
                  class="d-flex align-items-center p-2 pointer"
                  :toggle-collapse="i"
                  @click="
                    product_type = item;
                    !item.open ? getProducts(1, 25) : false;
                  "
                >
                  <div class="img">
                    <img
                      :src="image + '/product_types/' + item.image"
                      :alt="item.image"
                      @click="
                        image_view(image + '/product_types/' + item.image)
                      "
                    />
                  </div>
                  <strong class="p-2">{{ item.name }}</strong>
                </div>
                <collapse :id="i" @value="item.open = $event">
                  <div class="row gap-1">
                    <div class="col-12">
                      <input
                        type="text"
                        class="form-control"
                        :placeholder="$t('search')"
                        v-model="item.search"
                        @keyup="
                          product_type = item;
                          getProducts(1, 25);
                        "
                      />
                    </div>
                    <div class="col-12">
                      <ul class="list-group">
                        <li
                          class="list-group-item flex pointer px-2"
                          style="cursor: pointer"
                          v-for="item1 in item.products?.data"
                          :key="item1"
                          data-bs-toggle="modal"
                          data-bs-target="#trade-modal"
                          @click="
                            product = item1;
                            new_trade.price = item1.trade_price;
                          "
                        >
                          <div class="d-flex align-items-center">
                            <div class="img">
                              <img
                                :src="image + '/products/' + item1.image"
                                :alt="item1.image"
                              />
                            </div>
                            <div class="p-2">
                              <strong>{{ item1.name }}</strong>
                              <p class="m-0">
                                {{
                                  Intl.NumberFormat().format(
                                    item1.trade_price
                                  ) +
                                  " " +
                                  $t("currency")
                                }}
                              </p>
                            </div>
                          </div>
                        </li>
                      </ul>
                    </div>
                    <div class="col-12">
                      <pagination
                        :page="item.products?.current_page"
                        :pages="item.products?.page_count"
                        :limit="item.products?.page_limit"
                        @get="getProducts"
                      />
                    </div>
                  </div>
                </collapse>
              </div>
              <div class="col-12">
                <pagination
                  :page="product_types?.current_page"
                  :pages="product_types?.page_count"
                  :limit="product_types?.page_limit"
                  @get="getProductTypes"
                />
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-product-order-modal
            >
              {{ $t("close") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>

  <div
    class="modal fade"
    id="trade-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="trade-modal-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-order-modal-label">
            {{ product?.name }}
          </h5>
        </div>
        <form @submit.prevent="postTrade()">
          <div class="modal-body">
            <div class="row gap-1">
              <div class="col-12">
                <div class="input-group">
                  <input
                    class="form-control"
                    type="number"
                    :min="product?.unit ? 0.1 : 1"
                    :step="product?.unit ? 'any' : 1"
                    :placeholder="$t('trades.placeholders.value')"
                    required
                    v-model="new_trade.quantity"
                    @focus="
                      new_trade.quantity == 0
                        ? (new_trade.quantity = null)
                        : new_trade.quantity
                    "
                    @focusout="
                      new_trade.quantity == null
                        ? (new_trade.quantity = 0)
                        : new_trade.quantity
                    "
                  />
                  <div class="input-group-text">
                    {{ product?.olchov?.name }}
                  </div>
                </div>
              </div>
              <div class="col-12" v-if="false">
                <div class="input-group">
                  <input
                    class="form-control"
                    type="number"
                    min="0.1"
                    step="any"
                    :placeholder="$t('trades.placeholders.price')"
                    required
                    v-model="new_trade.price"
                    @focus="
                      new_trade.price == 0
                        ? (new_trade.price = null)
                        : new_trade.price
                    "
                    @focusout="
                      new_trade.price == null
                        ? (new_trade.price = 0)
                        : new_trade.price
                    "
                  />
                  <div class="input-group-text">
                    {{ $t("currency") }}
                  </div>
                </div>
              </div>
              <div class="col-12" v-if="false">
                <div class="input-group">
                  <input
                    class="form-control"
                    type="number"
                    min="0"
                    step="any"
                    :placeholder="$t('trades.placeholders.discount')"
                    v-model="new_trade.discount"
                    @focus="
                      new_trade.discount == 0
                        ? (new_trade.discount = null)
                        : new_trade.discount
                    "
                    @focusout="
                      new_trade.discount == null
                        ? (new_trade.discount = 0)
                        : new_trade.discount
                    "
                  />
                  <div class="input-group-text">
                    {{ $t("currency") }}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-trade-order-modal
            >
              {{ $t("close") }}
            </button>
            <button class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>

  <div
    class="modal fade"
    id="component-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="component-modal-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-order-modal-label">
            {{ product?.Trades?.product?.name }}
          </h5>
        </div>
        <!-- <form @submit.prevent="postTradeItem()"> -->
        <div class="modal-body">
          <div class="row">
            <hr class="mt-2" />
            <div class="col-md-5">
              <input
                class="form-control form-control-sm"
                type="search"
                :placeholder="$t('search')"
                v-model="search_product_components"
              />
              <ul
                class="list-group p-1 table-responsive"
                style="max-height: 60vh"
              >
                <li
                  class="list-group-item p-1"
                  v-for="(item, i) in filter_product_components"
                  :key="item"
                >
                  <p
                    class="m-0 pointer"
                    :toggle-collapse="i"
                    @click="
                      product_component = item;
                      product_item = null;
                      toggleCollapse(i);
                      getProductItems(item);
                    "
                  >
                    {{
                      item.product_type
                        ? item.product_type.name
                        : item.material_type.name
                    }}
                  </p>
                  <collapse :id="i" :ref="`productComponentCollapse${i}`">
                    <input
                      class="form-control form-control-sm"
                      type="search"
                      :placeholder="$t('search')"
                      v-model="search_product_items"
                      @keyup="getProductItems(item)"
                    />
                    <ul
                      class="list-group p-1 scroll-25"
                      @scroll="scrollProductItems($event, item)"
                    >
                      <li
                        class="list-group-item p-1 flex"
                        v-for="item1 in item.product_items?.data"
                        :key="item1"
                      >
                        <div class="d-flex align-items-center">
                          <div class="img">
                            <img
                              :src="image + '/products/' + item1.image"
                              :alt="item1.image"
                              @click="
                                image_view(image + '/products/' + item1.image)
                              "
                            />
                          </div>
                          <strong class="px-1">
                            {{ item1.name }}
                          </strong>
                        </div>
                        <button
                          type="button"
                          class="btn m-0 px-2 py-1 bg-gradient-primary"
                          data-bs-toggle="modal"
                          data-bs-target="#component-item-modal"
                          @click="
                            product_item = item1;
                            product_material = null;
                            new_component = {
                              component_id: 0,
                              material_id: 0,
                              material2_id: 0,
                              product_id: 0,
                              quantity: null,
                              price: null,
                              trade_id: 0,
                              size1: null,
                              size2: null,
                              comment: '',
                              order_id: this.$route.query.id,
                            };
                            material_price = null;
                            item1.price
                              ? (figure_price = item1.price)
                              : (figure_price = item1.trade_price);
                            item1.price
                              ? (new_component.price = item1.price)
                              : (new_component.price = item1.trade_price);
                            new_component_sum_price = 0;
                          "
                        >
                          <i class="fa fa-share"></i>
                        </button>
                      </li>
                    </ul>
                  </collapse>
                </li>
              </ul>
            </div>
            <div class="col-md-7">
              <div class="table-responsive" style="max-height: 60vh">
                <ul class="list-group">
                  <li
                    class="list-group-item px-3"
                    v-for="item in trade_items"
                    :key="item"
                  >
                    <div class="row">
                      <div
                        class="col-6 d-flex align-items-center"
                        v-if="item.component_item.product"
                      >
                        <img
                          class="component-img"
                          :src="
                            image +
                            '/products/' +
                            item.component_item.product.image
                          "
                          @click="
                            image_view(
                              `${image}/products/${item.component_item.product.image}`
                            )
                          "
                          :alt="item.component_item.product.image"
                        />
                      </div>
                      <div class="col-6">
                        <div class="row">
                          <div class="col-12">
                            <strong>
                              {{
                                item.component_item.product
                                  ? item.component_item.product.name
                                  : item.component_item.material.name
                              }}
                            </strong>
                            <p class="m-0">
                              {{
                                Intl.NumberFormat().format(item.price) +
                                " " +
                                $t("currency")
                              }}
                            </p>
                            <span
                              class="m-0"
                              v-if="item.component_item.product"
                            >
                              <p class="m-0">
                                {{
                                  $t("pre-order.placeholders.value") +
                                  ": " +
                                  item.quantity +
                                  " " +
                                  item.component_item.product.olchov.name
                                }}
                              </p>
                              <p v-if="item.material" class="m-0">
                                {{
                                  $t("material.material") +
                                  ": " +
                                  item.material.name
                                }}
                              </p>
                              <span
                                class="m-0"
                                v-if="item.trade_items_size.length"
                              >
                                <p
                                  class="m-0"
                                  v-if="
                                    item.trade_items_size[0].size1 ||
                                    item.trade_items_size[0].size2
                                  "
                                >
                                  {{
                                    $t("pre-order.placeholders.width") +
                                    ": " +
                                    item.trade_items_size[0].size1 +
                                    "cm"
                                  }}
                                  {{
                                    $t("pre-order.placeholders.height") +
                                    ": " +
                                    item.trade_items_size[0].size2 +
                                    "cm"
                                  }}
                                </p>
                                <p
                                  class="m-0"
                                  v-if="item.trade_items_size[0].comment"
                                >
                                  {{
                                    $t("pre-order.placeholders.comment") +
                                    ": " +
                                    item.trade_items_size[0].comment
                                  }}
                                </p>
                              </span>
                            </span>
                            <span
                              class="m-0"
                              v-if="item.component_item.material"
                            >
                              <p class="m-0">
                                {{
                                  $t("pre-order.placeholders.value") +
                                  ": " +
                                  item.quantity +
                                  " " +
                                  item.component_item.material.olchov.name
                                }}
                              </p>
                              <span
                                class="m-0"
                                v-if="item.trade_items_size.length"
                              >
                                <p
                                  class="m-0"
                                  v-if="
                                    item.trade_items_size[0].size1 ||
                                    item.trade_items_size[0].size2
                                  "
                                >
                                  {{
                                    $t("pre-order.placeholders.width") +
                                    ": " +
                                    item.trade_items_size[0].size1 +
                                    "cm"
                                  }}
                                  {{
                                    $t("pre-order.placeholders.height") +
                                    ": " +
                                    item.trade_items_size[0].size2 +
                                    "cm"
                                  }}
                                </p>
                                <p
                                  class="m-0"
                                  v-if="item.trade_items_size[0].comment"
                                >
                                  {{
                                    $t("pre-order.placeholders.comment") +
                                    ": " +
                                    item.trade_items_size[0].comment +
                                    "cm"
                                  }}
                                </p>
                              </span>
                            </span>
                          </div>
                          <div class="col-12">
                            <button
                              type="button"
                              class="btn btn-sm m-0 px-3 py-2 bg-gradient-danger"
                              @click="deleteTradeItem(item.id)"
                            >
                              <i class="fa fa-trash"></i>
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-component-modal
          >
            {{ $t("close") }}
          </button>
        </div>
        <!-- </form> -->
      </div>
    </div>
  </div>

  <div
    class="modal fade"
    id="component-item-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="component-item-modal-label"
    aria-hidden="true"
    data-bs-backdrop="static"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <span>
            <h5 class="modal-title" id="add-order-modal-label">
              {{ product_item?.name || product_item?.name }}
            </h5>
            <strong>{{
              $t("pre-order.sm") +
              " " +
              Intl.NumberFormat().format(product_item?.trade_price) +
              " " +
              $t("currency")
            }}</strong>
          </span>
        </div>
        <form @submit.prevent="postTradeItem()">
          <div class="modal-body">
            <div class="row">
              <div class="col-12" v-if="product_component?.product_type">
                {{ $t("material.title") }}
                <div class="dropdown">
                  <button
                    id="customerDropdown"
                    class="btn btn-sm w-100 mb-0 dropdown-toggle"
                    data-bs-toggle="dropdown"
                    @click="getProductMaterials()"
                  >
                    {{
                      product_material?.material?.name || $t("material.title")
                    }}
                  </button>
                  <div
                    class="dropdown-menu w-100 p-1"
                    aria-labelledby="customerDropdown"
                  >
                    <!-- <input
                    type="text"
                    class="form-control form-control-sm my-1"
                    :placeholder="$t('search')"
                    v-model="search_customer"
                    @keyup="getCustomers()"
                  /> -->
                    <ul
                      class="list-group scroll-25 p-1"
                      @scroll="scrollProductMaterials($event)"
                    >
                      <li
                        class="list-group-item"
                        v-for="item in product_materials?.data"
                        :key="item"
                        @click="
                          product_material = item;
                          getMaterialCalculate();
                        "
                      >
                        {{ item.material.name }}
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div class="col-12" v-if="product_item?.product_type.hasSize">
                {{ $t("pre-order.placeholders.size") }}
                <div class="row">
                  <div class="col-6">
                    <div class="input-group input-group-sm">
                      <input
                        class="form-control"
                        type="number"
                        min="0.1"
                        step="any"
                        :placeholder="$t('pre-order.placeholders.width')"
                        v-model="new_component.size1"
                        @keyup="calculate()"
                        @focus="
                          new_component.size1 == 0
                            ? (new_component.size1 = null)
                            : new_component.size1
                        "
                        @focusout="
                          new_component.size1 == null
                            ? (new_component.size1 = 0)
                            : new_component.size1
                        "
                      />
                      <div class="input-group-text">cm</div>
                    </div>
                  </div>
                  <div class="col-6">
                    <div class="input-group input-group-sm">
                      <input
                        class="form-control"
                        type="number"
                        min="0.1"
                        step="any"
                        :placeholder="$t('pre-order.placeholders.height')"
                        v-model="new_component.size2"
                        @keyup="calculate()"
                        @focus="
                          new_component.size2 == 0
                            ? (new_component.size2 = null)
                            : new_component.size2
                        "
                        @focusout="
                          new_component.size2 == null
                            ? (new_component.size2 = 0)
                            : new_component.size2
                        "
                      />
                      <div class="input-group-text">cm</div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-6 text-lowercase">
                {{ $t("supply.product-material") }} {{ $t("customer.price") }}

                <div class="input-group input-group-sm">
                  <input
                    class="form-control"
                    type="number"
                    v-model="material_price"
                    @keyup="calculate()"
                  />
                  <div class="input-group-text">{{ $t("currency") }}</div>
                </div>
              </div>
              <div class="col-6 text-lowercase">
                {{ $t("production.order.name") }} {{ $t("customer.price") }}

                <div class="input-group input-group-sm">
                  <input
                    class="form-control"
                    type="number"
                    v-model="figure_price"
                    @keyup="calculate()"
                  />
                  <div class="input-group-text">{{ $t("currency") }}</div>
                </div>
              </div>
              <div class="col-12">
                {{ $t("trades.placeholders.price") }}
                <div class="input-group input-group-sm">
                  <input
                    class="form-control"
                    type="number"
                    min="0.1"
                    step="any"
                    :placeholder="$t('trades.placeholders.price')"
                    required
                    v-model="new_component.price"
                    @keyup="calculateSum()"
                    @focus="
                      new_component.price == 0
                        ? (new_component.price = null)
                        : new_component.price
                    "
                    @focusout="
                      new_component.price == null
                        ? (new_component.price = 0)
                        : new_component.price
                    "
                  />
                  <div class="input-group-text">
                    {{ $t("currency") }}
                  </div>
                </div>
              </div>
              <div class="col-12">
                {{ $t("pre-order.placeholders.value") }}
                <div class="input-group input-group-sm">
                  <input
                    class="form-control"
                    type="number"
                    :min="product_item?.unit ? 0.1 : 1"
                    :step="product_item?.unit ? 'any' : 1"
                    :placeholder="$t('pre-order.placeholders.value')"
                    required
                    v-model="new_component.quantity"
                    @keyup="calculateSum()"
                    @focus="
                      new_component.quantity == 0
                        ? (new_component.quantity = null)
                        : new_component.quantity
                    "
                    @focusout="
                      new_component.quantity == null
                        ? (new_component.quantity = 0)
                        : new_component.quantity
                    "
                  />
                  <div class="input-group-text">
                    {{
                      product_item
                        ? detail_status
                          ? product_item?.olchov?.name
                          : product_item?.olchov
                        : " - "
                    }}
                  </div>
                </div>
              </div>
              <div class="col-12">
                {{ $t("pre-order.placeholders.sum-price") }}
                <div class="input-group input-group-sm">
                  <div class="form-control">
                    {{ Intl.NumberFormat().format(new_component_sum_price) }}
                  </div>
                  <!-- <input
                    class="form-control"
                    type="number"
                    min="0.1"
                    step="any"
                    :placeholder="$t('trades.placeholders.price')"
                    required
                    v-model="new_component.price"
                    @focus="
                      new_component.price == 0
                        ? (new_component.price = null)
                        : new_component.price
                    "
                    @focusout="
                      new_component.price == null
                        ? (new_component.price = 0)
                        : new_component.price
                    "
                  /> -->
                  <div class="input-group-text">
                    {{ $t("currency") }}
                  </div>
                </div>
              </div>
              <div class="col-12">
                {{ $t("pre-order.placeholders.comment") }}
                <input
                  class="form-control form-control-sm"
                  type="text"
                  :placeholder="$t('pre-order.placeholders.comment')"
                  v-model="new_component.comment"
                />
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              class="btn bg-gradient-primary"
              :disabled="!product_component?.product_type"
            >
              {{ $t("add") }}
            </button>
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-toggle="modal"
              data-bs-target="#component-modal"
              close-component-item-modal
            >
              {{ $t("close") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>

  <div id="image-viewer">
    <span class="close" @click="image_view()">&times;</span>
    <img class="modal-content" id="full-image" />
  </div>
</template>

<script>
import * as api from "../../server/api";
import pagination from "@/components/pagination.vue";
export default {
  name: "preOrderTrades",
  components: { pagination },
  data() {
    return {
      image: api.image,
      search_product_type: "",
      product_types: {
        current_page: 1,
        page_count: 1,
        page_limit: 25,
        data: [],
      },
      product_type: null,
      product: null,
      new_trade: {
        product_store_pr_id: 0,
        quantity: 1,
        price: 0,
        discount: 0,
        order_id: this.$route.query.id,
      },
      search_product_components: "",
      product_components: [],
      product_component: null,
      detail_status: true,
      search_product_items: "",
      product_items: null,
      product_item: null,
      product_materials: {
        current_page: 1,
        page_count: 1,
        limit: 25,
        data: [],
      },
      product_material: null,
      component_calculation: 0,
      new_component_sum_price: 0,
      new_component: {
        component_id: 0,
        material_id: 0,
        material2_id: 0,
        product_id: 0,
        quantity: 0,
        price: 0,
        trade_id: 0,
        size1: 0,
        size2: 0,
        comment: "",
        order_id: this.$route.query.id,
      },
      trades: null,
      trade_items: [],
      material_price: null,
      figure_price: null,
    };
  },
  computed: {
    filter_product_components() {
      return this.product_components.filter((item) => {
        if (item.product_type)
          return item.product_type.name
            .toLowerCase()
            .match(this.search_product_components.toLowerCase());
        else
          return item.material_type.name
            .toLowerCase()
            .match(this.search_product_components.toLowerCase());
      });
    },
  },
  created() {
    this.getTrades(1, 25);
  },
  methods: {
    toggleCollapse(i) {
      let collapses = [];
      for (const item in this.$refs) {
        collapses.push(item);
      }
      collapses.forEach((item, index) => {
        if (index !== i) this.$refs[item].toggle(false);
      });
    },
    getTrades(page, limit) {
      api.trades(this.$route.query.id, false, 0, page, limit).then((res) => {
        this.trades = res.data;
      });
    },
    postTrade() {
      this.new_trade.product_id = this.product.id;
      api.take_product_to_pre_order(this.new_trade).then(() => {
        api.success("close-trade-order-modal").then(() => {
          this.new_trade = {
            product_store_pr_id: 0,
            quantity: 1,
            price: 0,
            discount: 0,
            order_id: this.$route.query.id,
          };
          this.getTrades(1, 25);
        });
      });
    },
    getTradeItems() {
      api.trade_items(this.product.Trades.id).then((res) => {
        this.trade_items = res.data;
        this.trade_items.sort(
          (a, b) => b.component_item.product - a.component_item.product
        );
      });
    },
    postTradeItem() {
      this.new_component.size1 = this.new_component.size1
        ? this.new_component.size1
        : 0;
      this.new_component.size2 = this.new_component.size2
        ? this.new_component.size2
        : 0;
      this.new_component.component_id = this.product_component.id;
      this.new_component.trade_id = this.product.Trades.id;
      this.new_component.material2_id =
        this.product_material?.material?.id || 0;
      if (this.detail_status)
        this.new_component.product_id = this.product_item.id;
      else this.new_component.material_id = this.product_item.id;
      api.take_component_item_to_pre_order(this.new_component).then(() => {
        api.success("close-component-item-modal").then(() => {
          this.new_component = {
            component_id: 0,
            material_id: 0,
            material2_id: 0,
            product_id: 0,
            quantity: 0,
            price: 0,
            trade_id: 0,
            size1: 0,
            size2: 0,
            comment: "",
            order_id: this.$route.query.id,
          };
          this.getTrades(1, 25);
          this.getTradeItems();
        });
      });
    },
    deleteTrade(id) {
      api.delete_product_from_pre_order(id).then(() => {
        api.success().then(() => {
          this.getTrades(1, 25);
        });
      });
    },
    deleteTradeItem(id) {
      api.delete_component_item_from_pre_order(id).then(() => {
        api.success().then(() => {
          this.getTradeItems();
        });
      });
    },
    getProductTypes(page, limit) {
      api
        .get_product_types(page, limit, this.search_product_type)
        .then((res) => {
          this.product_types = res.data;
          this.product_types.data.forEach((item) => {
            item.search = "";
            item.products = {
              current_page: 1,
              page_count: 1,
              page_limit: 25,
            };
          });
        });
    },
    getProducts(page, limit) {
      api
        .get_products(
          page,
          limit,
          this.product_type.search,
          this.product_type.id,
          false
        )
        .then((res) => {
          this.product_type.products = res.data;
        });
    },
    getProduct() {
      api.get_product(this.product.Trades.product.id).then((res) => {
        this.product_components = [];
        res.data.product_cycles.forEach((item) => {
          this.product_components = this.product_components.concat(
            item.cycle.components
          );
          this.product_components.forEach((item) => {
            item.product_items = {
              current_page: 1,
              page_count: 1,
            };
          });
          this.product_components.sort(
            (a, b) => b.product_type_id - a.product_type_id
          );
        });
      });
    },
    getProductItems(item) {
      if (this.product_component.product_type) {
        this.detail_status = true;
        api
          .get_products(
            1,
            25,
            this.search_product_items,
            this.product_component.product_type.id,
            false
          )
          .then((res) => {
            item.product_items = res.data;
          });
      } else if (this.product_component.material_type) {
        this.detail_status = false;
        api
          .get_materials(
            1,
            25,
            this.search_product_items,
            this.product_component.material_type.id
          )
          .then((res) => {
            item.product_items = res.data;
          });
      }
    },
    scrollProductItems(event, item) {
      if (this.checkScrollEnd(event)) {
        if (item.product_items.current_page < item.product_items.page_count) {
          if (this.product_component.product_type) {
            this.detail_status = true;
            api
              .get_products(
                item.product_items.current_page + 1,
                25,
                this.search_product_items,
                this.product_component.product_type.id,
                false
              )
              .then((res) => {
                item.product_items.current_page = res.data.current_page;
                item.product_items.page_count = res.data.page_count;
                item.product_items.data = item.product_items.data.concat(
                  res.data.data
                );
              });
          } else if (this.product_component.material_type) {
            this.detail_status = false;
            api
              .get_materials(
                item.product_items.current_page + 1,
                25,
                this.search_product_items,
                this.product_component.material_type.id
              )
              .then((res) => {
                item.product_items.current_page = res.data.current_page;
                item.product_items.page_count = res.data.page_count;
                item.product_items.data = item.product_items.data.concat(
                  res.data.data
                );
              });
          }
        }
      }
    },
    getProductMaterials() {
      api.product_material(1, 25, 0, this.product_item.id).then((res) => {
        this.product_materials = res.data;
      });
    },
    scrollProductMaterials(event) {
      if (this.checkScrollEnd(event)) {
        if (
          this.product_materials.current_page <
          this.product_materials.page_count - 1
        ) {
          api
            .product_material(
              0,
              this.product_item.id,
              this.product_materials.current_page + 1,
              25
            )
            .then((res) => {
              this.product_materials.current_page = res.data.current_page;
              this.product_materials.data = this.product_materials.data.concat(
                res.data.data
              );
            });
        }
      }
    },
    getMaterialCalculate() {
      api
        .component_calculation(
          this.product_item.id,
          this.product_material.material_id
        )
        .then((res) => {
          this.component_calculation = res.data;
          let surface =
            this.new_component.size1 && this.new_component.size2
              ? this.new_component.size1 * this.new_component.size2
              : null;
          this.material_price = surface
            ? this.component_calculation * surface
            : this.component_calculation;
          this.figure_price = surface
            ? this.product_item.trade_price * surface
            : this.product_item.trade_price;
          this.calculate();
        });
    },
    calculate() {
      this.new_component.price = this.material_price
        ? this.figure_price + this.material_price
        : this.figure_price;
      this.calculateSum();
    },
    calculateSum() {
      this.new_component_sum_price =
        this.new_component.price * this.new_component.quantity;
    },
    checkScrollEnd(event) {
      const element = event.target;
      if (element.scrollTop + element.clientHeight >= element.scrollHeight) {
        return true;
      }
      return false;
    },
    countOrdinalNumber(index, page, limit) {
      let start = null,
        numbers = [];
      start = (page - 1) * limit;
      for (let i = 0; i < limit; i++) {
        numbers.push(start);
        start++;
      }
      return numbers[index] + 1;
    },
    image_view(src) {
      if (src) {
        document.getElementById("full-image").src = src;
        document.getElementById("image-viewer").style.display = "block";
      } else {
        document.getElementById("image-viewer").style.display = "none";
      }
    },
  },
};
</script>

<style scoped>
.list-group {
  padding: 0;
}

.flex {
  padding: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.img img {
  width: 40px;
  height: 40px;
  border-radius: 10px;
  object-fit: cover;
}

.component-img {
  width: 100%;
  height: 100px;
  object-fit: cover;
}

.pointer {
  cursor: pointer;
}
</style>
