<template>
  <div
    class="modal fade"
    id="utilization"
    tabindex="-1"
    role="dialog"
    aria-labelledby="utilization-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="utilization-label"></h5>
        </div>
        <form @submit.prevent="utilization">
          <div class="modal-body">
            <div class="row"></div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-utilization
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";

export default {
  name: "utilization",
  data() {
    return {
      body: {
        product_id: 0,
        type: "",
        value: 0,
        plant_id: 0,
      },
    };
  },
  methods: {
    utilization() {
      api.create_olchov(this.body).then(() => {
        this.body.plant_id = 0;
        this.body.value = 0;
        this.body.product_id = 0;
        api.success("close-utilization");
      });
    },
  },
};
</script>
