<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0 d-flex justify-content-between">
        <h6 class="mb-0">{{ $t("salary.title") }}</h6>
        <div style="width: 200px">
          <input
            type="month"
            class="form-control form-control-sm"
            :max="`${new Date().toISOString().slice(0, 7)}`"
            v-model="date"
            @change="get(page, limit)"
          />
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="table-responsive p-0 mb-0 mt-3" v-if="users.length">
          <table class="table align-items-center mb-0">
            <thead>
              <tr>
                <th
                  style="width: 10px; position: sticky; z-index: 9; left: 0"
                  class="w-custom-40 bg-white"
                >
                  
                </th>
                <th
                  style="
                    width: 200px;
                    position: sticky;
                    z-index: 9;
                    left: 59.5px;
                  "
                  class="bg-white"
                >
                  {{ $t("attandance.user") }}
                </th>
                <th v-for="day in day_count" :key="day">{{ day }}</th>
                <th
                  class="w-100px text-wrap"
                  style="font-size: 10px !important"
                >
                  {{ $t("salary.working_days") }}
                </th>
                <th
                  class="w-100px text-wrap"
                  style="font-size: 10px !important"
                >
                  {{ $t("salary.salary") }}
                </th>
                <th
                  class="w-100px text-wrap"
                  style="font-size: 10px !important"
                >
                  {{ $t("salary.salary_day") }}
                </th>
                <th
                  class="w-100px text-wrap"
                  style="font-size: 10px !important"
                >
                  {{ $t("salary.addition_wage") }}
                </th>
                <th
                  class="w-100px text-wrap"
                  style="font-size: 10px !important"
                >
                  {{ $t("salary.calculated_wage") }}
                </th>
                <th
                  class="w-100px text-wrap"
                  style="font-size: 10px !important"
                >
                  {{ $t("salary.tax") }}
                </th>
                <th
                  class="w-100px text-wrap"
                  style="font-size: 10px !important"
                >
                  {{ $t("salary.avance") }}
                </th>
                <th
                  class="w-100px text-wrap"
                  style="font-size: 10px !important"
                >
                  {{ $t("salary.wage") }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, i) in users" :key="item">
                <td
                  class="bg-white"
                  style="width: 10px; position: sticky; z-index: 9; left: 0"
                >
                  {{ i + 1 }}
                </td>
                <td
                  class="bg-white"
                  style="
                    width: 200px;
                    position: sticky;
                    z-index: 9;
                    left: 59.5px;
                  "
                >
                  <router-link
                    :to="
                      !$route.query.market_id
                        ? {
                            name: 'Profile',
                            query: {
                              id: item.Salary.user_id,
                            },
                          }
                        : {
                            name: 'Profile',
                            query: {
                              id: item.Salary.user_id,
                              market_id: $route.query.market_id
                                ? $route.query.market_id
                                : 0,
                            },
                          }
                    "
                  >
                    {{ item.name }}
                  </router-link>
                </td>
                <td
                  v-for="day in day_count"
                  :key="day"
                  class="bg-success text-center text-white border"
                >
                  <span v-if="!find_day(day, item.Salary.allworkdays)"></span>
                  <input
                    type="number"
                    class="form-control form-control-sm px-1 p-0 text-center"
                    v-model="find_day(day, item.Salary.allworkdays).value"
                    :class="
                      find_day(day, item.Salary.allworkdays).value <= 3 &&
                      find_day(day, item.Salary.allworkdays).value >= 0.1
                        ? ''
                        : 'border-danger'
                    "
                    @change="
                      $event.target.value <= 3 && $event.target.value >= 0.1
                        ? workday_determination(
                            find_day(day, item.Salary.allworkdays)
                          )
                        : ''
                    "
                    v-else
                  />
                </td>
                <td>
                  <input
                    type="number"
                    class="form-control form-control-sm px-1 p-0 text-center w-100px"
                    :value="item.Salary.workdays"
                    readonly
                  />
                </td>
                <td>
                  <input
                    type="number"
                    class="form-control form-control-sm px-1 p-0 text-center w-100px"
                    v-model="item.Salary.salary"
                    @change="$event.target.value > 0 ? salary_value(item) : ''"
                  />
                </td>
                <td>
                  <input
                    type="number"
                    class="form-control form-control-sm px-1 p-0 text-center w-100px"
                    :value="item.Salary.daily"
                    readonly
                  />
                </td>
                <td>
                  <input
                    type="number"
                    class="form-control form-control-sm px-1 p-0 text-center w-100px"
                    v-model="item.Salary.additional_wage"
                    @change="
                      $event.target.value > 0 ? salary_determination(item) : ''
                    "
                  />
                </td>
                <td>
                  <input
                    type="number"
                    class="form-control form-control-sm px-1 p-0 text-center w-100px"
                    v-model="item.Salary.calculated_wage"
                    @change="
                      $event.target.value > 0 ? salary_determination(item) : ''
                    "
                  />
                </td>
                <td>
                  <input
                    type="number"
                    class="form-control form-control-sm px-1 p-0 text-center w-100px"
                    v-model="item.Salary.tax"
                    @change="
                      $event.target.value > 0 ? salary_determination(item) : ''
                    "
                  />
                </td>
                <td>
                  <input
                    type="number"
                    class="form-control form-control-sm px-1 p-0 text-center w-100px"
                    :value="item.avanse"
                    readonly
                  />
                </td>
                <td>
                  <input
                    type="number"
                    class="form-control form-control-sm px-1 p-0 text-center w-100px"
                    :value="
                      item.Salary.calculated_wage +
                      item.Salary.additional_wage -
                      item.Salary.tax -
                      item.avanse
                    "
                    readonly
                  />
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <pagination
          :page="page"
          :pages="pages"
          :limit="limit"
          @get="get"
          v-if="users.length"
        />
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import pagination from "@/components/pagination.vue";

export default {
  components: { pagination },
  name: "salary-determination",
  data() {
    return {
      date: new Date().toISOString().slice(0, 7),
      page: 1,
      limit: 10,
      pages: 0,
      users: [],
      day_count: null,
      fulldate: new Date().toISOString(),
      isNumber: api.isNumber,
    };
  },
  methods: {
    get(page, limit) {
      api
        .salaries(this.date.slice(0, 4), this.date.slice(5, 7), page, limit)
        .then((res) => {
          this.pages = res.data.page_count;
          this.page = res.data.current_page;
          this.limit = res.data.page_limit;
          this.users = res.data.data;
          this.day_count =
            this.date == new Date().toISOString().slice(0, 7)
              ? Number(this.fulldate.slice(8, 10))
              : res.data.days_count;
        });
      this.$store.dispatch("setloading", false);
    },
    find_day(day, allworkdays) {
      return allworkdays.find(
        (e) => Number(e.date.slice(8, 10)) == Number(day)
      );
    },
    workday_determination(item) {
      api.workday_update(item.id, item.value).then(() => {
        this.get(this.page, this.limit);
      });
      this.$store.dispatch("setloading", false);
    },
    salary_determination(item) {
      api
        .salary_update({
          id: item.Salary.id,
          additional_wage: item.Salary.additional_wage,
          tax: item.Salary.tax,
          calculated_wage: item.Salary.calculated_wage,
        })
        .then(() => {
          this.get(this.page, this.limit);
        });
      this.$store.dispatch("setloading", false);
    },
    salary_value(item) {
      api.salary_value_update(item.Salary.id, item.Salary.salary).then(() => {
        this.get(this.page, this.limit);
      });
      this.$store.dispatch("setloading", false);
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>

<style scoped>
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}
</style>
