<template>
  <div
    class="modal fade"
    id="add-product-type"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-product-type-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-product-type-label">
            {{ $t("product-types.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('add', body)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("product-types.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="body.name"
                    :placeholder="$t('product-types.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="image">{{ $t("product-types.image") }}</label>
                  <input
                    id="image"
                    type="file"
                    class="form-control"
                    @change="body.image = $event.target.files[0]"
                    :placeholder="$t('product-types.image')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="olchov">{{ $t("product-types.olchov") }}</label>
                  <select
                    id="olchov"
                    class="form-select"
                    v-model="body.olchov_id"
                    required
                  >
                    <option
                      :value="olchov.id"
                      v-for="olchov in olchovs"
                      :key="olchov"
                    >
                      {{ olchov.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="type">{{
                    $t("product-types.detail.title")
                  }}</label>
                  <select
                    id="type"
                    class="form-select"
                    v-model="body.detail"
                    required
                  >
                    <option value="true">
                      {{ $t("product-types.detail.true") }}
                    </option>
                    <option value="false">
                      {{ $t("product-types.detail.false") }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="number">{{ $t("product-types.number") }}</label>
                  <input
                    id="number"
                    type="number"
                    class="form-control"
                    v-model="body.number"
                    @focus="
                      body.number == 0 ? (body.number = null) : body.number
                    "
                    @focusout="
                      body.number == null ? (body.number = 0) : body.number
                    "
                    :placeholder="$t('product-types.number')"
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-check my-2">
                  <label class="form-check-label" for="hasSize">
                    <b>
                      {{ $t("product-types.hasSize") }}
                    </b>
                  </label>
                  <input
                    class="form-check-input"
                    type="checkbox"
                    v-model="body.hasSize"
                    id="hasSize"
                    @click="body.hasSize = !body.hasSize"
                  />
                </div>
                <div class="form-check">
                  <label class="form-check-label" for="isReady">
                    <b>
                      {{ $t("product-types.isReady") }}
                    </b>
                  </label>
                  <input
                    class="form-check-input"
                    type="checkbox"
                    v-model="body.isReady"
                    id="isReady"
                    @click="body.isReady = !body.isReady"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="update-product-type"
    tabindex="-1"
    role="dialog"
    aria-labelledby="update-product-type-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="update-product-type-label">
            {{ $t("product-types.update") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('put', data)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("product-types.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="data.name"
                    :placeholder="$t('product-types.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="image">{{ $t("product-types.image") }}</label>
                  <input
                    id="image"
                    type="file"
                    class="form-control"
                    @change="data.image = $event.target.files[0]"
                    :placeholder="$t('product-types.image')"
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="olchov">{{ $t("product-types.olchov") }}</label>
                  <select
                    id="olchov"
                    class="form-select"
                    v-model="data.olchov_id"
                    required
                  >
                    <option
                      :value="olchov.id"
                      v-for="olchov in olchovs"
                      :key="olchov"
                    >
                      {{ olchov.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="type">{{
                    $t("product-types.detail.title")
                  }}</label>
                  <select
                    id="type"
                    class="form-select"
                    v-model="data.detail"
                    required
                  >
                    <option value="true">
                      {{ $t("product-types.detail.true") }}
                    </option>
                    <option value="false">
                      {{ $t("product-types.detail.false") }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="number">{{ $t("product-types.number") }}</label>
                  <input
                    id="number"
                    type="number"
                    class="form-control"
                    v-model="data.number"
                    :placeholder="$t('product-types.number')"
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-check my-2">
                  <label class="form-check-label" for="hasSize2">
                    <b>
                      {{ $t("product-types.hasSize") }}
                    </b>
                  </label>
                  <input
                    class="form-check-input"
                    type="checkbox"
                    v-model="data.hasSize"
                    :checked="data.hasSize"
                    id="hasSize2"
                    @click="data.hasSize = !data.hasSize"
                  />
                </div>
                <div class="form-check">
                  <label class="form-check-label" for="isReady2">
                    <b>
                      {{ $t("product-types.isReady") }}
                    </b>
                  </label>
                  <input
                    class="form-check-input"
                    type="checkbox"
                    v-model="data.isReady"
                    :checked="data.isReady"
                    id="isReady2"
                    @click="data.isReady = !data.isReady"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-put
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("update") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "add-product-type",
  props: ["olchovs", "item"],
  emits: ["add", "put"],
  data() {
    return {
      body: {
        name: "",
        image: "",
        olchov_id: null,
        detail: "",
        hasSize: false,
        isReady: false,
        number: 0,
      },
    };
  },
  computed: {
    data() {
      return this.item;
    },
  },
};
</script>
