<template>
  <div class="container-fluid py-4">
    <div class="card">
      <div class="card-header">
        <div class="row">
          <div class="col">
            <h6 class="m-0">{{ $t("routers.warehouse") }}</h6>
          </div>
          <div class="col text-end">
            <button
              class="btn bg-gradient-success mb-0 mr-2 text-end"
              type="button"
              data-bs-toggle="modal"
              data-bs-target="#accept-product-modal"
              @click="$refs.accept_product.getProducts()"
            >
              {{ $t("accept-product.title") }}
            </button>
          </div>
          <div class="col text-end">
            <strong>{{
              $t("sum-price") +
              " " +
              products?.products_sum_price +
              " " +
              $t("currency")
            }}</strong>
          </div>
        </div>
      </div>
      <div class="card-body overflow-auto" style="min-height: 50vh">
        <table class="table table-bordered">
          <thead>
            <tr>
              <th>{{ $t("product-store.table.image") }}</th>
              <th>{{ $t("product-store.table.name") }}</th>
              <th>{{ $t("supply.product-material") }}</th>
              <th>{{ $t("product-store.table.quantity") }}</th>
              <th>{{ $t("product-store.size") }} 1</th>
              <th>{{ $t("product-store.size") }} 2</th>
              <th>{{ $t("product-store.table.price") }}</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="store in products.data" :key="store">
              <td class="cursor-pointer me-3">
                <img
                  :src="`${image}/products/${store.Product_Store?.product?.image}`"
                  class="avatar avatar-sm"
                  alt="product"
                />
              </td>
              <td>{{ store.Product_Store?.product?.name }}</td>
              <td>{{ store.Product_Store?.material?.name }}</td>
              <td>
                {{ store.sum_quantity }}
                {{ store.Product_Store?.product?.olchov?.name }}
              </td>
              <td>{{ store.Product_Store?.size1 }} {{ $t("supply.sm") }}</td>
              <td>{{ store.Product_Store?.size2 }} {{ $t("supply.sm") }}</td>
              <td>
                {{ store.Product_Store.trade_price + " " + $t("currency") }}
              </td>
              <td>
                <button
                  class="btn btn-sm bg-gradient-warning m-0 px-3 py-2"
                  data-bs-toggle="modal"
                  data-bs-target="#edit"
                  @click="
                    product = {
                      id: store.Product_Store?.id,
                      image: '',
                      trade_price: store?.Product_Store?.trade_price,
                    }
                  "
                >
                  <i class="fa fa-edit"></i>
                </button>
              </td>
            </tr>
          </tbody>
          <tfoot v-if="!products.data.length">
            <tr>
              <td colspan="8">
                <data-notfound :data="products.data" />
              </td>
            </tr>
          </tfoot>
          <tfoot v-if="products.data.length">
            <tr>
              <td colspan="8">
                <pagination
                  :page="products.current_page"
                  :pages="products.pages"
                  :limit="products.limit"
                  @get="getProducts"
                />
              </td>
            </tr>
          </tfoot>
        </table>
      </div>
    </div>
  </div>
  <accept-product-transfer ref="accept_product" />
  <div
    class="modal fade"
    id="edit"
    tabindex="-1"
    role="dialog"
    aria-labelledby="edit-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title" id="edit-label">
            {{ product.name }}
          </h5>
        </div>
        <form @submit.prevent="putProduct()">
          <div class="modal-body">
            <div class="row">
              <div class="col-12 my-2">
                <div class="input-group">
                  <input
                    v-if="product"
                    class="form-control"
                    type="number"
                    min="1"
                    step="any"
                    required
                    v-model="product.trade_price"
                  />
                  <div class="input-group-text">{{ $t("currency") }}</div>
                </div>
              </div>
              <div class="col-12 my-2">
                <div class="form-group">
                  <input
                    id="image"
                    type="file"
                    class="form-control"
                    @change="product.image = $event.target.files[0]"
                    :placeholder="$t('product.image')"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-sm bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-edit
            >
              {{ $t("close") }}
            </button>
            <button class="btn btn-sm bg-gradient-primary">
              {{ $t("save") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import pagination from "@/components/pagination.vue";
import dataNotfound from "@/components/dataNotfound.vue";
import AcceptProductTransfer from "@/components/modals/AcceptProductTransfer.vue";
export default {
  name: "warehouse",
  components: { pagination, dataNotfound, AcceptProductTransfer },
  data() {
    return {
      image: api.image,
      search: "",
      acceptance_products: [],
      products: {
        current_page: 1,
        pages: 1,
        limit: 10,
        data: [],
      },
      product: {
        id: 0,
        image: "",
        trade_price: 0,
      },
    };
  },
  created() {
    this.getProducts(1, 10);
  },
  methods: {
    getProducts(page, limit) {
      api
        .products_store_trade(
          this.search,
          0,
          0,
          this.$route.query.market_id,
          page,
          limit
        )
        .then((res) => {
          this.products = res.data;
        });
    },
    putProduct() {
      let data = api.getFormData(this.product);
      if (!this.product.image) {
        data.delete("image");
      }
      api.put_product_store(data, this.product.id).then(() => {
        api.success("close-edit").then(() => {
          this.getProducts(1, 10);
        });
      });
    },
  },
};
</script>
