<template>
  <div
    class="modal fade"
    id="add-cycle"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-cycle-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-cycle-label">
            {{ $t("cycle.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('add', body)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("cycle.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="body.name"
                    :placeholder="$t('cycle.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="kpi">{{ $t("cycle.kpi") }}</label>
                  <div class="input-group">
                    <input
                      id="kpi"
                      type="number"
                      step="any"
                      @focus="body.kpi == 0 ? (body.kpi = null) : body.kpi"
                      @focusout="body.kpi == null ? (body.kpi = 0) : body.kpi"
                      class="form-control"
                      v-model="body.kpi"
                      :placeholder="$t('cycle.kpi')"
                      required
                    />
                    <span class="input-group-text">{{ $t("currency") }}</span>
                  </div>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="plan_value">{{ $t("cycle.plan_value") }}</label>
                  <div class="input-group">
                    <input
                      id="plan_value"
                      type="number"
                      step="any"
                      class="form-control"
                      v-model="body.plan_value"
                      @focus="
                        body.plan_value == 0
                          ? (body.plan_value = null)
                          : body.plan_value
                      "
                      @focusout="
                        body.plan_value == null
                          ? (body.plan_value = 0)
                          : body.plan_value
                      "
                      :placeholder="$t('cycle.plan_value')"
                      required
                    />
                    <span class="input-group-text">{{
                      $parent.product?.olchov?.name
                    }}</span>
                  </div>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="plan_day">{{ $t("cycle.plan_day") }}</label>
                  <div class="input-group">
                    <input
                      id="plan_day"
                      type="number"
                      step="any"
                      class="form-control"
                      v-model="body.plan_day"
                      @focus="
                        body.plan_day == 0
                          ? (body.plan_day = null)
                          : body.plan_day
                      "
                      @focusout="
                        body.plan_day == null
                          ? (body.plan_day = 0)
                          : body.plan_day
                      "
                      :placeholder="$t('cycle.plan_day')"
                      required
                    />
                    <span class="input-group-text">{{ $t("cycle.day") }}</span>
                  </div>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="spending_minut">{{
                    $t("cycle.spending_minut")
                  }}</label>
                  <div class="input-group">
                    <input
                      id="spending_minut"
                      type="number"
                      step="any"
                      @focus="
                        body.spending_minut == 0
                          ? (body.spending_minut = null)
                          : body.spending_minut
                      "
                      @focusout="
                        body.spending_minut == null
                          ? (body.spending_minut = 0)
                          : body.spending_minut
                      "
                      class="form-control"
                      v-model="body.spending_minut"
                      :placeholder="$t('cycle.spending_minut')"
                      required
                    />
                    <span class="input-group-text">{{
                      $t("cycle.minut")
                    }}</span>
                  </div>
                </div>
              </div>
              <div
                class="col-md-6 mt-3 d-flex align-items-center justify-content-around"
              >
                <div class="form-check">
                  <label class="form-check-label" for="special">
                    <b>
                      {{ $t("cycle.special") }}
                    </b>
                  </label>
                  <input
                    class="form-check-input"
                    type="checkbox"
                    v-model="body.special"
                    id="special"
                    @click="body.special = !body.special"
                  />
                </div>
                <div class="form-check">
                  <label class="form-check-label" for="hasperiod">
                    <b>
                      {{ $t("cycle.has-period") }}
                    </b>
                  </label>
                  <input
                    class="form-check-input"
                    type="checkbox"
                    v-model="body.hasPeriod"
                    id="hasperiod"
                    @click="body.hasPeriod = !body.hasPeriod"
                  />
                </div>
              </div>
              <div class="col-md-6 mt-3 d-flex align-items-center"></div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-cycle
            >
              {{ $t("close") }}
            </button>
            <button
              type="submit"
              class="btn bg-gradient-primary"
              :disabled="!body.name"
            >
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="update-cycle"
    tabindex="-1"
    role="dialog"
    aria-labelledby="update-cycle-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="update-cycle-label">
            {{ $t("cycle.update") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('put', data)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("cycle.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="data.name"
                    :placeholder="$t('cycle.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="kpi">{{ $t("cycle.kpi") }}</label>
                  <div class="input-group">
                    <input
                      id="kpi"
                      type="number"
                      step="any"
                      class="form-control"
                      v-model="data.kpi"
                      :placeholder="$t('cycle.kpi')"
                      required
                    />
                    <span class="input-group-text">{{ $t("currency") }}</span>
                  </div>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="plan_value">{{ $t("cycle.plan_value") }}</label>
                  <div class="input-group">
                    <input
                      id="plan_value"
                      type="number"
                      step="any"
                      class="form-control"
                      v-model="data.plan_value"
                      :placeholder="$t('cycle.plan_value')"
                      required
                    />
                    <span class="input-group-text">{{
                      $parent.product?.olchov?.name
                    }}</span>
                  </div>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="plan_day">{{ $t("cycle.plan_day") }}</label>
                  <div class="input-group">
                    <input
                      id="plan_day"
                      type="number"
                      step="any"
                      class="form-control"
                      v-model="data.plan_day"
                      :placeholder="$t('cycle.plan_day')"
                      required
                    />
                    <span class="input-group-text">{{ $t("cycle.day") }}</span>
                  </div>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="spending_minut">{{
                    $t("cycle.spending_minut")
                  }}</label>
                  <div class="input-group">
                    <input
                      id="spending_minut"
                      type="number"
                      step="any"
                      class="form-control"
                      v-model="data.spending_minut"
                      :placeholder="$t('cycle.spending_minut')"
                      required
                    />
                    <span class="input-group-text">{{
                      $t("cycle.minut")
                    }}</span>
                  </div>
                </div>
              </div>
              <div
                class="col-md-6 mt-3 d-flex align-items-center justify-content-around"
              >
                <div class="form-check">
                  <label class="form-check-label" for="special2">
                    <b>
                      {{ $t("cycle.special") }}
                    </b>
                  </label>
                  <input
                    class="form-check-input"
                    type="checkbox"
                    v-model="data.special"
                    id="special2"
                    :checked="data.special"
                    @click="data.special = !data.special"
                  />
                </div>
                <div class="form-check">
                  <label class="form-check-label" for="hasperiod2">
                    <b>
                      {{ $t("cycle.has-period") }}
                    </b>
                  </label>
                  <input
                    class="form-check-input"
                    type="checkbox"
                    v-model="data.hasPeriod"
                    id="hasperiod2"
                    @click="data.hasPeriod = !data.hasPeriod"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-cycle-put
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("update") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";

export default {
  name: "cycle",
  props: ["item"],
  emits: ["add", "put"],
  data() {
    return {
      body: {
        name: "",
        kpi: 0,
        product_type_id: Number(this.$route.query.id),
        type: "",
        code: "",
        component_name: "",
        plan_value: 0,
        plan_day: 0,
        spending_minut: 0,
        special: false,
        hasPeriod: false,
        components: [
          // type STR
          // code STR
          // name STR
        ],
      },
      pages: 0,
      page: 1,
      search: "",
      items: [],
    };
  },
  methods: {
    get(scroll) {
      this.page = scroll ? this.page : 1;
      if (this.body.type == "product") {
        api
          .get_product_types(
            scroll ? this.page + 1 : this.page,
            10,
            this.search
          )
          .then((res) => {
            this.pages = res.data.page_count;
            this.page = res.data.current_page;
            this.items = scroll
              ? this.items.concat(res.data.data)
              : res.data.data;
          });
      } else {
        api
          .get_material_types(
            scroll ? this.page + 1 : this.page,
            10,
            this.search
          )
          .then((res) => {
            this.pages = res.data.page_count;
            this.page = res.data.current_page;
            this.items = scroll
              ? this.items.concat(res.data.data)
              : res.data.data;
          });
      }
    },
    scroll() {
      let div = document.querySelector(".scroll-25");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.page < this.pages) {
          this.get(true);
        }
      }
    },
  },
  computed: {
    data() {
      return this.item;
    },
  },
};
</script>
