<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">{{ $t("shift.title") }}</h6>
          </div>
          <div class="col-md-6">
            <button
              class="btn btn-sm bg-gradient-primary float-end mb-0"
              data-bs-toggle="modal"
              data-bs-target="#add-shift"
            >
              {{ $t("shift.add-button") }}
            </button>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-lg-4 col-md-6 col-sm-12 my-2"
            v-for="shift in shifts"
            :key="shift"
          >
            <div class="card card-zoom h-100 shadow-sm">
              <div class="card-body">
                <div class="row">
                  <div class="col">
                    <h5 class="card-title text-muted mb-0">
                      {{ shift.name }}
                    </h5>
                    <hr class="my-1" />
                    <div class="mt-2">
                      <p class="mb-0 text-sm">
                        <b class="text-secondary mr-2">
                          {{ $t("shift.time.from") }}:
                          <span class="text-nowrap ms-1">{{
                            shift.from_time
                          }}</span></b
                        >
                      </p>
                      <p class="mb-0 text-sm">
                        <b class="text-secondary mr-2">
                          {{ $t("shift.time.to") }}:
                          <span class="text-nowrap ms-1">{{
                            shift.to_time
                          }}</span></b
                        >
                      </p>
                    </div>
                  </div>
                  <div
                    class="col-auto d-flex justify-content-center align-items-center"
                  >
                    <button
                      class="btn btn-sm mb-0 bg-gradient-warning px-3"
                      @click="
                        item = {
                          id: shift.id,
                          name: shift.name,
                          from_time: shift.from_time,
                          to_time: shift.to_time,
                        }
                      "
                      data-bs-toggle="modal"
                      data-bs-target="#update-shift"
                    >
                      <i class="fas fa-pen"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="shifts" />
            <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
          </div>
        </div>
      </div>
    </div>
  </div>
  <shift-modals @add="add" :item="item" @put="put" />
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";
import ShiftModals from "@/components/modals/ShiftModals.vue";

export default {
  components: { dataNotfound, Pagination, ShiftModals },
  name: "Shift",
  data() {
    return {
      shifts: [],
      pages: 0,
      page: 1,
      limit: 10,
      item: {},
    };
  },
  methods: {
    get(page, limit) {
      api.get_shifts(page, limit).then((res) => {
        this.pages = res.data.page_count;
        this.page = res.data.current_page;
        this.limit = res.data.page_limit;
        this.shifts = res.data.data;
      });
    },
    add(body) {
      api.create_shift(body).then(() => {
        body.name = "";
        body.from_time = "";
        body.to_time = "";
        api.success("close");
        this.get(this.page, this.limit);
      });
    },
    put(body) {
      api.put_shift(body, body.id).then(() => {
        api.success("close-put");
        this.get(this.page, this.limit);
      });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>
