<template>
  <div
    class="modal fade"
    id="add-plan"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-plan-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-plan-label">
            {{ $t("plan.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="create()">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <label>{{ $t("plan.product_type") }}</label>
                <div class="dropdown">
                  <button
                    class="form-control border-secondary-custom shadow-none mb-0 w-100"
                    data-bs-toggle="dropdown"
                    type="button"
                    aria-expanded="false"
                    @click="
                      product_type.pages = 0;
                      product_type.page = 1;
                      get_product_type(false);
                    "
                  >
                    {{ body.product_type_name }}
                  </button>
                  <ul
                    class="dropdown-menu w-100"
                    aria-labelledby="dropdownMenuButton1"
                  >
                    <input
                      type="text"
                      class="form-control"
                      :placeholder="$t('search')"
                      v-model="product_type.search"
                      @keyup="
                        get_product_type(false);
                        $store.dispatch('setloading', false);
                      "
                    />
                    <div
                      class="scroll-25"
                      @scroll="
                        scroll_product_type($event);
                        $store.dispatch('setloading', false);
                      "
                    >
                      <li
                        @click="
                          body.hasSize = product_type.hasSize;
                          body.product_id = 0;
                          body.product_name = $t('plan.product');
                          body.material_id = 0;
                          body.material_name = $t('plan.material');
                          body.product_type_id = product_type.id;
                          body.product_type_name = product_types.find(
                            (e) => body.product_type_id == e.id
                          ).name;
                          get_product(false);
                        "
                        v-for="product_type in product_types"
                        :key="product_type"
                      >
                        <a class="dropdown-item"> {{ product_type.name }} </a>
                      </li>
                    </div>
                    <data-notfound :data="product_types" class="text-lg" />
                  </ul>
                </div>
              </div>
              <div class="col-md-6">
                <label>{{ $t("plan.product") }}</label>
                <div class="dropdown">
                  <button
                    class="form-control border-secondary-custom shadow-none mb-0 w-100"
                    data-bs-toggle="dropdown"
                    type="button"
                    aria-expanded="false"
                    @click="
                      product.pages = 0;
                      product.page = 1;
                      get_product(false);
                    "
                    :disabled="!body.product_type_id"
                  >
                    {{ body.product_name }}
                  </button>
                  <ul
                    class="dropdown-menu w-100"
                    aria-labelledby="dropdownMenuButton1"
                  >
                    <input
                      type="text"
                      class="form-control"
                      :placeholder="$t('search')"
                      v-model="product.search"
                      @keyup="
                        get_product(false);
                        $store.dispatch('setloading', false);
                      "
                    />
                    <div
                      class="scroll-25"
                      @scroll="
                        scroll_product($event);
                        $store.dispatch('setloading', false);
                      "
                    >
                      <li
                        @click="
                          body.unit = product.unit;
                          body.olchov = product.olchov.name;
                          body.product_id = product.id;
                          body.product_name = products.find(
                            (e) => body.product_id == e.id
                          ).name;
                          get_product_material(false);
                        "
                        v-for="product in products"
                        :key="product"
                      >
                        <a class="dropdown-item"> {{ product.name }} </a>
                      </li>
                    </div>
                    <data-notfound :data="products" class="text-lg" />
                  </ul>
                </div>
              </div>
              <div class="col-md-6">
                <label>{{ $t("plan.material") }}</label>
                <div class="dropdown">
                  <button
                    class="form-control border-secondary-custom shadow-none mb-0 w-100"
                    data-bs-toggle="dropdown"
                    type="button"
                    aria-expanded="false"
                    @click="
                      product_material.pages = 0;
                      product_material.page = 1;
                      get_product_material(false);
                    "
                    :disabled="!body.product_id"
                  >
                    {{ body.material_name }}
                  </button>
                  <ul
                    class="dropdown-menu w-100"
                    aria-labelledby="dropdownMenuButton1"
                  >
                    <div
                      class="scroll-25"
                      @scroll="
                        scroll_product_material($event);
                        $store.dispatch('setloading', false);
                      "
                    >
                      <li
                        @click="
                          body.material_id = 0;
                          body.material_name = $t('plan.material');
                          body.material_id = product_material.material_id;
                          body.material_name = product_materials.find(
                            (e) => body.material_id == e.material_id
                          )?.material?.name;
                        "
                        v-for="product_material in product_materials"
                        :key="product_material"
                      >
                        <a class="dropdown-item">
                          {{ product_material.material?.name }}
                        </a>
                      </li>
                    </div>
                    <data-notfound :data="product_materials" class="text-lg" />
                  </ul>
                </div>
              </div>
              <div class="col-md-6" v-if="body.hasSize">
                <div class="form-group">
                  <label for="size1">{{ $t("production.size") }} 1</label>
                  <input
                    id="size1"
                    type="number"
                    class="form-control text-uppercase"
                    v-model="body.size1"
                    @focus="body.size1 == 0 ? (body.size1 = null) : body.size1"
                    @focusout="
                      body.size1 == null ? (body.size1 = 0) : body.size1
                    "
                    min="0"
                  />
                </div>
              </div>
              <div class="col-md-6" v-if="body.hasSize">
                <div class="form-group">
                  <label for="size2">{{ $t("production.size") }} 2</label>
                  <input
                    id="size2"
                    type="number"
                    class="form-control text-uppercase"
                    v-model="body.size2"
                    @focus="body.size2 == 0 ? (body.size2 = null) : body.size2"
                    @focusout="
                      body.size2 == null ? (body.size2 = 0) : body.size2
                    "
                    min="0"
                  />
                </div>
              </div>
              <div class="col-md-6">
                <label for="value">{{ $t("plan.value") }}</label>
                <div class="input-group">
                  <input
                    id="value"
                    type="number"
                    class="form-control"
                    v-model="body.value"
                    @focus="body.value == 0 ? (body.value = null) : body.value"
                    @focusout="
                      body.value == null ? (body.value = 0) : body.value
                    "
                    :step="body.unit ? `1` : `any`"
                    :placeholder="$t('plan.value')"
                    required
                  />
                  <span class="input-group-text" v-if="body.olchov">{{
                    body.olchov
                  }}</span>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="to_date">{{ $t("plan.to_date") }}</label>
                  <input
                    id="to_date"
                    type="date"
                    class="form-control"
                    v-model="body.to_date"
                    :placeholder="$t('plan.to_date')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="comment">{{ $t("cashier.comment") }}</label>
                  <textarea
                    id="comment"
                    class="form-control"
                    v-model="body.comment"
                    :placeholder="$t('cashier.comment')"
                    required
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="update-plan"
    tabindex="-1"
    role="dialog"
    aria-labelledby="update-plan-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="update-plan-label">
            {{ $t("update") }}
          </h5>
        </div>
        <form @submit.prevent="update()">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <label>{{ $t("plan.product_type") }}</label>
                <div class="dropdown">
                  <button
                    class="form-control border-secondary-custom shadow-none mb-0 w-100"
                    data-bs-toggle="dropdown"
                    type="button"
                    aria-expanded="false"
                    @click="
                      product_type.pages = 0;
                      product_type.page = 1;
                      get_product_type(false);
                    "
                  >
                    {{ item.product_type_name }}
                  </button>
                  <ul
                    class="dropdown-menu w-100"
                    aria-labelledby="dropdownMenuButton1"
                  >
                    <input
                      type="text"
                      class="form-control"
                      :placeholder="$t('search')"
                      v-model="product_type.search"
                      @keyup="
                        get_product_type(false);
                        $store.dispatch('setloading', false);
                      "
                    />
                    <div
                      class="scroll-25"
                      @scroll="
                        scroll_product_type($event);
                        $store.dispatch('setloading', false);
                      "
                    >
                      <li
                        @click="
                          item.product_id = 0;
                          item.product_name = $t('plan.product');
                          item.material_id = 0;
                          item.material_name = $t('plan.material');
                          body.product_type_id = product_type.id;
                          item.product_type_name = product_types.find(
                            (e) => body.product_type_id == e.id
                          ).name;
                          get_product(false);
                        "
                        v-for="product_type in product_types"
                        :key="product_type"
                      >
                        <a class="dropdown-item"> {{ product_type.name }} </a>
                      </li>
                    </div>
                    <data-notfound :data="product_types" class="text-lg" />
                  </ul>
                </div>
              </div>
              <div class="col-md-6">
                <label>{{ $t("plan.product") }}</label>
                <div class="dropdown">
                  <button
                    class="form-control border-secondary-custom shadow-none mb-0 w-100"
                    data-bs-toggle="dropdown"
                    type="button"
                    aria-expanded="false"
                    @click="
                      product.pages = 0;
                      product.page = 1;
                      get_product(false);
                    "
                    :disabled="!body.product_type_id"
                  >
                    {{ item.product_name }}
                  </button>
                  <ul
                    class="dropdown-menu w-100"
                    aria-labelledby="dropdownMenuButton1"
                  >
                    <input
                      type="text"
                      class="form-control"
                      :placeholder="$t('search')"
                      v-model="product.search"
                      @keyup="
                        get_product(false);
                        $store.dispatch('setloading', false);
                      "
                    />
                    <div
                      class="scroll-25"
                      @scroll="
                        scroll_product($event);
                        $store.dispatch('setloading', false);
                      "
                    >
                      <li
                        @click="
                          item.unit = product.unit;
                          item.material_id = 0;
                          item.material_name = '';
                          item.olchov = product.olchov.name;
                          item.product_id = product.id;
                          item.product_name = products.find(
                            (e) => item.product_id == e.id
                          ).name;
                        "
                        v-for="product in products"
                        :key="product"
                      >
                        <a class="dropdown-item"> {{ product.name }} </a>
                      </li>
                    </div>
                    <data-notfound :data="products" class="text-lg" />
                  </ul>
                </div>
              </div>
              <div class="col-md-6">
                <label>{{ $t("plan.material") }}</label>
                <div class="dropdown">
                  <button
                    class="form-control border-secondary-custom shadow-none mb-0 w-100"
                    data-bs-toggle="dropdown"
                    type="button"
                    aria-expanded="false"
                    @click="
                      product_material.pages = 0;
                      product_material.page = 1;
                      get_product_material(false);
                    "
                    :disabled="!item.product_id"
                  >
                    {{ item.material_name }}
                  </button>
                  <ul
                    class="dropdown-menu w-100"
                    aria-labelledby="dropdownMenuButton1"
                  >
                    <div
                      class="scroll-25"
                      @scroll="
                        scroll_product_material($event);
                        $store.dispatch('setloading', false);
                      "
                    >
                      <li
                        @click="
                          item.material_id = product_material.material_id;
                          item.material_name = product_materials.find(
                            (e) => item.material_id == e.material_id
                          )?.material?.name;
                        "
                        v-for="product_material in product_materials"
                        :key="product_material"
                      >
                        <a class="dropdown-item">
                          {{ product_material.material?.name }}
                        </a>
                      </li>
                    </div>
                    <data-notfound :data="product_materials" class="text-lg" />
                  </ul>
                </div>
              </div>
              <div class="col-md-6" v-if="item.hasSize">
                <div class="form-group">
                  <label for="size1">{{ $t("production.size") }} 1</label>
                  <input
                    id="size1"
                    type="number"
                    class="form-control text-uppercase"
                    v-model="item.size1"
                    @focus="item.size1 == 0 ? (item.size1 = null) : item.size1"
                    @focusout="
                      item.size1 == null ? (item.size1 = 0) : item.size1
                    "
                    min="0"
                  />
                </div>
              </div>
              <div class="col-md-6" v-if="item.hasSize">
                <div class="form-group">
                  <label for="size2">{{ $t("production.size") }} 2</label>
                  <input
                    id="size2"
                    type="number"
                    class="form-control text-uppercase"
                    v-model="item.size2"
                    @focus="item.size2 == 0 ? (item.size2 = null) : item.size2"
                    @focusout="
                      item.size2 == null ? (item.size2 = 0) : item.size2
                    "
                    min="0"
                  />
                </div>
              </div>
              <div class="col-md-6">
                <label for="value">{{ $t("plan.value") }}</label>
                <div class="input-group">
                  <input
                    id="value"
                    type="number"
                    class="form-control"
                    v-model="item.value"
                    :step="item.unit ? `1` : `any`"
                    :placeholder="$t('plan.value')"
                    required
                  />
                  <span class="input-group-text" v-if="item.olchov">{{
                    item.olchov
                  }}</span>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="to_date">{{ $t("plan.to_date") }}</label>
                  <input
                    id="to_date"
                    type="date"
                    class="form-control"
                    v-model="item.to_date"
                    :placeholder="$t('plan.to_date')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="comment">{{ $t("cashier.comment") }}</label>
                  <textarea
                    id="comment"
                    class="form-control"
                    v-model="item.comment"
                    :placeholder="$t('cashier.comment')"
                    required
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-put
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("update") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "../dataNotfound.vue";

export default {
  components: { dataNotfound },
  name: "plan-modals",
  data() {
    return {
      body: {
        product_type_id: 0,
        product_id: 0,
        value: 0,
        to_date: "",
        comment: "",
        size1: 0,
        size2: 0,
        disabled: false,
        product_type_name: this.$t("plan.product_type"),
        product_name: this.$t("plan.product"),
        olchov: "",
        hasSize: false,
        material_name: this.$t("plan.material"),
        material_id: 0,
        unit: false,
      },
      item: {},
      product_types: [],
      product_type: {
        pages: 0,
        page: 1,
        search: "",
      },
      products: [],
      product: {
        pages: 0,
        page: 1,
        search: "",
      },
      product_materials: [],
      product_material: {
        pages: 0,
        page: 1,
      },
    };
  },
  methods: {
    get_product_type(scroll) {
      this.product_type.page = scroll ? this.product_type.page : 1;
      api
        .get_product_types(
          scroll ? this.product_type.page + 1 : this.product_type.page,
          10,
          this.product_type.search
        )
        .then((res) => {
          this.product_type.pages = res.data.page_count;
          this.product_type.page = res.data.current_page;
          this.product_types = scroll
            ? this.product_types.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll_product_type(event) {
      let div = event.target;
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.product_type.page < this.product_type.pages) {
          this.get_product_type(true);
        }
      }
    },
    get_product(scroll) {
      this.product.page = scroll ? this.product.page : 1;
      api
        .get_products(
          scroll ? this.product.page + 1 : this.product.page,
          10,
          this.product.search,
          this.body.product_type_id,
          false
        )
        .then((res) => {
          this.product.pages = res.data.page_count;
          this.product.page = res.data.current_page;
          this.products = scroll
            ? this.products.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll_product(event) {
      let div = event.target;
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.product.page < this.product.pages) {
          this.get_product(true);
        }
      }
    },
    get_product_material(scroll) {
      this.product_material.page = scroll ? this.product_material.page : 1;
      api
        .product_material(
          scroll ? this.product_material.page + 1 : this.product_material.page,
          10,
          0,
          this.body.product_id
        )
        .then((res) => {
          this.product_material.pages = res.data.page_count;
          this.product_material.page = res.data.current_page;
          this.product_materials = scroll
            ? this.product_materials.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll_product_material(event) {
      let div = event.target;
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.product_material.page < this.product_material.pages) {
          this.get_product_material(true);
        }
      }
    },
    create() {
      api.create_plan(this.body).then(() => {
        this.body.to_date = "";
        this.body.olchov = "";
        this.body.comment = "";
        this.body.material_id = 0;
        this.body.size1 = 0;
        this.body.size2 = 0;
        this.body.product_id = 0;
        this.body.product_type_id = 0;
        this.body.product_name = this.$t("plan.product");
        this.body.product_type_name = this.$t("plan.product_type");
        this.body.material_name = this.$t("plan.material");
        this.body.value = 0;
        this.body.unit = false;
        api.success("close");
      });
    },
    update() {
      api.update_plan(this.item).then(() => {
        api.success("close-put");
        this.$parent.get(this.$parent.page, this.$parent.limit);
        this.item = {};
      });
    },
  },
};
</script>
