<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <h5 class="mb-0">{{ $t("indicator.material") }}</h5>
      </div>
      <div class="card-body py-2">
        <div class="table-responsive p-0 mb-0 mt-2">
          <table class="table table-bordered align-items-center mb-0">
            <thead>
              <tr>
                <th class="w-custom-40"></th>
                <th>{{ $t("supply.material") }}</th>
                <th>{{ $t("supply.value") }}</th>
                <th>{{ $t("supply.price") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, i) in materials" :key="item">
                <td>{{ i + 1 }}</td>
                <td>{{ item.material }}</td>
                <td>{{ Intl.NumberFormat().format(item.sum_value) }}</td>
                <td>
                  {{
                    Intl.NumberFormat().format(item.sum_price) +
                    " " +
                    $t("currency")
                  }}
                </td>
              </tr>
            </tbody>
            <tfoot v-if="!materials.length">
              <tr>
                <td colspan="4">
                  <data-notfound :data="[]" />
                </td>
              </tr>
            </tfoot>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";

export default {
  components: { dataNotfound },
  data() {
    return {
      materials: [],
    };
  },
  methods: {
    get() {
      api.needy_material_for_orders().then((res) => {
        this.materials = res.data;
      });
    },
  },
  created() {
    this.get();
  },
};
</script>
