<template>
  <div
    class="modal fade"
    id="products"
    tabindex="-1"
    role="dialog"
    aria-labelledby="products-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="products-label">
            {{ $t("material.product") }}
          </h5>
          <button
            type="button"
            class="btn shadow-none mb-0"
            data-bs-toggle="modal"
            data-bs-target="#products"
          >
            <i class="fas fa-xmark"></i>
          </button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-6">
              <button
                class="btn btn-sm w-100"
                type="button"
                :class="
                  content == 'attachment'
                    ? 'bg-gradient-primary'
                    : 'border border-primary'
                "
                @click="content = 'attachment'"
              >
                {{ $t("material.attachment") }}
              </button>
            </div>
            <div class="col-6">
              <button
                class="btn btn-sm w-100"
                :class="
                  content == 'product'
                    ? 'bg-gradient-primary'
                    : 'border border-primary'
                "
                @click="content = 'product'"
                type="button"
              >
                {{ $t("material.product") }}
              </button>
            </div>
          </div>
          <div class="attachment" v-if="content == 'attachment'">
            <div class="row">
              <div class="col">
                <div class="dropdown">
                  <button
                    class="form-control border-secondary-custom shadow-none mb-0 w-100"
                    data-bs-toggle="dropdown"
                    type="button"
                    aria-expanded="false"
                    @click="get_product_type(false)"
                  >
                    {{ product_type.name }}
                  </button>
                  <ul
                    class="dropdown-menu w-100"
                    aria-labelledby="dropdownMenuButton1"
                  >
                    <input
                      type="text"
                      class="form-control"
                      :placeholder="$t('search')"
                      v-model="product_type.search"
                      @keyup="
                        get_product_type(false);
                        $store.dispatch('setloading', false);
                      "
                    />
                    <div
                      class="scroll-25 product-type"
                      @scroll="
                        scroll_product_type();
                        $store.dispatch('setloading', false);
                      "
                    >
                      <li
                        @click="
                          body.values = [];
                          product_type.id = item.id;
                          product_type.name = item.name;
                          get_product();
                        "
                        v-for="item in product_types"
                        :key="item"
                      >
                        <a class="dropdown-item">
                          {{ item.name }}
                        </a>
                      </li>
                    </div>
                    <data-notfound :data="product_types" class="text-lg" />
                  </ul>
                </div>
              </div>
              <div class="col-auto">
                <button
                  class="btn bg-gradient-success mb-0"
                  :disabled="!body.values.length"
                  @click="attachment()"
                >
                  <i class="fas fa-circle-check"></i>
                </button>
              </div>
            </div>
            <div
              class="list-group mt-2 overflow-auto"
              v-if="products.length"
              style="max-height: 72vh"
            >
              <div class="list-group-item">
                <label class="d-flex align-items-center">
                  <div class="form-check">
                    <input
                      class="form-check-input me-1"
                      type="checkbox"
                      v-model="checkAll"
                    />
                  </div>
                  <b class="text-primary">{{ $t("all") }} </b>
                </label>
              </div>
              <div
                class="list-group-item"
                v-for="product in products"
                :key="product"
              >
                <label class="d-flex align-items-center">
                  <div class="form-check">
                    <input
                      class="form-check-input me-1"
                      type="checkbox"
                      :value="product.id"
                      v-model="body.values"
                    />
                  </div>
                  <b>
                    {{ product.name }}
                  </b>
                </label>
              </div>
            </div>
          </div>
          <div class="product" v-else>
            <ul class="list-group overflow-auto" style="max-height: 72vh">
              <li
                class="list-group-item list-group-item-action d-flex justify-content-between align-items-start"
                v-for="item in product_materials"
                :key="item"
              >
                <b>
                  {{ item.product.name }}
                </b>
                <button
                  class="btn btn-sm bg-gradient-danger px-3 mb-0"
                  @click="remove_product_material(item.id)"
                >
                  <i class="fas fa-circle-xmark"></i>
                </button>
              </li>
              <li class="list-group-item list-group-item-action p-0">
                <data-notfound :data="product_materials" />
                <pagination
                  :page="page"
                  :pages="pages"
                  :limit="limit"
                  @get="get"
                />
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "../dataNotfound.vue";
import Pagination from "../pagination.vue";

export default {
  components: { dataNotfound, Pagination },
  name: "ProductMaterial",
  data() {
    return {
      content: "attachment",
      body: {
        type: "MATERIAL",
        id: 0,
        values: [],
      },
      product_materials: [],
      page: 1,
      limit: 10,
      pages: 0,
      product_types: [],
      product_type: {
        id: 0,
        pages: 0,
        page: 1,
        search: "",
        name: this.$t("norm.product_types"),
      },
      products: [],
    };
  },
  methods: {
    get(page, limit) {
      api.product_material(page, limit, this.body.id, 0).then((res) => {
        this.pages = res.data.page_count;
        this.page = res.data.current_page;
        this.limit = res.data.page_limit;
        this.product_materials = res.data.data;
      });
    },
    get_product_type(scroll) {
      this.product_type.page = scroll ? this.product_type.page : 1;
      api
        .get_product_types(
          scroll ? this.product_type.page + 1 : this.product_type.page,
          10,
          this.product_type.search,
          this.body.product_type_id,
          false
        )
        .then((res) => {
          this.product_type.pages = res.data.page_count;
          this.product_type.page = res.data.current_page;
          this.product_types = scroll
            ? this.product_types.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll_product_type() {
      let div = document.querySelector(".product-type");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.product_type.page < this.product_type.pages) {
          this.get_product_type(true);
        }
      }
    },
    get_product() {
      api.get_products(1, 1000, "", this.product_type.id, false).then((res) => {
        this.products = res.data.data;
      });
    },
    attachment() {
      api.create_product_material(this.body).then(() => {
        this.body.values = [];
        this.content = "product";
        api.success();
        this.get(this.page, this.limit);
      });
    },
    remove_product_material(id) {
      api.delete_product_material(id).then(() => {
        api.success();
        this.get(this.page, this.limit);
      });
    },
  },
  computed: {
    checkAll: {
      get: function () {
        return this.products
          ? this.body.values.length == this.products.length
          : false;
      },
      set: function (value) {
        var values = [];
        if (value) {
          this.products.forEach(function (product) {
            values.push(product.id);
          });
        }
        this.body.values = values;
      },
    },
  },
};
</script>
