<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-3 my-1">
            <h6 class="mb-0">{{ $t("expense.title") }}</h6>
          </div>
          <div class="col-md-9">
            <div class="row">
              <div class="col-md-4 my-1">
                <button
                  class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
                  data-bs-toggle="modal"
                  data-bs-target="#pay-for-expense"
                  @click="
                    $refs.expense.get_cashregs();
                    $refs.expense.pay_body.source = 0;
                    $refs.expense.pay_body.type = 'variable_expense';
                  "
                >
                  {{ $t("expense.variable") }}
                </button>
              </div>
              <div class="col-md-4 my-1">
                <button
                  class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
                  data-bs-toggle="modal"
                  data-bs-target="#add-expense"
                >
                  {{ $t("expense.add-button") }}
                </button>
              </div>
              <div class="col-md-4 my-1">
                <router-link
                  :to="{
                    name: 'ExpenseHistory',
                    query: { market_id: $route.query.market_id },
                  }"
                  class="btn btn-sm bg-gradient-secondary float-end mb-0 w-100"
                >
                  <i class="fas fa-clock-rotate-left"></i>
                  {{ $t("expense.history") }}
                </router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-lg-4 col-md-6 col-sm-6 my-2"
            v-for="expense in expenses"
            :key="expense"
          >
            <div class="card card-stats h-100 shadow-sm">
              <div class="card-body">
                <div class="row">
                  <div class="col">
                    <h6 class="card-title text-muted mb-0">
                      {{ expense.name }}
                    </h6>
                  </div>
                  <div class="col-auto">
                    <button
                      class="btn btn-sm px-3 bg-gradient-primary mb-0 mx-1"
                      data-bs-toggle="modal"
                      data-bs-target="#pay-for-expense"
                      @click="
                        $refs.expense.get_cashregs();
                        $refs.expense.pay_body.source = expense.id;
                        $refs.expense.pay_body.type = 'fixed_expense';
                      "
                    >
                      <i class="fas fa-credit-card"></i>
                    </button>
                    <button
                      class="btn btn-sm px-3 bg-gradient-warning mb-0"
                      @click="
                        item = {
                          id: expense.id,
                          name: expense.name,
                        }
                      "
                      data-bs-toggle="modal"
                      data-bs-target="#update-expense"
                    >
                      <i class="fas fa-pen"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="expenses" />
            <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
          </div>
        </div>
      </div>
    </div>
  </div>
  <expense-modals ref="expense" @add="add" :item="item" @put="put" @pay="pay" />
</template>

<script>
import * as api from "@/server/api";
import pagination from "@/components/pagination.vue";
import DataNotfound from "@/components/dataNotfound.vue";
import ExpenseModals from "@/components/modals/ExpenseModals.vue";

export default {
  components: { pagination, DataNotfound, ExpenseModals },
  name: "epxnese",
  data() {
    return {
      expenses: [],
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      item: {},
    };
  },
  methods: {
    get(page, limit) {
      api
        .get_fixed_expenses(
          page,
          limit,
          this.search,
          0,
          this.$route.query.market_id
        )
        .then((res) => {
          this.pages = res.data.page;
          this.page = res.data.current_page;
          this.limit = res.data.limit;
          this.expenses = res.data.data;
        });
    },
    add(body) {
      api.create_fixed_expense(body).then(() => {
        body.name = "";
        api.success("close");
        this.get(this.page, this.limit);
      });
    },
    put(body) {
      api.update_fixed_expense(body).then(() => {
        api.success("close-put");
        this.get(this.page, this.limit);
      });
    },
    pay(body) {
      api.pay_for_expense(body).then(() => {
        body.money = 0;
        body.type = "";
        body.source = null;
        body.comment = "";
        body.kassa_id = null;
        api.success("close-expense");
        this.get(this.page, this.limit);
      });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>
