<template>
  <div class="container-fluid py-4">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div
            class="col-12 mb-2"
            style="overflow-x: auto; display: flex; flex-wrap: nowrap"
          >
            <div class="btn-group m-1">
              <button
                class="btn btn bg-gradient-success mb-0"
                @click="postOrder()"
              >
                <i class="fas fa-circle-plus"></i>
                <!-- {{ $t("order.add-button") }} -->
              </button>
            </div>
            <div
              class="btn-group m-1"
              v-for="(item, index) in orders"
              :key="item"
            >
              <button
                :class="
                  order?.id == item.id
                    ? 'bg-gradient-primary'
                    : 'bg-gradient-secondary'
                "
                class="btn btn mb-0"
                @click="
                  order = orders[index];
                  $refs.trades.start(order);
                "
                style="white-space: nowrap"
              >
                 {{ item.ordinal_number }}
              </button>
              <button
                class="btn btn mb-0 bg-gradient-danger px-3"
                @click="deleteOrder(item.id)"
              >
                <i class="fas fa-circle-xmark"></i>
              </button>
            </div>
          </div>
          <div class="col-lg-3 col-md-4 my-1">
            <h4 class="mb-0">
              {{ $t("order.title") }}
              {{ order?.ordinal_number ? ` -  ${order?.ordinal_number}` : "" }}
            </h4>
          </div>
          <div
            class="col-lg-3 col-md-4 my-1"
            v-if="order || $refs.trades?.trades?.data?.length"
          >
            <div class="dropdown">
              <input
                type="text"
                class="form-control"
                :placeholder="`${$t('cycle.product_type')} ${$t('search')}`"
                id="products-dropdown"
                data-bs-toggle="dropdown"
                @input="
                  ($event) =>
                    ($refs.trades.search_product = $event.target.value)
                "
                @keyup="$refs.trades.getProducts(1, 25)"
              />
              <div
                class="dropdown-menu w-100 p-1"
                aria-labelledby="products-dropdown"
              >
                <ul
                  class="list-group scroll-25 p-1"
                  @scroll="$refs.trades.scrollProducts($event)"
                >
                  <li
                    class="list-group-item"
                    v-for="item in $refs.trades?.products?.data"
                    :key="item"
                    data-bs-toggle="modal"
                    data-bs-target="#trade-modal"
                    @click="
                      $refs.trades.product = item;
                      $refs.trades.new_trade.price = item.Product_Store?.price;
                      $refs.trades.new_trade.size1 = item.Product_Store?.size1;
                      $refs.trades.new_trade.size2 = item.Product_Store?.size2;
                    "
                  >
                    {{
                      `${item?.Product_Store?.product?.name} ${
                        item?.Product_Store?.material?.name
                          ? " - " + item?.Product_Store?.material?.name
                          : ""
                      }`
                    }}
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div
            class="col-lg-3 col-md-4 my-1"
            v-if="order || $refs.trades?.trades?.data?.length"
          >
            <button
              class="btn btn bg-gradient-primary float-end mb-0 w-100"
              data-bs-toggle="modal"
              data-bs-target="#confirm-modal"
              @click="getBalance()"
            >
              {{ $t("order.confirm-button") }}
            </button>
          </div>
          <div class="col-lg-3 col-md-4 my-1" v-if="order">
            <div class="row">
              <div class="col">
                <button
                  class="btn btn bg-gradient-info float-end mb-0 px-2 w-100"
                  data-bs-toggle="modal"
                  data-bs-target="#products"
                  @click="$refs.trades.getProducts(1, 25)"
                >
                  {{ $t("order.add-trade-button") }}
                </button>
              </div>
              <div class="col-auto" style="padding-left: 0">
                <button
                  class="btn btn bg-gradient-secondary w-100 mb-0"
                  @click="$refs.orderCheck.start()"
                >
                  <i class="fa fa-print"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body">
        <trades ref="trades" />
      </div>
    </div>
  </div>

  <orderCheck
    :market-id="$route.query.market_id"
    :order-id="order?.id || 0"
    ref="orderCheck"
  />

  <div
    class="modal fade"
    id="confirm-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="confirm-modal-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="confirm-modal-label">
            {{ $t("order.confirm-modal-title") }}
          </h5>
        </div>
        <form @submit.prevent="putOrder()">
          <div class="modal-body">
            <p class="mb-2">
              <strong>{{
                Intl.NumberFormat().format(balance) + " " + $t("currency")
              }}</strong>
            </p>
            <div class="row gap-1">
              <div class="col-10">
                <div class="dropdown">
                  <button
                    id="customerDropdown"
                    class="btn btn-sm w-100 mb-0 dropdown-toggle"
                    data-bs-toggle="dropdown"
                  >
                    {{ customer ? customer.name : $t("order.customer") }}
                  </button>
                  <div
                    class="dropdown-menu w-100 p-1"
                    aria-labelledby="customerDropdown"
                  >
                    <input
                      type="text"
                      class="form-control form-control-sm my-1"
                      :placeholder="$t('search')"
                      v-model="search_customer"
                      @keyup="getCustomers()"
                    />
                    <ul
                      class="list-group scroll-25 p-1"
                      @scroll="scrollCustomers($event)"
                    >
                      <li
                        class="list-group-item"
                        v-for="item in customers?.data"
                        :key="item"
                        @click="customer = item"
                      >
                        {{ item.name }}
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div class="col-auto" style="padding-left: 0">
                <button
                  class="btn btn-sm mb-0 px-4"
                  type="button"
                  data-bs-toggle="modal"
                  data-bs-target="#add-customer"
                >
                  <i class="fas fa-circle-plus"></i>
                </button>
              </div>
              <div class="col-12">
                <div class="input-group">
                  <input
                    type="number"
                    min="0"
                    :max="max(order_confirm.money)"
                    step="any"
                    class="form-control"
                    :placeholder="$t('trades.placeholders.price')"
                    required
                    v-model="order_confirm.money"
                    @keyup="countLoan()"
                  />
                  <div class="input-group-text">{{ $t("currency") }}</div>
                </div>
              </div>
              <div class="col-12">
                <div class="input-group">
                  <input
                    type="number"
                    min="0"
                    :max="max(order_confirm.discount)"
                    step="any"
                    class="form-control"
                    :placeholder="$t('trades.placeholders.discount')"
                    v-model="order_confirm.discount"
                    @keyup="countLoan()"
                  />
                  <div class="input-group-text">{{ $t("currency") }}</div>
                </div>
              </div>
              <div
                class="col-12"
                v-if="
                  order_confirm.money &&
                  order_confirm.money + order_confirm.discount < balance
                "
              >
                <div class="input-group">
                  <input
                    type="number"
                    min="0"
                    :max="max(loan)"
                    step="any"
                    class="form-control"
                    :placeholder="$t('trades.placeholders.loan')"
                    disabled
                    v-model="loan"
                  />
                  <div class="input-group-text">{{ $t("currency") }}</div>
                </div>
              </div>
              <div class="col-12" v-if="loan">
                <div class="input-group">
                  <input
                    type="date"
                    class="form-control"
                    required
                    v-model="order_confirm.loan_repayment_date"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-confirm-modal
            >
              {{ $t("close") }}
            </button>
            <button class="btn bg-gradient-primary">
              {{ $t("order.confirm-button") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <customer-modals @add="add_customer" ref="customer" />
</template>

<script>
import * as api from "../../server/api";
import trades from "./trades.vue";
import orderCheck from "./orderCheck.vue";
import CustomerModals from "@/components/modals/CustomerModals.vue";
export default {
  name: "order",
  components: { trades, orderCheck, CustomerModals },
  data() {
    return {
      order: null,
      balance: null,
      search_customer: "",
      customers: null,
      customer: null,
      order_confirm: {
        id: 0,
        money: null,
        discount: null,
        customer_id: 0,
        loan_repayment_date: new Date().toLocaleString("uz", {
          year: "numeric",
          month: "2-digit",
          day: "2-digit",
        }),
      },
      loan: null,
      orders: [],
    };
  },
  mounted() {
    this.getCustomers();
    this.getOrders();
    this.$refs.trades.getProducts(1, 25);
  },
  methods: {
    max(value) {
      let number = this.balance;
      number -= this.order_confirm.money;
      number -= this.order_confirm.discount;
      number -= this.loan;
      return Number(number) + Number(value);
    },
    getOrders() {
      api
        .orders("", "", 0, 0, "false", 0, this.$route.query.market_id, 1, 1000)
        .then((res) => {
          this.orders = res.data.data;
          this.order_check(res.data);
        });
    },
    order_check(res) {
      if (res.data.length) {
        this.order = res.data[0];
        this.$refs.trades.start(this.order);
      } else {
        this.order = null;
        this.$refs.trades.trades = null;
        this.$store.dispatch("setloading", false);
      }
    },
    postOrder() {
      api
        .create_order({
          status: "false",
          delivery_date: null,
          customer_id: 0,
        })
        .then(() => {
          api.success();
          this.getOrders();
        });
    },
    deleteOrder(id) {
      api.delete_order(id).then(() => {
        api.success();
        this.getOrders();
      });
    },
    putOrder() {
      this.order_confirm.id = this.order.id;
      this.order_confirm.customer_id = this.customer ? this.customer.id : 0;
      this.order_confirm.discount = this.order_confirm.discount
        ? this.order_confirm.discount
        : 0;
      api.order_confirmation(this.order_confirm).then(() => {
        api.success("close-confirm-modal").then(() => {
          this.order_confirm = {
            id: 0,
            money: null,
            discount: null,
            customer_id: 0,
            loan_repayment_date: new Date().toLocaleString("uz", {
              year: "numeric",
              month: "2-digit",
              day: "2-digit",
            }),
          };
          this.loan = null;
          this.getOrders();
        });
      });
    },
    getCustomers() {
      api
        .customers(this.search_customer, 0, this.$route.query.market_id, 1, 25)
        .then((res) => {
          this.customers = res.data;
        });
    },
    scrollCustomers(event) {
      const element = event.target;
      if (element.scrollTop + element.clientHeight >= element.scrollHeight) {
        if (this.customers.current_page < this.customers.pages) {
          api
            .customers(
              this.search_customer,
              0,
              this.$route.query.market_id,
              this.customers.current_page + 1,
              25
            )
            .then((res) => {
              this.customers.current_page = res.data.current_page;
              this.customers.pages = res.data.pages;
              this.customers.data = this.customers.data.concat(res.data.data);
            });
        }
      }
    },
    getBalance() {
      api.trades(this.order.id, false, 1, 1, 25).then((res) => {
        this.balance = res.data;
      });
    },
    countLoan() {
      if (
        this.order_confirm.money &&
        this.order_confirm.money + this.order_confirm.discount < this.balance
      ) {
        this.loan =
          this.balance -
          (this.order_confirm.money + this.order_confirm.discount);
      } else {
        this.loan = null;
      }
    },
    add_customer(body) {
      body.phones.forEach((element) => {
        element.phone = `${element.dial_code}${element.phone}`;
      });
      api.create_customer(body).then((res) => {
        this.customer = res.data;
        body.name = "";
        body.type = "";
        body.phones = [{ phone: "", dial_code: "+7" }];
        api.success("close");
        this.getCustomers();
        document.querySelector('[data-bs-target="#confirm-modal"]').click();
      });
    },
  },
};
</script>
