<template>
  <div
    class="modal fade"
    id="add-user"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-user-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-user-label">
            {{ $t("users.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('add', body)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6 col-lg-4 col-sm-6">
                <div class="form-group">
                  <label for="name">{{ $t("users.table.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="body.name"
                    :placeholder="$t('users.table.name')"
                    minlength="5"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6">
                <div class="form-group">
                  <label for="nation">{{ $t("users.table.nation") }}</label>
                  <input
                    id="nation"
                    type="text"
                    class="form-control"
                    v-model="body.nation"
                    :placeholder="$t('users.table.nation')"
                    minlength="3"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6">
                <div class="form-group">
                  <label for="address">{{ $t("users.address") }}</label>
                  <input
                    id="address"
                    type="text"
                    class="form-control"
                    v-model="body.address"
                    :placeholder="$t('users.address')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6">
                <div class="form-group">
                  <label for="birthdate">{{
                    $t("users.table.birth-date")
                  }}</label>
                  <input
                    id="birthdate"
                    type="date"
                    class="form-control"
                    v-model="body.birthdate"
                    :placeholder="$t('users.table.birth-date')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6">
                <div class="form-group">
                  <label for="specialty">{{
                    $t("users.table.specialty")
                  }}</label>
                  <input
                    id="specialty"
                    type="text"
                    class="form-control"
                    v-model="body.specialty"
                    :placeholder="$t('users.table.specialty')"
                    minlength="5"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6">
                <div class="form-group">
                  <label for="passport">{{ $t("users.table.passport") }}</label>
                  <input
                    id="passport"
                    type="file"
                    class="form-control"
                    @change="body.passport = $event.target.files[0]"
                    :placeholder="$t('users.table.passport')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6">
                <div class="form-group">
                  <label for="workbegindate">{{
                    $t("users.table.work-begin-date")
                  }}</label>
                  <input
                    id="workbegindate"
                    type="date"
                    class="form-control"
                    v-model="body.workbegindate"
                    :placeholder="$t('users.table.work-begin-date')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6">
                <div class="form-group">
                  <label for="agreement_expiration">{{
                    $t("users.table.agreement-expiration")
                  }}</label>
                  <input
                    id="agreement_expiration"
                    type="date"
                    class="form-control"
                    v-model="body.agreement_expiration"
                    :placeholder="$t('users.table.agreement-expiration')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6">
                <div class="form-group">
                  <label for="username">{{ $t("users.table.username") }}</label>
                  <input
                    id="username"
                    type="text"
                    class="form-control"
                    v-model="body.username"
                    :placeholder="$t('users.table.username')"
                    minlength="5"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6">
                <div class="form-group">
                  <label for="password">{{ $t("users.table.password") }}</label>
                  <input
                    id="password"
                    type="password"
                    class="form-control"
                    v-model="body.password"
                    :placeholder="$t('users.table.password')"
                    minlength="5"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6">
                <div class="form-group">
                  <label for="role">{{ $t("users.table.role") }}</label>
                  <select
                    id="role"
                    class="form-select"
                    v-model="body.role"
                    required
                  >
                    <option value="admin">{{ $t("roles.admin") }}</option>
                    <option value="accountant">
                      {{ $t("roles.accountant") }}
                    </option>
                    <option value="analyst">{{ $t("roles.analyst") }}</option>
                    <option value="seller">{{ $t("roles.seller") }}</option>
                    <option value="worker">{{ $t("roles.worker") }}</option>
                    <option value="storeman">{{ $t("roles.storeman") }}</option>
                    <option value="plant_admin">
                      {{ $t("roles.plant_admin") }}
                    </option>
                    <option value="seller_admin">
                      {{ $t("roles.seller_admin") }}
                    </option>
                    <option value="founder">
                      {{ $t("roles.founder") }}
                    </option>
                  </select>
                </div>
              </div>
              <div
                class="col-md-6 col-lg-4 col-sm-6"
                v-for="(phone, i) in body.phones"
                :key="phone"
              >
                <div class="form-group">
                  <label :for="`phone${i}`"
                    >{{ $t("users.table.phone") }}
                    {{ body.phones.length > 1 ? i + 1 : "" }}</label
                  >
                  <div class="input-group">
                    <select
                      class="form-control w-25"
                      v-model="phone.dial_code"
                      required
                    >
                      <option
                        :value="code.dial_code"
                        v-for="code in phone_codes"
                        :key="code"
                      >
                        {{ code.dial_code }}
                      </option>
                    </select>
                    <input
                      :id="`phone${i}`"
                      type="number"
                      class="form-control"
                      :class="body.phones.length > 1 ? 'w-50' : 'w-75'"
                      v-model="phone.value"
                      :placeholder="$t('users.table.phone')"
                      min="1"
                      required
                    />
                    <button
                      class="btn bg-gradient-danger mb-0"
                      type="button"
                      v-if="i || (!i && body.phones.length > 1)"
                      @click="body.phones.splice(i, 1)"
                    >
                      <i class="fa-solid fa-circle-xmark"></i>
                    </button>
                  </div>
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6">
                <div class="form-group">
                  <label for="market">{{ $t("users.market") }}</label>
                  <select
                    id="market"
                    class="form-select"
                    v-model="body.market_id"
                  >
                    <option
                      :value="market.id"
                      v-for="market in markets"
                      :key="market"
                    >
                      {{ market.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6">
                <div class="form-group">
                  <label for="shift">{{ $t("plant.shift") }}</label>
                  <select
                    id="shift"
                    class="form-select"
                    v-model="body.shift_id"
                  >
                    <option
                      :value="shift.id"
                      v-for="shift in shifts"
                      :key="shift"
                    >
                      {{ shift.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6">
                <div class="form-group">
                  <label for="wage">{{ $t("plant.wage") }}</label>
                  <div class="input-group">
                    <input
                      id="wage"
                      type="number"
                      step="any"
                      class="form-control"
                      v-model="body.salary"
                      @focus="
                        body.salary == 0 ? (body.salary = null) : body.salary
                      "
                      @focusout="
                        body.salary == null ? (body.salary = 0) : body.salary
                      "
                      :placeholder="$t('plant.wage')"
                      required
                    />
                    <span class="input-group-text">
                      {{ $t("currency") }}
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              class="btn bg-gradient-primary"
              type="button"
              @click="body.phones.push({ value: '', dial_code: '+7' })"
            >
              <i class="fa-solid fa-phone"></i>
            </button>
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import codes from "@/language/CountryPhoneCode.json";
export default {
  name: "add-user",
  emits: ["add"],
  data() {
    return {
      body: {
        name: "",
        nation: "",
        birthdate: "",
        specialty: "",
        passport: "",
        workbegindate: "",
        agreement_expiration: "",
        username: "",
        password: "",
        disabled: false,
        phones: [{ value: "", dial_code: "+7" }],
        role: "",
        address: "",
        market_id: 0,
        salary: 0,
        shift_id: 0,
      },
      phone_codes: codes,
      markets: [],
      shifts: [],
    };
  },
  methods: {
    get_markets() {
      api.markets("", 0, 1, 1000).then((res) => {
        this.markets = res.data.data;
        this.get_shifts();
      });
    },
    get_shifts() {
      api.get_shifts(1, 100).then((res) => {
        this.shifts = res.data.data;
      });
    },
  },
};
</script>
