<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">
              {{ $route.query.name }} -
              <b>{{
                Intl.NumberFormat().format($route.query.balance) +
                " " +
                $t("currency")
              }}</b>
            </h6>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col">
                <div class="nav-wrapper position-relative end-0">
                  <ul
                    class="p-1 bg-transparent nav nav-pills nav-fill"
                    role="tablist"
                    id="pills-tab"
                  >
                    <li class="nav-item cursor-pointer">
                      <a
                        class="px-0 py-1 mb-0 nav-link active"
                        @click="
                          content = 'expense';
                          get_expense(expense.page, expense.limit);
                        "
                      >
                        <i class="fas fa-up-long text-danger"></i>
                        <span class="ms-1 text-danger">{{
                          $t("cashier.history.expense")
                        }}</span>
                      </a>
                    </li>
                    <li class="nav-item cursor-pointer">
                      <a
                        class="px-0 py-1 mb-0 nav-link"
                        @click="
                          content = 'income';
                          get_income(income.page, income.limit);
                        "
                      >
                        <i class="fas fa-down-long text-success"></i>
                        <span class="ms-1 text-success">{{
                          $t("cashier.history.income")
                        }}</span>
                      </a>
                    </li>
                  </ul>
                </div>
              </div>
              <div class="col-auto d-flex align-items-center">
                <button
                  class="btn btn-sm bg-gradient-primary mb-0 px-3 float-end"
                  data-bs-toggle="modal"
                  data-bs-target="#filter"
                >
                  <i class="fas fa-filter"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="expense" v-if="content == 'expense'">
          <div class="table-responsive p-0 mt-2">
            <table class="table table-bordered align-items-center mb-0">
              <thead>
                <tr>
                  <th class="w-custom-40">#</th>
                  <th>{{ $t("expense.user") }}</th>
                  <th>{{ $t("expense.money") }}</th>
                  <th>{{ $t("expense.comment") }}</th>
                  <th>{{ $t("expense.name") }}</th>
                  <th>{{ $t("expense.date") }}</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, i) in expenses" :key="item">
                  <td>{{ i + 1 }}</td>
                  <td>{{ item.user.name }}</td>
                  <td>
                    {{
                      `${Intl.NumberFormat().format(item.money)} ${$t(
                        "currency"
                      )}`
                    }}
                  </td>
                  <td>{{ item.comment }}</td>
                  <td>
                    {{
                      item.type == "fixed_expense"
                        ? $t("expense.fixed") + `(${item.fixed_expense?.name})`
                        : item.type == "variable_expense"
                        ? $t("expense.variable")
                        : item.type == "user_expense"
                        ? $t("expense.user")
                        : item.type == "order_expense"
                        ? $t("expense.order")
                        : item.type == "returned_product"
                        ? $t("expense.return")
                        : item.type == "user_salary"
                        ? $t("cashier.history.user_salary")
                        : ""
                    }}
                  </td>
                  <td>{{ format_date(item.time) }}</td>
                </tr>
              </tbody>
              <tfoot v-if="!expenses.length">
                <tr>
                  <td colspan="7">
                    <data-notfound :data="expenses" />
                  </td>
                </tr>
              </tfoot>
            </table>
          </div>
          <pagination
            :page="expense.page"
            :pages="expense.pages"
            :limit="expense.limit"
            @get="get_expense"
          />
        </div>
        <div class="income" v-else>
          <div class="table-responsive p-0 mt-2">
            <table class="table table-bordered align-items-center mb-0">
              <thead>
                <tr>
                  <th class="w-custom-40">#</th>
                  <th>{{ $t("cashier.history.money") }}</th>
                  <th>{{ $t("cashier.history.date") }}</th>
                  <th>{{ $t("cashier.history.comment") }}</th>
                  <th>{{ $t("cashier.history.source") }}</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, i) in incomes" :key="item">
                  <td>{{ i + 1 }}</td>
                  <td>
                    {{
                      `${Intl.NumberFormat().format(item.money)} ${$t(
                        "currency"
                      )}`
                    }}
                  </td>
                  <td>{{ format_date(item.time) }}</td>
                  <td>{{ item.comment }}</td>
                  <td>{{ $t(`cashier.history.${item.type}`) }}</td>
                </tr>
              </tbody>
              <tfoot v-if="!incomes.length">
                <tr>
                  <td colspan="5">
                    <data-notfound :data="incomes" />
                  </td>
                </tr>
              </tfoot>
            </table>
          </div>
          <pagination
            :page="income.page"
            :pages="income.pages"
            :limit="income.limit"
            @get="get_expense"
          />
        </div>
      </div>
    </div>
  </div>
  <!-- filter -->
  <div class="modal fade" id="filter">
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title">
            {{ $t("filter") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-6">
              <div class="form-group">
                <label for="from_date">{{ $t("from_date") }}</label>
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="expense.from_date"
                  v-if="content == 'expense'"
                />
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="income.from_date"
                  v-else
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("to_date") }}</label>
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="expense.to_date"
                  v-if="content == 'expense'"
                />
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="income.to_date"
                  v-else
                />
              </div>
            </div>
            <div class="col-md-12">
              <div class="form-group">
                <label for="source">{{ $t("cashier.history.source") }}</label>
                <select
                  id="source"
                  class="form-select"
                  v-model="income.type"
                  v-if="content == 'income'"
                >
                  <option value="order">
                    {{ $t("cashier.history.order") }}
                  </option>
                  <option value="loan">{{ $t("cashier.history.loan") }}</option>
                  <option value="user">{{ $t("cashier.history.user") }}</option>
                  <option value="fixed_expense">
                    {{ $t("cashier.history.fixed_expense") }}
                  </option>
                  <option value="variable_expense">
                    {{ $t("cashier.history.variable_expense") }}
                  </option>
                </select>
                <select
                  id="source"
                  class="form-select"
                  v-model="expense.type"
                  v-else
                >
                  <option value="variable_expense">
                    {{ $t("expense.variable") }}
                  </option>
                  <option value="fixed_expense">
                    {{ $t("expense.fixed") }}
                  </option>
                  <option value="order_expense">
                    {{ $t("expense.order") }}
                  </option>
                  <option value="user_expense">{{ $t("expense.user") }}</option>
                  <option value="user_salary">
                    {{ $t("cashier.history.user_salary") }}
                  </option>
                  <option value="returned_product">
                    {{ $t("expense.return") }}
                  </option>
                </select>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-sm bg-gradient-danger"
            data-bs-dismiss="modal"
            @click="
              content == 'expense'
                ? ((expense.from_date = ''),
                  (expense.to_date = ''),
                  (expense.type = ''),
                  get_expense(expense.page, expense.limit))
                : ((income.from_date = ''),
                  (income.to_date = ''),
                  (income.type = ''),
                  get_income(income.page, income.limit))
            "
          >
            <i class="fas fa-trash"></i>
          </button>
          <button
            class="btn btn-sm bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-filter
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn btn-sm bg-gradient-primary"
            data-bs-dismiss="modal"
            @click="
              content == 'expense'
                ? get_expense(expense.page, expense.limit)
                : get_income(income.page, income.limit)
            "
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "../../server/api";
import setNavPills from "@/assets/js/nav-pills.js";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";

export default {
  components: { dataNotfound, Pagination },
  name: "cashier-history",
  data() {
    return {
      kassa_id: this.$route.query.id,
      expenses: [],
      expense: {
        pages: 0,
        page: 1,
        limit: 10,
        type: "",
        from_date: "",
        to_date: "",
      },
      incomes: [],
      income: {
        pages: 0,
        page: 1,
        limit: 10,
        order_id: 0,
        loan_id: 0,
        user_id: 0,
        from_date: "",
        to_date: "",
        type: "",
      },
      content: "expense",
      market_id: this.$route.query.market_id || 0,
      format_date: api.format_date,
    };
  },
  methods: {
    get_expense(page, limit) {
      api
        .get_expenses(
          page,
          limit,
          this.expense.from_date,
          this.expense.to_date,
          this.kassa_id,
          this.expense.type,
          0,
          this.market_id
        )
        .then((res) => {
          this.expense.pages = res.data.page;
          this.expense.page = res.data.current_page;
          this.expense.limit = res.data.limit;
          this.expenses = res.data.data;
        });
    },
    get_income(page, limit) {
      api
        .get_incomes(
          page,
          limit,
          this.income.from_date,
          this.income.to_date,
          this.income.order_id,
          this.income.loan_id,
          this.income.user_id,
          this.kassa_id,
          this.income.type,
          this.market_id
        )
        .then((res) => {
          this.income.pages = res.data.page;
          this.income.page = res.data.current_page;
          this.income.limit = res.data.limit;
          this.incomes = res.data.data;
        });
    },
  },
  created() {
    this.get_expense(this.expense.page, this.expense.limit);
  },
  mounted() {
    setNavPills();
  },
};
</script>
