<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="row p-3 py-3">
        <div class="col-md col-lg col-12 my-1">
          <div class="nav-wrapper position-relative end-0">
            <ul
              class="bg-transparent nav nav-pills nav-fill"
              role="tablist"
              id="pills-tab"
            >
              <li class="nav-item cursor-pointer">
                <a
                  class="px-0 py-1 mb-0 nav-link active"
                  @click="
                    content = 'orders';
                    page = 1;
                    pages = 0;
                    getOrders(page, limit);
                  "
                >
                  <i class="fas fa-up-long text-danger"></i>
                  <span class="ms-1 text-danger">{{
                    $t("production.tab.tab1")
                  }}</span>
                </a>
              </li>
              <li class="nav-item cursor-pointer">
                <a
                  class="px-0 py-1 mb-0 nav-link"
                  @click="
                    content = 'production';
                    page = 1;
                    pages = 0;
                    getProductionOrders(page, limit);
                  "
                >
                  <i class="fas fa-down-long text-success"></i>
                  <span class="ms-1 text-success">{{
                    $t("production.tab.tab2")
                  }}</span>
                </a>
              </li>
            </ul>
          </div>
        </div>
        <div class="col-md-auto col-lg-auto col-12 my-1">
          <div class="btn-group w-100">
            <button
              class="btn btn-sm bg-gradient-primary mb-0"
              data-bs-toggle="modal"
              data-bs-target="#production-with-out-plan"
            >
              {{ $t("production.production-without-plan") }}
            </button>
            <button
              @click="$router.push('production-history')"
              class="btn btn-sm bg-gradient-secondary mb-0"
            >
              <i class="fa-solid fa-clock-rotate-left"></i>
            </button>
          </div>
        </div>
      </div>
      <div class="card-header py-0">
        <div class="row">
          <div class="col-md-6"></div>
          <div class="col-md-6 mb-2">
            <div class="row">
              <div class="col">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    content == 'orders'
                      ? getOrders(1, limit)
                      : getProductionOrders(1, limit);
                    $store.dispatch('setloading', false);
                  "
                />
              </div>
              <div class="col">
                <select
                  class="form-select form-select-sm"
                  v-model="selectedPlantId"
                  @change="savePlant()"
                >
                  <option :value="0">{{ $t("all") }}</option>
                  <option :value="selectedPlantId" disabled>
                    {{
                      selectedPlantName
                        ? selectedPlantName
                        : $t("production.plant")
                    }}
                  </option>
                  <option
                    :value="plant.id"
                    v-for="plant in plants"
                    :key="plant"
                  >
                    {{ plant.name }}
                  </option>
                </select>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-md-4 my-2"
            v-for="(order, index) in orders"
            :key="order"
          >
            <div
              class="card border"
              :class="
                content == 'orders'
                  ? count_date(new Date().toISOString(), order.to_date) > 10
                    ? 'border-success'
                    : count_date(new Date().toISOString(), order.to_date) <=
                        10 &&
                      count_date(new Date().toISOString(), order.to_date) > 5
                    ? 'border-warning'
                    : 'border-danger'
                  : ''
              "
            >
              <div class="card-body position-relative">
                <span
                  class="position-absolute top-0 start-100 translate-middle badge rounded-pill"
                  :class="
                    content == 'orders'
                      ? count_date(new Date().toISOString(), order.to_date) > 10
                        ? 'bg-success'
                        : count_date(new Date().toISOString(), order.to_date) <=
                            10 &&
                          count_date(new Date().toISOString(), order.to_date) >
                            5
                        ? 'bg-warning'
                        : 'bg-danger'
                      : ''
                  "
                >
                  {{
                    content == "orders"
                      ? count_date(new Date().toISOString(), order.to_date) > 0
                        ? `+${count_date(
                            new Date().toISOString(),
                            order.to_date
                          )}`
                        : count_date(new Date().toISOString(), order.to_date)
                      : ""
                  }}
                </span>
                <div class="row d-flex align-items-center">
                  <div class="col">
                    <div class="d-flex justify-content-between">
                      <div>
                        <h6 class="mb-0">
                          <b style="font-size: 14px">
                            {{ $t("production.order.name") }}:
                          </b>
                          <span class="ms-1" style="font-size: 16px">
                            {{ order.product_name }}
                          </span>
                        </h6>
                        <h6 class="mb-0">
                          <b style="font-size: 14px">
                            {{ $t("production.material") }}:
                          </b>
                          <span class="ms-1" style="font-size: 16px">
                            {{ order.material }}
                          </span>
                        </h6>
                        <h6 class="mb-0">
                          <b style="font-size: 14px">
                            {{ $t("production.order.progress") }}:
                          </b>
                          <span class="ms-1" style="font-size: 16px">
                            {{ order.cycle_name }}
                          </span>
                        </h6>
                        <h6 class="mb-0" v-if="content === 'orders'">
                          <b style="font-size: 14px">
                            {{ $t("production.order.orderer") }}:
                          </b>
                          <span class="ms-1" style="font-size: 16px">
                            {{ order.customer_name }}
                          </span>
                        </h6>
                        <h6 class="mb-0">
                          <b style="font-size: 14px">
                            {{ $t("production.order.quantity") }}:
                          </b>
                          <span class="ms-1" style="font-size: 16px">
                            {{ order.quantity }} {{ order.olchov_name }}
                          </span>
                        </h6>
                      </div>
                    </div>
                  </div>
                  <div class="col-auto">
                    <img
                      width="100"
                      height="100"
                      :src="`${image}/products/${order.product_image}`"
                      style="border-radius: 12px; object-fit: cover"
                      alt="Image"
                      @click="
                        image_view(`${image}/products/${order.product_image}`)
                      "
                    />
                  </div>
                </div>
                <button
                  class="btn btn-sm w-100 mb-0 mt-1"
                  :class="
                    content == 'orders'
                      ? count_date(new Date().toISOString(), order.to_date) > 10
                        ? 'bg-gradient-success'
                        : count_date(new Date().toISOString(), order.to_date) <=
                            10 &&
                          count_date(new Date().toISOString(), order.to_date) >
                            5
                        ? 'bg-gradient-warning'
                        : 'bg-gradient-danger'
                      : 'bg-gradient-secondary'
                  "
                  data-bs-toggle="modal"
                  :data-bs-target="`#production-details${index}`"
                  @click="
                    $refs.production.body.value = order.quantity;
                    cycle_id = order.cycle_id;
                    trade_item_id = order.trade_item_id;
                    detail = order;
                    getDetails();
                  "
                >
                  {{ $t("production.done") }}
                </button>
              </div>
            </div>
            <production-modals
              @get-details="getDetails"
              @get-orders="getOrders(page, limit)"
              @get-orders-production="getProductionOrders(page, limit)"
              :details="details"
              :detail="order"
              :content="content"
              :index="index"
              ref="production"
            />
          </div>
          <div class="col-12 mt-1">
            <data-notfound :data="orders" />
            <pagination
              v-if="content == 'orders'"
              :page="page"
              :pages="pages"
              :limit="limit"
              @get="getOrders"
            />
            <pagination
              v-else
              :page="page"
              :pages="pages"
              :limit="limit"
              @get="getProductionOrders"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
  <div id="image-viewer">
    <span class="close" @click="image_view()">&times;</span>
    <img class="modal-content" id="full-image" />
  </div>
  <production-without-plan />
</template>

<script>
import ProductionModals from "@/components/modals/ProductionModals.vue";
import setNavPills from "@/assets/js/nav-pills.js";
import * as api from "@/server/api";
import DataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";
import ProductionWithoutPlan from "@/components/modals/ProductionWithoutPlan.vue";
export default {
  components: {
    ProductionModals,
    DataNotfound,
    Pagination,
    ProductionWithoutPlan,
  },
  name: "Production",
  data() {
    return {
      all_plants: [],
      plants: [],
      orders: [],
      selectedPlantId: this.$store.state.user || 0,
      selectedPlantName: localStorage.getItem("selectedPlantName"),
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      image: api.image,
      cycle_id: null,
      trade_item_id: null,
      details: {},
      detail: {},
      content: "orders",
      count_date: api.countDays,
    };
  },
  methods: {
    getDetails(value) {
      api
        .get_order_details(
          this.detail.product_cycle_id,
          this.$store.state.user,
          this.detail.material_id,
          this.detail.plan_id,
          this.detail.trade_id ? this.detail.trade_id : 0,
          Number(value) ? Number(value) : 1,
          Number(this.detail.size1),
          Number(this.detail.size2)
        )
        .then((res) => {
          this.details = res.data;
        });
      this.$store.dispatch("setloading", false);
    },
    getPlants() {
      api.get_plants(1, 1000, "").then((res) => {
        this.all_plants = res.data.data;
        this.plants = res.data.data.filter((item) => {
          return this.selectedPlantId != item.id;
        });
      });
    },
    savePlant() {
      this.all_plants.forEach((item) => {
        if (item.id === this.selectedPlantId) {
          this.selectedPlantName = item.name;
          localStorage.setItem("selectedPlantName", item.name);
          this.plants = this.all_plants.filter((plant) => {
            return plant.id != this.selectedPlantId;
          });
        }
      });
      if (this.content === "production") {
        this.getProductionOrders(this.page, this.limit);
      } else {
        this.getOrders(this.page, this.limit);
      }
      this.$store.dispatch("setUser", this.selectedPlantId);
    },
    getOrders(page, limit) {
      api
        .get_orders(page, limit, this.search, this.selectedPlantId)
        .then((res) => {
          this.pages = res.data.page_count;
          this.page = res.data.current_page;
          this.orders = res.data.data;
        });
    },
    getProductionOrders(page, limit) {
      api
        .get_production_orders(page, limit, this.search, this.selectedPlantId)
        .then((res) => {
          this.pages = res.data.page_count;
          this.page = res.data.current_page;
          this.orders = res.data.data;
        });
    },
    image_view(src) {
      if (src) {
        document.getElementById("full-image").src = src;
        document.getElementById("image-viewer").style.display = "block";
      } else {
        document.getElementById("image-viewer").style.display = "none";
      }
    },
  },
  created() {
    this.getPlants();
    this.getOrders(this.page, this.limit);
  },
  mounted() {
    setNavPills();
  },
};
</script>
