<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-4 my-1">
            <h6 class="mb-0">{{ $route.query.name }}</h6>
          </div>
          <div class="col-md-4">
            <div class="nav-wrapper position-relative end-0">
              <ul
                class="p-1 bg-transparent nav nav-pills nav-fill"
                role="tablist"
                id="pills-tab"
              >
                <li class="nav-item cursor-pointer">
                  <a
                    class="px-0 py-1 mb-0 nav-link active"
                    data-bs-toggle="tab"
                    data-bs-target="#pills-supply"
                    id="pills-supply-tab"
                    role="tab"
                    aria-selected="true"
                    @click="
                      tab_type = 'supply';
                      search = '';
                    "
                  >
                    <i class="fas fa-clock-rotate-left"></i>
                    <span class="ms-1">{{
                      $t("material-history.supply")
                    }}</span>
                  </a>
                </li>
                <li class="nav-item cursor-pointer">
                  <a
                    class="px-0 py-1 mb-0 nav-link"
                    data-bs-toggle="tab"
                    data-bs-target="#pills-transfer"
                    id="pills-transfer-tab"
                    role="tab"
                    aria-selected="false"
                    @click="
                      tab_type = 'transfer';
                      search = '';
                      transfer_item.transfers.length
                        ? ''
                        : get_transfer(transfer_item.page, transfer_item.limit);
                    "
                  >
                    <i class="fas fa-clock-rotate-left"></i>
                    <span class="ms-1">{{
                      $t("material-history.transfer")
                    }}</span>
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <div class="col-md-4 my-1">
            <div class="row">
              <div class="col">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    tab_type == 'supply'
                      ? get_supply(1, supply_item.limit)
                      : get_transfer(1, transfer_item.limit);
                    $store.dispatch('setloading', false);
                  "
                />
              </div>
              <div class="col-auto ps-0">
                <button
                  class="btn btn-sm bg-gradient-primary mb-0 px-3"
                  data-bs-toggle="modal"
                  :data-bs-target="`#${
                    tab_type == 'supply' ? 'filter' : 'filter-transfer'
                  }`"
                  @click="$refs.supplyfilter.get_plants()"
                >
                  <i class="fa-solid fa-filter"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div v-if="tab_type == 'supply'">
          <div class="table-responsive p-0 mt-2">
            <table class="table table-bordered align-items-center mb-0">
              <thead>
                <tr>
                  <th>#</th>
                  <th>{{ $t("supply.value") }}</th>
                  <th>{{ $t("supply.price") }}</th>
                  <th>{{ $t("supply.date") }}</th>
                  <th>{{ $t("supply.plant") }}</th>
                  <th>{{ $t("supply.supplier") }}</th>
                  <th class="w-custom-40"></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(supply, i) in supply_item.supplies" :key="supply">
                  <td>{{ i + 1 }}</td>
                  <td>
                    {{
                      `${Intl.NumberFormat().format(supply.value)} ${
                        supply.olchov
                      }`
                    }}
                  </td>
                  <td>
                    {{
                      `${Intl.NumberFormat().format(supply.price)} ${$t(
                        "currency"
                      )}`
                    }}
                  </td>
                  <td>{{ supply.created_at.replace("T", " - ") }}</td>
                  <td>{{ supply.plant }}</td>
                  <td>{{ supply.supplier }}</td>
                  <td>
                    <button
                      class="btn btn-sm bg-gradient-warning mb-0 px-3"
                      data-bs-toggle="modal"
                      data-bs-target="#supply-update"
                      @click="
                        item = {
                          id: supply.supply_id,
                          material_id: supply.material_id,
                          supplier_id: supply.supplier_id,
                          value: supply.value,
                          price: supply.price,
                          olchov_id: supply.olchov_id,
                          plant_id: supply.plant_id,
                          olchov: supply.olchov,
                          has_volume: supply.has_volume,
                        }
                      "
                    >
                      <i class="fas fa-pen"></i>
                    </button>
                  </td>
                  <!-- If the role is admin, edit the supply -->
                </tr>
              </tbody>
              <tfoot v-if="!supply_item.supplies.length">
                <tr>
                  <td colspan="7">
                    <data-notfound :data="supply_item.supplies" />
                  </td>
                </tr>
              </tfoot>
            </table>
          </div>
          <pagination
            :page="supply_item.page"
            :pages="supply_item.pages"
            :limit="supply_item.limit"
            @get="get_supply"
          />
        </div>
        <div v-else>
          <div class="table-responsive p-0 mt-2">
            <table class="table table-bordered align-items-center mb-0">
              <thead>
                <tr>
                  <th>#</th>
                  <th>{{ $t("material-history.value") }}</th>
                  <th>{{ $t("material-history.from_plant") }}</th>
                  <th>{{ $t("material-history.to_plant") }}</th>
                  <th>{{ $t("material-history.date") }}</th>
                  <th class="w-custom-40"></th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="(transfer, i) in transfer_item.transfers"
                  :key="transfer"
                >
                  <td>{{ i + 1 }}</td>
                  <td>
                    {{ transfer.Material_Transfer.value }}
                    {{ transfer.Olchov.name }}
                  </td>
                  <td>{{ transfer.Source.name }}</td>
                  <td>{{ transfer.Plant.name }}</td>
                  <td>
                    {{ format_date(transfer.Material_Transfer.created_at) }}
                  </td>
                  <td>
                    <button
                      class="btn btn-sm bg-gradient-danger mb-0 px-3"
                      @click="remove_transfer(transfer.Material_Transfer.id)"
                    >
                      <i class="fas fa-circle-xmark"></i>
                    </button>
                  </td>
                  <!-- If the role is admin, remove the transfer -->
                </tr>
              </tbody>
              <tfoot v-if="!transfer_item.transfers.length">
                <tr>
                  <td colspan="7">
                    <data-notfound :data="transfer_item.transfers" />
                  </td>
                </tr>
              </tfoot>
            </table>
          </div>
          <pagination
            :page="transfer_item.page"
            :pages="transfer_item.pages"
            :limit="transfer_item.limit"
            @get="get_transfer"
          />
        </div>
      </div>
    </div>
  </div>
  <supply-history-filter
    ref="supplyfilter"
    @filter="filter"
    @transfer-filter="transfer_filter"
    @supply-update="supply_put"
    :item="item"
  />
</template>

<script>
import * as api from "@/server/api";
import SupplyHistoryFilter from "@/components/modals/SupplyHistoryModal.vue";
import setNavPills from "@/assets/js/nav-pills.js";
import DataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";

export default {
  components: { SupplyHistoryFilter, DataNotfound, Pagination },
  name: "supply-history",
  data() {
    return {
      supply_item: {
        supplies: [],
        pages: 0,
        page: 1,
        limit: 10,
        supplier_id: 0,
        material_id: this.$route.query.id,
        plant_id: 0,
        from_date: "",
        to_date: "",
      },
      transfer_item: {
        transfers: [],
        pages: 0,
        page: 1,
        limit: 10,
        source_id: 0,
        material_id: this.$route.query.id,
        plant_id: 0,
        from_date: "",
        to_date: "",
      },
      tab_type: "supply",
      search: "",
      item: {},
      format_date: api.format_date,
    };
  },
  methods: {
    get_supply(page, limit) {
      api
        .get_supplies(
          page,
          limit,
          this.search,
          this.supply_item.supplier_id,
          this.supply_item.plant_id,
          this.supply_item.material_id,
          this.supply_item.from_date,
          this.supply_item.to_date
        )
        .then((res) => {
          this.supply_item.pages = res.data.page_count;
          this.supply_item.page = res.data.current_page;
          this.supply_item.limit = res.data.page_limit;
          this.supply_item.supplies = res.data.data;
        });
    },
    filter(body, bool) {
      this.supply_item.supplier_id = body.supplier_id;
      this.supply_item.plant_id = body.plant_id;
      this.supply_item.from_date = body.from_date;
      this.supply_item.to_date = body.to_date;
      bool ? api.success("close-filter") : api.success();
      this.get_supply(this.supply_item.page, this.supply_item.limit);
    },
    get_transfer(page, limit) {
      api
        .get_material_transfers(
          page,
          limit,
          this.search,
          this.transfer_item.material_id,
          this.transfer_item.plant_id,
          this.transfer_item.source_id,
          this.transfer_item.from_date,
          this.transfer_item.to_date
        )
        .then((res) => {
          this.transfer_item.pages = res.data.page_count;
          this.transfer_item.page = res.data.current_page;
          this.transfer_item.limit = res.data.page_limit;
          this.transfer_item.transfers = res.data.data;
        });
    },
    remove_transfer(id) {
      api.delete_material_transfer(id).then(() => {
        api.success();
        this.get_transfer(this.transfer_item.page, this.transfer_item.limit);
      });
    },
    transfer_filter(body, bool) {
      this.transfer_item.from_date = body.from_date;
      this.transfer_item.to_date = body.to_date;
      bool ? api.success("close-filter-transfer") : api.success();
      this.get_transfer(this.transfer_item.page, this.transfer_item.limit);
    },
    supply_put(body) {
      api.put_supply(body).then(() => {
        api.success("close-put");
        this.get_supply(this.supply_item.page, this.supply_item.limit);
      });
    },
  },
  created() {
    this.get_supply(this.supply_item.page, this.supply_item.limit);
  },
  mounted() {
    setNavPills();
  },
};
</script>
