<template>
  <div
    class="modal fade"
    id="take-supply"
    tabindex="-1"
    role="dialog"
    aria-labelledby="take-supply-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="take-supply-label">
            {{ $t("supply.take") }}
          </h5>
        </div>
        <form @submit.prevent="take()">
          <div class="modal-body">
            <div class="row" id="form">
              <div class="col-md-6" v-if="take_from == 'all'">
                <div class="form-group">
                  <label for="type">{{ $t("supply.type") }}</label>
                  <select
                    id="type"
                    class="form-select"
                    v-model="content"
                    @change="
                      has_volume = false;
                      body.material_id = 0;
                      body.olchov_id = 0;
                      body.plant_id = 0;
                      body.price = 0;
                      body.product_cycle_id = 0;
                      body.product_id = 0;
                      body.product_material_id = 0;
                      body.value = 0;
                      product_type.product_type_id = 0;
                      material_type.material_type_id = 0;
                      product.name = $t('supply.product');
                      material.name = $t('supply.material');
                      product_type.name = $t('supply.product_type');
                      material_type.name = $t('supply.material_type');
                      supplier.name = $t('supply.supplier');
                    "
                    required
                  >
                    <option value="material">
                      {{ $t("supply.material") }}
                    </option>
                    <option value="product">{{ $t("supply.product") }}</option>
                  </select>
                </div>
              </div>
              <div class="col-md-6" v-if="content == 'material'">
                <div class="form-group">
                  <label for="plant">{{ $t("supply.plant") }}</label>
                  <select
                    id="plant"
                    class="form-select"
                    v-model="body.plant_id"
                    @change="
                      has_volume = false;
                      type == 'true' ? get_m_type(false) : '';
                      body.material_id = 0;
                      material_type.material_type_id = null;
                      material.name = $t('supply.material');
                      material_type.name = $t('supply.material_type');
                    "
                    required
                  >
                    <option
                      :value="plant.id"
                      v-for="plant in plants"
                      :key="plant"
                    >
                      {{ plant.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div
                class="col-md-6"
                v-if="type == 'true' && content == 'material'"
              >
                <div class="form-group">
                  <label>{{ $t("supply.material_type") }}</label>
                  <div class="dropdown">
                    <button
                      class="form-control border-secondary-custom shadow-none mb-0 w-100"
                      data-bs-toggle="dropdown"
                      aria-expanded="false"
                      :disabled="!body.plant_id"
                    >
                      {{ material_type.name }}
                    </button>
                    <ul
                      class="dropdown-menu w-100"
                      aria-labelledby="dropdownMenuButton1"
                    >
                      <input
                        type="text"
                        class="form-control"
                        :placeholder="$t('search')"
                        v-model="material_type.search"
                        @keyup="
                          get_m_type(false);
                          $store.dispatch('setloading', false);
                        "
                      />
                      <div
                        class="scroll-25 material-type"
                        @scroll="
                          scroll_material_type();
                          $store.dispatch('setloading', false);
                        "
                      >
                        <li
                          @click="
                            has_volume = false;
                            material_type.material_type_id =
                              item.Material_Type.id;
                            material_type.name = material_type.material_types.find(
                              (e) =>
                                material_type.material_type_id ==
                                e.Material_Type.id
                            ).Material_Type.name;
                            material.name = $t('supply.material');
                            body.material_id = 0;
                            get_materials();
                          "
                          v-for="item in material_type.material_types"
                          :key="item"
                        >
                          <a class="dropdown-item">
                            {{ item.Material_Type.name }}
                          </a>
                        </li>
                      </div>
                      <data-notfound
                        :data="material_type.material_types"
                        class="text-lg"
                      />
                    </ul>
                  </div>
                </div>
              </div>
              <div
                class="col-md-6"
                v-if="type == 'true' && content == 'material'"
              >
                <div class="form-group">
                  <label>{{ $t("supply.material") }}</label>
                  <div class="dropdown">
                    <button
                      class="form-control border-secondary-custom shadow-none mb-0 w-100"
                      data-bs-toggle="dropdown"
                      aria-expanded="false"
                      :disabled="!material_type.material_type_id"
                    >
                      {{ material.name }}
                    </button>
                    <ul
                      class="dropdown-menu w-100"
                      aria-labelledby="dropdownMenuButton1"
                    >
                      <input
                        type="text"
                        class="form-control"
                        :placeholder="$t('search')"
                        v-model="material.search"
                        @keyup="
                          get_materials(false);
                          $store.dispatch('setloading', false);
                        "
                      />
                      <div
                        class="scroll-25 material"
                        @scroll="
                          scroll_material();
                          $store.dispatch('setloading', false);
                        "
                      >
                        <li
                          @click="
                            unit = item.unit;
                            has_volume = false;
                            body.material_id = item.id;
                            body.olchov_id = item.olchov_id;
                            body.price = item.price;
                            has_volume = item.has_volume;
                            material.olchov_name = item.olchov;
                            material.name = material.materials.find(
                              (e) => body.material_id == e.id
                            ).name;
                          "
                          v-for="item in material.materials"
                          :key="item"
                        >
                          <a class="dropdown-item">
                            {{ item.name }}
                          </a>
                        </li>
                      </div>
                      <data-notfound
                        :data="material.materials"
                        class="text-lg"
                      />
                    </ul>
                  </div>
                </div>
              </div>
              <div class="col-md-6" v-if="content == 'product'">
                <div class="form-group">
                  <label>{{ $t("supply.product_type") }}</label>
                  <div class="dropdown">
                    <button
                      class="form-control border-secondary-custom shadow-none mb-0 w-100"
                      data-bs-toggle="dropdown"
                      aria-expanded="false"
                      @click="get_p_type(false)"
                    >
                      {{ product_type.name }}
                    </button>
                    <ul
                      class="dropdown-menu w-100"
                      aria-labelledby="dropdownMenuButton1"
                    >
                      <input
                        type="text"
                        class="form-control"
                        :placeholder="$t('search')"
                        v-model="product_type.search"
                        @keyup="
                          get_p_type(false);
                          $store.dispatch('setloading', false);
                        "
                      />
                      <div
                        class="scroll-25 product-type"
                        @scroll="
                          scroll_product_type();
                          $store.dispatch('setloading', false);
                        "
                      >
                        <li
                          @click="
                            body.product_cycle_id = 0;
                            product_type.product_type_id = item.id;
                            product_type.name = product_type.product_types.find(
                              (e) => product_type.product_type_id == e.id
                            ).name;
                            product.name = $t('supply.product');
                            body.product_id = 0;
                            get_products();
                          "
                          v-for="item in product_type.product_types"
                          :key="item"
                        >
                          <a class="dropdown-item">
                            {{ item.name }}
                          </a>
                        </li>
                      </div>
                      <data-notfound
                        :data="product_type.product_types"
                        class="text-lg"
                      />
                    </ul>
                  </div>
                </div>
              </div>
              <div class="col-md-6" v-if="content == 'product'">
                <div class="form-group">
                  <label>{{ $t("supply.product") }}</label>
                  <div class="dropdown">
                    <button
                      class="form-control border-secondary-custom shadow-none mb-0 w-100"
                      data-bs-toggle="dropdown"
                      aria-expanded="false"
                      :disabled="!product_type.product_type_id"
                    >
                      {{ product.name }}
                    </button>
                    <ul
                      class="dropdown-menu w-100"
                      aria-labelledby="dropdownMenuButton1"
                    >
                      <input
                        type="text"
                        class="form-control"
                        :placeholder="$t('search')"
                        v-model="product.search"
                        @keyup="
                          get_products(false);
                          $store.dispatch('setloading', false);
                        "
                      />
                      <div
                        class="scroll-25 product"
                        @scroll="
                          scroll_product();
                          $store.dispatch('setloading', false);
                        "
                      >
                        <li
                          @click="
                            unit = item.unit;
                            body.product_cycle_id = 0;
                            body.product_id = item.id;
                            body.olchov_id = item.olchov_id;
                            product.olchov_name = item.olchov.name;
                            product.name = product.products.find(
                              (e) => body.product_id == e.id
                            ).name;
                            get_product_cycles();
                            get_product_material();
                          "
                          v-for="item in product.products"
                          :key="item"
                        >
                          <a class="dropdown-item">
                            {{ item.name }}
                          </a>
                        </li>
                      </div>
                      <data-notfound :data="product.products" class="text-lg" />
                    </ul>
                  </div>
                </div>
              </div>
              <div
                class="col-md-6"
                v-if="content == 'product' && body.product_id"
              >
                <div class="form-group">
                  <label for="product-cycle">{{
                    $t("supply.product-cycle")
                  }}</label>
                  <select
                    id="product-cycle"
                    class="form-select"
                    v-model="body.product_cycle_id"
                    @change="get_next_product_cycle_plants()"
                    required
                  >
                    <option value="0">{{ $t("supply.ready-product") }}</option>
                    <option
                      :value="product_cycle.id"
                      v-for="product_cycle in product_cycles"
                      :key="product_cycle"
                    >
                      {{ product_cycle.cycle.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div
                class="col-md-6"
                v-if="content == 'product' && body.product_cycle_id"
              >
                <div class="form-group">
                  <label for="product-cycle-plant">{{
                    $t("supply.plant")
                  }}</label>
                  <select
                    id="product-cycle-plant"
                    class="form-select"
                    v-model="body.plant_id"
                    required
                  >
                    <option
                      :value="item.id"
                      v-for="item in product_cycle_plants"
                      :key="item"
                    >
                      {{ item.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div
                class="col-md-6"
                v-if="
                  content == 'product' &&
                  body.product_id &&
                  product_materials.length
                "
                :class="has_volume ? 'my-2' : ''"
              >
                <div class="form-group">
                  <label for="product-material">{{
                    $t("supply.product-material")
                  }}</label>
                  <select
                    id="product-material"
                    class="form-select"
                    v-model="body.product_material_id"
                  >
                    <option
                      :value="product_material.material_id"
                      v-for="product_material in product_materials"
                      :key="product_material"
                    >
                      {{ product_material.material?.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6" v-if="take_from != 'all'">
                <div class="form-group">
                  <label>{{ $t("supply.supplier") }}</label>
                  <div class="dropdown">
                    <button
                      class="form-control border-secondary-custom shadow-none mb-0 w-100"
                      data-bs-toggle="dropdown"
                      aria-expanded="false"
                      @click="
                        supplier.search = '';
                        get_supplier(false);
                      "
                    >
                      {{ supplier.name }}
                    </button>
                    <ul
                      class="dropdown-menu w-100"
                      aria-labelledby="dropdownMenuButton1"
                    >
                      <input
                        type="text"
                        class="form-control"
                        :placeholder="$t('search')"
                        v-model="supplier.search"
                        @keyup="
                          get_materials(false);
                          $store.dispatch('setloading', false);
                        "
                      />
                      <div
                        class="scroll-25 supplier"
                        @scroll="
                          scroll_supplier();
                          $store.dispatch('setloading', false);
                        "
                      >
                        <li
                          @click="
                            body.supplier_id = item.id;
                            supplier.name = supplier.suppliers.find(
                              (e) => body.supplier_id == e.id
                            ).name;
                          "
                          v-for="item in supplier.suppliers"
                          :key="item"
                        >
                          <a class="dropdown-item">
                            {{ item.name }}
                          </a>
                        </li>
                      </div>
                      <data-notfound
                        :data="supplier.suppliers"
                        class="text-lg"
                      />
                    </ul>
                  </div>
                </div>
              </div>
              <div class="col-md-6" :class="has_volume ? 'my-2' : ''">
                <div class="form-group">
                  <label for="price">{{ $t("supply.price") }}</label>
                  <div class="input-group">
                    <input
                      id="price"
                      type="number"
                      class="form-control"
                      v-model="body.price"
                      @focus="
                        body.price == 0 ? (body.price = null) : body.price
                      "
                      @focusout="
                        body.price == null ? (body.price = 0) : body.price
                      "
                      step="any"
                      :placeholder="$t('supply.price')"
                      required
                    />
                    <span class="input-group-text">{{ $t("currency") }}</span>
                  </div>
                </div>
              </div>
              <div class="col-md-6" :class="has_volume ? 'my-2' : ''">
                <div class="d-flex justify-content-end">
                  <div class="popover-custom" v-if="has_volume">
                    <div class="row">
                      <div class="col">
                        <div class="input-group input-group-sm">
                          <input
                            type="number"
                            class="form-control"
                            v-model="diametr"
                            step="any"
                            @keyup="formula()"
                            :placeholder="$t('supply.diametr')"
                          />
                          <span class="input-group-text">{{
                            $t("supply.sm")
                          }}</span>
                        </div>
                      </div>
                      <div class="col">
                        <div class="input-group input-group-sm">
                          <input
                            type="number"
                            class="form-control"
                            v-model="length"
                            step="any"
                            @keyup="formula()"
                            :placeholder="$t('supply.length')"
                          />
                          <span class="input-group-text">{{
                            $t("supply.meter")
                          }}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="value">{{ $t("supply.value") }}</label>
                  <div class="input-group">
                    <input
                      id="value"
                      type="number"
                      class="form-control"
                      v-model="body.value"
                      @focus="
                        body.value == 0 ? (body.value = null) : body.value
                      "
                      @focusout="
                        body.value == null ? (body.value = 0) : body.value
                      "
                      :step="unit ? `1` : `any`"
                      :placeholder="$t('supply.value')"
                      required
                    />
                    <span class="input-group-text" v-if="product.olchov_name">{{
                      product.olchov_name
                    }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button
              type="submit"
              class="btn bg-gradient-primary"
              :disabled="
                content == 'material'
                  ? type == 'true'
                    ? !body.material_id ||
                      !body.olchov_id ||
                      !body.plant_id ||
                      !body.price ||
                      !body.supplier_id ||
                      !body.value ||
                      !material_type.material_type_id
                    : !body.material_id ||
                      !body.olchov_id ||
                      !body.plant_id ||
                      !body.price ||
                      !body.supplier_id ||
                      !body.value
                  : !body.product_id ||
                    !body.olchov_id ||
                    !body.price ||
                    !body.supplier_id ||
                    !body.value
              "
            >
              {{ $t("save") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "../dataNotfound.vue";

export default {
  components: { dataNotfound },
  name: "create-supply",
  props: ["item", "plants", "type", "take_from"],
  emits: ["get"],
  data() {
    return {
      body: {
        product_id: 0,
        material_id: 0,
        product_cycle_id: 0,
        product_material_id: 0,
        supplier_id: null,
        value: 0,
        price: 0,
        olchov_id: null,
        plant_id: null,
      },
      material_type: {
        pages: 0,
        page: 1,
        search: "",
        material_types: [],
        name: this.$t("supply.material_type"),
        material_type_id: null,
      },
      material: {
        pages: 0,
        page: 1,
        search: "",
        materials: [],
        name: this.$t("supply.material"),
        olchov_name: "",
      },
      supplier: {
        pages: 0,
        page: 1,
        search: "",
        suppliers: [],
        name: this.$t("supply.supplier"),
      },
      product_type: {
        pages: 0,
        page: 1,
        search: "",
        product_types: [],
        name: this.$t("supply.product_type"),
        product_type_id: null,
      },
      product: {
        pages: 0,
        page: 1,
        search: "",
        products: [],
        name: this.$t("supply.product"),
        olchov_name: "",
      },
      diametr: null,
      length: null,
      has_volume: false,
      content: "material",
      product_cycles: [],
      product_materials: [],
      product_cycle_plants: [],
      unit: false,
    };
  },
  methods: {
    // material type
    get_m_type(scroll) {
      this.material_type.page = scroll ? this.material_type.page : 1;
      api
        .get_material_types(
          scroll ? this.material_type.page + 1 : this.material_type.page,
          10,
          this.material_type.search,
          this.body.plant_id
        )
        .then((res) => {
          this.material_type.pages = res.data.page_count;
          this.material_type.page = res.data.current_page;
          this.material_type.material_types = scroll
            ? this.material_type.material_types.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll_material_type() {
      let div = document.querySelector(".material-type");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.material_type.page < this.material_type.pages) {
          this.get_m_type(true);
        }
      }
    },
    // material
    get_materials(scroll) {
      this.material.page = scroll ? this.material.page : 1;
      api
        .get_materials(
          scroll ? this.material.page + 1 : this.material.page,
          10,
          this.material.search,
          this.material_type.material_type_id
        )
        .then((res) => {
          this.material.pages = res.data.page_count;
          this.material.page = res.data.current_page;
          this.material.materials = scroll
            ? this.material.materials.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll_material() {
      let div = document.querySelector(".material");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.material.page < this.material.pages) {
          this.get_materials(true);
        }
      }
    },
    // product type
    get_p_type(scroll) {
      this.product_type.page = scroll ? this.product_type.page : 1;
      api
        .get_product_types(
          scroll ? this.product_type.page + 1 : this.product_type.page,
          10,
          this.product_type.search
        )
        .then((res) => {
          this.product_type.pages = res.data.page_count;
          this.product_type.page = res.data.current_page;
          this.product_type.product_types = scroll
            ? this.product_type.product_types.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll_product_type() {
      let div = document.querySelector(".product-type");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.product_type.page < this.product_type.pages) {
          this.get_p_type(true);
        }
      }
    },
    // product
    get_products(scroll) {
      this.product.page = scroll ? this.product.page : 1;
      api
        .get_products(
          scroll ? this.product.page + 1 : this.product.page,
          10,
          this.product.search,
          this.product_type.product_type_id,
          false
        )
        .then((res) => {
          this.product.pages = res.data.page_count;
          this.product.page = res.data.current_page;
          this.product.products = scroll
            ? this.product.products.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll_product() {
      let div = document.querySelector(".product");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.product.page < this.product.pages) {
          this.get_products(true);
        }
      }
    },
    // supplier
    get_supplier(scroll) {
      this.supplier.page = scroll ? this.supplier.page : 1;
      api
        .get_suppliers(
          scroll ? this.supplier.page + 1 : this.supplier.page,
          10,
          this.supplier.search
        )
        .then((res) => {
          this.supplier.pages = res.data.page_count;
          this.supplier.page = res.data.current_page;
          this.supplier.suppliers = scroll
            ? this.supplier.suppliers.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll_supplier() {
      let div = document.querySelector(".supplier");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.supplier.page < this.supplier.pages) {
          this.get_supplier(true);
        }
      }
    },
    take() {
      api.create_supply(this.body).then(() => {
        this.body.product_id = 0;
        this.body.product_cycle_id = 0;
        this.body.product_material_id = 0;
        this.body.material_id = 0;
        this.body.olchov_id = null;
        this.body.plant_id = null;
        this.body.price = 0;
        if (this.take_from != "all") this.body.supplier_id = null;
        this.body.value = 0;
        this.material_type.material_type_id = null;
        this.material_type.name = this.$t("supply.material_type");
        this.material.name = this.$t("supply.material");
        this.product_type.name = this.$t("supply.product_type");
        this.product.name = this.$t("supply.product");
        this.material.olchov_name = "";
        this.supplier.name = "";
        this.has_volume = false;
        this.diametr = null;
        this.length = null;
        this.unit = false;
        api.success("close");
        this.$emit("get");
      });
    },
    formula() {
      var number = this.diametr * this.length * 0.9;
      this.body.value = api.number_format(number);
    },
    select_material_id(item) {
      setInterval(() => {
        if (!this.body.material_id) {
          this.body.material_id = item.material.id;
        }
      }, 1);
      this.body.olchov_id = item.material.olchov_id;
      this.body.price = item.material.price;
      this.has_volume = item.material.has_volume;
      this.material.olchov_name = item.olchov.name;
    },
    get_product_cycles() {
      api
        .product_cycles(1, 100, "", 0, this.body.product_id, false)
        .then((res) => {
          this.product_cycles = res.data.data;
        });
    },
    get_product_material() {
      api.product_material(1, 10000, 0, this.body.product_id).then((res) => {
        this.product_materials = res.data.data;
      });
    },
    get_next_product_cycle_plants() {
      api.next_product_cycle_plants(this.body.product_cycle_id).then((res) => {
        console.log(res.data);
        this.product_cycle_plants = res.data;
      });
    },
  },
};
</script>
