<template>
  <div
    class="modal fade"
    id="accept-product-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="transfer-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="transfer-label">
            {{ $t("accept-product.title") }}
          </h5>
        </div>
        <form @submit.prevent="accept()">
          <div class="modal-body">
            <table class="table table-bordered">
              <thead>
                <tr>
                  <th>{{ $t("accept-product.name") }}</th>
                  <th>{{ $t("supply.product-material") }}</th>
                  <th>{{ $t("accept-product.value") }}</th>
                  <th v-if="column_size">{{ $t("production.size") }} 1</th>
                  <th v-if="column_size">{{ $t("production.size") }} 2</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="store in products" :key="store">
                  <td>{{ store.product?.name }}</td>
                  <td>{{ store.material?.name }}</td>
                  <td>
                    {{ store.value }}
                    {{ store.product?.olchov?.name }}
                  </td>
                  <td v-if="column_size">
                    {{ store.size1 ? `${store.size1} ${$t("supply.sm")}` : `` }}
                  </td>
                  <td v-if="column_size">
                    {{ store.size2 ? `${store.size2} ${$t("supply.sm")}` : `` }}
                  </td>
                </tr>
              </tbody>
              <tfoot v-if="!products.length">
                <tr>
                  <td colspan="5">
                    <data-notfound :data="products" />
                  </td>
                </tr>
              </tfoot>
            </table>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-transfer
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("accept-product.title") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";

export default {
  name: "accept-product",
  components: { dataNotfound },
  data() {
    return {
      products: [],
    };
  },
  methods: {
    getProducts() {
      api.get_acceptance_product(this.$route.query.market_id).then((res) => {
        this.products = res.data;
      });
    },
    accept() {
      let data = [];
      this.products.forEach((product) => {
        data.push(product.id);
      });
      api.accept_products(data).then(() => {
        api.success("close-transfer");
        this.$parent.getProducts(1, 25);
      });
    },
  },
  computed: {
    column_size() {
      let result = false;
      this.products.forEach((element) => {
        if (element.size1 && element.size2) {
          result = true;
        }
      });
      return result;
    },
  },
};
</script>
