<template>
  <div
    v-show="this.$store.state.layout === 'landing'"
    class="landing-bg h-100 bg-gradient-primary position-fixed w-100"
  ></div>
  <sidenav
    :custom_class="this.$store.state.mcolor"
    v-if="this.$store.state.showSidenav"
  />
  <main
    class="main-content position-relative max-height-vh-100 h-100 border-radius-lg"
  >
    <!-- nav -->
    <navbar
      :class="[navClasses]"
      :textWhite="
        this.$store.state.isAbsolute ? 'text-white opacity-8' : 'text-white'
      "
      :minNav="navbarMinimize"
      v-if="this.$store.state.showNavbar"
    />
    <router-view />
    <app-footer v-show="this.$store.state.showFooter" />
    <configurator
      :toggle="toggleConfigurator"
      :class="[
        this.$store.state.showConfig ? 'show' : '',
        this.$store.state.hideConfigButton ? 'd-none' : '',
      ]"
    />
    <a
      v-if="
        ($route.name != 'Market' &&
          $route.name != 'Signin' &&
          role == 'seller') ||
        ($route.name != 'Market' &&
          $route.name != 'Signin' &&
          role == 'seller_admin')
      "
      class="px-3 py-2 back-plugin-button text-dark position-fixed"
      @click="$router.back()"
    >
      <i class="fa-solid fa-arrow-left"></i>
    </a>
    <is-loading v-if="loading" />
  </main>
</template>
<script>
import Sidenav from "./template/Sidenav";
import Configurator from "@/template/Configurator.vue";
import Navbar from "@/template/Navbars/Navbar.vue";
import AppFooter from "@/template/Footer.vue";
import { mapMutations } from "vuex";
import IsLoading from "./components/loading/isLoading.vue";
import { activateDarkMode, deactivateDarkMode } from "@/assets/js/dark-mode";

export default {
  name: "App",
  components: {
    Sidenav,
    Configurator,
    Navbar,
    AppFooter,
    IsLoading,
  },
  data() {
    return {
      connection: new WebSocket(
        `wss://api2.f9.crud.uz/ws/connection?token=${localStorage.getItem(
          "access_token"
        )}`
      ),
      role: localStorage["role"],
    };
  },
  methods: {
    ...mapMutations(["toggleConfigurator", "navbarMinimize"]),
    connect() {
      this.connection = new WebSocket(
        `wss://api2.f9.crud.uz/ws/connection?token=${localStorage.getItem(
          "access_token"
        )}`
      );
    },
  },
  mounted() {
    let role = localStorage.getItem("role");
    if (role !== "seller" && role !== "seller_admin") {
      document
        .querySelector("#sidenav-main")
        .setAttribute("data-color", localStorage.getItem("mcolor"));
      this.$store.state.mcolor = `card-background-mask-${localStorage.getItem(
        "mcolor"
      )}`;
    }
    if (!("Notification" in window)) {
      return;
    } else if (Notification.permission !== "denied") {
      Notification.requestPermission((permission) => {
        if (permission === "granted") {
          this.connection.onmessage = (e) => {
            let data = JSON.parse(e.data);
            new Notification(data.title, {
              body: data.body,
              icon: data.imgUrl,
            });
          };

          this.connection.onclose = () => {
            this.connect();
          };
        }
      });
    }
  },
  computed: {
    navClasses() {
      return {
        "position-sticky bg-white left-auto top-2 z-index-sticky":
          this.$store.state.isNavFixed && !this.$store.state.darkMode,
        "position-sticky bg-default left-auto top-2 z-index-sticky":
          this.$store.state.isNavFixed && this.$store.state.darkMode,
        "position-absolute px-4 mx-0 w-100 z-index-2": this.$store.state
          .isAbsolute,
        "px-0 mx-4": !this.$store.state.isAbsolute,
      };
    },
    loading() {
      return this.$store.getters.loading;
    },
  },
  beforeMount() {
    this.$store.state.isTransparent = "bg-transparent";
  },
  created() {
    if (JSON.parse(localStorage.getItem("darkmode")) === true) {
      this.$store.state.darkMode = true;
      this.$store.state.sidebarType = "bg-default";
      activateDarkMode();
    } else {
      this.$store.state.darkMode = false;
      this.$store.state.sidebarType = "bg-white";
      deactivateDarkMode();
    }
  },
};
</script>

<style>
@import url(./assets/css/main.css);
</style>
