<template>
  <div
    class="modal fade"
    id="transfer"
    tabindex="-1"
    role="dialog"
    aria-labelledby="transfer-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="transfer-label">
            {{ $t("material-transfer.title") }}
          </h5>
        </div>
        <form @submit.prevent="transfer()">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="source">{{
                    $t("material-transfer.from_plant")
                  }}</label>
                  <select
                    id="source"
                    class="form-select"
                    v-model="body.source_id"
                    required
                  >
                    <option
                      :value="source.plant?.id"
                      v-for="source in material_plants"
                      :key="source"
                    >
                      {{ source.plant?.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="plant">{{
                    $t("material-transfer.to_plant")
                  }}</label>
                  <select
                    id="plant"
                    class="form-select"
                    v-model="body.plant_id"
                    required
                  >
                    <option
                      :disabled="plant.id == body.source_id"
                      :value="plant.id"
                      v-for="plant in plants"
                      :key="plant"
                    >
                      {{ plant.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-12">
                <div class="form-group">
                  <label for="value"
                    >{{ $t("material-transfer.value") }}:
                    {{
                      body.source_id
                        ? material_plants.find(
                            (e) => body.source_id == e.plant.id
                          )?.value +
                          " " +
                          body.olchov
                        : ""
                    }}</label
                  >
                  <div class="input-group">
                    <input
                      type="number"
                      class="form-control"
                      step="any"
                      :placeholder="$t('material-transfer.value')"
                      v-model="body.value"
                      @focus="
                        body.value == 0 ? (body.value = null) : body.value
                      "
                      @focusout="
                        body.value == null ? (body.value = 0) : body.value
                      "
                      required
                    />
                    <span class="input-group-text">{{ body.olchov }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-transfer
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("save") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";

export default {
  name: "material-transfer",
  props: ["plants"],
  emits: ["get"],
  data() {
    return {
      body: {
        material_id: null,
        source_id: null,
        value: 0,
        plant_id: null,
        olchov: "",
      },
      material_plants: [],
    };
  },
  methods: {
    get_material_plants(material_id, olchov) {
      this.body.material_id = material_id;
      this.body.olchov = olchov;
      api.get_material_transfer_plants(material_id).then((res) => {
        this.material_plants = res.data;
      });
    },
    transfer() {
      api.create_material_transfer(this.body).then(() => {
        this.body.material_id = null;
        this.body.olchov = "";
        this.body.plant_id = null;
        this.body.source_id = null;
        this.body.value = 0;
        api.success("close-transfer");
        this.$emit("get");
      });
    },
  },
};
</script>
