<template>
  <div
    class="modal fade"
    :id="`transfer${product.Product_Store.id}`"
    tabindex="-1"
    role="dialog"
    aria-labelledby="transfer-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="transfer-label">
            {{ $t("material-transfer.title") }}
          </h5>
        </div>
        <form @submit.prevent="transfer()">
          <div class="modal-body">
            <div class="row">
              <!-- <div class="col-md-6 mb-2">
                <div class="form-group">
                  <label for="size1">{{ $t("production.size") }} 1</label>
                  <input
                    id="size1"
                    type="number"
                    class="form-control text-uppercase"
                    v-model="body.size1"
                    min="0"
                  />
                </div>
              </div>
              <div class="col-md-6 mb-2">
                <div class="form-group">
                  <label for="size2">{{ $t("production.size") }} 2</label>
                  <input
                    id="size2"
                    type="number"
                    class="form-control text-uppercase"
                    v-model="body.size2"
                    min="0"
                  />
                </div>
              </div> -->
              <div class="col-md-6 mb-2">
                <div class="form-group">
                  <label for="store">{{ $t("material-transfer.store") }}</label>
                  <select
                    id="store"
                    class="form-select"
                    v-model="body.market_id"
                    required
                  >
                    <option
                      :value="store.id"
                      v-for="store in markets"
                      :key="store"
                    >
                      {{ store?.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6 mb-2">
                <div class="form-group">
                  <label for="value"
                    >{{ $t("material-transfer.value") }}:
                    {{ product.sum_quantity }}
                    {{ product.Product_Store?.product?.olchov?.name }}
                  </label>
                  <div class="input-group">
                    <input
                      type="number"
                      class="form-control"
                      step="any"
                      :placeholder="$t('material-transfer.value')"
                      v-model="body.value"
                      min="1"
                      required
                    />
                    <span class="input-group-text">{{
                      product.Product_Store?.product?.olchov?.name
                    }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              :class="`btn bg-gradient-secondary close_transfer${product.Product_Store.id}`"
              data-bs-dismiss="modal"
              close-transfer
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("save") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";

export default {
  name: "product-transfer",
  props: ["markets", "product"],
  emits: ["get"],
  data() {
    return {
      body: {
        product_id: this.product.Product_Store.product_id,
        market_id: null,
        value: this.product.sum_quantity,
        size1: this.product.Product_Store.size1,
        size2: this.product.Product_Store.size2,
        tomarket: true,
        material_id: this.product.Product_Store.material_id,
      },
    };
  },
  methods: {
    transfer() {
      api.product_transfer(this.body).then(() => {
        api.success("close-transfer");
        document
          .querySelector(`.close_transfer${this.product.Product_Store.id}`)
          .click();
        this.$emit("get");
      });
    },
  },
};
</script>
