import axios from "axios";
import store from "@/store/index";
import Swal from "sweetalert2";
import { url } from "./baseurl";

export function format_date(date) {
  return new Date(date).toLocaleString("en-GB", {
    hour12: false,
  });
}

export function isNumber(evt) {
  evt = evt ? evt : window.event;
  var charCode = evt.which ? evt.which : evt.keyCode;
  if (charCode > 31 && (charCode < 48 || charCode > 57) && charCode !== 46) {
    evt.preventDefault();
  } else {
    return true;
  }
}

export function set_pages(property, data) {
  if (typeof data !== "undefined")
    sessionStorage[property] = JSON.stringify(data);
  else return JSON.parse(sessionStorage[property] || false);
}

export function select_default_page(role) {
  let route = "";
  let market_id = localStorage["market_id"];
  if (role == "analyst") route = "/plan-products";
  else if (role == "plant_admin") route = "/production";
  else if (role == "admin") route = "/admin-statistic";
  else if (role == "accountant") route = "/admin-statistic";
  else if (role == "storeman") route = "/warehouse";
  else if (role == "seller_admin" || role == "seller")
    route = market_id ? `/market?id=${market_id}` : "/signin";
  else route = "/dashboard-default";
  return route;
}

export function countDays(start, end) {
  let start_date = start ? new Date(start) : new Date(),
    end_date = new Date(end),
    difference = end_date.getTime() - start_date.getTime(),
    days = Math.ceil(difference / (1000 * 3600 * 24));
  return days;
}

// fixed number format
export function number_format(number) {
  var x = parseFloat(number).toFixed(2);
  return x;
}

async function api_reference(endpoint, method, data) {
  await store.dispatch("setloading", true);

  let result = null,
    error = null;
  await axios
    .create({
      baseURL: `${url}${endpoint}`,
      headers: {
        Authorization: `Bearer ${localStorage["access_token"]}`,
      },
    })
    .request({ method: method, data: data })
    .then((res) => {
      result = res;
    })
    .catch((err) => {
      catchError(err);
      error = err;
    })
    .finally(() => {
      store.dispatch("setloading", false);
    });
  return new Promise((res, rej) => {
    if (result) {
      res(result);
    } else {
      rej(error);
    }
  });
}

// form data function

export const getFormData = (object) =>
  Object.entries(object).reduce((fd, [key, val]) => {
    if (Array.isArray(val)) {
      val.forEach((v) => fd.append(key, v));
    } else {
      fd.append(key, val);
    }
    return fd;
  }, new FormData());

// images link
export const image = `${url}images`;

// success
export function success(target) {
  if (target !== undefined) {
    if (typeof target == "number") {
      document.querySelectorAll("[data-dismiss='modal']")[target].click();
    } else if (typeof target == "string") {
      document.querySelector(`[${target}]`).click();
    }
  }
  let image = document.querySelectorAll("#image");
  image.forEach((element) => {
    if (element) {
      element.value = null;
    }
  });

  return Swal.mixin({
    toast: true,
    position: "top-end",
    showConfirmButton: false,
    timer: 800,
    timerProgressBar: true,
  }).fire({
    icon: "success",
    title: "Amaliyot bajarildi",
  });
}

// errors
export function catchError(error) {
  if (error.response?.status == 401) {
    localStorage.removeItem("role");
    localStorage.removeItem("access_token");
    Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 2000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    })
      .fire({
        icon: "error",
        title: "Foydalanuvchi nomi yoki parolda xatolik",
      })
      .then(() => {
        if (window.location.pathname != "/signin") {
          window.location.href = "/signin";
        }
      });
  } else if (error.message == "Network Error") {
    return Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 2000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    }).fire({
      icon: "warning",
      title: "Server kutilgan ma'lumotni bermadi",
    });
  } else if (error.response?.status == 422) {
    return Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 2000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    }).fire({
      icon: "error",
      title: "Xatolik mavjud",
    });
  } else if (error.response?.status == 404) {
    return Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 2000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    }).fire({
      icon: "error",
      title: "URL manzil notog'ri !",
    });
  } else if (error.response?.status == 400) {
    return Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 2000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    }).fire({
      icon: "error",
      title: error.response?.data.detail,
    });
  }
}

