export function get_users(
  page,
  limit,
  search,
  disabled,
  role,
  balance,
  market_id
) {
  let role_filer = `&role=${role}`;
  let search_filter = "";
  if (search) {
    search_filter = `&search=${search}`;
  } else {
    search_filter = ``;
  }
  return api_reference(
    `users?page=${page}&limit=${limit}&disabled=${disabled}&balance=${balance}${search_filter}${
      role ? role_filer : ""
    }&market_id=${market_id}`,
    "GET"
  );
}

