export function production_create(
  product_cycle_id,
  material_id,
  plan_id,
  plant_id,
  next_plant_id,
  value,
  size1,
  size2,
  norm,
  trade_id,
  trade_item_id,
  // detail
  period
) {
  const date = new Date().toISOString();
  return api_reference(`production/create`, "POST", {
    product_cycle_id: product_cycle_id,
    material_id: material_id,
    plan_id: plan_id,
    plant_id: plant_id,
    next_plant_id: next_plant_id,
    value: value,
    size1: size1,
    size2: size2,
    norm: norm,
    trade_id: trade_id,
    trade_item_id: trade_item_id,
    // detail: detail,
    period: period ? period : date.slice(0, 10),
  });
}

