<template>
  <div
    class="modal fade"
    id="add-olchov"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-olchov-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-olchov-label">
            {{ $t("olchov.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('add', body)">
          <div class="modal-body">
            <div class="row">
              <div class="col-12">
                <div class="form-group">
                  <label for="name">{{ $t("olchov.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="body.name"
                    :placeholder="$t('olchov.name')"
                    required
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="update-olchov"
    tabindex="-1"
    role="dialog"
    aria-labelledby="update-olchov-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="update-olchov-label">
            {{ $t("olchov.update") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('put', data)">
          <div class="modal-body">
            <div class="row">
              <div class="col-12">
                <div class="form-group">
                  <label for="name">{{ $t("olchov.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="data.name"
                    :placeholder="$t('olchov.name')"
                    required
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-put
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("update") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "add-olchov",
  props: ["item"],
  emits: ["add", "put"],
  data() {
    return {
      body: {
        name: "",
      },
    };
  },
  computed: {
    data() {
      return this.item;
    },
  },
};
</script>
