<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col">
            <h5 class="mb-0">{{ $t("indicator.sum_income") }}</h5>
          </div>
          <div class="col-auto">
            <button
              class="btn btn-sm bg-gradient-primary mx-2 mb-0 px-3"
              data-bs-toggle="modal"
              data-bs-target="#filter"
            >
              <i class="fas fa-filter"></i>
            </button>
          </div>
        </div>
      </div>
      <div class="card-body py-2">
        <div class="table-responsive p-0 mb-0">
          <table class="table table-bordered align-items-center mb-0">
            <thead>
              <tr>
                <th>{{ $t("cashier.history.user") }}</th>
                <th>{{ $t("expense.money") }}</th>
                <th>{{ $t("cashier.history.comment") }}</th>
                <th>{{ $t("product-store.date") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="income in incomes" :key="income">
                <td>{{ income.user.username }}</td>
                <td>
                  {{
                    `${Intl.NumberFormat().format(income.money)} ${$t(
                      "currency"
                    )}`
                  }}
                </td>
                <td>{{ income.comment }}</td>
                <td>{{ format_date(income.time) }}</td>
              </tr>
            </tbody>
            <tfoot v-if="!incomes.length">
              <tr>
                <td colspan="4">
                  <data-notfound :data="incomes" />
                </td>
              </tr>
            </tfoot>
          </table>
        </div>
        <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
      </div>
    </div>
  </div>
  <div class="modal fade" id="filter">
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title">
            {{ $t("filter") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-6">
              <div class="form-group">
                <label for="from_date">{{ $t("from_date") }}</label>
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="from_date"
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("to_date") }}</label>
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="to_date"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-sm bg-gradient-danger"
            data-bs-dismiss="modal"
            @click="
              (from_date = new Date(
                new Date().getFullYear(),
                new Date().getMonth(),
                1
              )
                .toISOString()
                .substring(0, 10)),
                (to_date = new Date(
                  new Date().getFullYear(),
                  new Date().getMonth(),
                  new Date().getDate() + 1
                )
                  .toISOString()
                  .substring(0, 10));
              get(page, limit);
            "
          >
            <i class="fas fa-trash"></i>
          </button>
          <button
            class="btn btn-sm bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-filter
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn btn-sm bg-gradient-primary"
            data-bs-dismiss="modal"
            @click="get(page, limit)"
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import Pagination from "@/components/pagination.vue";
import DataNotfound from "@/components/dataNotfound.vue";

export default {
  components: { Pagination, DataNotfound },

  data() {
    return {
      incomes: [],
      from_date: new Date(new Date().getFullYear(), new Date().getMonth(), 1)
        .toISOString()
        .substring(0, 10),
      to_date: new Date(
        new Date().getFullYear(),
        new Date().getMonth(),
        new Date().getDate() + 1
      )
        .toISOString()
        .substring(0, 10),
      order_id: 0,
      loan_id: 0,
      user_id: 0,
      kassa_id: 0,
      type: "",
      market_id: 0,
      page: 1,
      limit: 10,
      pages: 0,
      format_date: api.format_date,
    };
  },
  methods: {
    get(page, limit) {
      api
        .get_incomes(
          page,
          limit,
          this.from_time,
          this.to_time,
          this.order_id,
          this.loan_id,
          this.user_id,
          this.kassa_id,
          this.type,
          this.market_id
        )
        .then((res) => {
          this.page = res.data.current_page;
          this.pages = res.data.pages;
          this.limit = res.data.limit;
          this.incomes = res.data.data;
        });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>

<style lang="scss" scoped></style>
