<template>
  <div class="container-fluid py-4">
    <div class="card">
      <div class="card-header">
        <div class="row">
          <div class="col-md-4">
            <h6 class="mb-0">{{ $t("pre-order.title") }}</h6>
          </div>
          <div class="col-md-4"></div>
          <div class="col-md-4">
            <button
              class="btn btn-sm mb-0 w-100 bg-gradient-success"
              data-bs-toggle="modal"
              data-bs-target="#add-order-modal"
            >
              Yangi buyurtma
            </button>
          </div>
        </div>
      </div>
      <div class="card-body">
        <div class="table-responsive">
          <table class="table table-sm table-hover table-bordered">
            <thead>
              <tr>
                <th>{{ $t("users.table.id") }}</th>
                <th>{{ $t("pre-order.table.customer") }}</th>
                <th>{{ $t("pre-order.table.date") }}</th>
                <th>{{ $t("pre-order.table.period") }}</th>
                <th>{{ $t("pre-order.table.delivery-date") }}</th>
                <th>
                  <button
                    class="btn btn-sm mb-0 py-1 px-3 bg-gradient-primary"
                    data-bs-toggle="modal"
                    data-bs-target="#filter"
                  >
                    <i class="fa fa-filter"></i>
                  </button>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in orders?.data" :key="item">
                <td>{{ item.id }}</td>
                <td>{{ item.customer ? item.customer.name : "" }}</td>
                <td>{{ format_date(item.time) }}</td>
                <td>
                  {{
                    countDays(item.time, item.delivery_date) +
                    " " +
                    $t("pre-order.table.day")
                  }}
                </td>
                <td>{{ item.delivery_date }}</td>
                <td>
                  <div class="btn-group">
                    <router-link
                      :to="{
                        path: '/pre-order',
                        query: {
                          id: item.id,
                          market_id: $route.query.market_id,
                        },
                      }"
                      class="btn btn-sm mb-0 py-1 px-3 bg-gradient-primary"
                    >
                      <i class="fa fa-eye"></i>
                    </router-link>
                    <button
                      class="btn btn-sm mb-0 py-1 px-3 bg-gradient-info"
                      @click="
                        order = item;
                        getTrades(1, 25, true);
                      "
                    >
                      <i class="fa fa-info"></i>
                    </button>
                    <button
                      class="btn btn-sm mb-0 py-1 px-3 bg-gradient-danger"
                      @click="deleteOrder(item.id)"
                    >
                      <i class="fa fa-trash"></i>
                    </button>
                  </div>
                </td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <td colspan="6">
                  <pagination
                    :page="orders?.current_page"
                    :pages="orders?.pages"
                    :limit="orders?.limit"
                    @get="getOrders"
                  />
                </td>
              </tr>
            </tfoot>
          </table>
        </div>
      </div>
    </div>
  </div>

  <div
    class="modal fade"
    id="add-order-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-order-modal-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-order-modal-label">
            {{ $t("pre-order.title") }}
          </h5>
        </div>
        <form @submit.prevent="postOrder()">
          <div class="modal-body">
            <div class="row gap-1">
              <div class="col-10">
                <div class="dropdown">
                  <button
                    id="customerDropdown"
                    class="btn btn-sm w-100 mb-0 dropdown-toggle"
                    data-bs-toggle="dropdown"
                    @click="getCustomers()"
                  >
                    {{ customer ? customer?.name : $t("order.customer") }}
                  </button>
                  <div
                    class="dropdown-menu w-100 p-1"
                    aria-labelledby="customerDropdown"
                  >
                    <input
                      type="text"
                      class="form-control form-control-sm my-1"
                      :placeholder="$t('search')"
                      v-model="search_customer"
                      @keyup="getCustomers()"
                    />
                    <ul
                      class="list-group scroll-25 p-1"
                      @scroll="scrollCustomers($event)"
                    >
                      <li
                        class="list-group-item"
                        v-for="item in customers?.data"
                        :key="item"
                        @click="customer = item"
                      >
                        {{ item.name }}
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div class="col-auto" style="padding-left: 0">
                <button
                  class="btn btn-sm mb-0 px-4"
                  type="button"
                  data-bs-toggle="modal"
                  data-bs-target="#add-customer"
                >
                  <i class="fas fa-circle-plus"></i>
                </button>
              </div>
              <div class="col-12">
                <div class="input-group">
                  <input
                    type="date"
                    class="form-control"
                    required
                    v-model="new_order.delivery_date"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-order-modal
            >
              {{ $t("close") }}
            </button>
            <button class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>

  <button
    data-bs-toggle="modal"
    data-bs-target="#order-status-modal"
    order-status-modal-button
    v-show="false"
  ></button>
  <div
    class="modal fade"
    id="order-status-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="order-status-modal-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="order-status-modal-label">
            {{ $t("pre-order.title") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row gap-1">
            <div class="col-12">
              <div class="table-responsive" style="max-height: 60vh">
                <ul
                  class="list-group"
                  v-if="trades.data.length && trade_items.length"
                >
                  <li
                    class="list-group-item"
                    v-for="item in trades.data"
                    :key="item"
                  >
                    <p class="mb-2">
                      <img
                        :src="image + '/products/' + item.Trades.product.image"
                        :alt="item.Trades.product.image"
                      />
                      {{ item.Trades.product.name }}
                    </p>
                    <!-- <p
                      class="m-0 d-flex justify-content-between align-items-center"
                    >
                      <progress
                        :max="item.sum_quantity"
                        :value="
                          findTradeItems(item)?.product_store.find(
                            (item) => item.trade_item_id == 0
                          )
                            ? findTradeItems(item).product_store.find(
                                (item) => item.trade_item_id == 0
                              ).product_store_sum_value
                            : 0
                        "
                      ></progress>
                      <span>
                        {{
                          (findTradeItems(item)?.product_store.find(
                            (item) => item.trade_item_id == 0
                          )
                            ? findTradeItems(item).product_store.find(
                                (item) => item.trade_item_id == 0
                              ).product_store_sum_value
                            : 0) +
                          " / " +
                          item.sum_quantity +
                          " " +
                          item.Trades.product.olchov.name
                        }}
                      </span>
                    </p> -->
                    <ul class="list-group">
                      <li
                        class="list-group-item"
                        v-for="item1 in item.Trades.trade_items"
                        :key="item1"
                      >
                        <p class="m-0">
                          <img
                            v-if="item1.component_item.product"
                            :src="
                              image +
                              '/products/' +
                              item1.component_item.product?.image
                            "
                            :alt="item1.component_item.product?.image"
                          />
                          <span class="p-2">
                            {{
                              item1.component_item.product
                                ? item1.component_item.product?.name
                                : item1.component_item.material?.name
                            }}
                          </span>
                        </p>
                        <p class="m-0">
                          {{
                            item1.component_item.product
                              ? item1.trade_items_size.length
                                ? (item1.trade_items_size[0].size1
                                    ? `${$t("pre-order.placeholders.width")}: ${
                                        item1.trade_items_size[0].size1
                                      }cm`
                                    : ``) +
                                  (item1.trade_items_size[0].size2
                                    ? `, ${$t(
                                        "pre-order.placeholders.height"
                                      )}: ${item1.trade_items_size[0].size2}cm`
                                    : ``) +
                                  (item1.trade_items_size[0].comment
                                    ? `, ${$t(
                                        "pre-order.placeholders.comment"
                                      )}: ${item1.trade_items_size[0].comment}`
                                    : ``)
                                : ``
                              : item1.trade_items_size.length
                              ? (item1.trade_items_size[0].size1
                                  ? `${$t("pre-order.placeholders.width")}: ${
                                      item1.trade_items_size[0].size1
                                    }cm`
                                  : ``) +
                                (item1.trade_items_size[0].size2
                                  ? `, ${$t(
                                      "pre-order.placeholders.height"
                                    )}: ${item1.trade_items_size[0].size2}cm`
                                  : ``) +
                                (item1.trade_items_size[0].comment
                                  ? `, ${$t(
                                      "pre-order.placeholders.comment"
                                    )}: ${item1.trade_items_size[0].comment}`
                                  : ``)
                              : ``
                          }}
                        </p>
                        <hr class="m-2" />
                        <p>
                          <i class="fa px-2 fa-check"></i>
                          <span>
                            {{
                              findTradeItem(item1)?.sum_product_store +
                              findTradeItem(item1)?.sum_material_store +
                              " / " +
                              item1.quantity +
                              " " +
                              (item1.component_item.product
                                ? item1.component_item.product?.olchov.name
                                : item1.component_item.material?.olchov.name)
                            }}
                          </span>
                          <progress
                            :max="item1.quantity"
                            :value="
                              findTradeItem(item1)?.sum_product_store +
                              findTradeItem(item1)?.sum_material_store
                                ? findTradeItem(item1)?.sum_product_store +
                                  findTradeItem(item1)?.sum_material_store
                                : 0
                            "
                          ></progress>
                        </p>
                        <p v-if="findTradeItem(item1)?.sum_cycle_store">
                          <i class="fa px-2 fa-recycle"></i>
                          <span>
                            {{
                              findTradeItem(item1)?.sum_cycle_store +
                              " / " +
                              item1.quantity +
                              " " +
                              (item1.component_item.product
                                ? item1.component_item.product?.olchov.name
                                : item1.component_item.material?.olchov.name)
                            }}
                          </span>
                          <progress
                            :max="item1.quantity"
                            :value="findTradeItem(item1)?.sum_cycle_store"
                          ></progress>
                        </p>
                      </li>
                    </ul>
                  </li>
                </ul>
              </div>
            </div>
            <div class="col-12">
              <pagination
                :page="trades.current_page"
                :pages="trades.pages"
                :limit="trades.limit"
                @get="getTrades"
              />
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-order-status-modal
          >
            {{ $t("close") }}
          </button>
        </div>
      </div>
    </div>
  </div>

  <div class="modal fade" id="filter">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title">
            {{ $t("filter") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row gap-1">
            <div class="col-12">
              <label for="customerDropdown">{{ $t("order.customer") }}</label>
              <div class="dropdown">
                <button
                  id="customerDropdown"
                  class="btn btn-sm w-100 mb-0 dropdown-toggle"
                  data-bs-toggle="dropdown"
                  @click="getCustomers()"
                >
                  {{
                    filter.customer
                      ? filter.customer?.name
                      : $t("order.customer")
                  }}
                </button>
                <div
                  class="dropdown-menu w-100 p-1"
                  aria-labelledby="customerDropdown"
                >
                  <input
                    type="text"
                    class="form-control form-control-sm my-1"
                    :placeholder="$t('search')"
                    v-model="search_customer"
                    @keyup="getCustomers()"
                  />
                  <ul
                    class="list-group scroll-25 p-1"
                    @scroll="scrollCustomers($event)"
                  >
                    <li
                      class="list-group-item"
                      v-for="item in customers?.data"
                      :key="item"
                      @click="filter.customer = item"
                    >
                      {{ item.name }}
                    </li>
                  </ul>
                </div>
              </div>
            </div>
            <div class="col-12">
              <div class="form-group">
                <label for="from_date">{{ $t("from_date") }}</label>
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="filter.from_time"
                />
              </div>
            </div>
            <div class="col-12">
              <div class="form-group">
                <label for="to_date">{{ $t("to_date") }}</label>
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="filter.to_time"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-sm bg-gradient-danger"
            data-bs-dismiss="modal"
            @click="
              filter = { customer: null, from_time: '', to_time: '' };
              getOrders(1, 25);
            "
          >
            <i class="fas fa-trash"></i>
          </button>
          <button
            class="btn btn-sm bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-filter
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn btn-sm bg-gradient-primary"
            data-bs-dismiss="modal"
            @click="getOrders(1, 25)"
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
  <customer-modals @add="add_customer" ref="customer" />
</template>

<script>
import pagination from "@/components/pagination.vue";
import * as api from "../../server/api";
import CustomerModals from "@/components/modals/CustomerModals.vue";
export default {
  name: "preOrders",
  components: { pagination, CustomerModals },
  data() {
    return {
      image: api.image,
      filter: {
        customer: null,
        from_time: "",
        to_time: "",
      },
      orders: {
        current_page: 1,
        pages: 1,
        limit: 25,
        data: [],
      },
      order: null,
      search_customer: "",
      customers: [],
      new_order: {
        status: "pre_order",
        delivery_date: "",
        customer_id: 0,
      },
      customer: null,
      trades: {
        current_page: 1,
        pages: 1,
        limit: 25,
        data: [],
      },
      trade_items: [],
      format_date: api.format_date,
    };
  },
  created() {
    this.getOrders(1, 25);
  },
  methods: {
    getOrders(page, limit) {
      const customer_id = this.filter.customer ? this.filter.customer.id : 0;
      api
        .orders(
          this.filter.from_time,
          this.filter.to_time,
          customer_id,
          0,
          "pre_order",
          0,
          this.$route.query.market_id,
          page,
          limit
        )
        .then((res) => {
          this.orders = res.data;
        });
    },
    postOrder() {
      this.new_order.customer_id = this.customer ? this.customer.id : 0;
      api.create_order(this.new_order).then(() => {
        api.success("close-order-modal").then(() => {
          this.new_order = {
            status: "pre_order",
            delivery_date: "",
            customer_id: 0,
          };
          this.customer = null;
          this.getOrders(1, 25);
        });
      });
    },
    deleteOrder(id) {
      api.delete_order(id).then(() => {
        api.success().then(() => {
          this.getOrders(1, 25);
        });
      });
    },
    getTrades(page, limit, next) {
      api.trades(this.order.id, false, 0, page, limit).then((res) => {
        this.trades = res.data;
        this.getTradeStatus(next);
      });
    },
    getTradeStatus(next) {
      api.trade_status(this.order.id).then((res) => {
        this.trade_items = res.data;
        if (next) document.querySelector("[order-status-modal-button]").click();
      });
    },
    findTradeItem(trade_item) {
      return this.trade_items.find((item) => {
        return (
          item.trade_id == trade_item.trade_id &&
          item.trade_item_id == trade_item.id
        );
      });
    },
    countDays(start, end) {
      let start_date = start ? new Date(start) : new Date(),
        end_date = new Date(end),
        difference = end_date.getTime() - start_date.getTime(),
        days = Math.ceil(difference / (1000 * 3600 * 24));
      return days;
    },
    getCustomers() {
      api
        .customers(this.search_customer, 0, this.$route.query.market_id, 1, 25)
        .then((res) => {
          this.customers = res.data;
        });
    },
    scrollCustomers(event) {
      const element = event.target;
      if (element.scrollTop + element.clientHeight >= element.scrollHeight) {
        if (this.customers.current_page < this.customers.pages) {
          api
            .customers(
              this.search_customer,
              0,
              this.$route.query.market_id,
              this.customers.current_page,
              25
            )
            .then((res) => {
              this.customers.current_page = res.data.current_page;
              this.customers.pages = res.data.pages;
              this.customers.data = this.customers.data.concat(res.data.data);
            });
        }
      }
    },
    add_customer(body) {
      body.phones.forEach((element) => {
        element.phone = `${element.dial_code}${element.phone}`;
      });
      api.create_customer(body).then((res) => {
        this.customer = res.data;
        body.name = "";
        body.type = "";
        body.phones = [{ phone: "", dial_code: "+7" }];
        api.success("close");
        this.getCustomers(true);
        document.querySelector('[data-bs-target="#add-order-modal"]').click();
      });
    },
  },
};
</script>

<style scoped>
p {
  margin: 0;
}

img {
  width: 50px;
  height: 50px;
  object-fit: contain;
  border-radius: 10px;
}

progress {
  width: 100%;
}

p span {
  text-align: center;
}
</style>
