<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">{{ $route.query.name }}</h6>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col-md-6 my-1">
                <button
                  class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
                  data-bs-toggle="modal"
                  data-bs-target="#add-plant-user"
                  @click="get_shifts()"
                >
                  {{ $t("plant.add-user") }}
                </button>
              </div>
              <div class="col-md-6 my-1">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    get(1, limit);
                    $store.dispatch('setloading', false);
                  "
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-lg-4 col-md-6 col-sm-12 my-2"
            v-for="plant_user in plant_users"
            :key="plant_user"
          >
            <div class="card card-zoom h-100 shadow-sm">
              <div class="card-body pb-2">
                <div class="row">
                  <div class="col">
                    <h6 class="card-title text-primary mb-0">
                      {{ plant_user.user.name }} ({{
                        plant_user.admin
                          ? $t("plant.role.admin")
                          : $t("plant.role.worker")
                      }})
                    </h6>
                  </div>
                  <div class="col-auto">
                    <button
                      class="btn btn-sm bg-gradient-danger mb-0 px-3"
                      @click="
                        put({
                          id: plant_user.id,
                          plant_id: plant_user.plant_id,
                          user_id: plant_user.user_id,
                          admin: plant_user.admin,
                          disabled: true,
                        })
                      "
                    >
                      <i class="fas fa-trash"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="plant_users" />
            <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
          </div>
        </div>
      </div>
    </div>
  </div>
  <plan-view-modals @add="add" ref="modal" />
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";
import PlanViewModals from "@/components/modals/PlantViewModals.vue";

export default {
  components: { dataNotfound, Pagination, PlanViewModals },
  name: "PlantView",
  data() {
    return {
      plant_users: [],
      pages: 0,
      page: 1,
      limit: 10,
      disabled: false,
      plant_id: this.$route.query.id,
      search: "",
    };
  },
  methods: {
    get(page, limit) {
      api
        .get_plant_users(page, limit, this.search, this.plant_id)
        .then((res) => {
          this.pages = res.data.page_count;
          this.page = res.data.current_page;
          this.limit = res.data.page_limit;
          this.plant_users = res.data.data;
        });
    },
    add(body) {
      api.create_plant_user(body).then(() => {
        body.name = this.$t("plant.user");
        body.user_id = null;
        body.admin = "";
        api.success("close");
        this.get(this.page, this.limit);
      });
    },
    put(body) {
      api.put_plant_user(body).then(() => {
        api.success();
        this.get(this.page, this.limit);
      });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>
