<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">{{ $route.query.name }}</h6>
          </div>
          <div class="col-6 my-1">
            <div class="row">
              <div class="col">
                <div class="nav-wrapper position-relative end-0">
                  <ul
                    class="p-1 bg-transparent nav nav-pills nav-fill"
                    role="tablist"
                    id="pills-tab"
                  >
                    <li class="nav-item cursor-pointer">
                      <a
                        class="px-0 py-1 mb-0 nav-link active"
                        @click="
                          content = 'loan';
                          get_loan(loan.page, loan.limit);
                        "
                      >
                        <span class="ms-1">{{ $t("customer.loan") }}</span>
                      </a>
                    </li>
                    <li class="nav-item cursor-pointer">
                      <a
                        class="px-0 py-1 mb-0 nav-link"
                        @click="
                          content = 'order';
                          get_order(order.page, order.limit);
                        "
                      >
                        <span class="ms-1">{{ $t("customer.order") }}</span>
                      </a>
                    </li>
                  </ul>
                </div>
              </div>
              <div class="col-auto d-flex align-items-center">
                <button
                  class="btn btn-sm bg-gradient-primary mb-0 px-3 float-end"
                  data-bs-toggle="modal"
                  :data-bs-target="`${
                    content == 'loan' ? '#filter-loan' : '#filter-order'
                  }`"
                >
                  <i class="fas fa-filter"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row" v-if="content == 'loan'">
          <div
            class="col-md-4 col-lg-3 col-sm-6 my-2"
            v-for="loan in loans"
            :key="loan"
          >
            <div class="card h-100 shadow-sm border">
              <div class="card-body">
                <h6 class="mb-0">
                  <b> {{ loan.Loans?.order?.ordinal_number }}.</b>
                </h6>
                <hr class="my-1" />
                <div class="ms-2">
                  <div class="d-flex align-items-center text-wrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fas fa-user me-3"></i>
                    </div>
                    <h6 class="mb-0">
                      {{ loan.Loans.order?.customer?.name }}
                    </h6>
                  </div>
                  <div class="d-flex align-items-center text-nowrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fas fa-phone me-3"></i>
                    </div>
                    <h6 class="mb-0">
                      {{ loan.Loans.order?.customer?.phones[0]?.number }}
                    </h6>
                  </div>
                  <div class="d-flex align-items-center text-nowrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fas fa-money-bill me-3"></i>
                    </div>
                    <h6 class="mb-0">
                      {{
                        Intl.NumberFormat().format(loan.Loans.money) +
                        " " +
                        $t("currency")
                      }}
                    </h6>
                  </div>
                  <div class="d-flex align-items-center text-nowrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fa-solid fa-money-bill-transfer me-3"></i>
                    </div>
                    <h6 class="mb-0 text-success">
                      {{
                        Intl.NumberFormat().format(loan.sum_income) +
                        " " +
                        $t("currency")
                      }}
                    </h6>
                  </div>
                  <div class="d-flex align-items-center text-nowrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fa-solid fa-money-bill-transfer me-3"></i>
                    </div>
                    <h6 class="mb-0 text-danger">
                      {{
                        Intl.NumberFormat().format(loan.Loans.residual) +
                        " " +
                        $t("currency")
                      }}
                    </h6>
                  </div>
                </div>
                <hr class="my-1" />
                <div class="row">
                  <div class="col">
                    <router-link
                      :to="{
                        name: 'LoanHistory',
                        query: {
                          id: loan.Loans.id,
                          market_id: $route.query.market_id,
                        },
                      }"
                      class="btn btn-sm bg-gradient-secondary mb-0 w-100 py-2"
                    >
                      <i class="fa-solid fa-clock-rotate-left"></i>
                    </router-link>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="loans" />
            <pagination
              :page="loan.page"
              :pages="loan.pages"
              :limit="loan.limit"
              @get="get_loan"
            />
          </div>
        </div>
        <div class="row" v-else>
          <div
            class="col-md-4 col-lg-4 col-sm-6 my-2"
            v-for="order in orders"
            :key="order"
          >
            <div class="card h-100 shadow-sm border">
              <div class="card-body">
                <div class="row d-flex align-items-center">
                  <div class="col">
                    <h6 class="mb-0 d-flex justify-content-between fw-normal">
                      <b> {{ order.ordinal_number }}.</b>
                      <b>
                        {{ format_date(order.time) }}
                      </b>
                    </h6>
                  </div>
                  <div class="col-auto">
                    <button
                      class="btn btn-sm bg-gradient-primary mb-0 px-3"
                      @click="$refs.order.start(order.id)"
                    >
                      <i class="fas fa-eye"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="orders" />
            <pagination
              :page="order.page"
              :pages="order.pages"
              :limit="order.limit"
              @get="get_order"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
  <loan-history-modal @loan-filter="loan_filter" @order-filter="order_filter" />
  <order-modal ref="order" />
</template>

<script>
import * as api from "@/server/api";
import setNavPills from "@/assets/js/nav-pills.js";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";
import LoanHistoryModal from "@/components/modals/LoanHistoryModal.vue";
import OrderModal from "@/components/modals/orderModal.vue";
export default {
  components: { dataNotfound, Pagination, LoanHistoryModal, OrderModal },
  name: "history",
  data() {
    return {
      content: "loan",
      id: this.$route.query.id,
      loan: {
        from_time: "",
        to_time: "",
        status: false,
        page: 1,
        pages: 0,
        limit: 10,
      },
      order: {
        from_time: "",
        to_time: "",
        status: false,
        page: 1,
        pages: 0,
        limit: 10,
      },
      loans: [],
      orders: [],
      format_date: api.format_date,
    };
  },
  methods: {
    get_loan(page, limit) {
      api
        .get_loans(
          page,
          limit,
          this.loan.from_time,
          this.loan.to_time,
          this.id,
          this.loan.status,
          0,
          this.$route.query.market_id
        )
        .then((res) => {
          this.loan.pages = res.data.pages;
          this.loan.page = res.data.current_page;
          this.loan.limit = res.data.limit;
          this.loans = res.data.data;
        });
    },
    get_order(page, limit) {
      api
        .orders(
          this.order.from_time,
          this.order.to_time,
          this.id,
          0,
          this.order.status,
          0,
          this.$route.query.market_id,
          page,
          limit
        )
        .then((res) => {
          this.order.pages = res.data.pages;
          this.order.page = res.data.current_page;
          this.order.limit = res.data.limit;
          this.orders = res.data.data;
        });
    },
    loan_filter(body, bool) {
      this.loan.from_time = body.from_date;
      this.loan.to_time = body.to_date;
      this.loan.status = body.status;
      bool ? api.success("loan-filter") : api.success();
      this.get_loan(this.loan.page, this.loan.limit);
    },
    order_filter(body, bool) {
      this.order.from_time = body.from_date;
      this.order.to_time = body.to_date;
      this.order.status = body.status;
      bool ? api.success("order-filter") : api.success();
      this.get_order(this.order.page, this.order.limit);
    },
  },
  created() {
    this.get_loan(this.loan.page, this.loan.limit);
  },
  mounted() {
    setNavPills();
  },
};
</script>
