<template>
  <div class="py-4 container-fluid">
    <div class="card h-100 h-auto">
      <div class="nav-wrapper position-relative end-0">
        <ul
          class="p-1 bg-transparent nav nav-pills nav-fill"
          role="tablist"
          id="pills-tab"
        >
          <li class="nav-item cursor-pointer">
            <a
              class="px-0 py-1 mb-0 nav-link active"
              @click="
                content = 'warehouse';
                status_product_type = true;
                get();
              "
            >
              <i class="fas fa-up-long text-danger"></i>
              <span class="ms-1 text-danger">{{
                $t("product-store.title")
              }}</span>
            </a>
          </li>
          <li class="nav-item cursor-pointer">
            <a
              class="px-0 py-1 mb-0 nav-link"
              @click="
                content = 'inprogress';
                status_product_type = false;
                get();
              "
            >
              <i class="fas fa-down-long text-success"></i>
              <span class="ms-1 text-success">{{
                $t("product-store.table.step")
              }}</span>
            </a>
          </li>
        </ul>
      </div>

      <div v-if="content === 'warehouse'">
        <div class="card-header pb-0">
          <div class="row mb-3">
            <div
              class="col-md-4 col-12 col-lg-4 col-sm-4 my-1 align-items-center d-flex"
            >
              <h5 class="card-title">
                {{
                  product_types.find((e) => e.id == selectedCategory)?.name ||
                  ""
                }}
              </h5>
            </div>
            <div
              class="col-md-4 col-12 col-lg-4 col-sm-4 my-1 align-items-center d-flex justify-content-end"
            >
              <button
                class="btn bg-gradient-success btn-sm w-100 mb-0 mr-2 position-relative"
                type="button"
                data-bs-toggle="modal"
                data-bs-target="#product-store-modal"
              >
                <span
                  class="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-primary"
                  v-if="readyProducts.length"
                >
                  {{ readyProducts.length }}
                </span>
                {{ $t("product-store.inncome.title") }}
              </button>
            </div>
            <div
              class="col-md-4 col-12 col-lg-4 col-sm-4 my-1 align-items-center d-flex"
            >
              <input
                type="search"
                :placeholder="$t('search')"
                class="form-control form-control-sm"
                v-model="search"
                @keyup="
                  getProductStores(1, 10);
                  $store.dispatch('setloading', false);
                "
              />
            </div>
          </div>
        </div>
        <div class="card-body pt-0 overflow-auto" style="min-height: 50vh">
          <table class="table table-bordered">
            <thead>
              <tr>
                <th>{{ $t("product-store.table.name") }}</th>
                <th v-if="column">{{ $t("material.title") }}</th>
                <th>{{ $t("product-store.table.image") }}</th>
                <th>{{ $t("product-store.table.quantity") }}</th>
                <th>{{ $t("product-store.table.quantity_order") }}</th>
                <th class="w-custom-40"></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="store in product_stores" :key="store">
                <td>{{ store.product_name }}</td>
                <td v-if="column">
                  {{ store.Product_Store.material?.name || "" }}
                </td>
                <td>
                  <img
                    :src="`${image}/products/${store.Product_Store.product.image}`"
                    class="avatar avatar-sm"
                    alt="product"
                  />
                </td>
                <td>
                  {{ Math.floor(store.sum_quantity * 1000) / 1000 }}
                  {{ store.Product_Store?.product?.olchov?.name }}
                </td>
                <td>
                  {{ store.sum_quantity_bron }}
                  {{ store.Product_Store?.product?.olchov?.name }}
                </td>
                <td>
                  <button
                    class="btn bg-gradient-success mb-0 position-relative btn-sm px-3"
                    type="button"
                    data-bs-toggle="modal"
                    :data-bs-target="`#transfer${store.Product_Store.id}`"
                    v-if="store.isReady"
                  >
                    <i class="fa-solid fa-arrow-right"></i>
                  </button>
                  <!-- <button
                    class="btn bg-gradient-primary mb-0 position-relative btn-sm px-3 ms-1"
                    type="button"
                    data-bs-toggle="modal"
                    data-bs-target="#utilization"
                    @click="
                      $refs.utilization.body.product_id =
                        store.Product_Store.product?.id
                    "
                  >
                    <i class="fa-solid fa-arrows-spin"></i>
                  </button> -->
                </td>
                <product-transfer-modal
                  :markets="markets"
                  :product="store"
                  @get="getProductStores"
                />
              </tr>
            </tbody>
            <tfoot v-if="!product_stores.length">
              <tr>
                <td colspan="6">
                  <data-notfound :data="product_stores" />
                </td>
              </tr>
            </tfoot>
            <tfoot v-if="pages">
              <tr>
                <td colspan="6">
                  <pagination
                    :page="page"
                    :pages="pages"
                    :limit="limit"
                    @get="getProductStores"
                  />
                </td>
              </tr>
            </tfoot>
          </table>
        </div>
        <div class="card-footer">
          <div class="row flex-nowrap overflow-auto">
            <div
              class="col-md-2"
              v-for="product_type in product_types"
              :key="product_type"
            >
              <div
                class="card shadow"
                @click="
                  selectedCategory = product_type.id;
                  getProductStores(1, 10);
                "
              >
                <img
                  :src="`${image}/product_types/${product_type.image}`"
                  class="card-img-top"
                  style="
                    max-inline-size: 100%;
                    block-size: auto;
                    aspect-ratio: 2/1;
                    object-fit: contain;
                    height: 70px;
                  "
                  alt="..."
                />
                <div class="card-body">
                  <span
                    class="badge badge-pill bg-primary text-capitalize d-flex justify-content-center"
                    style="text-wrap: balance"
                  >
                    {{ product_type.name }}
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div v-else>
        <div class="card-body pt-0 overflow-auto" style="min-height: 50vh">
          <table class="table table-bordered">
            <thead>
              <tr>
                <th>{{ $t("product-store.table.name") }}</th>
                <th v-if="column_cycle">{{ $t("supply.product-material") }}</th>
                <th>{{ $t("product-store.table.image") }}</th>
                <th>{{ $t("product-store.table.step") }}</th>
                <th>{{ $t("product-store.table.quantity") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="store in in_cycle_products" :key="store">
                <td>{{ store.Cycle_Store?.product?.name || "" }}</td>
                <td v-if="column_cycle">
                  {{ store.Cycle_Store?.material?.name || "" }}
                </td>
                <td>
                  <img
                    :src="`${image}/products/${store.Cycle_Store?.product?.image}`"
                    class="avatar avatar-sm"
                    alt="product"
                  />
                </td>
                <td>
                  {{ store.Cycle_Store?.cycle?.name || "" }}
                </td>
                <td>
                  {{ store.sum_quantity }}
                  {{ store.Cycle_Store?.olchov?.name || "" }}
                </td>
              </tr>
            </tbody>
            <tfoot v-if="!in_cycle_products.length">
              <tr>
                <td colspan="5">
                  <data-notfound :data="in_cycle_products" />
                </td>
              </tr>
            </tfoot>
          </table>
        </div>
        <div class="card-footer">
          <div class="row flex-nowrap overflow-auto">
            <div
              class="col-md-2"
              v-for="product_type in product_types"
              :key="product_type"
            >
              <div
                class="card shadow"
                @click="
                  selectedCategory = product_type.id;
                  getInCycleProducts();
                "
              >
                <img
                  :src="`${image}/product_types/${product_type.image}`"
                  class="card-img-top"
                  style="
                    max-inline-size: 100%;
                    block-size: auto;
                    aspect-ratio: 2/1;
                    object-fit: contain;
                    height: 70px;
                  "
                  alt="..."
                />
                <div class="card-body">
                  <span
                    class="badge badge-pill bg-primary text-capitalize d-flex justify-content-center"
                    style="text-wrap: balance !important"
                  >
                    {{ product_type.name }}
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <product-store-modal
    @get-product-stores="getProductStores"
    @get-ready-products="getReadyProducts"
    :readyProducts="readyProducts"
  />
  <utilization ref="utilization" />
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import ProductStoreModal from "@/components/modals/ProductStoreModal.vue";
import Pagination from "@/components/pagination.vue";
import ProductTransferModal from "@/components/modals/ProductTransferModal.vue";
import setNavPills from "@/assets/js/nav-pills.js";
import Utilization from "@/components/modals/Utilization.vue";
export default {
  components: {
    dataNotfound,
    ProductStoreModal,
    Pagination,
    ProductTransferModal,
    Utilization,
  },
  name: "product-store",
  data() {
    return {
      readyProducts: [],
      product_stores: [],
      in_cycle_products: [],
      product_types: [],
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      id: 0,
      image: api.image,
      selectedCategory: null,
      markets: [],
      content: "warehouse",
      status_product_type: true,
    };
  },
  methods: {
    get() {
      api
        .get_product_types(1, 1000, "", this.status_product_type)
        .then((res) => {
          this.product_types = res.data.data;

          if (res.data.data.length) {
            this.selectedCategory = res.data.data[0].id;
            this.getProductStores(this.page, this.limit);
            this.getInCycleProducts();
          }
        });
    },
    get_markets() {
      api.markets("", 0, 1, 1000).then((res) => {
        this.markets = res.data.data;
      });
    },
    getReadyProducts() {
      api.get_ready_products().then((res) => {
        this.readyProducts = res.data;
      });
    },
    getProductStores(page = this.page, limit = this.limit) {
      api
        .get_product_stores(page, limit, this.search, this.selectedCategory, 0)
        .then((res) => {
          this.pages = res.data.pages;
          this.page = res.data.current_page;
          this.product_stores = res.data.data;
        });
    },
    getInCycleProducts() {
      api.get_in_cycle_products(this.selectedCategory).then((res) => {
        this.in_cycle_products = res.data;
      });
    },
  },
  created() {
    this.get();
    this.get_markets();
  },
  computed: {
    column() {
      let result = false;
      this.product_stores.forEach((element) => {
        if (element.Product_Store.material) {
          result = true;
        }
      });
      return result;
    },
    column_cycle() {
      let result = false;
      this.in_cycle_products.forEach((element) => {
        if (element.Cycle_Store.material) {
          result = true;
        }
      });
      return result;
    },
  },
  mounted() {
    setNavPills();
  },
};
</script>
