<template>
  <div
    class="modal fade"
    id="product-store-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="take-supply-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <div class="row w-100">
            <div class="col-md-6 my-1">
              <h5 class="modal-title">
                {{ $t("product-store.inncome.title") }}
              </h5>
            </div>
            <div class="col-md-6 my-1">
              <input
                type="search"
                :placeholder="$t('search')"
                class="form-control form-control-sm"
                v-model="search"
                @keyup="
                  getReadyProducts();
                  $store.dispatch('setloading', false);
                "
              />
            </div>
          </div>
        </div>
        <div class="modal-body">
          <table class="table table-bordered">
            <thead>
              <tr>
                <th>{{ $t("product-store.inncome.name") }}</th>
                <th>{{ $t("product-store.inncome.image") }}</th>
                <th>{{ $t("product-store.inncome.quantity") }}</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="product in readyProducts" :key="product">
                <td>{{ product.product.name }}</td>
                <td class="me-3">
                  <img
                    :src="`${image}/products/${product.product.image}`"
                    class="avatar avatar-sm"
                    alt="product"
                  />
                </td>
                <td>
                  {{ product.value }}
                  {{ product.olchov.name }}
                </td>
                <td>
                  <button
                    type="button"
                    class="btn mb-0 btn-outline-success"
                    @click="acceptReadyProduct(product.id)"
                  >
                    <i class="fas fa-check"></i>
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn bg-gradient-secondary"
            data-bs-dismiss="modal"
            close
          >
            {{ $t("close") }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
export default {
  name: "product-store-modal",
  props: ["readyProducts"],
  emits: ["get-product-stores", "get-ready-products"],
  data() {
    return {
      search: "",
      image: api.image,
    };
  },
  methods: {
    acceptReadyProduct(id) {
      api.accept_ready_product(id).then(() => {
        this.$emit("get-ready-products");
        this.$emit("get-product-stores");
      });
    },
  },
  created() {
    this.$emit("get-ready-products");
  },
};
</script>
