<template>
  <div
    class="modal fade"
    id="cycle-works"
    tabindex="-1"
    role="dialog"
    aria-labelledby="cycle-works-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="cycle-works-label">
            {{ $t("cycle.work") }}
          </h5>
          <button
            type="button"
            class="btn shadow-none py-0 mb-0"
            data-bs-toggle="modal"
            data-bs-target="#cycle-works"
          >
            <i class="fas fa-xmark"></i>
          </button>
        </div>
        <div class="modal-body">
          <form @submit.prevent="add()">
            <div class="row">
              <div class="col">
                <div class="form-group">
                  <input
                    type="text"
                    class="form-control"
                    v-model="body.name"
                    :placeholder="$t('olchov.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-auto">
                <button
                  class="btn bg-gradient-success mb-0"
                  type="submit"
                  :disabled="!body.name"
                >
                  <i class="fas fa-circle-check"></i>
                </button>
              </div>
            </div>
          </form>
          <hr />
          <ul class="list-group">
            <li
              v-for="work in works"
              :key="work"
              class="list-group-item list-group-item-action d-flex justify-content-between align-items-start"
            >
              {{ work.name }}
              <span
                class="badge bg-gradient-danger rounded-pill cursor-pointer"
                @click="
                  put({
                    id: work.id,
                    cycle_id: work.cycle_id,
                    name: work.name,
                    disabled: true,
                  })
                "
                ><i class="fas fa-xmark"></i
              ></span>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";

export default {
  name: "cycle-work",
  data() {
    return {
      body: {
        cycle_id: 0,
        name: "",
      },
      works: [],
      pages: 0,
      page: 1,
      limit: 10,
    };
  },
  methods: {
    get(page, limit) {
      api.get_works(page, limit, this.body.cycle_id).then((res) => {
        this.pages = res.data.page_count;
        this.page = res.data.current_page;
        this.limit = res.data.page_limit;
        this.works = res.data.data;
      });
    },
    add() {
      api.create_work(Array(this.body)).then(() => {
        this.body.name = "";
        api.success();
        this.get(this.page, this.limit);
      });
    },
    put(body) {
      api.update_work(body).then(() => {
        api.success();
        this.get(this.page, this.limit);
      });
    },
  },
};
</script>
