<template>
  <div class="container-fluid py-4">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row d-flex align-items-center">
          <div class="col-md-6">
            <div class="nav-wrapper position-relative end-0">
              <ul
                class="p-1 bg-transparent nav nav-pills nav-fill"
                role="tablist"
                id="pills-tab"
              >
                <li class="nav-item cursor-pointer">
                  <a
                    class="px-0 py-1 mb-0 nav-link active"
                    @click="
                      content = 'kassa';
                      getCashiers();
                    "
                  >
                    <i class="fas fa-box"></i>
                    <span class="ms-1">{{ $t("cashier.title") }}</span>
                  </a>
                </li>
                <li class="nav-item cursor-pointer">
                  <a
                    class="px-0 py-1 mb-0 nav-link"
                    @click="
                      content = 'users';
                      get_users(user.page, user.limit);
                    "
                  >
                    <i class="fas fa-users"></i>
                    <span class="ms-1">{{ $t("cashier.users") }}</span>
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col-md-6 my-1">
                <button
                  class="btn btn-sm w-100 mb-0 bg-gradient-primary"
                  data-bs-toggle="modal"
                  data-bs-target="#add-cashier"
                >
                  {{ $t("cashier.add-cashier-button") }}
                </button>
              </div>
              <div class="col-md-6 my-1">
                <input
                  type="text"
                  class="form-control form-control-sm"
                  :placeholder="$t('search')"
                  v-model="search"
                  @keyup="getCashiers()"
                  v-if="content == 'kassa'"
                />
                <input
                  type="text"
                  class="form-control form-control-sm"
                  :placeholder="$t('search')"
                  v-model="user.search"
                  @keyup="get_users(user.page, user.limit)"
                  v-else
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row" v-if="content == 'kassa'">
          <div
            class="col-md-4 col-sm-6 my-2"
            v-for="item in cashiers"
            :key="item"
          >
            <div class="card">
              <div class="card-body">
                <div class="row gap-1">
                  <div class="col-12">
                    <strong>{{ item.name }}</strong>
                    <p>{{ item.comment }}</p>
                    <hr class="mx-0" />
                    <b>{{
                      Intl.NumberFormat().format(item.balance) +
                      " " +
                      $t("currency")
                    }}</b>
                  </div>
                  <div class="col-12">
                    <div class="row">
                      <div class="col">
                        <router-link
                          class="btn btn-sm mb-0 w-100 bg-gradient-primary"
                          :to="{
                            name: 'CashierHistory',
                            query: {
                              id: item.id,
                              name: item.name,
                              market_id: market_id,
                              balance: item.balance,
                            },
                          }"
                        >
                          <i class="fa fa-eye"></i>
                        </router-link>
                      </div>
                      <div class="col">
                        <button
                          class="btn btn-sm mb-0 w-100 bg-gradient-warning"
                          data-bs-toggle="modal"
                          data-bs-target="#edit-cashier"
                          @click="
                            edit_cashier.id = item.id;
                            edit_cashier.name = item.name;
                            edit_cashier.comment = item.comment;
                          "
                        >
                          <i class="fas fa-pen"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="row" v-else>
          <div class="col-md-4 col-sm-6 my-2" v-for="user in users" :key="user">
            <div class="card card-zoom h-100 shadow-sm">
              <div class="card-body">
                <h6 class="card-title text-muted mb-0">
                  {{ user.name }}
                </h6>
                <h6 class="card-title text-success mb-0">
                  {{
                    Intl.NumberFormat().format(user.balance) +
                    " " +
                    $t("currency")
                  }}
                </h6>
                <div class="row">
                  <div class="col">
                    <button
                      class="btn bg-gradient-primary btn-sm mb-0 w-100"
                      data-bs-toggle="modal"
                      data-bs-target="#take-money"
                      @click="
                        $refs.take.body.user_id = user.id;
                        $refs.take.max = user.balance;
                      "
                    >
                      <i class="fas fa-hand-holding-dollar"></i>
                    </button>
                  </div>
                  <div class="col">
                    <button
                      class="btn bg-gradient-success btn-sm mb-0 w-100"
                      data-bs-toggle="modal"
                      data-bs-target="#give-money"
                      @click="$refs.take.pay.source = user.id"
                    >
                      <i class="fas fa-money-bill-transfer"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

  <div
    class="modal fade"
    id="add-cashier"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-cashier-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-cashier-label">
            {{ $t("cashier.add-modal-title") }}
          </h5>
        </div>
        <form @submit.prevent="postCashier()">
          <div class="modal-body">
            <div class="row">
              <div class="col-12">
                <div class="form-group">
                  <label for="name">{{
                    $t("cashier.placeholders.name")
                  }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    :placeholder="$t('cashier.placeholders.name')"
                    required
                    v-model="new_cashier.name"
                  />
                </div>
              </div>
              <div class="col-12">
                <div class="form-group">
                  <label for="comment">{{
                    $t("cashier.placeholders.comment")
                  }}</label>
                  <input
                    id="comment"
                    type="text"
                    class="form-control"
                    :placeholder="$t('cashier.placeholders.comment')"
                    required
                    v-model="new_cashier.comment"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-add-cashier-modal
            >
              {{ $t("close") }}
            </button>
            <button class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>

  <div
    class="modal fade"
    id="edit-cashier"
    tabindex="-1"
    role="dialog"
    aria-labelledby="edit-cashier-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="edit-cashier-label">
            {{ $t("cashier.edit-modal-title") }}
          </h5>
        </div>
        <form @submit.prevent="putCashier()">
          <div class="modal-body">
            <div class="row">
              <div class="col-12">
                <div class="form-group">
                  <label for="name_put">{{
                    $t("cashier.placeholders.name")
                  }}</label>
                  <input
                    id="name_put"
                    type="text"
                    class="form-control"
                    :placeholder="$t('cashier.placeholders.name')"
                    required
                    v-model="edit_cashier.name"
                  />
                </div>
              </div>
              <div class="col-12">
                <div class="form-group">
                  <label for="comment_put">{{
                    $t("cashier.placeholders.comment")
                  }}</label>
                  <input
                    id="comment_put"
                    type="text"
                    class="form-control"
                    :placeholder="$t('cashier.placeholders.comment')"
                    required
                    v-model="edit_cashier.comment"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-edit-cashier-modal
            >
              {{ $t("close") }}
            </button>
            <button class="btn bg-gradient-primary">
              {{ $t("save") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>

  <take-money-from-user
    ref="take"
    :cashiers="cashiers"
    @take="take_money"
    @pay="pay"
  />
</template>

<script>
import * as api from "../../server/api";
import setNavPills from "@/assets/js/nav-pills.js";
import TakeMoneyFromUser from "@/components/modals/TakeMoneyFromUser.vue";

export default {
  components: { TakeMoneyFromUser },
  name: "cashier",
  data() {
    return {
      search: "",
      cashiers: [],
      new_cashier: {
        name: "",
        comment: "",
      },
      edit_cashier: {
        id: null,
        name: "",
        comment: "",
      },
      content: "kassa",
      users: [],
      user: {
        pages: 0,
        page: 1,
        limit: 10,
      },
      market_id: this.$route.query.market_id || 0,
    };
  },
  methods: {
    getCashiers() {
      api.kassa(this.search, 0, this.market_id).then((res) => {
        this.cashiers = res.data;
      });
    },
    postCashier() {
      api.create_kassa(this.new_cashier).then(() => {
        api.success("close-add-cashier-modal").then(() => {
          this.new_cashier = {
            name: "",
            comment: "",
          };
          this.getCashiers();
        });
      });
    },
    putCashier() {
      api.update_kassa(this.edit_cashier).then(() => {
        api.success("close-edit-cashier-modal").then(() => {
          this.getCashiers();
        });
      });
    },
    get_users(page, limit) {
      api
        .get_users(
          page,
          limit,
          this.user.search,
          false,
          "",
          true,
          this.market_id
        )
        .then((res) => {
          this.user.pages = res.data.page_count;
          this.user.page = res.data.current_page;
          this.user.limit = res.data.page_limit;
          this.users = res.data.data;
        });
    },
    take_money(body) {
      api.take_money_from_user(body).then(() => {
        api.success("close-take-money").then(() => {
          body.money = 0;
          body.user_id = null;
          body.kassa_id = null;
          body.comment = "";
          this.getCashiers();
          this.get_users(this.user.page, this.user.limit);
        });
      });
    },
    pay(body) {
      api.pay_for_expense(body).then(() => {
        body.money = 0;
        body.source = null;
        body.comment = "";
        body.kassa_id = null;
        api.success("close-pay");
        this.get(this.page, this.limit);
      });
    },
  },
  created() {
    this.getCashiers();
  },
  mounted() {
    setNavPills();
  },
};
</script>

<style scoped>
hr {
  margin: 10px;
}

p {
  margin: 0;
}
</style>
