<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">{{ $route.query.name }}</h6>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col-md-6"></div>
              <div class="col-md-6 my-1">
                <button
                  class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
                  data-bs-toggle="modal"
                  data-bs-target="#add-norm"
                  @click="$refs.norm.get_olchovs()"
                >
                  {{ $t("norm.add-button") }}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-lg-4 col-md-6 col-sm-6 my-2"
            v-for="norm in norms"
            :key="norm"
          >
            <div class="card card-stats h-100 shadow-sm">
              <div class="card-body">
                <h6 class="mb-0 card-title">
                  <b>{{ $t("norm.products") }}:</b> {{ norm.products }}
                </h6>
                <h6>
                  <b> {{ $t("norm.value") }}:</b> {{ norm.value }}
                  {{ norm.olchov.name || "" }}
                </h6>
                <div class="progress mb-3" style="height: 20px">
                  <div
                    class="progress-bar"
                    role="progressbar"
                    style="min-width: 50px"
                    :style="`width: ${Math.round(
                      (norm.done_value / norm.value) * 100
                    )}%`"
                    :aria-valuenow="
                      Math.round((norm.done_value / norm.value) * 100)
                    "
                    aria-valuemin="0"
                  >
                    {{ norm.done_value }} {{ norm.olchov.name || "" }}
                  </div>
                </div>

                <button
                  class="btn btn-sm bg-gradient-danger mb-0 w-100"
                  @click="remove(norm)"
                >
                  <b><i class="fas fa-trash"></i> {{ $t("delete") }}</b>
                </button>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="norms" />
            <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
          </div>
        </div>
      </div>
    </div>
  </div>
  <norm-modals ref="norm" />
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";
import NormModals from "@/components/modals/NormModals.vue";

export default {
  components: { dataNotfound, Pagination, NormModals },
  name: "norm",
  data() {
    return {
      plant_id: this.$route.query.id,
      pages: 0,
      page: 1,
      limit: 10,
      norms: [],
    };
  },
  methods: {
    get(page, limit) {
      api.get_norms(page, limit, this.plant_id).then((res) => {
        this.pages = res.data.page_count;
        this.page = res.data.current_page;
        this.limit = res.data.page_limit;
        this.norms = res.data.data;
      });
    },
    remove(norm) {
      norm.disabled = true;
      api.update_norm(norm).then(() => {
        api.success();
        this.get(this.page, this.limit);
      });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>
