<template>
  <div
    class="modal fade"
    id="return"
    tabindex="-1"
    role="dialog"
    aria-labelledby="return-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header d-block">
          <h5 class="modal-title" id="filter-label">
            {{ product?.Trades?.product_store?.product?.name }}
          </h5>
          <p>
            {{
              $t("trades.placeholders.price") +
              ": " +
              currency.format(product?.Trades?.price) +
              " " +
              $t("currency")
            }}
          </p>
          <p>
            {{
              $t("trades.placeholders.discount") +
              ": " +
              currency.format(product?.Trades?.discount) +
              " " +
              $t("currency")
            }}
          </p>
          <p>
            {{
              $t("trades.placeholders.value") +
              ": " +
              product?.sum_quantity +
              " " +
              (product?.Trades?.product
                ? product?.Trades?.product?.olchov?.name
                : product?.Trades?.product_store?.product?.olchov?.name)
            }}
          </p>
        </div>
        <form @submit.prevent="postReturnProduct()">
          <div class="modal-body">
            <div class="row gap-1" v-if="product">
              <div class="col-12">
                <div class="input-group">
                  <input
                    class="form-control"
                    type="number"
                    min="0.1"
                    :max="product.sum_quantity"
                    step="any"
                    :placeholder="$t('trades.placeholders.value')"
                    v-model="return_product.quantity"
                  />
                  <div class="input-group-text">
                    {{ product?.Trades?.product_store?.product?.olchov?.name }}
                  </div>
                </div>
              </div>
              <div class="col-12">
                <div class="input-group">
                  <input
                    class="form-control"
                    type="number"
                    :min="
                      product?.Trades?.product_store?.product?.unit ? 0.1 : 1
                    "
                    :max="product.Trades.price * return_product.quantity"
                    :step="
                      product?.Trades?.product_store?.product?.unit ? 'any' : 1
                    "
                    :placeholder="$t('trades.placeholders.price')"
                    v-model="return_product.price"
                  />
                  <div class="input-group-text">{{ $t("currency") }}</div>
                </div>
              </div>
              <div class="col-12">
                <select
                  class="form-select"
                  required
                  v-model="return_product.kassa_id"
                >
                  <option v-for="item in cashiers" :key="item" :value="item.id">
                    {{ item.name }}
                  </option>
                </select>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-sm bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-return
            >
              {{ $t("close") }}
            </button>
            <button class="btn btn-sm bg-gradient-primary">
              {{ $t("save") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api.js";
export default {
  name: "returnProductModal",
  data() {
    return {
      currency: Intl.NumberFormat(),
      product: null,
      cashiers: [],
      return_product: {
        trade_id: 0,
        quantity: null,
        price: null,
        kassa_id: 0,
      },
    };
  },
  created() {
    this.getCashiers();
  },
  methods: {
    set(product) {
      this.product = product;
      this.return_product.price = null;
      this.return_product.quantity = null;
      this.return_product.trade_id = product.Trades.id;
      this.getCashiers();
    },
    getCashiers() {
      api.kassa("", 0, this.$route.query.market_id).then((res) => {
        this.cashiers = res.data;
        if (this.cashiers.length)
          this.return_product.kassa_id = this.cashiers[0].id;
      });
    },
    postReturnProduct() {
      api.return_product(this.return_product).then(() => {
        api.success("close-return");
      });
    },
  },
};
</script>

<style scoped>
p {
  margin: 0;
}
</style>
