<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">{{ $t("supplier.title") }}</h6>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col-md-6 my-1">
                <button
                  class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
                  data-bs-toggle="modal"
                  data-bs-target="#add-supplier"
                >
                  {{ $t("supplier.add-button") }}
                </button>
              </div>
              <div class="col-md-6 my-1">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    get(1, limit);
                    $store.dispatch('setloading', false);
                  "
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-lg-4 col-md-4 col-sm-6 my-2"
            v-for="(supplier, i) in suppliers"
            :key="supplier"
          >
            <div class="card card-stats shadow-sm">
              <div class="card-body p-3">
                <h6
                  class="mb-0 d-flex justify-content-between align-items-center"
                >
                  <b> {{ i + 1 }}.</b>
                  <button
                    class="btn btn-sm bg-gradient-warning mb-0 px-3"
                    data-bs-toggle="modal"
                    data-bs-target="#update-supplier"
                    @click="
                      item = {
                        id: supplier.id,
                        name: supplier.name,
                        phones: supplier.phones,
                        materials: [],
                      }
                    "
                  >
                    <i class="fas fa-pen"></i>
                  </button>
                </h6>
                <hr class="my-1" />
                <h6 class="card-title mb-0">
                  <i class="fas fa-user"></i>
                  <span class="ms-1">
                    {{ supplier.name }}
                  </span>
                </h6>
                <h6 class="card-title mb-0">
                  <i class="fas fa-coins"></i>
                  <span
                    class="ms-1"
                    :class="`text-${
                      supplier.balance > 0 ? 'success' : 'danger'
                    }`"
                  >
                    {{ Intl.NumberFormat().format(supplier.balance) }}
                    {{ $t("currency") }}
                  </span>
                </h6>
                <h6 class="card-title mb-0 d-flex">
                  <i class="fas fa-phone my-1 d-flex"></i>
                  <span class="ms-1" v-if="!supplier.show_phones">
                    <a :href="`tel:+${supplier.phones[0]?.number}`"
                      >{{ supplier.phones[0]?.number }}
                    </a>
                  </span>
                  <span class="ms-1" v-else>
                    <a
                      :href="`tel:+${phone.number}`"
                      v-for="phone in supplier.phones"
                      :key="phone"
                      class="d-block"
                      >{{ phone.number }}</a
                    >
                  </span>
                  <span
                    class="cursor-pointer ms-1"
                    v-if="supplier.phones.length > 1"
                    @click="supplier.show_phones = !supplier.show_phones"
                    ><i
                      :class="`fas fa-angle-${
                        supplier.show_phones ? 'up' : 'down'
                      }`"
                    ></i
                  ></span>
                </h6>
                <div class="row">
                  <div class="col my-1">
                    <router-link
                      :to="{
                        name: 'SupplierInfo',
                        query: { id: supplier.id, name: supplier.name },
                      }"
                      class="btn btn-sm w-100 bg-gradient-primary mb-0 py-2"
                    >
                      <i class="fas fa-eye"></i>
                    </router-link>
                  </div>
                  <div class="col my-1" v-if="role != 'analyst'">
                    <button
                      class="btn btn-sm w-100 bg-gradient-secondary mb-0 py-2"
                      data-bs-toggle="modal"
                      data-bs-target="#give-money"
                      @click="
                        body.source = supplier.id;
                        get_cashregs();
                      "
                    >
                      <i class="fas fa-hand-holding-dollar"></i>
                    </button>
                  </div>
                  <div class="col my-1">
                    <button
                      class="btn btn-sm w-100 bg-gradient-primary mb-0 py-2"
                      data-bs-toggle="modal"
                      data-bs-target="#take-supply"
                      @click="
                        get_plants();
                        $refs.supplymodal.body.supplier_id = supplier.id;
                      "
                    >
                      <i class="fas fa-share"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="suppliers" />
            <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
          </div>
        </div>
      </div>
    </div>
  </div>
  <supplier-modals @add="add" :item="item" @put="put" />
  <supply-modals
    ref="supplymodal"
    type="true"
    take_from="all"
    :plants="plants"
    @get="get(page, limit)"
  />

  <div
    class="modal fade"
    id="give-money"
    tabindex="-1"
    role="dialog"
    aria-labelledby="give-money-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="give-money-label">
            {{ $t("users.give-money") }}
          </h5>
        </div>
        <form @submit.prevent="give_money">
          <div class="modal-body">
            <div class="form-group">
              <label for="kassa">{{ $t("cashier.title") }}</label>
              <div class="input-group">
                <select
                  class="form-select"
                  v-model="body.kassa_id"
                  @change="
                    body.cashreg_balance = cashregs.find(
                      (e) => e.id == body.kassa_id
                    ).balance
                  "
                  required
                >
                  <option
                    :value="cashreg.id"
                    v-for="cashreg in cashregs"
                    :key="cashreg"
                  >
                    {{ cashreg.name }}
                  </option>
                </select>
                <span class="input-group-text border border-1">
                  <b>
                    {{
                      `${$t("supplier.balance")}: ${Intl.NumberFormat().format(
                        body.cashreg_balance
                      )} ${$t("currency")}`
                    }}
                  </b>
                </span>
              </div>
            </div>
            <div class="form-group">
              <label for="money">{{ $t("cashier.money") }}</label>
              <div class="input-group">
                <input
                  id="money"
                  type="number"
                  class="form-control"
                  step="any"
                  min="1"
                  :max="body.cashreg_balance"
                  v-model="body.money"
                  @focus="body.money == 0 ? (body.money = null) : body.money"
                  @focusout="body.money == null ? (body.money = 0) : body.money"
                  :placeholder="$t('cashier.money')"
                  required
                />
                <span class="input-group-text">{{ $t("currency") }}</span>
              </div>
            </div>
            <div class="form-group">
              <label for="comment">{{ $t("cashier.comment") }}</label>
              <textarea
                id="comment"
                class="form-control"
                v-model="body.comment"
                :placeholder="$t('cashier.comment')"
                required
              />
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-give-money
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("users.give-money") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";
import SupplierModals from "@/components/modals/SupplierModals.vue";
import SupplyModals from "@/components/modals/SupplyModals.vue";

export default {
  components: { dataNotfound, Pagination, SupplierModals, SupplyModals },
  name: "Suppliers",
  data() {
    return {
      suppliers: [],
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      item: {},
      plants: [],
      body: {
        money: 0,
        type: "supplier_payment",
        source: 0,
        comment: "",
        kassa_id: 0,
        cashreg_balance: null,
      },
      cashregs: [],
      role: localStorage["role"],
    };
  },
  methods: {
    get(page, limit) {
      api.get_suppliers(page, limit, this.search).then((res) => {
        this.pages = res.data.page_count;
        this.page = res.data.current_page;
        this.limit = res.data.page_limit;
        res.data.data.forEach((element) => {
          element.show_phones = false;
        });
        this.suppliers = res.data.data;
      });
    },
    add(body) {
      api.create_supplier(body).then(() => {
        body.name = "";
        body.supplier = "";
        body.balance = 0;
        body.phones = [{ value: "", number: "", dial_code: "+7" }];
        api.success("close-supplier");
        this.get(this.page, this.limit);
      });
    },
    put(body) {
      api.put_supplier(body).then(() => {
        api.success("close-put");
        this.get(this.page, this.limit);
      });
    },
    get_plants() {
      api.get_plants(1, 1000, "").then((res) => {
        this.plants = res.data.data;
        this.get(this.page, this.limit);
      });
    },
    give_money() {
      api.pay_for_expense(this.body).then(() => {
        this.body.comment = "";
        this.body.kassa_id = 0;
        this.body.money = 0;
        this.body.source = 0;
        api.success("close-give-money");
        this.get(this.page, this.limit);
      });
    },
    get_cashregs() {
      api.kassa("", 0, 0).then((res) => {
        this.cashregs = res.data;
      });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>
