<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">{{ $t("material-types.title") }}</h6>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col-md-6 my-1">
                <button
                  class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
                  data-bs-toggle="modal"
                  data-bs-target="#add-material-type"
                  @click="get_olchovs()"
                >
                  {{ $t("material-types.add-button") }}
                </button>
              </div>
              <div class="col-md-6 my-1">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    get(1, limit);
                    $store.dispatch('setloading', false);
                  "
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-lg-4 col-md-6 col-sm-12 my-2"
            v-for="material_type in material_types"
            :key="material_type"
          >
            <div class="card card-zoom h-100 shadow-sm">
              <div class="card-body">
                <div class="row">
                  <div class="col">
                    <h5 class="card-title text-muted mb-0">
                      {{ material_type.Material_Type.name }}({{
                        $t("material-types.count") +
                        ": " +
                        material_type.material_count
                      }})
                    </h5>
                    <h6 class="text-muted mb-0" style="font-size: 13.5px">
                      {{ $t("product-types.olchov") }}:
                      {{ material_type.Material_Type.olchov?.name }}
                    </h6>
                    <hr class="my-1" />
                    <div class="d-flex mt-2">
                      <button
                        class="btn btn-sm px-3 mb-0 bg-gradient-warning"
                        @click="
                          get_olchovs();
                          material_type_id = material_type.Material_Type.id;
                          item = {
                            olchov_id: material_type.Material_Type.olchov_id,
                            name: material_type.Material_Type.name,
                            image: '',
                            hidden: material_type.Material_Type.hidden,
                          };
                        "
                        data-bs-toggle="modal"
                        data-bs-target="#update-material-type"
                      >
                        <i class="fas fa-pen"></i>
                      </button>
                      <button
                        class="btn btn-sm px-3 mb-0 bg-gradient-danger ms-1"
                        @click="
                          material_type_id = material_type.Material_Type.id;
                          item = {
                            olchov_id: material_type.Material_Type.olchov_id,
                            name: material_type.Material_Type.name,
                            hidden: true,
                            image: '',
                          };
                          remove(item);
                        "
                      >
                        <i class="fas fa-trash"></i>
                      </button>
                      <router-link
                        :to="{
                          name: 'Material',
                          query: {
                            id: material_type.Material_Type.id,
                            name: material_type.Material_Type.name,
                          },
                        }"
                        class="btn btn-sm px-3 mb-0 bg-gradient-primary ms-1"
                      >
                        <i class="fas fa-eye"></i>
                      </router-link>
                    </div>
                  </div>
                  <div
                    class="col-auto d-flex justify-content-center align-items-center"
                  >
                    <div
                      class="avatar avatar-xl cursor-pointer"
                      @click="
                        image_view(
                          `${image}/material_types/${material_type.Material_Type.image}`
                        )
                      "
                    >
                      <img
                        height="80"
                        width="80"
                        style="object-fit: cover"
                        :src="`${image}/material_types/${material_type.Material_Type.image}`"
                        class="text-white rounded-2"
                        alt="Material Type"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="material_types" />
            <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
          </div>
        </div>
      </div>
    </div>
  </div>
  <div id="image-viewer">
    <span class="close" @click="image_view()">&times;</span>
    <img class="modal-content" id="full-image" />
  </div>
  <material-type-modals :olchovs="olchovs" @add="add" :item="item" @put="put" />
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";
import MaterialTypeModals from "@/components/modals/MaterialTypeModals.vue";
import Swal from "sweetalert2";

export default {
  components: { dataNotfound, Pagination, MaterialTypeModals },
  name: "MaterialType",
  data() {
    return {
      material_types: [],
      olchovs: [],
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      image: api.image,
      item: {},
      material_type_id: null,
    };
  },
  methods: {
    get(page, limit) {
      api.get_material_types(page, limit, this.search, 0).then((res) => {
        this.pages = res.data.page_count;
        this.page = res.data.current_page;
        this.limit = res.data.page_limit;
        this.material_types = res.data.data;
      });
    },
    add(body) {
      api.create_material_type(api.getFormData(body)).then(() => {
        body.name = "";
        body.image = "";
        body.olchov_id = 0;
        api.success("close");
        this.get(this.page, this.limit);
      });
    },
    image_view(src) {
      if (src) {
        document.getElementById("full-image").src = src;
        document.getElementById("image-viewer").style.display = "block";
      } else {
        document.getElementById("image-viewer").style.display = "none";
      }
    },
    put(body) {
      let data = api.getFormData(body);
      if (!body.image) {
        data.delete("image");
      }
      api.put_material_type(data, this.material_type_id).then(() => {
        api.success("close-put");
        this.get(this.page, this.limit);
      });
    },
    remove(body) {
      Swal.fire({
        title: this.$t("delete_text"),
        showCancelButton: true,
        confirmButtonText: this.$t("confirm"),
        cancelButtonText: this.$t("cancel"),
      }).then((result) => {
        if (result.isConfirmed) {
          this.put(body);
        }
      });
    },
    get_olchovs() {
      api.get_olchovs(1, 1000, "").then((res) => {
        this.olchovs = res.data.data;
      });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>
