<template>
  <nav>
    <ul class="pagination pagination-sm my-1 justify-content-center d-flex">
      <li class="page-item">
        <button class="page-link" type="button" @click="get(page, limited)">
          <i class="fa fa-rotate"></i>
        </button>
      </li>
      <li class="page-item">
        <button
          class="page-link"
          type="button"
          @click="get(1, limited)"
          :disabled="page == 1"
        >
          <i class="fa fa-angle-double-left"></i>
        </button>
      </li>
      <li class="page-item">
        <button
          class="page-link"
          type="button"
          :disabled="page == 1"
          @click="get(page - 1, limited)"
        >
          <i class="fa fa-angle-left"></i>
        </button>
      </li>
      <li class="page-item" v-if="page - 2 > 0" @click="get(page - 2, limited)">
        <button class="page-link" type="button">
          {{ page - 2 }}
        </button>
      </li>
      <li class="page-item" v-if="page - 1 > 0" @click="get(page - 1, limited)">
        <button class="page-link" type="button">
          {{ page - 1 }}
        </button>
      </li>
      <li class="page-item active">
        <button class="page-link" type="button">
          {{ page }}
        </button>
      </li>
      <li class="page-item">
        <button
          class="page-link"
          type="button"
          v-if="page + 1 <= pages"
          @click="get(page + 1, limited)"
        >
          {{ page + 1 }}
        </button>
      </li>
      <li class="page-item">
        <button
          class="page-link"
          type="button"
          v-if="page + 2 <= pages"
          @click="get(page + 2, limited)"
        >
          {{ page + 2 }}
        </button>
      </li>
      <li class="page-item">
        <button
          class="page-link"
          type="button"
          :disabled="page == pages || !pages"
          @click="get(page + 1, limited)"
        >
          <i class="fa fa-angle-right"></i>
        </button>
      </li>
      <li class="page-item">
        <button
          class="page-link"
          type="button"
          :disabled="page == pages || !pages"
          @click="get(pages, limited)"
        >
          <i class="fa fa-angle-double-right"></i>
        </button>
      </li>
      <li class="page-item mx-1">
        <select
          class="form-control form-control-sm"
          v-model="limited"
          @change="get(1, limited)"
        >
          <option disabled value="">limit</option>
          <option value="10">10</option>
          <option value="25">25</option>
          <option value="50">50</option>
        </select>
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  name: "pagination",
  props: ["page", "pages", "limit"],
  emits: ["get"],
  data() {
    return {
      limited: this.limit,
    };
  },
  methods: {
    get(page, limit) {
      this.$emit("get", page, limit);
    },
  },
};
</script>
