<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="p-3 pb-0 card-header">
        <div class="d-flex align-items-center justify-content-between">
          <h6 class="mb-0">{{ $t("statistic.title") }}</h6>
          <button
            class="btn btn-sm bg-gradient-primary mb-0 px-3 float-end"
            data-bs-toggle="modal"
            data-bs-target="#filter"
          >
            <i class="fas fa-filter"></i>
          </button>
        </div>
      </div>
      <div class="p-3 card-body">
        <div class="row mb-3">
          <div
            class="text-center col-12 col-sm-5 col-md-5 col-lg-5"
            style="height: 197px !important"
            v-show="
              balances?.sum_expense != 0 ||
              balances?.sum_income != 0 ||
              balances?.sum_order_discount != 0 ||
              balances?.sum_returned_price != 0 ||
              balances?.sum_trade_discount != 0 ||
              balances?.sum_trade_price != 0 ||
              balances?.sum_trade_tan_narx != 0
            "
          >
            <div class="chart" id="chart-content">
              <canvas
                class="chart-canvas"
                id="chart-consumption"
                height="197"
              ></canvas>
            </div>
          </div>
          <div
            class="col-12"
            v-if="balances != null"
            :class="
              balances?.sum_expense == 0 &&
              balances?.sum_income == 0 &&
              balances?.sum_order_discount == 0 &&
              balances?.sum_returned_price == 0 &&
              balances?.sum_trade_discount == 0 &&
              balances?.sum_trade_price == 0 &&
              balances?.sum_trade_tan_narx == 0
                ? 'col-sm-12 col-md-12 col-lg-12'
                : 'col-sm-7 col-md-7 col-lg-7'
            "
          >
            <div class="table-responsive">
              <table class="table mb-0 align-items-center">
                <tbody>
                  <tr>
                    <td>
                      <div class="px-2 py-0 d-flex">
                        <span class="badge bg-primary me-2">&nbsp;</span>
                        <div class="d-flex flex-column justify-content-center">
                          <h6 class="mb-0 text-sm">
                            {{ $t("statistic.sum_expense") }}
                          </h6>
                        </div>
                      </div>
                    </td>
                    <td class="text-sm text-center align-middle">
                      <span class="text-xs font-weight-bold"
                        >{{ balances?.sum_expense }} {{ $t("currency") }}</span
                      >
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <div class="px-2 py-0 d-flex">
                        <span class="badge bg-secondary me-3">&nbsp;</span>
                        <div class="d-flex flex-column justify-content-center">
                          <h6 class="mb-0 text-sm">
                            {{ $t("statistic.sum_income") }}
                          </h6>
                        </div>
                      </div>
                    </td>
                    <td class="text-sm text-center align-middle">
                      <span class="text-xs font-weight-bold"
                        >{{ balances?.sum_income }} {{ $t("currency") }}</span
                      >
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <div class="px-2 py-0 d-flex">
                        <span class="badge bg-info me-3">&nbsp;</span>
                        <div class="d-flex flex-column justify-content-center">
                          <h6 class="mb-0 text-sm">
                            {{ $t("statistic.discount") }}
                          </h6>
                        </div>
                      </div>
                    </td>
                    <td class="text-sm text-center align-middle">
                      <span class="text-xs font-weight-bold"
                        >{{
                          this.balances?.sum_order_discount +
                          balances?.sum_trade_discount
                        }}
                        {{ $t("currency") }}</span
                      >
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <div class="px-2 py-0 d-flex">
                        <span class="badge bg-success me-3">&nbsp;</span>
                        <div class="d-flex flex-column justify-content-center">
                          <h6 class="mb-0 text-sm">
                            {{ $t("statistic.sum_returned_price") }}
                          </h6>
                        </div>
                      </div>
                    </td>
                    <td class="text-sm text-center align-middle">
                      <span class="text-xs font-weight-bold"
                        >{{ balances?.sum_returned_price }}
                        {{ $t("currency") }}</span
                      >
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <div class="px-2 py-0 d-flex">
                        <span class="badge bg-warning me-3">&nbsp;</span>
                        <div class="d-flex flex-column justify-content-center">
                          <h6 class="mb-0 text-sm">
                            {{ $t("statistic.sum_trade_price") }}
                          </h6>
                        </div>
                      </div>
                    </td>
                    <td class="text-sm text-center align-middle">
                      <span class="text-xs font-weight-bold"
                        >{{ balances?.sum_trade_price }}
                        {{ $t("currency") }}</span
                      >
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <div class="px-2 py-0 d-flex">
                        <span class="badge bg-danger me-3">&nbsp;</span>
                        <div class="d-flex flex-column justify-content-center">
                          <h6 class="mb-0 text-sm">
                            {{ $t("statistic.sum_trade_tan_narx") }}
                          </h6>
                        </div>
                      </div>
                    </td>
                    <td class="text-sm text-center align-middle">
                      <span class="text-xs font-weight-bold"
                        >{{ balances?.sum_trade_tan_narx }}
                        {{ $t("currency") }}</span
                      >
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="filter"
    tabindex="-1"
    role="dialog"
    aria-labelledby="filter-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title" id="filter-label">
            {{ $t("filter") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-6">
              <div class="form-group">
                <label for="from_date">{{ $t("from_date") }}</label>
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="from_time"
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("to_date") }}</label>
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="to_time"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-sm bg-gradient-danger"
            @click="
              from_time = '';
              to_time = '';
            "
          >
            <i class="fas fa-trash"></i>
          </button>
          <button
            class="btn btn-sm bg-gradient-secondary"
            data-bs-dismiss="modal"
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn btn-sm bg-gradient-primary"
            @click="createChart()"
            data-bs-dismiss="modal"
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import Chart from "chart.js/auto";

export default {
  name: "consumption-by-room-chart",
  data() {
    return {
      balances: null,
      from_time: new Date(new Date().getFullYear(), new Date().getMonth(), 1)
        .toISOString()
        .substring(0, 10),
      to_time: new Date(
        new Date().getFullYear(),
        new Date().getMonth(),
        new Date().getDate() + 1
      )
        .toISOString()
        .substring(0, 10),
    };
  },
  methods: {
    createChart() {
      document.getElementById("chart-consumption").remove();
      let canvas = document.createElement("canvas");
      canvas.id = "chart-consumption";
      canvas.className = "chart-canvas";
      canvas.height = "197";
      document.getElementById("chart-content").append(canvas);
      var ctx1 = document.getElementById("chart-consumption").getContext("2d");

      var gradientStroke1 = ctx1.createLinearGradient(0, 230, 0, 50);

      gradientStroke1.addColorStop(1, "rgba(203,12,159,0.2)");
      gradientStroke1.addColorStop(0.2, "rgba(72,72,176,0.0)");
      gradientStroke1.addColorStop(0, "rgba(203,12,159,0)"); //purple colors

      api
        .get_statistics(
          this.$route.query.market_id,
          this.from_time,
          this.to_time
        )
        .then((res) => {
          this.balances = res.data;
          new Chart(ctx1, {
            type: "pie",
            data: {
              labels: [
                this.$t("statistic.sum_expense"),
                this.$t("statistic.sum_income"),
                this.$t("statistic.discount"),
                this.$t("statistic.sum_returned_price"),
                this.$t("statistic.sum_trade_price"),
                this.$t("statistic.sum_trade_tan_narx"),
              ],
              datasets: [
                {
                  label: "Statistics",
                  weight: 9,
                  // cutout: 90,
                  tension: 0.9,
                  pointRadius: 1,
                  borderWidth: 1,
                  backgroundColor: [
                    "#5e72e4",
                    "#8392ab",
                    "#11cdef",
                    "#2dce89",
                    "#fb6340",
                    "#f5365c",
                  ],

                  data: [
                    this.balances?.sum_expense,
                    this.balances?.sum_income,
                    this.balances?.sum_order_discount +
                      this.balances?.sum_trade_discount,
                    this.balances?.sum_returned_price,
                    this.balances?.sum_trade_price,
                    this.balances?.sum_trade_tan_narx,
                  ],
                  fill: false,
                },
              ],
            },
            options: {
              responsive: true,
              maintainAspectRatio: false,
              plugins: {
                legend: {
                  display: false,
                },
              },
              interaction: {
                intersect: false,
                mode: "index",
              },
              scales: {
                y: {
                  grid: {
                    drawBorder: false,
                    display: false,
                    drawOnChartArea: false,
                    drawTicks: false,
                  },
                  ticks: {
                    display: false,
                  },
                },
                x: {
                  grid: {
                    drawBorder: false,
                    display: false,
                    drawOnChartArea: false,
                    drawTicks: false,
                  },
                  ticks: {
                    display: false,
                  },
                },
              },
            },
          });
        });
    },
  },
  mounted() {
    this.createChart();
  },
};
</script>
