<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">{{ $t("customer.title") }}</h6>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col-md-6 my-1">
                <button
                  class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
                  data-bs-toggle="modal"
                  data-bs-target="#add-customer"
                >
                  {{ $t("customer.add-button") }}
                </button>
              </div>
              <div class="col-md-6 my-1">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    get(1, limit);
                    $store.dispatch('setloading', false);
                  "
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-md-4 col-lg-3 col-sm-6 my-2"
            v-for="customer in customers"
            :key="customer"
          >
            <div class="card h-100 shadow-sm border">
              <div class="card-body">
                <h6 class="mb-0">
                  <b> {{ customer.id }}.</b>
                </h6>
                <hr class="my-1" />
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-user"></i>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{ customer.name }}
                  </h6>
                </div>
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-id-card-clip"></i>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{
                      customer.type == "premium"
                        ? $t("customer.premium")
                        : customer.type == "sifat"
                        ? $t("customer.quality")
                        : customer.type == "narx"
                        ? $t("customer.price")
                        : customer.type == `qora ro'yxat`
                        ? $t("customer.black-list")
                        : customer.type
                    }}
                  </h6>
                </div>
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-phone"></i>
                  </div>
                  <h6 class="ms-2 mb-0 d-flex justify-content-between">
                    <span v-if="!customer.show_phones">
                      <a :href="`tel:${customer.phones[0]?.number}`"
                        >{{ customer.phones[0]?.number }}
                      </a>
                    </span>
                    <span
                      v-else
                      @click="customer.show_phones = !customer.show_phones"
                    >
                      <a
                        class="d-block"
                        :href="`tel:${phone.number}`"
                        v-for="phone in customer.phones"
                        :key="phone"
                        >{{ phone.number }}
                      </a>
                    </span>
                    <span
                      class="cursor-pointer ms-1"
                      v-if="customer.phones.length > 1 && !customer.show_phones"
                      @click="customer.show_phones = !customer.show_phones"
                      >...</span
                    >
                  </h6>
                </div>
                <div class="row">
                  <div class="col">
                    <button
                      class="btn btn-sm bg-gradient-warning mb-0 w-100 py-2"
                      data-bs-toggle="modal"
                      data-bs-target="#put-customer"
                      @click="
                        $refs.customer.get_property({
                          id: customer.id,
                          name: customer.name,
                          type: customer.type,
                          phones: customer.phones,
                        })
                      "
                    >
                      <i class="fa-solid fa-pen"></i>
                    </button>
                  </div>
                  <div class="col">
                    <router-link
                      :to="{
                        name: 'CustomerHistory',
                        query: {
                          id: customer.id,
                          name: customer.name,
                          market_id: $route.query.market_id,
                        },
                      }"
                      class="btn btn-sm bg-gradient-primary mb-0 w-100 py-2"
                    >
                      <i class="fa-solid fa-clock-rotate-left"></i>
                    </router-link>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="customers" />
            <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
          </div>
        </div>
      </div>
    </div>
  </div>
  <customer-modals @add="add" ref="customer" @put="put" />
</template>

<script>
import * as api from "@/server/api";
import CustomerModals from "@/components/modals/CustomerModals.vue";
import Pagination from "@/components/pagination.vue";
import DataNotfound from "@/components/dataNotfound.vue";

export default {
  components: { CustomerModals, Pagination, DataNotfound },
  name: "customer",
  data() {
    return {
      customers: [],
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
    };
  },
  methods: {
    get(page, limit) {
      api
        .customers(this.search, 0, this.$route.query.market_id, page, limit)
        .then((res) => {
          this.pages = res.data.pages;
          this.page = res.data.current_page;
          this.limit = res.data.limit;
          res.data.data.forEach((element) => {
            element.show_phones = false;
          });
          this.customers = res.data.data;
        });
    },
    add(body) {
      body.phones.forEach((element) => {
        element.phone = `${element.dial_code}${element.phone}`;
      });
      api.create_customer(body).then(() => {
        body.name = "";
        body.type = "";
        body.phones = [{ phone: "", dial_code: "+7" }];
        api.success("close");
        this.get(this.page, this.limit);
      });
    },
    put(body) {
      body.phones.forEach((element) => {
        element.phone = element.dial_code + "" + element.value;
      });
      api.update_customer(body).then(() => {
        api.success("close-put");
        this.get(this.page, this.limit);
      });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>
