<template>
  <main class="mt-0 main-content">
    <section>
      <div class="page-header min-vh-100">
        <div class="container">
          <div class="row">
            <div
              class="mx-auto col-xl-4 col-lg-5 col-md-7 d-flex flex-column mx-lg-0"
            >
              <div class="card card-plain">
                <div class="pb-0 card-header text-start">
                  <h4 class="font-weight-bolder">{{ $t("sign-in.title") }}</h4>
                  <p class="mb-0">{{ $t("sign-in.description") }}</p>
                </div>
                <div class="card-body">
                  <form role="form" @submit.prevent="signIn()">
                    <div class="mb-3">
                      <div class="form-group">
                        <input
                          type="text"
                          :placeholder="$t('sign-in.placeholders.username')"
                          class="form-control form-control-lg"
                          v-model="body.username"
                          minlength="5"
                          autocomplete="on"
                          required
                        />
                      </div>
                    </div>
                    <div class="mb-3">
                      <input
                        type="password"
                        :placeholder="$t('sign-in.placeholders.password')"
                        class="form-control form-control-lg"
                        minlength="5"
                        autocomplete="on"
                        v-model="body.password"
                        required
                      />
                    </div>
                    <switch-language />
                    <div class="text-center">
                      <button
                        class="btn btn-lh bg-gradient-primary mt-3 mb-0 w-100"
                        type="submit"
                      >
                        {{ $t("sign-in.enter") }}
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
            <div
              class="top-0 my-auto text-center col-6 d-lg-flex d-none h-100 pe-0 position-absolute end-0 justify-content-center flex-column"
            >
              <div
                class="position-relative bg-gradient-dark h-100 m-3 px-7 border-radius-lg d-flex flex-column justify-content-center overflow-hidden sign-in-column-bg"
              >
                <span class="mask bg-gradient-dark opacity-6"></span>
                <h4
                  class="mt-5 text-white font-weight-bolder position-relative"
                >
                  "F9 Group"
                </h4>
                <p class="text-white position-relative">
                  {{ $t("sign-in-column") }}.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import SwitchLanguage from "@/components/switchLanguage.vue";
import * as api from "@/server/api";
const body = document.getElementsByTagName("body")[0];

export default {
  name: "signin",
  data() {
    return {
      body: {
        username: "",
        password: "",
      },
      access_token: localStorage["access_token"],
    };
  },
  components: {
    SwitchLanguage,
  },
  methods: {
    signIn() {
      api.signin(this.body).then((response) => {
        this.require(response.data);
      });
    },
    require(data) {
      localStorage["access_token"] = data.access_token;
      localStorage["role"] = data.user.role;
      localStorage["user_id"] = data.user.id;
      if (data.user.role == "seller_admin" || data.user.role == "seller") {
        localStorage["market_id"] = data.user.market_id;
        location.href = `/market?id=${data.user.market_id}`;
      } else window.location.href = api.select_default_page(data.user.role);
    },
  },
  created() {
    this.$store.state.hideConfigButton = true;
    this.$store.state.showNavbar = false;
    this.$store.state.showSidenav = false;
    this.$store.state.showFooter = false;
    body.classList.remove("bg-gray-100");
  },
  beforeUnmount() {
    this.$store.state.hideConfigButton = false;
    this.$store.state.showNavbar = true;
    this.$store.state.showSidenav = true;
    this.$store.state.showFooter = true;
    body.classList.add("bg-gray-100");
  },
};
</script>
