<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-3 my-1">
            <h5 class="mb-0">{{ $t("routers.warehouse") }}</h5>
          </div>
          <div class="col-md-3 my-1">
            <div class="dropdown">
              <button
                class="form-control border-secondary-custom form-control-sm shadow-none mb-0 w-100"
                data-bs-toggle="dropdown"
                aria-expanded="false"
                @click="get_m_type(false)"
              >
                {{ material_type.name }}
              </button>
              <ul
                class="dropdown-menu w-100"
                aria-labelledby="dropdownMenuButton1"
              >
                <input
                  type="text"
                  class="form-control"
                  :placeholder="$t('search')"
                  v-model="material_type.search"
                  @keyup="
                    get_m_type(false);
                    $store.dispatch('setloading', false);
                  "
                />
                <div
                  class="scroll-25 material-type-stores"
                  @scroll="
                    scroll_material_type();
                    $store.dispatch('setloading', false);
                  "
                >
                  <li>
                    <a
                      class="dropdown-item"
                      @click="
                        material_type_id = 0;
                        get(page, limit);
                      "
                    >
                      {{ $t("all") }}
                    </a>
                  </li>
                  <li
                    @click="
                      material_type_id = item.Material_Type.id;
                      material_type.name = material_type.material_types.find(
                        (e) => material_type_id == e.Material_Type.id
                      ).Material_Type.name;
                      get(page, limit);
                    "
                    v-for="item in material_type.material_types"
                    :key="item"
                  >
                    <a class="dropdown-item">
                      {{ item.Material_Type.name }}
                    </a>
                  </li>
                </div>
                <data-notfound
                  :data="material_type.material_types"
                  class="text-lg"
                />
              </ul>
            </div>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col-md-6 my-1">
                <select
                  class="form-select form-select-sm"
                  v-model="plant_id"
                  @change="get(page, limit)"
                >
                  <option value="0">{{ $t("all") }}</option>
                  <option
                    :value="plant.id"
                    v-for="plant in plants"
                    :key="plant"
                  >
                    {{ plant.name }}
                  </option>
                </select>
              </div>
              <div class="col-md-6 my-1">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    get(1, limit);
                    $store.dispatch('setloading', false);
                  "
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-lg-4 col-md-6 col-sm-12 my-2"
            v-for="material in materials"
            :key="material"
          >
            <div class="card card-zoom h-100 shadow-sm">
              <div class="card-body">
                <div class="row">
                  <div class="col">
                    <h5 class="card-title text-dark mb-0">
                      {{ material.material?.name }}
                    </h5>
                    <h6 class="text-muted mb-0" v-if="plant_id == 0">
                      {{ $t("material-history.plant") }}:
                      {{ material.plant?.name }}
                    </h6>
                    <h6 class="text-muted mb-0">
                      {{
                        `${$t("supply.value")}: ${number_format(
                          material.value
                        )} ${material.olchov?.name}`
                      }}
                    </h6>
                  </div>
                  <div class="col-auto">
                    <div class="dropdown">
                      <button
                        class="btn shadow-none mb-0"
                        type="button"
                        id="proche"
                        data-bs-toggle="dropdown"
                      >
                        <i class="fa-solid fa-ellipsis"></i>
                      </button>
                      <ul class="dropdown-menu" aria-labelledby="proche">
                        <li
                          @click="
                            $refs.supplymodal.select_material_id(material)
                          "
                        >
                          <a
                            class="dropdown-item cursor-pointer"
                            data-bs-toggle="modal"
                            data-bs-target="#take-supply"
                            @click="
                              $refs.supplymodal.select_material_id(material)
                            "
                            >{{ $t("supply.take") }}</a
                          >
                        </li>
                        <li>
                          <a
                            class="dropdown-item cursor-pointer"
                            data-bs-toggle="modal"
                            data-bs-target="#transfer"
                            @click="
                              $refs.transfer.get_material_plants(
                                material.material?.id,
                                material.olchov?.name
                              )
                            "
                            >{{ $t("material-transfer.title") }}</a
                          >
                        </li>
                        <li>
                          <router-link
                            class="dropdown-item cursor-pointer"
                            :to="{
                              name: 'SupplyHistory',
                              query: {
                                id: material.material?.id,
                                material_id: $route.query.id,
                                name: material.material?.name,
                              },
                            }"
                          >
                            {{ $t("history") }}
                          </router-link>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="materials" />
            <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
          </div>
        </div>
      </div>
    </div>
  </div>
  <supply-modals
    ref="supplymodal"
    type="false"
    take_from="material"
    :plants="plants"
    @get="get(page, limit)"
  />
  <transfer-modal ref="transfer" :plants="plants" @get="get(page, limit)" />
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";
import SupplyModals from "@/components/modals/SupplyModals.vue";
import TransferModal from "@/components/modals/TransferModal.vue";

export default {
  components: { dataNotfound, Pagination, SupplyModals, TransferModal },
  name: "material-stores",
  data() {
    return {
      materials: [],
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      plant_id: 0,
      material_type_id: 0,
      plants: [],
      image: api.image,
      number_format: api.number_format,
      material_type: {
        pages: 0,
        page: 1,
        search: "",
        material_types: [],
        name: this.$t("supply.material_type"),
      },
    };
  },
  methods: {
    get(page, limit) {
      api
        .get_material_stores(
          page,
          limit,
          this.search,
          this.material_type_id,
          this.plant_id
        )
        .then((res) => {
          this.pages = res.data.page_count;
          this.page = res.data.current_page;
          this.limit = res.data.page_limit;
          this.materials = res.data.data;
        });
    },
    get_plants() {
      api.get_plants(1, 1000, "").then((res) => {
        this.plants = res.data.data;
        this.get(this.page, this.limit);
      });
    },
    image_view(src) {
      if (src) {
        document.getElementById("full-image").src = src;
        document.getElementById("image-viewer").style.display = "block";
      } else {
        document.getElementById("image-viewer").style.display = "none";
      }
    },
    get_m_type(scroll) {
      this.material_type.page = scroll ? this.material_type.page : 1;
      api
        .get_material_types(
          scroll ? this.material_type.page + 1 : this.material_type.page,
          10,
          this.material_type.search,
          this.plant_id
        )
        .then((res) => {
          this.material_type.pages = res.data.page_count;
          this.material_type.page = res.data.current_page;
          this.material_type.material_types = scroll
            ? this.material_type.material_types.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll_material_type() {
      let div = document.querySelector(".material-type-stores");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.material_type.page < this.material_type.pages) {
          this.get_m_type(true);
        }
      }
    },
  },
  created() {
    this.get_plants();
  },
};
</script>
