<template>
  <div class="container-fluid py-4">
    <div class="card">
      <div class="card-header">
        <div class="row">
          <div class="col-md-4">
            <h6 class="mb-0">{{ $t("orders-history.title") }}</h6>
          </div>
          <div class="col-md-4"></div>
          <div class="col-md-4">
            <button
              class="btn btn-sm mb-0 px-3 py-2 bg-gradient-primary float-end"
              data-bs-toggle="modal"
              data-bs-target="#filter"
            >
              <i class="fa fa-filter"></i>
            </button>
          </div>
        </div>
      </div>
      <div class="card-body">
        <div class="table-responsive">
          <table class="table table-sm table-hover">
            <thead>
              <tr>
                <th>{{ $t("orders-history.table.type") }}</th>
                <th>{{ $t("orders-history.table.date") }}</th>
                <th>{{ $t("orders-history.table.delivery-date") }}</th>
                <th>{{ $t("orders-history.table.customer") }}</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in orders.data" :key="item">
                <td>
                  {{
                    item.delivery_date == "0000-00-00"
                      ? $t("orders-history.table.order")
                      : $t("orders-history.table.pre-order")
                  }}
                </td>
                <td>{{ item.time.substring(0, 10) }}</td>
                <td>
                  {{
                    item.delivery_date == "0000-00-00" ? "" : item.delivery_date
                  }}
                </td>
                <td>{{ item.customer ? item.customer.name : "" }}</td>
                <td>
                  <button
                    class="btn btn-sm m-0 px-2 py-1 bg-gradient-primary"
                    @click="$refs.orderModal.start(item.id)"
                  >
                    <i class="fa fa-eye"></i>
                  </button>
                </td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <td colspan="5">
                  <pagination
                    :page="orders.current_page"
                    :pages="orders.pages"
                    :limit="orders.limit"
                    @get="getOrders"
                  />
                </td>
              </tr>
            </tfoot>
          </table>
        </div>
      </div>
    </div>
  </div>

  <orderModal ref="orderModal" />

  <div class="modal fade" id="filter">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title">
            {{ $t("filter") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row gap-1">
            <div class="col-12">
              <label for="customerDropdown">{{ $t("order.customer") }}</label>
              <div class="dropdown">
                <button
                  id="customerDropdown"
                  class="btn btn-sm w-100 mb-0 dropdown-toggle"
                  data-bs-toggle="dropdown"
                  @click="getCustomers()"
                >
                  {{
                    filter.customer
                      ? filter.customer?.name
                      : $t("order.customer")
                  }}
                </button>
                <div
                  class="dropdown-menu w-100 p-1"
                  aria-labelledby="customerDropdown"
                >
                  <input
                    type="text"
                    class="form-control form-control-sm my-1"
                    :placeholder="$t('search')"
                    v-model="search_customer"
                    @keyup="getCustomers()"
                  />
                  <ul
                    class="list-group scroll-25 p-1"
                    @scroll="scrollCustomers($event)"
                  >
                    <li
                      class="list-group-item"
                      v-for="item in customers?.data"
                      :key="item"
                      @click="filter.customer = item"
                    >
                      {{ item.name }}
                    </li>
                  </ul>
                </div>
              </div>
            </div>
            <div class="col-12">
              <div class="form-group">
                <label for="from_date">{{ $t("from_date") }}</label>
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="filter.from_time"
                />
              </div>
            </div>
            <div class="col-12">
              <div class="form-group">
                <label for="to_date">{{ $t("to_date") }}</label>
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="filter.to_time"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-sm bg-gradient-danger"
            data-bs-dismiss="modal"
            @click="
              filter = { customer: null, from_time: '', to_time: '' };
              getOrders(1, 25);
            "
          >
            <i class="fas fa-trash"></i>
          </button>
          <button
            class="btn btn-sm bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-filter
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn btn-sm bg-gradient-primary"
            data-bs-dismiss="modal"
            @click="getOrders(1, 25)"
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "../../server/api";
import pagination from "@/components/pagination.vue";
import orderModal from "../../components/modals/orderModal.vue";
export default {
  name: "ordersHistory",
  components: { pagination, orderModal },
  data() {
    return {
      filter: {
        customer: null,
        from_time: "",
        to_time: "",
      },
      search_customer: "",
      customers: null,
      orders: {
        current_page: 1,
        pages: 1,
        limit: 25,
        data: [],
      },
    };
  },
  created() {
    this.getOrders(1, 25);
  },
  methods: {
    getOrders(page, limit) {
      const customer_id = this.filter.customer ? this.filter.customer.id : 0;
      api
        .orders(
          this.filter.from_time,
          this.filter.to_time,
          customer_id,
          0,
          "true",
          0,
          this.$route.query.market_id,
          page,
          limit
        )
        .then((res) => {
          this.orders = res.data;
        });
    },
    getCustomers() {
      api
        .customers(this.search_customer, 0, this.$route.query.market_id, 1, 25)
        .then((res) => {
          this.customers = res.data;
        });
    },
    scrollCustomers(event) {
      const element = event.target;
      if (element.scrollTop + element.clientHeight >= element.scrollHeight) {
        if (this.customers.current_page < this.customers.pages) {
          api
            .customers(
              this.search_customer,
              0,
              this.$route.query.market_id,
              this.customers.current_page,
              25
            )
            .then((res) => {
              this.customers.current_page = res.data.current_page;
              this.customers.pages = res.data.pages;
              this.customers.data = this.customers.data.concat(res.data.data);
            });
        }
      }
    },
  },
};
</script>
