<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">{{ $route.query.name }}</h6>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col-auto">
                <div class="form-check">
                  <input
                    class="form-check-input"
                    type="checkbox"
                    id="flexCheckChecked"
                    @change="
                      all_cycles = !all_cycles;
                      get();
                    "
                  />
                  <label
                    class="form-check-label fw-bold my-1"
                    for="flexCheckChecked"
                  >
                    {{ $t("cycle.all_cycle") }}
                  </label>
                </div>
              </div>
              <div class="col">
                <button
                  class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
                  data-bs-toggle="modal"
                  data-bs-target="#attachment-cycle"
                  @click="get_cycles()"
                >
                  {{ $t("cycle.add-button") }}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row my-2 cycle" v-for="(cycle, i) in cycles" :key="cycle">
          <div :class="all_cycles ? 'col-12' : 'col-10 col-sm-10 col-md-11'">
            <div class="card shadow-sm">
              <div class="card-body py-3 cycle">
                <div class="row d-flex align-items-center">
                  <div class="col-md context">
                    <h5 class="card-title mb-0">
                      <b class="text-primary"> {{ cycle.cycle.name }} </b>
                    </h5>
                  </div>
                  <div class="col-auto">
                    <button
                      class="btn btn-sm bg-gradient-danger mb-0 px-3 w-100"
                      @click="
                        remove({
                          cycle_id: cycle.cycle_id,
                          product_id: cycle.product_id,
                          id: cycle.id,
                          disabled: true,
                        })
                      "
                      v-if="!all_cycles"
                    >
                      <i class="fas fa-trash"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div
            class="col-2 col-sm-2 col-md-1 d-flex justify-content-center align-items-center"
            v-if="!all_cycles"
          >
            <div>
              <button
                class="btn btn-sm bg-gradient-primary text-white px-3 mb-0 d-block"
                :disabled="i == 0"
                @click="update_cycle_position(cycle.id, 'top')"
              >
                <i class="fa-solid fa-arrow-up"></i>
              </button>
              <button
                class="btn btn-sm bg-gradient-primary text-white px-3 mb-0 my-1"
                :disabled="cycles.length - 1 == i"
                @click="update_cycle_position(cycle.id, 'bottom')"
              >
                <i class="fa-solid fa-arrow-down"></i>
              </button>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <data-notfound :data="cycles" />
          </div>
        </div>
      </div>
    </div>
  </div>

  <div
    class="modal fade"
    id="attachment-cycle"
    tabindex="-1"
    role="dialog"
    aria-labelledby="attachment-cycle-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="attachment-cycle-label">
            {{ $t("cycle.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="attachment">
          <div class="modal-body">
            <div class="list-group">
              <div class="list-group-item">
                <label class="d-flex align-items-center">
                  <div class="form-check">
                    <input
                      class="form-check-input me-1"
                      type="checkbox"
                      v-model="checkAll"
                    />
                  </div>
                  <b class="text-primary">{{ $t("all") }} </b>
                </label>
              </div>
              <div
                class="list-group-item d-flex align-items-center"
                v-for="cycle in attachment_cycles"
                :key="cycle"
              >
                <label class="d-flex align-items-center">
                  <div class="form-check">
                    <input
                      class="form-check-input me-1"
                      type="checkbox"
                      :value="{ cycle_id: cycle.id, product_id: product_id }"
                      v-model="body"
                    />
                  </div>
                  <b>
                    {{ cycle.name }}
                  </b>
                </label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("cycle.attachment") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import DataNotfound from "@/components/dataNotfound.vue";

export default {
  components: {
    DataNotfound,
  },
  name: "Cycle",
  data() {
    return {
      cycles: [],
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      product_id: Number(this.$route.query.id),
      attachment_cycles: [],
      body: [],
      all_cycles: false,
    };
  },
  methods: {
    get() {
      api
        .product_cycles(1, 100, "", 0, this.product_id, this.all_cycles)
        .then((res) => {
          this.cycles = res.data.data;
        });
    },
    update_cycle_position(id, direction) {
      api.cycle_position({ id: id, direction: direction }).then(() => {
        this.get();
      });
    },
    get_cycles() {
      api
        .get_cycles(1, 100, "", this.$route.query.product_type_id)
        .then((res) => {
          this.attachment_cycles = res.data.data;
          this.body = [];
          this.cycles.forEach((element) => {
            this.body.push({
              cycle_id: element.cycle_id,
              product_id: this.product_id,
            });
          });
        });
    },
    attachment() {
      api.create_product_cycle(this.body).then(() => {
        this.body = [];
        api.success("close");
        this.get();
      });
    },
    remove(body) {
      api.update_product_cycle(body).then(() => {
        api.success();
        this.get();
      });
    },
  },
  created() {
    this.get();
  },
  computed: {
    checkAll: {
      get: function () {
        return this.attachment_cycles
          ? this.body.length == this.attachment_cycles.length
          : false;
      },
      set: function (value) {
        var values = [];
        var th = this;
        if (value) {
          this.attachment_cycles.forEach(function (product) {
            values.push({
              cycle_id: product.id,
              product_id: th.product_id,
            });
          });
        }
        this.body = values;
      },
    },
  },
};
</script>

<style scoped>
@media (max-width: 768px) {
  .col-md.context h6 {
    font-size: 16px !important;
  }
}
</style>
