<template>
  <div
    class="modal fade"
    id="add-product"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-product-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-product-label">
            {{ $t("product.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('add', body)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("product.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="body.name"
                    :placeholder="$t('product.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="image">{{ $t("product.image") }}</label>
                  <input
                    id="image"
                    type="file"
                    class="form-control"
                    @change="body.image = $event.target.files[0]"
                    :placeholder="$t('product.image')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="olchov">{{ $t("product.olchov") }}</label>
                  <select
                    id="olchov"
                    class="form-select"
                    v-model="body.olchov_id"
                    required
                  >
                    <option
                      :value="olchov.id"
                      v-for="olchov in olchovs"
                      :key="olchov"
                    >
                      {{ olchov.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="unit">{{ $t("product.unit.title") }}</label>
                  <select
                    id="unit"
                    class="form-select"
                    v-model="body.unit"
                    required
                  >
                    <option value="true">{{ $t("product.unit.true") }}</option>
                    <option value="false">
                      {{ $t("product.unit.false") }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="alarm">{{ $t("product.alarm") }}</label>
                  <input
                    id="alarm"
                    type="number"
                    min="0"
                    class="form-control"
                    @focus="
                      body.alarm_value == 0
                        ? (body.alarm_value = null)
                        : body.alarm_value
                    "
                    @focusout="
                      body.alarm_value == null
                        ? (body.alarm_value = 0)
                        : body.alarm_value
                    "
                    v-model="body.alarm_value"
                    :placeholder="$t('product.alarm')"
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="production_price"
                    >{{ $t("product.production_price") }}
                    {{
                      $route.query.hasSize === "true"
                        ? ` - ${$t("pre-order.sm")}`
                        : ""
                    }}</label
                  >
                  <div class="input-group">
                    <input
                      id="production_price"
                      type="number"
                      class="form-control"
                      @focus="
                        body.plant_price == 0
                          ? (body.plant_price = null)
                          : body.plant_price
                      "
                      @focusout="
                        body.plant_price == null
                          ? (body.plant_price = 0)
                          : body.plant_price
                      "
                      v-model="body.plant_price"
                      :placeholder="$t('product.production_price')"
                      required
                    />
                    <span class="input-group-text">{{ $t("currency") }}</span>
                  </div>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="price"
                    >{{ $t("product.price") }}
                    {{
                      $route.query.hasSize === "true"
                        ? ` - ${$t("pre-order.sm")}`
                        : ""
                    }}</label
                  >
                  <div class="input-group">
                    <input
                      id="price"
                      type="number"
                      class="form-control"
                      v-model="body.trade_price"
                      @focus="
                        body.trade_price == 0
                          ? (body.trade_price = null)
                          : body.trade_price
                      "
                      @focusout="
                        body.trade_price == null
                          ? (body.trade_price = 0)
                          : body.trade_price
                      "
                      :placeholder="$t('product.price')"
                      required
                    />
                    <span class="input-group-text">{{ $t("currency") }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="update-product"
    tabindex="-1"
    role="dialog"
    aria-labelledby="update-product-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="update-product-label">
            {{ $t("product.update") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('put', data)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("product.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="data.name"
                    :placeholder="$t('product.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="image">{{ $t("product.image") }}</label>
                  <input
                    id="image"
                    type="file"
                    class="form-control"
                    @change="data.image = $event.target.files[0]"
                    :placeholder="$t('product.image')"
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="olchov">{{ $t("product.olchov") }}</label>
                  <select
                    id="olchov"
                    class="form-select"
                    v-model="data.olchov_id"
                    required
                  >
                    <option
                      :value="olchov.id"
                      v-for="olchov in olchovs"
                      :key="olchov"
                    >
                      {{ olchov.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="unit">{{ $t("product.unit.title") }}</label>
                  <select
                    id="unit"
                    class="form-select"
                    v-model="data.unit"
                    required
                  >
                    <option value="true">{{ $t("product.unit.true") }}</option>
                    <option value="false">
                      {{ $t("product.unit.false") }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="alarm">{{ $t("product.alarm") }}</label>
                  <input
                    id="alarm"
                    type="number"
                    min="0"
                    class="form-control"
                    v-model="data.alarm_value"
                    :placeholder="$t('product.alarm')"
                  />
                </div>
              </div>

              <div class="col-md-6">
                <div class="form-group">
                  <label for="production_price"
                    >{{ $t("product.production_price") }}
                    {{
                      $route.query.hasSize === "true"
                        ? ` - ${$t("pre-order.sm")}`
                        : ""
                    }}</label
                  >
                  <div class="input-group">
                    <input
                      id="production_price"
                      type="number"
                      class="form-control"
                      v-model="data.plant_price"
                      :placeholder="$t('product.production_price')"
                      required
                    />
                    <span class="input-group-text">{{ $t("currency") }}</span>
                  </div>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="price"
                    >{{ $t("product.price") }}
                    {{
                      $route.query.hasSize === "true"
                        ? ` - ${$t("pre-order.sm")}`
                        : ""
                    }}</label
                  >
                  <div class="input-group">
                    <input
                      id="price"
                      type="number"
                      class="form-control"
                      v-model="data.trade_price"
                      :placeholder="$t('product.price')"
                      required
                    />
                    <span class="input-group-text">{{ $t("currency") }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-put
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("update") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="clone-product"
    tabindex="-1"
    role="dialog"
    aria-labelledby="clone-product-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="clone-product-label">
            {{ $t("product.clone") }}
          </h5>
        </div>
        <form
          @submit.prevent="
            $emit('clone', data.id, data.name, sizes, file);
            sizes = [
              {
                size1: '',
                size2: '',
                size3: '',
              },
            ];
            file = '';
          "
        >
          <div class="modal-body">
            <div class="row gap-1">
              <div class="col-12">
                <input
                  type="text"
                  class="form-control"
                  required
                  :placeholder="$t('product.name')"
                  v-model="data.name"
                />
              </div>
              <!-- <div class="col-12">
                <input
                  id="image"
                  type="file"
                  class="form-control"
                  @change="file = $event.target.files[0]"
                  :placeholder="$t('product.file')"
                />
              </div> -->
              <div class="col-12" v-for="item in sizes" :key="item">
                <div class="row">
                  <div class="col">
                    <div class="input-group input-group-sm">
                      <input
                        type="text"
                        class="form-control"
                        :placeholder="$t('width')"
                        v-model="item.size1"
                      />
                      <div class="input-group-text">cm</div>
                    </div>
                  </div>
                  <div class="col">
                    <div class="input-group input-group-sm">
                      <input
                        type="text"
                        class="form-control"
                        :placeholder="$t('height')"
                        v-model="item.size2"
                      />
                      <div class="input-group-text">cm</div>
                    </div>
                  </div>
                  <div class="col">
                    <div class="input-group input-group-sm">
                      <input
                        type="text"
                        class="form-control"
                        :placeholder="$t('altitude')"
                        v-model="item.size3"
                      />
                      <div class="input-group-text">cm</div>
                      <button
                        v-if="sizes.length > 1"
                        class="btn bg-gradient-danger m-0"
                        @click="sizes.splice(sizes.indexOf(item), 1)"
                      >
                        <i class="fa fa-trash"></i>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-primary"
              @click="
                sizes.push({
                  size1: '',
                  size2: '',
                  size3: '',
                })
              "
            >
              <i class="fa fa-plus px-1"></i>
            </button>
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-clone
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("save") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "add-product",
  props: ["olchovs", "item"],
  emits: ["add", "put", "clone"],
  data() {
    return {
      body: {
        name: "",
        image: "",
        olchov_id: this.$route.query.olchov_id,
        product_type_id: this.$route.query.id,
        unit: "",
        alarm_value: 0,
        trade_price: 0,
        plant_price: 0,
      },
      sizes: [
        {
          size1: "",
          size2: "",
          size3: "",
        },
      ],
      file: "",
    };
  },
  computed: {
    data() {
      return this.item;
    },
  },
};
</script>
