<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row d-flex align-items-center">
          <div class="col-6 my-1">
            <h6 class="mb-0">{{ $t("loan.title") }}</h6>
          </div>
          <div class="col-6 my-1">
            <div class="nav-wrapper position-relative end-0">
              <ul
                class="p-1 bg-transparent nav nav-pills nav-fill"
                role="tablist"
                id="pills-tab"
              >
                <li class="nav-item cursor-pointer">
                  <a
                    class="px-0 py-1 mb-0 nav-link active"
                    @click="
                      content = 'income';
                      get_income(income.page, income.limit);
                    "
                  >
                    <span class="ms-1">{{
                      $t("loan.history.pay-history")
                    }}</span>
                  </a>
                </li>
                <li class="nav-item cursor-pointer">
                  <a
                    class="px-0 py-1 mb-0 nav-link"
                    @click="
                      content = 'loan';
                      get_loan();
                    "
                  >
                    <span class="ms-1">{{
                      $t("loan.history.loan-order")
                    }}</span>
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row" v-if="content == 'income'">
          <div
            class="col-md-4 col-lg-3 col-sm-6 my-2"
            v-for="(income, i) in incomes"
            :key="income"
          >
            <div class="card h-100 shadow-sm border">
              <div class="card-body">
                <h6 class="mb-0">
                  <b> {{ i + 1 }}.</b>
                </h6>
                <hr class="my-1" />
                <div class="ms-2">
                  <div class="d-flex align-items-center text-wrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fas fa-user me-3"></i>
                    </div>
                    <h6 class="mb-0">
                      {{ income.user.name }}
                    </h6>
                  </div>
                  <div class="d-flex align-items-center text-nowrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fas fa-money-bill me-3"></i>
                    </div>
                    <h6 class="mb-0">
                      {{
                        Intl.NumberFormat().format(income.money) +
                        " " +
                        $t("currency")
                      }}
                    </h6>
                  </div>
                  <div class="d-flex align-items-center text-nowrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="far fa-calendar me-3"></i>
                    </div>
                    <h6 class="mb-0">{{ format_date(income.time) }}</h6>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="incomes" />
            <pagination
              :page="income.page"
              :pages="income.pages"
              :limit="income.limit"
              @get="get_income"
            />
          </div>
        </div>
        <div class="row" v-else>
          <div class="col-md-12">
            <div class="card shadow-sm border">
              <div class="card-body">
                <h5 class="mb-0 d-flex justify-content-between">
                  <b>
                    {{
                      `${$t("loan.order")}  ${
                        loan?.Loans.order.ordinal_number
                      }.`
                    }}
                  </b>
                  {{ format_date(loan?.Loans.order.time) }}
                </h5>
                <hr class="my-1" />
                <div class="row">
                  <div class="col">
                    <h6 class="mb-0 d-flex justify-content-between">
                      {{ $t("loan.history.order-balance") }}
                      <span>
                        {{
                          Intl.NumberFormat().format(
                            loan?.Loans.order_balance
                          ) +
                          " " +
                          $t("currency")
                        }}
                      </span>
                    </h6>
                    <h6 class="mb-0 d-flex justify-content-between">
                      {{ $t("loan.history.order-payment-amount") }}
                      <span>
                        {{
                          Intl.NumberFormat().format(
                            loan?.Loans.order_payment_amount
                          ) +
                          " " +
                          $t("currency")
                        }}
                      </span>
                    </h6>
                    <h6 class="mb-0 d-flex justify-content-between">
                      {{ $t("loan.history.discount") }}
                      <span>
                        {{
                          Intl.NumberFormat().format(
                            loan?.Loans.order.discount
                          ) +
                          " " +
                          $t("currency")
                        }}
                      </span>
                    </h6>
                  </div>
                  <div class="col">
                    <h6 class="mb-0 d-flex justify-content-between">
                      {{ $t("loan.history.loan-balance") }}
                      <span>
                        {{
                          Intl.NumberFormat().format(loan?.Loans.money) +
                          " " +
                          $t("currency")
                        }}
                      </span>
                    </h6>
                    <h6 class="mb-0 d-flex justify-content-between">
                      {{ $t("loan.history.payment-amount") }}
                      <span class="text-success">
                        {{
                          Intl.NumberFormat().format(loan?.sum_income) +
                          " " +
                          $t("currency")
                        }}
                      </span>
                    </h6>
                    <h6 class="mb-0 d-flex justify-content-between">
                      {{ $t("loan.history.debt-amount") }}
                      <span class="text-danger">
                        {{
                          Intl.NumberFormat().format(loan?.Loans.residual) +
                          " " +
                          $t("currency")
                        }}
                      </span>
                    </h6>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div
            class="col-md-4 col-lg-4 col-sm-6 my-2"
            v-for="item in trades"
            :key="item"
          >
            <div class="card h-100 shadow-sm border">
              <div class="card-body">
                <div class="mx-2">
                  <div class="d-flex align-items-center text-wrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fas fa-box-open me-2"></i>
                    </div>
                    <h6 class="mb-0">
                      {{ $t("loan.history.product") }}:
                      {{ item.Trades?.product_store.product.name }}
                    </h6>
                  </div>
                  <div class="d-flex align-items-center text-nowrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fas fa-money-bill me-2"></i>
                    </div>
                    <h6 class="mb-0">
                      {{ $t("loan.history.price") }}:
                      {{
                        `${Intl.NumberFormat().format(item.Trades?.price)} ${$t(
                          "currency"
                        )}`
                      }}
                    </h6>
                  </div>
                  <div class="d-flex align-items-center text-nowrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fas fa-percent me-2"></i>
                    </div>
                    <h6 class="mb-0">
                      {{ $t("loan.history.discount") }}:
                      {{
                        `${Intl.NumberFormat().format(
                          item.Trades?.discount
                        )} ${$t("currency")}`
                      }}
                    </h6>
                  </div>
                  <div class="d-flex align-items-center text-nowrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fas fa-scale-unbalanced-flip me-2"></i>
                    </div>
                    <h6 class="mb-0">
                      {{ $t("loan.history.quantity") }}:
                      {{
                        `${Intl.NumberFormat().format(item.Trades?.quantity)} ${
                          item.Trades?.product_store.product.olchov.name
                        }`
                      }}
                    </h6>
                  </div>
                  <div class="d-flex align-items-center text-nowrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fas fa-money-bill me-2"></i>
                    </div>
                    <h6 class="mb-0">
                      {{ $t("loan.history.amount") }}:
                      {{
                        `${Intl.NumberFormat().format(
                          item.Trades?.price * item.Trades?.quantity -
                            item.Trades?.discount
                        )} ${$t("currency")}`
                      }}
                    </h6>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import setNavPills from "@/assets/js/nav-pills.js";
import DataNotfound from "@/components/dataNotfound.vue";
import pagination from "@/components/pagination.vue";

export default {
  components: { pagination, DataNotfound },
  name: "loan-history",
  data() {
    DataNotfound;
    return {
      loan: null,
      income: {
        page: 1,
        pages: 0,
        limit: 10,
      },
      content: "income",
      incomes: [],
      trade: {
        page: 1,
        pages: 0,
        limit: 10,
      },
      trades: [],
      format_date: api.format_date,
    };
  },
  methods: {
    get_income(page, limit) {
      api
        .get_incomes(
          page,
          limit,
          "",
          "",
          0,
          this.$route.query.id,
          0,
          0,
          "loan",
          this.$route.query.market_id
        )
        .then((res) => {
          this.income.pages = res.data.page;
          this.income.page = res.data.current_page;
          this.income.limit = res.data.limit;
          this.incomes = res.data.data;
        });
    },
    get_loan() {
      api
        .get_loans(
          1,
          1,
          "",
          "",
          0,
          false,
          this.$route.query.id,
          this.$route.query.market_id
        )
        .then((res) => {
          this.loan = res.data;
          this.get_balances();
        });
    },
    get_balances() {
      if (this.loan.Loans.order.id) {
        api.trades(this.loan.Loans.order.id, false, 1, 1, 1).then((res) => {
          this.loan.Loans.order_balance = res.data;
          api
            .get_incomes(
              1,
              1,
              "",
              "",
              this.loan.Loans.order.id,
              0,
              0,
              0,
              "order",
              this.$route.query.market_id
            )
            .then((res) => {
              this.loan.Loans.order_payment_amount = res.data.data[0]?.money;
              this.get_trades(this.trade.page, this.trade.limit);
            });
        });
      }
    },
    get_trades(page, limit) {
      api
        .trades(
          this.loan.Loans.order.id,
          this.loan.Loans.order.delivery_date == "0000-00-00" ? true : false,
          0,
          page,
          limit
        )
        .then((res) => {
          this.trade.pages = res.data.page;
          this.trade.page = res.data.current_page;
          this.trade.limit = res.data.limit;
          this.trades = res.data.data;
        });
    },
  },
  created() {
    this.get_income(this.income.page, this.income.limit);
  },
  mounted() {
    setNavPills();
  },
};
</script>
