<template>
  <div class="modal fade" id="production-with-out-plan">
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">
            {{ $t("production.production-without-plan") }}
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
          ></button>
        </div>
        <form @submit.prevent="post">
          <div class="modal-body">
            <div class="row">
              <div class="col-12 mb-2" v-if="details?.before_cycle">
                <div class="card shadow-none border border-secondary-custom">
                  <div class="card-body">
                    <span
                      >{{ $t("production.material_name") }}:
                      {{ details?.before_cycle?.Cycle?.name }}</span
                    >
                    <br />
                    <div
                      v-if="details?.before_cycle?.Cycle?.hasPeriod"
                      class="row"
                    >
                      <div class="col-auto">
                        {{ $t("production.warehouse") }}:
                      </div>
                      <div class="col-8">
                        <table class="table table-sm table-bordered">
                          <thead>
                            <tr>
                              <th class="py-1"></th>
                              <th class="py-1">
                                {{ $t("product-store.table.quantity") }}
                              </th>
                              <th class="py-1">
                                {{ $t("product-store.date") }}
                              </th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr
                              v-for="(warehouse, i) in details?.before_cycle
                                ?.warehouse"
                              :key="warehouse"
                            >
                              <td>{{ i + 1 }}</td>
                              <td>
                                {{ warehouse.value }}
                                {{ product?.olchov?.name }}
                              </td>
                              <td
                                :class="
                                  count_date(
                                    new Date().toISOString(),
                                    warehouse.period
                                  ) > 10
                                    ? 'text-success'
                                    : count_date(
                                        new Date().toISOString(),
                                        warehouse.period
                                      ) <= 10 &&
                                      count_date(
                                        new Date().toISOString(),
                                        warehouse.period
                                      ) > 5
                                    ? 'text-warning'
                                    : 'text-danger'
                                "
                              >
                                {{ format_date(warehouse.period).slice(0, 10) }}
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                    <div v-else>
                      {{ $t("production.warehouse") }}:
                      {{ details?.before_cycle?.warehouse }}
                      {{ product?.olchov?.name }}
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-md-6">
                <div class="dropdown">
                  <button
                    id="product_typeDropdown"
                    class="btn w-100 mb-0 dropdown-toggle"
                    data-bs-toggle="dropdown"
                    @click="getProductTypes()"
                  >
                    {{
                      product_type ? product_type.name : $t("plan.product_type")
                    }}
                  </button>
                  <div
                    class="dropdown-menu w-100 p-1"
                    aria-labelledby="product_typeDropdown"
                  >
                    <input
                      type="text"
                      class="form-control my-1"
                      :placeholder="$t('search')"
                      v-model="search_product_type"
                      @keyup="getProductTypes()"
                    />
                    <ul
                      class="list-group scroll-25 p-1"
                      @scroll="scrollProductTypes($event)"
                    >
                      <li
                        class="list-group-item cursor-pointer"
                        v-for="item in product_types?.data"
                        :key="item"
                        @click="
                          body.product_cycle_id = 0;
                          product = null;
                          product_type = item;
                        "
                      >
                        {{ item.name }}
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div class="col-md-6">
                <div class="dropdown">
                  <button
                    id="productDropdown"
                    class="btn w-100 mb-0 dropdown-toggle"
                    data-bs-toggle="dropdown"
                    :disabled="!product_type?.id"
                    @click="getProducts()"
                  >
                    {{ product ? product.name : $t("plan.product") }}
                  </button>
                  <div
                    class="dropdown-menu w-100 p-1"
                    aria-labelledby="productDropdown"
                  >
                    <input
                      type="text"
                      class="form-control my-1"
                      :placeholder="$t('search')"
                      v-model="search_product"
                      @keyup="getProducts()"
                    />
                    <ul
                      class="list-group scroll-25 p-1"
                      @scroll="scrollProducts($event)"
                    >
                      <li
                        class="list-group-item cursor-pointer"
                        v-for="item in products?.data"
                        :key="item"
                        @click="
                          product = item;
                          getProductCycles();
                          getMaterials();
                        "
                      >
                        {{ item.name }}
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="product-cycle">{{
                    $t("supply.product-cycle")
                  }}</label>
                  <select
                    id="product-cycle"
                    class="form-select"
                    v-model="body.product_cycle_id"
                    :disabled="!product?.id"
                    @change="getDetails()"
                    required
                  >
                    <option
                      :value="item.id"
                      v-for="item in product_cycles"
                      :key="item"
                    >
                      {{ item.cycle?.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="product-material">{{
                    $t("supply.product-material")
                  }}</label>
                  <select
                    id="product-material"
                    class="form-select"
                    v-model="body.material_id"
                    @change="getDetails()"
                    :disabled="!product?.id"
                  >
                    <option
                      :value="item.material_id"
                      v-for="item in materials"
                      :key="item"
                    >
                      {{ item.material?.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6" v-if="product_type?.hasSize">
                <div class="form-group">
                  <label for="size1">{{ $t("production.size") }} 1</label>
                  <input
                    id="size1"
                    type="number"
                    class="form-control"
                    v-model="body.size1"
                    @keyup="getDetails()"
                    min="0"
                  />
                </div>
              </div>
              <div class="col-md-6" v-if="product_type?.hasSize">
                <div class="form-group">
                  <label for="size2">{{ $t("production.size") }} 2</label>
                  <input
                    id="size2"
                    type="number"
                    class="form-control"
                    v-model="body.size2"
                    @keyup="getDetails()"
                    min="0"
                  />
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col" v-if="details?.next_plants?.length > 1">
                <div class="form-group">
                  <label for="plant">{{ $t("production.plant") }}</label>
                  <select
                    id="plant"
                    class="form-select"
                    v-model="body.next_plant_id"
                    :disabled="!details?.next_plants"
                    required
                  >
                    <option
                      v-for="plant in details?.next_plants"
                      :key="plant"
                      :value="plant.id"
                    >
                      {{ plant.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="norm">{{ $t("production.norm") }}</label>
                  <input
                    id="norm"
                    type="number"
                    class="form-control"
                    v-model="body.norm"
                    step="any"
                    min="0"
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="value">{{ $t("production.value") }}</label>
                  <div class="input-group">
                    <input
                      id="value"
                      type="number"
                      class="form-control"
                      v-model="body.value"
                      @keyup="getDetails()"
                      step="any"
                      min="0"
                      required
                    />
                    <span class="input-group-text">{{
                      product?.olchov?.name
                    }}</span>
                    <button
                      :disabled="body.value <= 0"
                      class="btn bg-gradient-success mb-0"
                      @click="getDetails()"
                      type="button"
                    >
                      <i class="fas fa-check"></i>
                    </button>
                  </div>
                </div>
              </div>
              <div class="col-md-6" v-if="details?.this_cycle?.hasPeriod">
                <label for="period">{{ $t("production.period") }}</label>
                <input
                  id="period"
                  type="date"
                  class="form-control"
                  :min="new Date().toISOString().slice(0, 10)"
                  required
                  v-model="body.period"
                />
              </div>
            </div>
            <table
              class="table table-bordered mt-2"
              v-if="details?.components?.length"
            >
              <thead>
                <tr>
                  <th>{{ $t("production.material_name") }}</th>
                  <th>{{ $t("production.needed_value") }}</th>
                  <th>{{ $t("production.warehouse") }}</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="item in details?.components"
                  :key="item"
                  :class="
                    item.needy_value > item.warehouse ? 'table-danger' : ''
                  "
                >
                  <td v-if="item.Component_Item.material">
                    {{ item.Component_Item.material?.name }}
                  </td>
                  <td v-else>{{ item.Component_Item.product?.name }}</td>
                  <td v-if="item.Component_Item.material">
                    {{ item.needy_value }}
                    {{ item.Component_Item.material?.olchov?.name }}
                  </td>
                  <td v-else>
                    {{ item.needy_value }}
                    {{ item.Component_Item.product?.olchov?.name }}
                  </td>
                  <td v-if="item.Component_Item.material">
                    {{ item.warehouse }}
                    {{ item.Component_Item.material?.olchov?.name }}
                  </td>
                  <td v-else>
                    {{ item.warehouse }}
                    {{ item.Component_Item.product?.olchov?.name }}
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-danger"
              data-bs-dismiss="modal"
              close-production
            >
              {{ $t("close") }}
            </button>
            <button
              type="submit"
              class="btn bg-gradient-primary"
              :disabled="
                details?.before_cycle?.warehouse < body.value || body.value <= 0
              "
            >
              {{ $t("production.doit") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";

export default {
  name: "production-without-plan",
  data() {
    return {
      body: {
        product_cycle_id: 0,
        material_id: 0,
        plan_id: 0,
        next_plant_id: 0,
        value: 0,
        size1: 0,
        size2: 0,
        norm: 0,
        trade_id: 0,
        trade_item_id: 0,
        period: "",
      },
      search_product_type: "",
      product_types: null,
      product_type: null,
      search_product: "",
      products: null,
      product: null,
      product_cycles: null,
      materials: null,
      details: null,
      count_date: api.countDays,
      format_date: api.format_date,
    };
  },
  methods: {
    getProductTypes() {
      api.get_product_types(1, 10, this.search_product_type).then((res) => {
        this.product_types = res.data;
      });
    },
    scrollProductTypes(event) {
      const element = event.target;
      if (
        element.scrollTop + element.clientHeight >=
        element.scrollHeight - 1
      ) {
        if (this.product_types.current_page < this.product_types.page_count) {
          api
            .get_product_types(
              this.product_types?.current_page + 1,
              10,
              this.search_product_type
            )
            .then((res) => {
              this.product_types.current_page = res.data.current_page;
              this.product_types.page_count = res.data.page_count;
              this.product_types.data = this.product_types.data.concat(
                res.data.data
              );
            });
        }
      }
    },
    getProducts() {
      api
        .get_products(1, 10, this.search_product, this.product_type?.id, false)
        .then((res) => {
          this.products = res.data;
        });
    },
    scrollProducts(event) {
      const element = event.target;
      if (
        element.scrollTop + element.clientHeight >=
        element.scrollHeight - 1
      ) {
        if (this.products.current_page < this.products.page_count) {
          api
            .get_products(
              this.products?.current_page + 1,
              10,
              this.search_product,
              this.product_type?.id,
              false
            )
            .then((res) => {
              this.products.current_page = res.data.current_page;
              this.products.page_count = res.data.page_count;
              this.products.data = this.products.data.concat(res.data.data);
            });
        }
      }
    },
    getProductCycles() {
      api.product_cycles(1, 100, "", 0, this.product?.id, false).then((res) => {
        this.product_cycles = res.data.data;
      });
    },
    getMaterials() {
      api.product_material(1, 10000, 0, this.product?.id).then((res) => {
        this.materials = res.data.data;
      });
    },
    getDetails() {
      api
        .get_order_details(
          this.body.product_cycle_id,
          this.$store.state.user,
          this.body.material_id,
          0,
          0,
          this.body.value ? this.body.value : 1,
          Number(this.body.size1),
          Number(this.body.size2)
        )
        .then((res) => {
          this.details = res.data;
        });
      this.$store.dispatch("setloading", false);
    },
    post() {
      api
        .production_create(
          this.body.product_cycle_id,
          this.body.material_id,
          this.body.plan_id,
          this.$store.state.user,
          this.details?.next_plants?.length
            ? this.details?.next_plants?.length === 1
              ? this.details?.next_plants[0]?.id
              : this.body.next_plant_id
            : 0,
          Number(this.body.value),
          Number(this.body.size1),
          Number(this.body.size2),
          Number(this.body.norm),
          this.body.trade_id ? this.body.trade_id : 0,
          this.body.trade_item_id ? this.body.trade_item_id : 0,
          this.body.period
          // this.content === "orders" ? this.detail.detail : true
        )
        .then(() => {
          api.success("close-production");
          this.details = null;
          this.body.product_cycle_id = 0;
          this.body.norm = 0;
          this.body.size2 = 0;
          this.body.size1 = 0;
          this.body.value = 0;
          this.body.plan_id = 0;
          this.body.material_id = 0;
          this.body.period = "";
          this.product = null;
          this.products = null;
          this.product_type = null;
          this.product_types = null;
          this.product_cycles = null;
          this.materials = null;
          if (this.content === "production") {
            this.$parent.getProductionOrders(1, 25);
          } else {
            this.$parent.getOrders(1, 25);
          }
        });
    },
  },
};
</script>
