<template>
  <div class="container-fluid py-4">
    <div class="card">
      <div class="card-header">
        <div class="row">
          <div class="col-md-3 mb-1">
            <h6 class="title m-0">
              {{ order?.ordinal_number }}
              <i>{{ order?.customer ? order?.customer.name : "" }}</i>
            </h6>
            <p class="m-0">
              {{ order?.time.substring(0, 10) + " - " + order?.delivery_date }}
            </p>
          </div>
          <div class="col-md-3 mb-1">
            <div class="btn-group btn-group-sm w-100">
              <button
                class="btn mb-0 px-1 bg-gradient-primary"
                data-bs-toggle="modal"
                data-bs-target="#pay-modal"
                @click="$refs.modals.getPayHistory(1, 25)"
              >
                <i class="fa fa-arrow-down"></i>
                <i class="fa fa-money px-1"></i>
              </button>
              <button
                class="btn mb-0 px-1 bg-gradient-danger"
                data-bs-toggle="modal"
                data-bs-target="#expense-modal"
                @click="$refs.modals.getExpenseHistory(1, 25)"
              >
                <i class="fa fa-arrow-up"></i>
                <i class="fa fa-money px-1"></i>
              </button>
            </div>
          </div>
          <div class="col-md-3 mb-1">
            <div class="row">
              <div class="col-4">
                <button
                  class="btn btn-sm w-100 mb-0 px-1 bg-gradient-info"
                  data-bs-toggle="modal"
                  data-bs-target="#customer-modal"
                >
                  <!-- {{ $t("pre-order.table.customer") }} -->
                  <i class="fa fa-handshake"></i>
                </button>
              </div>
              <div class="col-4">
                <button
                  class="btn btn-sm w-100 mb-0 px-1 bg-gradient-warning"
                  data-bs-toggle="modal"
                  data-bs-target="#date-modal"
                >
                  <i class="fa fa-calendar"></i>
                </button>
              </div>
              <div class="col-4">
                <button
                  class="btn btn-sm w-100 mb-0 px-1 bg-gradient-primary"
                  data-bs-toggle="modal"
                  data-bs-target="#image-modal"
                >
                  <i class="fa fa-image"></i>
                </button>
              </div>
            </div>
          </div>
          <div class="col-md-3 mb-1">
            <div class="row">
              <div class="col-3">
                <button
                  class="btn btn-sm m-0 px-2 bg-gradient-info w-100"
                  @click="$refs.orderCheck.start()"
                >
                  <i class="fa fa-print"></i>
                </button>
              </div>
              <div class="col-9">
                <div class="btn-group btn-group-sm w-100">
                  <button
                    class="btn btn-sm m-0 px-1 bg-gradient-success"
                    data-bs-toggle="modal"
                    data-bs-target="#product-modal"
                    @click="$refs.trades.getProductTypes(1, 25)"
                  >
                    <i class="fa fa-plus"></i>
                    <i class="fa fa-box px-1"></i>
                  </button>
                  <button
                    class="btn btn-sm m-0 px-1 bg-gradient-success"
                    data-bs-toggle="modal"
                    data-bs-target="#confirm-modal"
                    @click="
                      getOrder();
                      getBalance();
                    "
                  >
                    <i class="far fa-circle-check"></i>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body">
        <preOrderTrades ref="trades" />
      </div>
    </div>
  </div>

  <preOrderModals ref="modals" />

  <orderCheck
    :market-id="$route.query.market_id"
    :order-id="$route.query.id"
    ref="orderCheck"
  />

  <div
    class="modal fade"
    id="confirm-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="confirm-modal-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="confirm-modal-label">
            {{ $t("order.confirm-modal-title") }}
          </h5>
        </div>
        <form @submit.prevent="putOrder()">
          <div class="modal-body">
            <p class="mb-2">
              <strong>{{
                Intl.NumberFormat().format(balance - sum_income) +
                " " +
                $t("currency")
              }}</strong>
            </p>
            <div class="row gap-1">
              <div class="col-12">
                <input
                  type="file"
                  accept=".jpeg, .png, .jpg"
                  class="form-control"
                  confirm-file
                />
              </div>
              <div class="col-12">
                <div class="input-group">
                  <input
                    type="number"
                    min="0"
                    :max="max(order_confirm.money)"
                    step="any"
                    class="form-control"
                    :placeholder="$t('trades.placeholders.price')"
                    required
                    v-model="order_confirm.money"
                    @keyup="countLoan()"
                  />
                  <div class="input-group-text">{{ $t("currency") }}</div>
                </div>
              </div>
              <div class="col-12">
                <div class="input-group">
                  <input
                    type="number"
                    min="0"
                    :max="max(order_confirm.discount)"
                    step="any"
                    class="form-control"
                    :placeholder="$t('trades.placeholders.discount')"
                    v-model="order_confirm.discount"
                    @keyup="countLoan()"
                  />
                  <div class="input-group-text">{{ $t("currency") }}</div>
                </div>
              </div>
              <div
                class="col-12"
                v-if="
                  order_confirm.money &&
                  order_confirm.money + order_confirm.discount <
                    balance - sum_income
                "
              >
                <div class="input-group">
                  <input
                    type="number"
                    min="0"
                    :max="max(loan)"
                    step="any"
                    class="form-control"
                    :placeholder="$t('trades.placeholders.loan')"
                    disabled
                    v-model="loan"
                  />
                  <div class="input-group-text">{{ $t("currency") }}</div>
                </div>
              </div>
              <div class="col-12" v-if="loan">
                <div class="input-group">
                  <input
                    type="date"
                    class="form-control"
                    required
                    v-model="order_confirm.loan_repayment_date"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-confirm-modal
            >
              {{ $t("close") }}
            </button>
            <button class="btn bg-gradient-primary">
              {{ $t("order.confirm-button") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "../../server/api";
import preOrderTrades from "./preOrderTrades.vue";
import preOrderModals from "./preOrderModals.vue";
import orderCheck from "./orderCheck.vue";
export default {
  name: "preOrder",
  components: { preOrderTrades, preOrderModals, orderCheck },
  data() {
    return {
      image: api.image,
      order: null,
      balance: null,
      sum_income: null,
      order_confirm: {
        id: 0,
        money: null,
        discount: null,
        customer_id: 0,
        loan_repayment_date: new Date().toLocaleString("uz", {
          year: "numeric",
          month: "2-digit",
          day: "2-digit",
        }),
      },
      loan: null,
    };
  },
  created() {
    this.getOrder();
  },
  methods: {
    max(value) {
      let number = this.balance - this.sum_income;
      number -= this.order_confirm.money;
      number -= this.order_confirm.discount;
      number -= this.loan;
      return Number(number) + Number(value);
    },
    getOrder() {
      api
        .orders(
          "",
          "",
          0,
          0,
          "pre_order",
          this.$route.query.id,
          this.$route.query.market_id,
          1,
          1
        )
        .then((res) => {
          this.order = res.data.order;
          this.sum_income = res.data.sum_income;
          this.$refs.modals.customer = this.order.customer;
          this.$refs.modals.date = this.order.delivery_date;
        });
    },
    putOrder() {
      this.order_confirm.id = this.order.id;
      this.order_confirm.customer_id = this.order.customer_id;
      this.order_confirm.discount = this.order_confirm.discount
        ? this.order_confirm.discount
        : 0;
      api.order_confirmation(this.order_confirm).then(() => {
        api.success("close-confirm-modal").then(() => {
          this.order_confirm = {
            id: 0,
            money: null,
            discount: null,
            customer_id: 0,
            loan_repayment_date: new Date().toLocaleString("uz", {
              year: "numeric",
              month: "2-digit",
              day: "2-digit",
            }),
          };
          this.loan = null;
          this.postOrderImage();
        });
      });
    },
    async postOrderImage() {
      let data = new FormData();
      const file = document.querySelector("[confirm-file]").files[0];
      data.append("file", file);
      data.append("comment", "pre_order");
      data.append("order_id", this.$route.query.id);
      if (file) {
        await api.create_order_file(data).then(() => {
          api.success("close-confirm-modal").then(() => {
            this.$router.push("/pre-orders");
          });
        });
      }
      this.$router.push({
        path: "/pre-orders",
        query: { market_id: this.$route.query.market_id },
      });
    },
    getBalance() {
      api.trades(this.order.id, false, 1, 1, 25).then((res) => {
        this.balance = res.data;
      });
    },
    countLoan() {
      if (
        this.order_confirm.money &&
        this.order_confirm.money + this.order_confirm.discount <
          this.balance - this.sum_income
      ) {
        this.loan =
          this.balance -
          this.sum_income -
          (this.order_confirm.money + this.order_confirm.discount);
      } else {
        this.loan = null;
      }
    },
  },
};
</script>

<style scoped>
[toggle-collapse] {
  cursor: pointer;
}
</style>
