<template>
  <div class="container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-4">
            <h5 class="mb-0">{{ $t("indicator.supply") }}</h5>
          </div>
          <div class="col-md-4"></div>
          <div class="col-md-4">
            <div class="row">
              <div class="col">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    get(page, limit);
                    $store.dispatch('setloading', false);
                  "
                />
              </div>
              <div class="col-auto">
                <button
                  class="btn btn-sm bg-gradient-primary mx-2 mb-0 px-3"
                  data-bs-toggle="modal"
                  data-bs-target="#filter"
                >
                  <i class="fas fa-filter"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body py-2">
        <div class="table-responsive p-0 mb-0">
          <table class="table table-bordered align-items-center mb-0">
            <thead>
              <tr>
                <th scope="col">{{ $t("material.title") }}</th>
                <th scope="col">{{ $t("supply.value") }}</th>
                <th scope="col">{{ $t("supply.price") }}</th>
                <th scope="col">{{ $t("supplier.supplier") }}</th>
                <th scope="col">{{ $t("expense.date") }}</th>
                <th scope="col">{{ $t("attandance.user") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="suppli in supplies.data || []" :key="suppli">
                <td>{{ suppli.material }}</td>
                <td>{{ suppli.value }} {{ suppli.olchov }}</td>
                <td>{{ suppli.price }}</td>
                <td>{{ suppli.supplier }}</td>
                <td>{{ suppli.created_at }}</td>
                <td>{{ suppli.user }}</td>
              </tr>
            </tbody>
            <tfoot v-if="!supplies?.data">
              <tr>
                <td colspan="6">
                  <data-notfound :data="[]" />
                </td>
              </tr>
            </tfoot>
          </table>
        </div>
        <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
      </div>
    </div>
  </div>
  <!-- filter -->
  <div class="modal fade" id="filter">
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title">
            {{ $t("filter") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-6">
              <div class="form-group">
                <label for="from_date">{{ $t("from_date") }}</label>
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="from_date"
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("to_date") }}</label>
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="to_date"
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("supply.supplier") }}</label>
                <!-- <input id="to_date" class="form-control" v-model="to_date" />
                 -->
                <select v-model="supplier_id" class="form-select">
                  <option
                    :value="supplier.id"
                    v-for="supplier in supplies.participated_suppliers || []"
                    :key="supplier"
                  >
                    {{ supplier.name }}
                  </option>
                </select>
              </div>
            </div>

            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("supply.material") }}</label>
                <!-- <input id="to_date" class="form-control" v-model="to_date" />
                 -->
                <select v-model="plant_id" class="form-select">
                  <option
                    :value="plant.id"
                    v-for="plant in supplies.participated_materials || []"
                    :key="plant"
                  >
                    {{ plant.name }}
                  </option>
                </select>
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("supply.material") }}</label>
                <!-- <input id="to_date" class="form-control" v-model="to_date" />
                 -->
                <select v-model="product_id" class="form-select">
                  <option
                    :value="product.id"
                    v-for="product in supplies.participated_products || []"
                    :key="product"
                  >
                    {{ product.name }}
                  </option>
                </select>
              </div>
            </div>

            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("supply.material") }}</label>
                <!-- <input id="to_date" class="form-control" v-model="to_date" />
                 -->
                <select v-model="material_id" class="form-select">
                  <option
                    :value="material.id"
                    v-for="material in supplies.participated_product_materials ||
                    []"
                    :key="material"
                  >
                    {{ material.name }}
                  </option>
                </select>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-sm bg-gradient-danger"
            data-bs-dismiss="modal"
            @click="
              from_date = new Date(
                new Date().getFullYear(),
                new Date().getMonth(),
                1
              )
                .toISOString()
                .substring(0, 10);
              to_date = new Date(
                new Date().getFullYear(),
                new Date().getMonth(),
                new Date().getDate() + 1
              )
                .toISOString()
                .substring(0, 10);
              product_id = 0;
              material_id = 0;
              plant_id = 0;
              supplier_id = 0;
              get(page, limit);
            "
          >
            <i class="fas fa-trash"></i>
          </button>
          <button
            class="btn btn-sm bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-filter
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn btn-sm bg-gradient-primary"
            data-bs-dismiss="modal"
            @click="get(page, limit)"
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";
export default {
  components: { dataNotfound, Pagination },
  data() {
    return {
      supplies: [],
      from_date: new Date(new Date().getFullYear(), new Date().getMonth(), 1)
        .toISOString()
        .substring(0, 10),
      to_date: new Date(
        new Date().getFullYear(),
        new Date().getMonth(),
        new Date().getDate() + 1
      )
        .toISOString()
        .substring(0, 10),
      title: this.$route.query.name,
      supplier_id: 0,
      plant_id: 0,
      material_id: 0,
      product_id: 0,
      search: "",
      participated_suppliers: [],
      page: 1,
      pages: 0,
      limit: 10,
    };
  },
  methods: {
    get(page, limit) {
      api
        .get_supplies(
          page,
          limit,
          this.search,
          this.supplier_id,
          this.plant_id,
          this.material_id,
          this.from_date,
          this.to_date
        )
        .then((res) => {
          this.pages = res.data?.page_count;
          this.page = res.data?.current_page;
          this.limit = res.data?.page_limit;
          this.supplies = res.data;
        });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>

<style lang="scss" scoped></style>
