<template>
  <div
    class="modal fade"
    id="add-shift"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-shift-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-shift-label">
            {{ $t("shift.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('add', body)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-12">
                <div class="form-group">
                  <label for="name">{{ $t("shift.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="body.name"
                    :placeholder="$t('shift.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="from_time">{{ $t("shift.time.from") }}</label>
                  <input
                    id="from_time"
                    type="time"
                    class="form-control"
                    v-model="body.from_time"
                    :placeholder="$t('shift.time.from')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="to_time">{{ $t("shift.time.to") }}</label>
                  <input
                    id="to_time"
                    type="time"
                    class="form-control"
                    v-model="body.to_time"
                    :placeholder="$t('shift.time.to')"
                    required
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="update-shift"
    tabindex="-1"
    role="dialog"
    aria-labelledby="update-shift-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="update-shift-label">
            {{ $t("shift.update") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('put', data)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-12">
                <div class="form-group">
                  <label for="name">{{ $t("shift.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="data.name"
                    :placeholder="$t('shift.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="from_time">{{ $t("shift.time.from") }}</label>
                  <input
                    id="from_time"
                    type="time"
                    class="form-control"
                    v-model="data.from_time"
                    :placeholder="$t('shift.time.from')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="to_time">{{ $t("shift.time.to") }}</label>
                  <input
                    id="to_time"
                    type="time"
                    class="form-control"
                    v-model="data.to_time"
                    :placeholder="$t('shift.time.to')"
                    required
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-put
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("update") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "add-shift",
  props: ["item"],
  emits: ["add", "put"],
  data() {
    return {
      body: {
        name: "",
        from_time: "",
        to_time: "",
      },
    };
  },
  computed: {
    data() {
      return this.item;
    },
  },
};
</script>
