<template>
  <div
    class="modal fade"
    id="pay-for-loan"
    tabindex="-1"
    role="dialog"
    aria-labelledby="pay-for-loan-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="pay-for-loan-label">
            {{ $t("loan.pay") }}
          </h5>
        </div>
        <form @submit.prevent="pay()">
          <div class="modal-body">
            <div class="form-group">
              <label for="money">{{ $t("loan.money") }}</label>
              <div class="d-flex">
                <div class="input-group mx-1">
                  <input
                    id="money"
                    type="number"
                    class="form-control"
                    step="any"
                    @focus="body.money == 0 ? (body.money = null) : body.money"
                    @focusout="
                      body.money == null ? (body.money = 0) : body.money
                    "
                    v-model="body.money"
                    :placeholder="$t('loan.money')"
                    required
                  />
                  <span class="input-group-text">{{ $t("currency") }}</span>
                </div>
                <button
                  class="btn bg-gradient-primary mb-0"
                  type="button"
                  @click="body.money = $parent.residual_price"
                >
                  <i class="fa-solid fa-magnet"></i>
                </button>
              </div>
            </div>
            <div class="form-group">
              <label for="comment">{{ $t("loan.comment") }}</label>
              <textarea
                id="comment"
                class="form-control"
                v-model="body.comment"
                :placeholder="$t('loan.comment')"
              />
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("save") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="filter"
    tabindex="-1"
    role="dialog"
    aria-labelledby="filter-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title" id="filter-label">
            {{ $t("filter") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-6">
              <div class="form-group">
                <label for="from_date">{{ $t("from_date") }}</label>
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="filter_body.from_date"
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("to_date") }}</label>
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="filter_body.to_date"
                />
              </div>
            </div>
            <div class="col-md-12">
              <div class="form-group">
                <label for="status">{{ $t("loan.status.title") }}</label>
                <select
                  id="status"
                  class="form-select"
                  v-model="filter_body.status"
                >
                  <option value="true">{{ $t("loan.status.true") }}</option>
                  <option value="false">{{ $t("loan.status.false") }}</option>
                </select>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-sm bg-gradient-danger"
            @click="
              filter_body = {
                status: false,
                from_date: '',
                to_date: '',
              };
              $emit('filter', filter_body, false);
            "
          >
            <i class="fas fa-trash"></i>
          </button>
          <button
            class="btn btn-sm bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-filter
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn btn-sm bg-gradient-primary"
            @click="$emit('filter', filter_body, true)"
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";

export default {
  name: "loan-modals",
  emits: ["filter"],
  data() {
    return {
      body: {
        money: 0,
        source: null,
        comment: "",
      },
      filter_body: {
        to_date: "",
        from_date: "",
        status: false,
      },
    };
  },
  methods: {
    pay() {
      api.take_money_from_loan(this.body).then(() => {
        this.body = {
          money: 0,
          source: null,
          comment: "",
        };
        api.success("close");
        this.$parent.get(this.$parent.page, this.$parent.limit);
      });
    },
  },
};
</script>
