<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">{{ $t("product-types.title") }}</h6>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col-md-6 my-1">
                <button
                  class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
                  data-bs-toggle="modal"
                  data-bs-target="#add-product-type"
                  @click="get_olchovs()"
                >
                  {{ $t("product-types.add-button") }}
                </button>
              </div>
              <div class="col-md-6 my-1">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    get(1, limit);
                    $store.dispatch('setloading', false);
                  "
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-lg-4 col-md-6 col-sm-12 my-2"
            v-for="product_type in product_types"
            :key="product_type"
          >
            <div class="card card-zoom h-100 shadow-sm">
              <div class="card-body">
                <div class="row">
                  <div class="col">
                    <h6 class="card-title text-primary mb-0">
                      {{ product_type.name }}
                      <h6 class="text-muted mb-0" style="font-size: 13.5px">
                        {{ $t("product-types.olchov") }}:
                        {{ product_type.olchov?.name }}
                      </h6>
                      <h6 class="text-muted mb-0" style="font-size: 13.5px">
                        {{ $t("product-types.detail.title") }}:
                        {{
                          product_type.detail
                            ? $t("product-types.detail.true")
                            : $t("product-types.detail.false")
                        }}
                      </h6>
                      <h6 class="text-muted mb-0" style="font-size: 13.5px">
                        {{ $t("product-types.number") }}:
                        {{ product_type.number }}
                      </h6>
                      <h6 class="text-muted mb-0" style="font-size: 13.5px">
                        {{ $t("product-types.hasSize") }}:
                        {{
                          product_type.hasSize
                            ? $t("product-types.has-size")
                            : $t("product-types.not-size")
                        }}
                      </h6>
                      <h6
                        class="text-success mb-0"
                        v-if="product_type.isReady"
                        style="font-size: 13.5px"
                      >
                        {{ $t("product-types.isReady") }}
                      </h6>
                    </h6>
                    <hr class="my-1" />
                    <div class="d-flex mt-2">
                      <button
                        class="btn btn-sm px-3 mb-0 bg-gradient-warning"
                        @click="
                          get_olchovs();
                          product_type_id = product_type.id;
                          item = {
                            name: product_type.name,
                            olchov_id: product_type.olchov?.id,
                            image: '',
                            disabled: product_type.disabled,
                            detail: product_type.detail,
                            hasSize: product_type.hasSize,
                            number: product_type.number,
                            isReady: product_type.isReady,
                          };
                        "
                        data-bs-toggle="modal"
                        data-bs-target="#update-product-type"
                      >
                        <i class="fas fa-pen"></i>
                      </button>
                      <button
                        :disabled="product_type.olchov?.id ? false : true"
                        class="btn btn-sm px-3 mb-0 bg-gradient-danger ms-1"
                        @click="
                          product_type_id = product_type.id;
                          item = {
                            name: product_type.name,
                            olchov_id: product_type.olchov?.id,
                            image: '',
                            disabled: true,
                            detail: product_type.detail,
                            hasSize: product_type.hasSize,
                            number: product_type.number,
                            isReady: product_type.isReady,
                          };
                          remove(item);
                        "
                      >
                        <i class="fas fa-trash"></i>
                      </button>
                      <router-link
                        :to="{
                          name: 'Product',
                          query: {
                            id: product_type.id,
                            olchov_id: product_type.olchov_id,
                            name: product_type.name,
                            hasSize: product_type.hasSize,
                          },
                        }"
                        class="btn btn-sm px-3 mb-0 bg-gradient-primary ms-1"
                      >
                        <i class="fas fa-eye"></i>
                      </router-link>
                    </div>
                  </div>
                  <div
                    class="col-auto d-flex justify-content-center align-items-center"
                  >
                    <div
                      class="avatar avatar-xl cursor-pointer"
                      @click="
                        image_view(
                          `${image}/product_types/${product_type.image}`
                        )
                      "
                    >
                      <img
                        height="80"
                        width="80"
                        style="object-fit: cover"
                        :src="`${image}/product_types/${product_type.image}`"
                        class="text-white rounded-2"
                        alt="Product Type"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="product_types" />
            <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
          </div>
        </div>
      </div>
    </div>
  </div>
  <div id="image-viewer">
    <span class="close" @click="image_view()">&times;</span>
    <img class="modal-content" id="full-image" />
  </div>
  <product-type-modals :olchovs="olchovs" @add="add" :item="item" @put="put" />
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";
import ProductTypeModals from "@/components/modals/ProductTypeModals.vue";
import Swal from "sweetalert2";

export default {
  components: { dataNotfound, Pagination, ProductTypeModals },
  name: "ProductType",
  data() {
    return {
      product_types: [],
      olchovs: [],
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      image: api.image,
      item: {},
      product_type_id: null,
    };
  },
  methods: {
    get(page, limit) {
      api.get_product_types(page, limit, this.search).then((res) => {
        this.pages = res.data.page_count;
        this.page = res.data.current_page;
        this.limit = res.data.page_limit;
        this.product_types = res.data.data;
      });
    },
    get_olchovs() {
      api.get_olchovs(1, 1000, "").then((res) => {
        this.olchovs = res.data.data;
      });
    },
    add(body) {
      api.create_product_type(api.getFormData(body)).then(() => {
        body.name = "";
        body.image = "";
        body.olchov_id = null;
        body.detail = "";
        body.hasSize = false;
        body.isReady = false;
        api.success("close");
        this.get(this.page, this.limit);
      });
    },
    image_view(src) {
      if (src) {
        document.getElementById("full-image").src = src;
        document.getElementById("image-viewer").style.display = "block";
      } else {
        document.getElementById("image-viewer").style.display = "none";
      }
    },
    put(body) {
      let data = api.getFormData(body);
      if (!body.image) {
        data.delete("image");
      }
      api.put_product_type(data, this.product_type_id).then(() => {
        api.success("close-put");
        this.get(this.page, this.limit);
      });
    },
    remove(body) {
      Swal.fire({
        title: this.$t("delete_text"),
        showCancelButton: true,
        confirmButtonText: this.$t("confirm"),
        cancelButtonText: this.$t("cancel"),
      }).then((result) => {
        if (result.isConfirmed) {
          this.put(body);
        }
      });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>
