<template>
  <div
    class="modal fade"
    id="add-norm"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-norm-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-norm-label">
            {{ $t("norm.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="add()">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-12">
                <div class="row">
                  <div class="col-md-6 my-1">
                    <div class="dropdown">
                      <button
                        class="form-control border-secondary-custom shadow-none mb-0 w-100"
                        data-bs-toggle="dropdown"
                        type="button"
                        aria-expanded="false"
                        @click="get_product_type(false)"
                      >
                        {{ product_type.name }}
                      </button>
                      <ul
                        class="dropdown-menu w-100"
                        aria-labelledby="dropdownMenuButton1"
                      >
                        <input
                          type="text"
                          class="form-control"
                          :placeholder="$t('search')"
                          v-model="product_type.search"
                          @keyup="
                            get_product_type(false);
                            $store.dispatch('setloading', false);
                          "
                        />
                        <div
                          class="scroll-25 product-type"
                          @scroll="
                            scroll_product_type();
                            $store.dispatch('setloading', false);
                          "
                        >
                          <li
                            @click="
                              body.product_type_id = item.id;
                              product_type.name = item.name;
                              get_product(false);
                            "
                            v-for="item in product_types"
                            :key="item"
                          >
                            <a class="dropdown-item">
                              {{ item.name }}
                            </a>
                          </li>
                        </div>
                        <data-notfound :data="product_types" class="text-lg" />
                      </ul>
                    </div>
                  </div>
                  <div class="col-md-6 my-1">
                    <div class="row">
                      <div class="col">
                        <div class="dropdown">
                          <button
                            class="form-control border-secondary-custom shadow-none mb-0 w-100"
                            data-bs-toggle="dropdown"
                            type="button"
                            aria-expanded="false"
                            :disabled="!body.product_type_id"
                          >
                            {{ product.name }}
                          </button>
                          <ul
                            class="dropdown-menu w-100"
                            aria-labelledby="dropdownMenuButton1"
                          >
                            <input
                              type="text"
                              class="form-control"
                              :placeholder="$t('search')"
                              v-model="product.search"
                              @keyup="
                                get_product(false);
                                $store.dispatch('setloading', false);
                              "
                            />
                            <div
                              class="scroll-25 product"
                              @scroll="
                                scroll_product();
                                $store.dispatch('setloading', false);
                              "
                            >
                              <li
                                @click="
                                  body.product_id = item.id;
                                  product.name = `${item.name} (${item.olchov?.name})`;
                                "
                                v-for="item in products"
                                :key="item"
                              >
                                <a class="dropdown-item"> {{ item.name }} </a>
                              </li>
                            </div>
                            <data-notfound :data="products" class="text-lg" />
                          </ul>
                        </div>
                      </div>
                      <div class="col-auto">
                        <button
                          class="btn bg-gradient-success mb-0"
                          @click="
                            body.products_id.push({
                              name: product.name,
                              id: body.product_id,
                            });
                            product.name = $t('norm.products');
                            product_type.name = $t('norm.product_types');
                            body.product_id = 0;
                            body.product_type_id = 0;
                          "
                          type="button"
                        >
                          <i class="fas fa-circle-check"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-md-6 my-1">
                <div class="form-group">
                  <label for="value">{{ $t("norm.value") }}</label>
                  <input
                    id="value"
                    type="number"
                    class="form-control"
                    v-model="body.value"
                    @focus="body.value == 0 ? (body.value = null) : body.value"
                    @focusout="
                      body.value == null ? (body.value = 0) : body.value
                    "
                    step="any"
                    :placeholder="$t('norm.value')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6 my-1">
                <div class="form-group">
                  <label for="olchov">{{ $t("material.olchov") }}</label>
                  <select
                    id="olchov"
                    class="form-select"
                    v-model="body.olchov_id"
                    required
                  >
                    <option
                      :value="olchov.id"
                      v-for="olchov in olchovs"
                      :key="olchov"
                    >
                      {{ olchov.name }}
                    </option>
                  </select>
                </div>
              </div>
            </div>
            <ul class="list-group">
              <li
                v-for="(item, i) in body.products_id"
                :key="item"
                class="list-group-item list-group-item-action d-flex justify-content-between align-items-center"
              >
                <span>
                  {{ item.name }}
                </span>
                <span
                  class="badge badge-danger badge-pill cursor-pointer"
                  @click="body.products_id.splice(i, 1)"
                  ><i class="fas fa-circle-xmark"></i
                ></span>
              </li>
            </ul>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button
              type="submit"
              class="btn bg-gradient-primary"
              :disabled="
                !body.products_id.length || !body.olchov_id || !body.value
              "
            >
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "../dataNotfound.vue";

export default {
  components: { dataNotfound },
  name: "norm-modals",
  data() {
    return {
      body: {
        products: "",
        plant_id: this.$route.query.id,
        value: 0,
        olchov_id: 0,
        products_id: [],
        product_type_id: 0,
        product_id: 0,
      },
      olchovs: [],
      products: [],
      product_types: [],
      product: {
        pages: 0,
        page: 1,
        search: "",
        name: this.$t("norm.products"),
      },
      product_type: {
        pages: 0,
        page: 1,
        search: "",
        name: this.$t("norm.product_types"),
      },
    };
  },
  methods: {
    get_olchovs() {
      api.get_olchovs(1, 1000, "").then((res) => {
        this.olchovs = res.data.data;
      });
    },
    get_product(scroll) {
      this.product.page = scroll ? this.product.page : 1;
      api
        .get_products(
          scroll ? this.product.page + 1 : this.product.page,
          10,
          this.product.search,
          this.body.product_type_id,
          false
        )
        .then((res) => {
          this.product.pages = res.data.page_count;
          this.product.page = res.data.current_page;
          this.products = scroll
            ? this.products.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll_product() {
      let div = document.querySelector(".product");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.product.page < this.product.pages) {
          this.get_product(true);
        }
      }
    },
    get_product_type(scroll) {
      this.product_type.page = scroll ? this.product_type.page : 1;
      api
        .get_product_types(
          scroll ? this.product_type.page + 1 : this.product_type.page,
          10,
          this.product_type.search,
          this.body.product_type_id,
          false
        )
        .then((res) => {
          this.product_type.pages = res.data.page_count;
          this.product_type.page = res.data.current_page;
          this.product_types = scroll
            ? this.product_types.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll_product_type() {
      let div = document.querySelector(".product-type");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.product_type.page < this.product_type.pages) {
          this.get_product_type(true);
        }
      }
    },
    add() {
      this.body.products = this.body.products_id
        .map(function (product) {
          return product.id;
        })
        .join(this.body.products_id.length > 1 ? ";" : "");
      api.create_norm(this.body).then(() => {
        this.body.olchov_id = 0;
        this.body.product_id = 0;
        this.body.product_type_id = 0;
        this.body.products = "";
        this.body.products_id = [];
        this.body.value = 0;
        this.product.name = "";
        this.product_type.name = "";
        api.success("close");
        this.$parent.get(this.$parent.page, this.$parent.limit);
      });
    },
  },
};
</script>
