<template>
  <nav
    class="navbar navbar-main navbar-expand-lg px-0 shadow-none border-radius-xl"
    v-bind="$attrs"
    id="navbarBlur"
    data-scroll="true"
  >
    <div class="px-3 py-1 container-fluid">
      <div
        class="mt-2 collapse navbar-collapse mt-sm-0 me-md-0 me-sm-4 me-sm-4"
        id="navbar"
      >
        <i
          v-if="
            $route.path != default_page &&
            role !== 'seller' &&
            role !== 'seller_admin'
          "
          @click="$router.back()"
          class="fa-solid fa-circle-left text-white fs-4 cursor-pointer"
        ></i>
        <div class="pe-md-3 d-flex align-items-center ms-md-auto"></div>
        <ul class="navbar-nav justify-content-end">
          <li
            v-if="role !== 'seller' && role !== 'seller_admin'"
            class="nav-item d-xl-none d-flex align-items-center"
            :class="$route.name == 'Profile' ? 'pe-2' : ''"
          >
            <a
              @click="toggleSidebar"
              class="p-0 nav-link text-white"
              id="iconNavbarSidenav"
            >
              <div class="sidenav-toggler-inner cursor-pointer">
                <i class="sidenav-toggler-line bg-white"></i>
                <i class="sidenav-toggler-line bg-white"></i>
                <i class="sidenav-toggler-line bg-white"></i>
              </div>
            </a>
          </li>
          <li
            v-if="
              (role == 'seller' && $route.name !== 'Market') ||
              (role == 'seller_admin' && $route.name !== 'Market')
            "
            class="nav-item d-flex align-items-center"
            :class="$route.name == 'Profile' ? 'pe-2' : ''"
          >
            <a
              class="p-0 nav-link text-white"
              id="iconNavbarSidenav"
              data-bs-toggle="modal"
              data-bs-target="#trade-navbar"
            >
              <div class="sidenav-toggler-inner cursor-pointer">
                <i class="sidenav-toggler-line bg-white"></i>
                <i class="sidenav-toggler-line bg-white"></i>
                <i class="sidenav-toggler-line bg-white"></i>
              </div>
            </a>
          </li>
          <li
            class="nav-item d-flex align-items-center"
            :class="$route.name != 'Profile' ? 'px-3' : 'px-1'"
          >
            <a class="p-0 nav-link text-white">
              <i
                class="fa fa-cog fixed-plugin-button-nav cursor-pointer"
                @click="toggleConfigurator"
              ></i>
            </a>
          </li>
          <li
            class="nav-item d-flex align-items-center pe-2"
            v-if="$route.name != 'Profile'"
          >
            <router-link
              :to="{ name: 'Profile' }"
              class="p-0 nav-link text-white"
            >
              <i class="fa fa-user fixed-plugin-button-nav cursor-pointer"></i>
            </router-link>
          </li>
          <li class="nav-item d-flex align-items-center px-2">
            <a
              class="px-0 nav-link font-weight-bold text-white"
              @click="logOut()"
            >
              <i class="fa fa-power-off cursor-pointer"></i>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </nav>
  <trade-navigation />
</template>
<script>
import { mapMutations, mapActions } from "vuex";
import TradeNavigation from "./TradeNavigation.vue";
import { select_default_page } from "@/server/api";

export default {
  components: { TradeNavigation },
  name: "navbar",
  data() {
    return {
      showMenu: false,
      role: localStorage.getItem("role"),
    };
  },
  props: ["minNav", "textWhite"],
  created() {
    this.minNav;
  },
  methods: {
    ...mapMutations(["navbarMinimize", "toggleConfigurator"]),
    ...mapActions(["toggleSidebarColor"]),
    toggleSidebar() {
      this.toggleSidebarColor(!this.$store.state.darkMode ? "bg-white" : "");
      this.navbarMinimize();
    },
    logOut() {
      localStorage.removeItem("role");
      localStorage.removeItem("access_token");
      this.$router.push("/signin");
    },
  },
  computed: {
    currentRouteName() {
      return this.$route.name;
    },
    default_page() {
      return select_default_page(this.role);
    },
  },
};
</script>

<style scoped>
.nav-link {
  font-size: 0.9rem !important;
}
</style>
