<template>
  <div
    class="modal fade"
    id="add-customer"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-customer-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-customer-label">
            {{ $t("customer.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('add', body)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("customer.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="body.name"
                    :placeholder="$t('customer.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="type">{{ $t("customer.type") }}</label>
                  <select
                    id="type"
                    class="form-control"
                    v-model="body.type"
                    required
                  >
                    <option value="premium">
                      {{ $t("customer.premium") }}
                    </option>
                    <option value="sifat">
                      {{ $t("customer.quality") }}
                    </option>
                    <option value="narx">
                      {{ $t("customer.price") }}
                    </option>
                    <option value="qora ro'yxat">
                      {{ $t("customer.black-list") }}
                    </option>
                  </select>
                </div>
              </div>
              <div
                class="col-md-12"
                v-for="(phone, i) in body.phones"
                :key="phone"
              >
                <div class="form-group">
                  <label :for="`phone${i}`"
                    >{{ $t("customer.phone") }}
                    {{ body.phones.length > 1 ? i + 1 : "" }}</label
                  >
                  <div class="input-group">
                    <select
                      class="form-control w-custom-40"
                      v-model="phone.dial_code"
                      required
                    >
                      <option
                        :value="code.dial_code"
                        v-for="code in phone_codes"
                        :key="code"
                      >
                        {{ code.dial_code }}
                      </option>
                    </select>
                    <input
                      :id="`phone${i}`"
                      type="number"
                      class="form-control"
                      :class="body.phones.length > 1 ? 'w-50' : 'w-75'"
                      v-model="phone.phone"
                      :placeholder="$t('customer.phone')"
                      min="1"
                      required
                    />
                    <button
                      class="btn bg-gradient-danger mb-0"
                      type="button"
                      v-if="i || (!i && body.phones.length > 1)"
                      @click="body.phones.splice(i, 1)"
                    >
                      <i class="fa-solid fa-circle-xmark"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              class="btn bg-gradient-primary"
              type="button"
              @click="body.phones.push({ phone: '', dial_code: '+7' })"
            >
              <i class="fa-solid fa-phone"></i>
            </button>
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="put-customer"
    tabindex="-1"
    role="dialog"
    aria-labelledby="put-customer-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="put-customer-label">
            {{ $t("customer.update") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('put', data)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("customer.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="data.name"
                    :placeholder="$t('customer.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="type">{{ $t("customer.type") }}</label>
                  <select
                    id="type"
                    class="form-control"
                    v-model="data.type"
                    required
                  >
                    <option value="premium">
                      {{ $t("customer.premium") }}
                    </option>
                    <option value="sifat">
                      {{ $t("customer.quality") }}
                    </option>
                    <option value="narx">
                      {{ $t("customer.price") }}
                    </option>
                    <option value="qora ro'yxat">
                      {{ $t("customer.black-list") }}
                    </option>
                  </select>
                </div>
              </div>
              <div
                class="col-md-12"
                v-for="(phone, i) in data.phones"
                :key="phone"
              >
                <div class="form-group">
                  <label :for="`phone${i}`"
                    >{{ $t("customer.phone") }}
                    {{ data.phones.length > 1 ? i + 1 : "" }}</label
                  >
                  <div class="input-group">
                    <select
                      class="form-control w-custom-40"
                      v-model="phone.dial_code"
                      required
                    >
                      <option
                        :value="code.dial_code"
                        v-for="code in phone_codes"
                        :key="code"
                      >
                        {{ code.dial_code }}
                      </option>
                    </select>
                    <input
                      :id="`phone${i}`"
                      type="number"
                      class="form-control"
                      :class="data.phones.length > 1 ? 'w-50' : 'w-75'"
                      v-model="phone.value"
                      :placeholder="$t('customer.phone')"
                      min="1"
                      required
                    />
                    <button
                      class="btn bg-gradient-danger mb-0"
                      type="button"
                      v-if="i || (!i && data.phones.length > 1)"
                      @click="data.phones.splice(i, 1)"
                    >
                      <i class="fa-solid fa-circle-xmark"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              class="btn bg-gradient-primary"
              type="button"
              @click="data.phones.push({ value: '', id: 0, dial_code: '+7' })"
            >
              <i class="fa-solid fa-phone"></i>
            </button>
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-put
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("update") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import codes from "@/language/CountryPhoneCode.json";

export default {
  name: "customer-modal",
  emits: ["add", "put"],
  data() {
    return {
      body: {
        name: "",
        type: "",
        phones: [{ phone: "", dial_code: "+7" }],
      },
      phone_codes: codes,
      data: {},
    };
  },
  methods: {
    get_property(body) {
      body.phones.forEach((element) => {
        element.id = 0;
        if (element.number.slice(0, 4) == "+998") {
          element.dial_code = "+998";
          let num = element.number.split("+998");
          element.value = num[1];
        } else if (element.number.slice(0, 4) == "+992") {
          element.dial_code = "+992";
          let num = element.number.split("+992");
          element.value = num[1];
        } else if (element.number.slice(0, 4) == "+996") {
          element.dial_code = "+996";
          let num = element.number.split("+996");
          element.value = num[1];
        } else if (element.number.slice(0, 4) == "+993") {
          element.dial_code = "+993";
          let num = element.number.split("+993");
          element.value = num[1];
        } else if (element.number.slice(0, 2) == "+7") {
          element.dial_code = "+7";
          let num = element.number.split("+7");
          element.value = num[1];
        } else {
          element.dial_code = "";
          element.value = element.number;
        }
      });
      this.data = body;
    },
  },
};
</script>
