<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col">
            <h5 class="mb-0">{{ $t("indicator.sum_expense") }}</h5>
          </div>
          <div class="col-auto">
            <button
              class="btn btn-sm bg-gradient-primary mx-2 mb-0 px-3"
              data-bs-toggle="modal"
              data-bs-target="#filter"
            >
              <i class="fas fa-filter"></i>
            </button>
          </div>
        </div>
      </div>
      <div class="card-body my-2">
        <div class="table-responsive p-0 mb-0">
          <table class="table table-bordered align-items-center mb-0">
            <thead>
              <tr>
                <th>{{ $t("expense.comment") }}</th>
                <th>{{ $t("expense.money") }}</th>
                <th>{{ $t("expense.kassa") }}</th>
                <th>{{ $t("expense.date") }}</th>
                <th>{{ $t("cashier.history.user") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="expense in expenses" :key="expense">
                <td>{{ expense.comment }}</td>
                <td>{{ expense.money }} {{ $t("currency") }}</td>
                <td>{{ expense.kassa.name }}</td>
                <td>{{ format_date(expense.time) }}</td>
                <td>{{ expense.user.name }}</td>
              </tr>
            </tbody>
            <tfoot v-if="!expenses.length">
              <tr>
                <td colspan="5">
                  <data-notfound :data="expenses" />
                </td>
              </tr>
            </tfoot>
          </table>
        </div>
        <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
      </div>
    </div>
  </div>
  <!-- filter -->
  <div class="modal fade" id="filter">
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title">
            {{ $t("filter") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-6">
              <div class="form-group">
                <label for="from_date">{{ $t("from_date") }}</label>
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="from_date"
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("to_date") }}</label>
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="to_date"
                />
              </div>
            </div>
            <div class="col-md-12">
              <div class="form-group">
                <label for="source">{{ $t("cashier.history.source") }}</label>
                <select id="source" class="form-select" v-model="type">
                  <option value="order">
                    {{ $t("cashier.history.order") }}
                  </option>
                  <option value="loan">{{ $t("cashier.history.loan") }}</option>
                  <option value="user">{{ $t("cashier.history.user") }}</option>
                  <option value="fixed_expense">
                    {{ $t("cashier.history.fixed_expense") }}
                  </option>
                  <option value="variable_expense">
                    {{ $t("cashier.history.variable_expense") }}
                  </option>
                </select>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-sm bg-gradient-danger"
            data-bs-dismiss="modal"
            @click="
              from_date = new Date(
                new Date().getFullYear(),
                new Date().getMonth(),
                1
              )
                .toISOString()
                .substring(0, 10);
              to_date = new Date(
                new Date().getFullYear(),
                new Date().getMonth(),
                new Date().getDate() + 1
              )
                .toISOString()
                .substring(0, 10);
              type = '';
              get(page, limit);
            "
          >
            <i class="fas fa-trash"></i>
          </button>
          <button
            class="btn btn-sm bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-filter
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn btn-sm bg-gradient-primary"
            data-bs-dismiss="modal"
            @click="get(page, limit)"
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";
export default {
  components: { dataNotfound, Pagination },
  data() {
    return {
      expenses: [],
      pages: 0,
      page: 1,
      limit: 10,
      type: "",
      from_date: new Date(new Date().getFullYear(), new Date().getMonth(), 1)
        .toISOString()
        .substring(0, 10),
      to_date: new Date(
        new Date().getFullYear(),
        new Date().getMonth(),
        new Date().getDate() + 1
      )
        .toISOString()
        .substring(0, 10),
      market_id: 0,
      format_date: api.format_date,
    };
  },
  methods: {
    get(page, limit) {
      api
        .get_expenses(
          page,
          limit,
          this.from_date,
          this.to_date,
          0,
          this.type,
          0,
          0
        )
        .then((res) => {
          this.pages = res.data.page;
          this.page = res.data.current_page;
          this.limit = res.data.limit;
          this.expenses = res.data.data;
        });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>

<style lang="scss" scoped></style>
