<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">{{ $route.query.name }}</h6>
          </div>
          <div class="col-md-6">
            <div class="row align-items-center supplier-header">
              <div class="col">
                <div class="nav-wrapper position-relative end-0">
                  <ul
                    class="p-1 bg-transparent nav nav-pills nav-fill"
                    role="tablist"
                    id="pills-tab"
                  >
                    <li class="nav-item cursor-pointer">
                      <a
                        class="px-0 py-1 mb-0 nav-link active"
                        @click="
                          from_date = new Date(
                            new Date().getFullYear(),
                            new Date().getMonth(),
                            1
                          )
                            .toISOString()
                            .substring(0, 10);
                          to_date = new Date(
                            new Date().getFullYear(),
                            new Date().getMonth(),
                            new Date().getDate() + 1
                          )
                            .toISOString()
                            .substring(0, 10);
                          content = 'supplies';
                          get_supplies(supply.page, supply.limit);
                        "
                      >
                        <i class="fas fa-box"></i>
                        <span class="ms-1">{{ $t("supplier.supplies") }}</span>
                      </a>
                    </li>
                    <li
                      class="nav-item cursor-pointer"
                      v-if="role != 'analyst'"
                    >
                      <a
                        class="px-0 py-1 mb-0 nav-link"
                        @click="
                          from_date = new Date(
                            new Date().getFullYear(),
                            new Date().getMonth(),
                            1
                          )
                            .toISOString()
                            .substring(0, 10);
                          to_date = new Date(
                            new Date().getFullYear(),
                            new Date().getMonth(),
                            new Date().getDate() + 1
                          )
                            .toISOString()
                            .substring(0, 10);
                          content = 'moneys';
                          get_moneys(money.page, money.limit);
                        "
                      >
                        <i class="fas fa-coins"></i>
                        <span class="ms-1">{{ $t("supplier.moneys") }}</span>
                      </a>
                    </li>
                  </ul>
                </div>
              </div>
              <div class="col-auto">
                <button
                  class="btn btn-sm bg-gradient-primary mx-2 mb-0 px-3"
                  data-bs-toggle="modal"
                  data-bs-target="#filter"
                >
                  <i class="fas fa-filter"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0 mt-2">
        <div v-if="content == 'supplies'">
          <div class="table-responsive p-0 mb-0">
            <table class="table table-bordered align-items-center mb-0">
              <thead>
                <tr>
                  <th scope="col">{{ $t("material.title") }}</th>
                  <th scope="col">{{ $t("supply.value") }}</th>
                  <th scope="col">{{ $t("supply.price") }}</th>
                  <th scope="col">{{ $t("expense.money") }}</th>
                  <th scope="col">{{ $t("expense.date") }}</th>
                  <th scope="col">{{ $t("attandance.user") }}</th>
                  <th scope="col">{{ $t("supplier.warehouse") }}</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item in supplies || []" :key="item">
                  <td>
                    {{ item.material }}
                    {{
                      item.product_material ? `- ${item.product_material}` : ""
                    }}
                  </td>
                  <td>{{ item.value }} {{ item.olchov }}</td>
                  <td>{{ Intl.NumberFormat().format(item.price) }}</td>
                  <td>
                    {{ Intl.NumberFormat().format(item.value * item.price) }}
                  </td>
                  <td>{{ format_date(item.created_at) }}</td>
                  <td>{{ item.user }}</td>
                  <td>{{ item.plant_name }}</td>
                </tr>
                <tr v-if="supplies.length">
                  <td colspan="3"></td>
                  <td>
                    {{ $t("supplier.total_amount") }}:
                    {{ Intl.NumberFormat().format(total_price) }}
                  </td>
                  <td colspan="3"></td>
                </tr>
              </tbody>
              <tfoot v-if="!supplies.length">
                <tr>
                  <td colspan="7">
                    <data-notfound :data="[]" />
                  </td>
                </tr>
              </tfoot>
            </table>
          </div>
          <pagination
            :page="supply.page"
            :pages="supply.pages"
            :limit="supply.limit"
            @get="get_supplies"
          />
        </div>
        <div v-else>
          <div class="table-responsive p-0 mb-2">
            <table class="table table-bordered align-items-center mb-0">
              <thead>
                <tr>
                  <th style="width: 40px"></th>
                  <th>{{ $t("expense.date") }}</th>
                  <th>{{ $t("expense.money") }}</th>
                  <th>{{ $t("expense.kassa") }}</th>
                  <th>{{ $t("expense.comment") }}</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(money, i) in moneys" :key="money">
                  <td>{{ i + 1 }}</td>
                  <td>
                    {{ format_date(money.time) }}
                  </td>
                  <td>
                    {{
                      `${Intl.NumberFormat().format(money.money)} ${$t(
                        "currency"
                      )}`
                    }}
                  </td>
                  <td>{{ money.kassa.name }}</td>
                  <td>{{ money.comment }}</td>
                </tr>
              </tbody>
              <tfoot v-if="!moneys.length">
                <tr>
                  <td colspan="5">
                    <data-notfound :data="moneys" />
                  </td>
                </tr>
              </tfoot>
            </table>
          </div>
          <pagination
            :page="money.page"
            :pages="money.pages"
            :limit="money.limit"
            @get="get_moneys"
          />
        </div>
      </div>
    </div>
  </div>

  <!-- filter -->
  <div class="modal fade" id="filter">
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title">
            {{ $t("filter") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-6">
              <div class="form-group">
                <label for="from_date">{{ $t("from_date") }}</label>
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="from_date"
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("to_date") }}</label>
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="to_date"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-sm bg-gradient-danger"
            data-bs-dismiss="modal"
            @click="
              from_date = new Date(
                new Date().getFullYear(),
                new Date().getMonth(),
                1
              )
                .toISOString()
                .substring(0, 10);
              to_date = new Date(
                new Date().getFullYear(),
                new Date().getMonth(),
                new Date().getDate() + 1
              )
                .toISOString()
                .substring(0, 10);
              content == 'supplies'
                ? get_supplies(supply.page, supply.limit)
                : get_moneys(money.page, money.limit);
            "
          >
            <i class="fas fa-trash"></i>
          </button>
          <button
            class="btn btn-sm bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-filter
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn btn-sm bg-gradient-primary"
            data-bs-dismiss="modal"
            @click="
              content == 'supplies'
                ? get_supplies(supply.page, supply.limit)
                : get_moneys(money.page, money.limit)
            "
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import setNavPills from "@/assets/js/nav-pills.js";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";

export default {
  components: { dataNotfound, Pagination },
  name: "supplier-info",
  data() {
    return {
      format_date: api.format_date,
      content: "supplies",
      from_date: new Date(new Date().getFullYear(), new Date().getMonth(), 1)
        .toISOString()
        .substring(0, 10),
      to_date: new Date(
        new Date().getFullYear(),
        new Date().getMonth(),
        new Date().getDate() + 1
      )
        .toISOString()
        .substring(0, 10),
      money: {
        page: 1,
        pages: 0,
        limit: 10,
      },
      moneys: [],
      supply: {
        plant_id: 0,
        material_id: 0,
        search: "",
        page: 1,
        pages: 0,
        limit: 10,
      },
      supplies: [],
      total_price: null,
      role: localStorage["role"],
    };
  },
  methods: {
    get_moneys(page, limit) {
      api
        .get_expenses(
          page,
          limit,
          this.from_date,
          this.to_date,
          0,
          "supplier_payment",
          this.$route.query.id,
          0
        )
        .then((res) => {
          this.money.pages = res.data.page;
          this.money.page = res.data.current_page;
          this.money.limit = res.data.limit;
          this.moneys = res.data.data;
        });
    },
    get_supplies(page, limit) {
      api
        .get_supplies(
          page,
          limit,
          this.supply.search,
          this.$route.query.id,
          this.supply.plant_id,
          this.supply.material_id,
          this.from_date,
          this.to_date
        )
        .then((res) => {
          this.supply.pages = res.data.page_count;
          this.supply.page = res.data.current_page;
          this.supply.limit = res.data.page_limit;
          this.supplies = res.data.data;
          this.total_price = res.data.total_supply_cost;
        });
    },
  },
  created() {
    this.get_supplies(this.supply.page, this.supply.limit);
  },
  mounted() {
    setNavPills();
  },
};
</script>
