<template>
  <div
    v-show="this.$store.state.layout === 'default'"
    class="min-height-250 position-absolute w-100"
    :class="`${this.$store.state.darkMode ? 'bg-transparent' : 'bg-primary'}`"
  />
  <aside
    v-if="role == 'seller' || role == 'seller_admin' ? false : true"
    class="my-3 border-0 sidenav navbar navbar-vertical navbar-expand-xs border-radius-xl fixed-start ms-3"
    :class="` 
    ${
      this.$store.state.layout === 'landing'
        ? 'bg-transparent shadow-none'
        : ' '
    } ${this.$store.state.sidebarType}`"
    id="sidenav-main"
  >
    <div class="sidenav-header">
      <i
        class="top-0 p-3 cursor-pointer fas fa-times text-secondary opacity-5 position-absolute end-0 d-xl-none"
        aria-hidden="true"
        id="iconSidenav"
        @click="toggleSidebar"
      ></i>
      <router-link class="m-0 navbar-brand" to="/">
        <img
          :src="
            this.$store.state.darkMode ||
            this.$store.state.sidebarType === 'bg-default'
              ? logoWhite
              : logo
          "
          class="navbar-brand-img h-100"
          alt="main_logo"
        />
        <span class="ms-2 font-weight-bold me-2">F9 Group</span>
      </router-link>
    </div>
    <hr class="mt-0 horizontal dark" />
    <sidenav-list :cardBg="custom_class" />
  </aside>
</template>
<script>
import SidenavList from "./SidenavList.vue";
import logo from "@/assets/img/logo-f9.png";
import logoWhite from "@/assets/img/logo-f9.png";
import { mapMutations, mapActions } from "vuex";

export default {
  name: "index",
  props: ["custom_class", "layout"],
  components: {
    SidenavList,
  },
  data() {
    return {
      logo,
      logoWhite,
      role: localStorage.getItem("role"),
    };
  },
  methods: {
    ...mapMutations(["navbarMinimize", "toggleConfigurator"]),
    ...mapActions(["toggleSidebarColor"]),
    toggleSidebar() {
      this.toggleSidebarColor(!this.$store.state.darkMode ? "bg-white" : "");
      this.navbarMinimize();
    },
  },
};
</script>
