<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-6 my-1">
            <h6 class="mb-0">{{ $t("loan.title") }}</h6>
          </div>
          <div class="col-6 my-1">
            <div class="row d-flex align-items-center">
              <div class="col"></div>
              <div class="col-auto">
                <button
                  class="btn btn-sm bg-gradient-primary mb-0 px-3 float-end"
                  data-bs-toggle="modal"
                  data-bs-target="#filter"
                >
                  <i class="fas fa-filter"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-md-4 col-lg-3 col-sm-6 my-2"
            v-for="loan in loans"
            :key="loan"
          >
            <div class="card h-100 shadow-sm border">
              <div class="card-body">
                <h6 class="mb-0">
                  <b> {{ loan.Loans?.order?.ordinal_number }}.</b>
                </h6>
                <hr class="my-1" />
                <div class="ms-2">
                  <div class="d-flex align-items-center text-wrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fas fa-user me-3"></i>
                    </div>
                    <h6 class="mb-0">
                      {{ loan.Loans.order?.customer?.name }}
                    </h6>
                  </div>
                  <div class="d-flex align-items-center text-nowrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fas fa-phone me-3"></i>
                    </div>
                    <h6 class="mb-0">
                      {{ loan.Loans.order?.customer?.phones[0]?.number }}
                    </h6>
                  </div>
                  <div class="d-flex align-items-center text-nowrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fas fa-money-bill me-3"></i>
                    </div>
                    <h6 class="mb-0">
                      {{
                        Intl.NumberFormat().format(loan.Loans.money) +
                        " " +
                        $t("currency")
                      }}
                    </h6>
                  </div>
                  <div class="d-flex align-items-center text-nowrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fa-solid fa-money-bill-transfer me-3"></i>
                    </div>
                    <h6 class="mb-0 text-success">
                      {{
                        Intl.NumberFormat().format(loan.sum_income) +
                        " " +
                        $t("currency")
                      }}
                    </h6>
                  </div>
                  <div class="d-flex align-items-center text-nowrap">
                    <div
                      class="icon h6 mb-0 text-center d-flex align-items-center justify-content-center"
                    >
                      <i class="fa-solid fa-money-bill-transfer me-3"></i>
                    </div>
                    <h6 class="mb-0 text-danger">
                      {{
                        Intl.NumberFormat().format(loan.Loans.residual) +
                        " " +
                        $t("currency")
                      }}
                    </h6>
                  </div>
                </div>
                <hr class="my-1" />
                <div class="row">
                  <div class="col">
                    <button
                      class="btn btn-sm bg-gradient-primary mb-0 w-100 py-2"
                      data-bs-toggle="modal"
                      data-bs-target="#pay-for-loan"
                      @click="
                        residual_price = loan.Loans.residual;
                        $refs.loan.body.source = loan.Loans.id;
                      "
                    >
                      <i class="fa-solid fa-money-bill-transfer"></i>
                    </button>
                  </div>
                  <div class="col">
                    <router-link
                      :to="{
                        name: 'LoanHistory',
                        query: {
                          id: loan.Loans.id,
                          market_id: $route.query.market_id,
                        },
                      }"
                      class="btn btn-sm bg-gradient-secondary mb-0 w-100 py-2"
                    >
                      <i class="fa-solid fa-clock-rotate-left"></i>
                    </router-link>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="loans" />
            <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
          </div>
        </div>
      </div>
    </div>
  </div>

  <loan-modals ref="loan" @filter="filter" />
</template>

<script>
import * as api from "@/server/api";
import LoanModals from "@/components/modals/LoanModals.vue";
import DataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";

export default {
  components: { LoanModals, DataNotfound, Pagination },
  name: "loan",
  data() {
    return {
      loans: [],
      pages: 0,
      page: 1,
      limit: 10,
      customer_id: 0,
      status: false,
      from_time: "",
      to_time: "",
      residual_price: null,
    };
  },
  methods: {
    get(page, limit) {
      api
        .get_loans(
          page,
          limit,
          this.from_time,
          this.to_time,
          this.customer_id,
          this.status,
          0,
          this.$route.query.market_id
        )
        .then((res) => {
          this.pages = res.data.pages;
          this.page = res.data.current_page;
          this.limit = res.data.limit;
          this.loans = res.data.data;
        });
    },
    filter(body, bool) {
      this.from_time = body.from_date;
      this.to_time = body.to_date;
      this.status = body.status;
      bool ? api.success("close-filter") : api.success();
      this.get(this.page, this.limit);
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>
