<template>
  <div
    class="modal fade"
    id="user-kpi"
    tabindex="-1"
    role="dialog"
    aria-labelledby="user-kpi-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="user-kpi-label">
            {{ $t("cycle.kpi-title") }}
            <p class="d-block mb-0 text-primary">
              <b>
                {{ `${$t("cycle.kpi")}: ${body.value} ${$t("currency")}` }}
              </b>
            </p>
          </h5>
          <button
            type="button"
            class="btn shadow-none py-0 mb-0"
            data-bs-toggle="modal"
            data-bs-target="#user-kpi"
          >
            <i class="fas fa-xmark"></i>
          </button>
        </div>
        <div class="modal-body">
          <form @submit.prevent="create_kpi()">
            <div class="row">
              <div class="col">
                <div class="dropdown">
                  <button
                    class="form-control border-secondary-custom shadow-none mb-0 w-100"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                    @click="get_users(false)"
                  >
                    {{ body.name }}
                  </button>
                  <ul
                    class="dropdown-menu w-100"
                    aria-labelledby="dropdownMenuButton1"
                  >
                    <input
                      type="text"
                      class="form-control"
                      :placeholder="$t('search')"
                      v-model="user.search"
                      @keyup="
                        get_users(false);
                        $store.dispatch('setloading', false);
                      "
                    />
                    <div
                      class="scroll-25 kpi-users"
                      @scroll="
                        scroll_users();
                        $store.dispatch('setloading', false);
                      "
                    >
                      <li
                        @click="
                          body.user_id = user.id;
                          body.name = users.find(
                            (e) => body.user_id == e.id
                          ).name;
                        "
                        v-for="user in users"
                        :key="user"
                      >
                        <a class="dropdown-item"> {{ user.name }} </a>
                      </li>
                    </div>
                    <data-notfound :data="users" class="text-lg" />
                  </ul>
                </div>
              </div>
              <div class="col-auto">
                <button
                  class="btn bg-gradient-success mb-0"
                  type="submit"
                  :disabled="!body.user_id"
                >
                  <i class="fas fa-circle-check"></i>
                </button>
              </div>
            </div>
          </form>
          <div class="col-md-12 my-2">
            <ul class="list-group">
              <li
                v-for="item in kpis"
                :key="item"
                class="list-group-item list-group-item-action d-flex justify-content-between align-items-center"
              >
                <div>
                  <span class="d-block">
                    {{ item.user.name }}
                  </span>
                </div>
                <div>
                  <span
                    class="badge badge-danger badge-pill cursor-pointer"
                    @click="remove_kpi(item)"
                    ><i class="fas fa-circle-xmark"></i
                  ></span>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "../dataNotfound.vue";

export default {
  components: { dataNotfound },
  name: "CycleKpi",
  data() {
    return {
      body: {
        user_id: 0,
        cycle_id: 0,
        value: null,
        name: this.$t("cycle.users"),
      },
      pages: 0,
      page: 1,
      limit: 10,
      kpis: [],
      item: {},
      user: {
        pages: 0,
        page: 1,
        search: "",
      },
      users: [],
    };
  },
  methods: {
    get(page, limit) {
      api
        .get_user_kpi(page, limit, "", 0, false, this.body.cycle_id)
        .then((res) => {
          this.pages = res.data.page_count;
          this.page = res.data.current_page;
          this.limit = res.data.page_limit;
          this.kpis = res.data.data;
        });
    },
    create_kpi() {
      api.create_user_kpi(this.body).then(() => {
        this.body.user_id = null;
        this.body.name = this.$t("cycle.users");
        api.success();
        this.get(this.page, this.limit);
      });
    },
    put() {
      api.update_user_kpi(this.item).then(() => {
        api.success();
        this.get(this.page, this.limit);
      });
    },
    get_users(scroll) {
      this.user.page = scroll ? this.user.page : 1;
      api
        .get_users(
          scroll ? this.user.page + 1 : this.user.page,
          10,
          this.user.search,
          false,
          "",
          false,
          0
        )
        .then((res) => {
          this.user.pages = res.data.page_count;
          this.user.page = res.data.current_page;
          this.users = scroll
            ? this.users.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll_users() {
      let div = document.querySelector(".kpi-users");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.user.page < this.user.pages) {
          this.get_users(true);
        }
      }
    },
    remove_kpi(item) {
      item.disabled = true;
      api.update_user_kpi(item).then(() => {
        api.success();
        this.get(this.page, this.limit);
      });
    },
  },
};
</script>
