<template>
  <main>
    <div class="container-fluid">
      <div class="page-header min-height-250 profile-header">
        <span class="mask bg-gradient-primary opacity-6"></span>
      </div>
      <div class="card shadow-lg mt-n6">
        <div class="card-body p-3">
          <div class="row gx-4">
            <div class="col-auto">
              <div
                class="avatar-xl cursor-pointer"
                @click="image_view(`${image}/passports/${user.passport}`)"
              >
                <img
                  v-if="user?.passport"
                  :src="`${image}/passports/${user.passport}`"
                  alt="profile_image"
                  class="avatar-xl rounded-circle img-thumbnail"
                />
              </div>
            </div>
            <div class="col-auto my-auto">
              <div class="h-100">
                <h5 class="mb-1">{{ user.name }}</h5>
                <p class="mb-0 font-weight-bold text-sm">
                  {{ user.specialty }}
                </p>
              </div>
            </div>
            <div class="col-auto my-auto ms-auto">
              <div class="dropdown">
                <button
                  class="btn shadow-none mb-0"
                  type="button"
                  id="proche"
                  data-bs-toggle="dropdown"
                >
                  <i class="fa-solid fa-ellipsis"></i>
                </button>
                <ul class="dropdown-menu" aria-labelledby="proche">
                  <li
                    @click="
                      get_markets();
                      update = !update;
                    "
                    v-if="!user.disabled"
                  >
                    <a class="dropdown-item cursor-pointer">{{
                      $t("update")
                    }}</a>
                  </li>
                  <li v-if="!$route.query.market_id">
                    <router-link
                      class="dropdown-item cursor-pointer"
                      to="/users"
                      >{{ $t("users.profile.all-users") }}</router-link
                    >
                  </li>
                  <li
                    v-if="!user.disabled && $route.query.id"
                    @click="
                      body.disabled = true;
                      edit(body);
                    "
                  >
                    <a class="dropdown-item cursor-pointer">
                      {{ $t("users.demission") }}
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <hr v-if="user" />
          <div class="row" v-if="user">
            <div class="col-12 col-sm-6 col-md-6 col-lg-6">
              <div class="nav-wrapper position-relative end-0">
                <ul
                  class="p-1 bg-transparent nav nav-pills nav-fill"
                  role="tablist"
                  id="pills-tab"
                >
                  <li class="nav-item cursor-pointer">
                    <a
                      class="px-0 py-1 mb-0 nav-link active"
                      data-bs-toggle="tab"
                      data-bs-target="#pills-main"
                      id="pills-main-tab"
                      role="tab"
                      aria-selected="true"
                    >
                      <i class="fas fa-user"></i>
                      <span class="ms-1">{{ $t("users.profile.main") }}</span>
                    </a>
                  </li>
                  <li class="nav-item cursor-pointer">
                    <a
                      class="px-0 py-1 mb-0 nav-link"
                      data-bs-toggle="tab"
                      data-bs-target="#pills-attandance"
                      id="pills-attandance-tab"
                      role="tab"
                      aria-selected="false"
                      @click="get_attandance(page, limit)"
                    >
                      <i class="fa-solid fa-calendar-days"></i>
                      <span class="ms-1">{{
                        $t("users.profile.attandance")
                      }}</span>
                    </a>
                  </li>
                  <li class="nav-item cursor-pointer" v-if="role != 'analyst'">
                    <a
                      class="px-0 py-1 mb-0 nav-link"
                      data-bs-toggle="tab"
                      data-bs-target="#pills-wage"
                      id="pills-wage-tab"
                      role="tab"
                      aria-selected="false"
                      @click="get_paid_wage(wage.page, wage.limit)"
                    >
                      <i class="fa-solid fa-coins"></i>
                      <span class="ms-1">{{
                        $t("users.profile.wage-period")
                      }}</span>
                    </a>
                  </li>
                  <!-- <li class="nav-item cursor-pointer">
                    <a
                      class="px-0 py-1 mb-0 nav-link"
                      data-bs-toggle="tab"
                      data-bs-target="#pills-setting"
                      id="pills-setting-tab"
                      role="tab"
                      aria-selected="false"
                      @click="$refs.kpi.get_kpis(1, 10, user_id)"
                    >
                      <i class="fas fa-coins"></i>
                      <span class="ms-1">{{ $t("cycle.kpi") }}</span>
                    </a>
                  </li> -->
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="py-4 container-fluid">
      <div class="row mb-2" v-if="update">
        <div class="col-md-12">
          <div class="card">
            <div class="card-header pb-0">
              <div class="d-flex align-items-center">
                <p class="text-uppercase text-sm">
                  {{ $t("users.profile.user-information") }}
                </p>
              </div>
            </div>
            <div class="card-body">
              <form @submit.prevent="edit(body)" id="form">
                <div class="row">
                  <div class="col-md-6 col-lg-4 col-sm-6">
                    <div class="form-group">
                      <label for="name">{{ $t("users.table.name") }}</label>
                      <input
                        id="name"
                        type="text"
                        class="form-control"
                        v-model="body.name"
                        :placeholder="$t('users.table.name')"
                        minlength="5"
                        required
                      />
                    </div>
                  </div>
                  <div class="col-md-6 col-lg-4 col-sm-6">
                    <div class="form-group">
                      <label for="nation">{{ $t("users.table.nation") }}</label>
                      <input
                        id="nation"
                        type="text"
                        class="form-control"
                        v-model="body.nation"
                        :placeholder="$t('users.table.nation')"
                        minlength="3"
                        required
                      />
                    </div>
                  </div>
                  <div class="col-md-6 col-lg-4 col-sm-6">
                    <div class="form-group">
                      <label for="address">{{ $t("users.address") }}</label>
                      <input
                        id="address"
                        type="text"
                        class="form-control"
                        v-model="body.address"
                        :placeholder="$t('users.address')"
                        required
                      />
                    </div>
                  </div>
                  <div class="col-md-6 col-lg-4 col-sm-6">
                    <div class="form-group">
                      <label for="birthdate">{{
                        $t("users.table.birth-date")
                      }}</label>
                      <input
                        id="birthdate"
                        type="date"
                        class="form-control"
                        v-model="body.birthdate"
                        :placeholder="$t('users.table.birth-date')"
                        required
                      />
                    </div>
                  </div>
                  <div class="col-md-6 col-lg-4 col-sm-6">
                    <div class="form-group">
                      <label for="specialty">{{
                        $t("users.table.specialty")
                      }}</label>
                      <input
                        id="specialty"
                        type="text"
                        class="form-control"
                        v-model="body.specialty"
                        :placeholder="$t('users.table.specialty')"
                        minlength="5"
                        required
                      />
                    </div>
                  </div>
                  <div class="col-md-6 col-lg-4 col-sm-6">
                    <div class="form-group">
                      <label for="image">{{
                        $t("users.table.passport")
                      }}</label>
                      <input
                        id="image"
                        type="file"
                        class="form-control"
                        @change="body.passport = $event.target.files[0]"
                        :placeholder="$t('users.table.passport')"
                      />
                    </div>
                  </div>
                  <div class="col-md-6 col-lg-4 col-sm-6">
                    <div class="form-group">
                      <label for="workbegindate">{{
                        $t("users.table.work-begin-date")
                      }}</label>
                      <input
                        id="workbegindate"
                        type="date"
                        class="form-control"
                        v-model="body.workbegindate"
                        :placeholder="$t('users.table.work-begin-date')"
                        required
                      />
                    </div>
                  </div>
                  <div class="col-md-6 col-lg-4 col-sm-6">
                    <div class="form-group">
                      <label for="agreement_expiration">{{
                        $t("users.table.agreement-expiration")
                      }}</label>
                      <input
                        id="agreement_expiration"
                        type="date"
                        class="form-control"
                        v-model="body.agreement_expiration"
                        :placeholder="$t('users.table.agreement-expiration')"
                        required
                      />
                    </div>
                  </div>
                  <div class="col-md-6 col-lg-4 col-sm-6">
                    <div class="form-group">
                      <label for="username">{{
                        $t("users.table.username")
                      }}</label>
                      <input
                        id="username"
                        type="text"
                        class="form-control"
                        v-model="body.username"
                        :placeholder="$t('users.table.username')"
                        minlength="5"
                        required
                      />
                    </div>
                  </div>
                  <div class="col-md-6 col-lg-4 col-sm-6">
                    <div class="form-group">
                      <label for="password">{{
                        $t("users.table.password")
                      }}</label>
                      <input
                        id="password"
                        type="password"
                        class="form-control"
                        v-model="body.password"
                        :placeholder="$t('users.table.password')"
                        minlength="5"
                      />
                    </div>
                  </div>
                  <div class="col-md-6 col-lg-4 col-sm-6">
                    <div class="form-group">
                      <label for="role">{{ $t("users.table.role") }}</label>
                      <select
                        id="role"
                        class="form-select"
                        v-model="body.role"
                        required
                      >
                        <option value="admin">{{ $t("roles.admin") }}</option>
                        <option value="accountant">
                          {{ $t("roles.accountant") }}
                        </option>
                        <option value="analyst">
                          {{ $t("roles.analyst") }}
                        </option>
                        <option value="seller">{{ $t("roles.seller") }}</option>
                        <option value="worker">{{ $t("roles.worker") }}</option>
                        <option value="plant_admin">
                          {{ $t("roles.plant_admin") }}
                        </option>
                        <option value="seller_admin">
                          {{ $t("roles.seller_admin") }}
                        </option>
                        <option value="founder">
                          {{ $t("roles.founder") }}
                        </option>
                      </select>
                    </div>
                  </div>
                  <div
                    class="col-md-6 col-lg-4 col-sm-6"
                    v-for="(phone, i) in body?.phones"
                    :key="phone"
                  >
                    <div class="form-group">
                      <label :for="`phone${i}`"
                        >{{ $t("users.table.phone") }}
                        {{ body.phones.length > 1 ? i + 1 : "" }}</label
                      >
                      <div class="input-group">
                        <select
                          class="form-control w-25"
                          v-model="phone.dial_code"
                          required
                        >
                          <option
                            :value="code.dial_code"
                            v-for="code in phone_codes"
                            :key="code"
                          >
                            {{ code.dial_code }}
                          </option>
                        </select>
                        <input
                          :id="`phone${i}`"
                          type="number"
                          class="form-control"
                          :class="body.phones.length > 1 ? 'w-50' : 'w-75'"
                          v-model="phone.value"
                          :placeholder="$t('users.table.phone')"
                          min="1"
                          required
                        />
                        <button
                          class="btn bg-gradient-danger mb-0"
                          type="button"
                          v-if="i || (!i && body.phones.length > 1)"
                          @click="body.phones.splice(i, 1)"
                        >
                          <i class="fa-solid fa-circle-xmark"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                  <div class="col-md-6 col-lg-4 col-sm-6">
                    <div class="form-group">
                      <label for="market">{{ $t("users.market") }}</label>
                      <select
                        id="market"
                        class="form-select"
                        v-model="body.market_id"
                      >
                        <option
                          :value="market.id"
                          v-for="market in markets"
                          :key="market"
                        >
                          {{ market.name }}
                        </option>
                      </select>
                    </div>
                  </div>
                  <div class="col-md-6 col-lg-4 col-sm-6">
                    <div class="form-group">
                      <label for="shift">{{ $t("plant.shift") }}</label>
                      <select
                        id="shift"
                        class="form-select"
                        v-model="body.shift_id"
                      >
                        <option
                          :value="shift.id"
                          v-for="shift in shifts"
                          :key="shift"
                        >
                          {{ shift.name }}
                        </option>
                      </select>
                    </div>
                  </div>
                  <div class="col-md-6 col-lg-4 col-sm-6">
                    <div class="form-group">
                      <label for="wage">{{ $t("plant.wage") }}</label>
                      <div class="input-group">
                        <input
                          id="wage"
                          type="number"
                          step="any"
                          class="form-control"
                          v-model="body.salary"
                          @focus="
                            body.salary == 0
                              ? (body.salary = null)
                              : body.salary
                          "
                          @focusout="
                            body.salary == null
                              ? (body.salary = 0)
                              : body.salary
                          "
                          :placeholder="$t('plant.wage')"
                          required
                        />
                        <span class="input-group-text">
                          {{ $t("currency") }}
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="row mt-2">
                  <div class="col-12 col-md-6 col-sm-6 col-lg-6 my-1">
                    <div class="btn-group w-100">
                      <button
                        class="btn btn-sm bg-gradient-primary mb-0 rounded-3"
                        type="button"
                        @click="
                          body.phones.push({ value: '', dial_code: '+7' })
                        "
                      >
                        <i class="fa-solid fa-phone"></i>
                      </button>
                      <button
                        class="btn btn-sm bg-gradient-success mb-0 w-100 ms-1 rounded-3"
                        form="form"
                      >
                        {{ $t("users.profile.save") }}
                      </button>
                    </div>
                  </div>
                  <div class="col-12 col-md-6 col-sm-6 col-lg-6 my-1">
                    <button
                      class="btn btn-sm bg-gradient-danger mb-0 w-100 rounded-3"
                      @click="update = !update"
                    >
                      {{ $t("close") }}
                    </button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
      <div class="tab-content" id="pills-tabContent">
        <div
          class="tab-pane fade show active"
          id="pills-main"
          role="tabpanel"
          aria-labelledby="pills-main-tab"
        >
          <div class="card shadow-sm">
            <div class="card-header border-bottom mx-3 px-0 py-3">
              <h5 class="card-title mb-0">
                {{ $t("users.profile.main-data") }}
              </h5>
            </div>
            <div class="card-body">
              <div class="row" v-if="user?.name">
                <div class="col-md-6">
                  <ul class="list-group">
                    <li
                      class="list-group-item d-flex justify-content-between align-items-start list-group-item-action"
                    >
                      <div class="ms-2 me-auto">
                        <div class="fw-bold">
                          <i class="fa-solid fa-calendar-days"></i>
                          {{ $t("users.table.work-begin-date") }}
                        </div>
                      </div>
                      {{ format_date(user?.workBeginDate).slice(0, 10) }}
                    </li>
                    <li
                      class="list-group-item d-flex justify-content-between align-items-start list-group-item-action"
                    >
                      <div class="ms-2 me-auto">
                        <div class="fw-bold">
                          <i class="fa-solid fa-calendar-days"></i>
                          {{ $t("users.table.birth-date") }}
                        </div>
                      </div>
                      {{ format_date(user?.birthDate).slice(0, 10) }}
                    </li>
                    <li
                      class="list-group-item d-flex justify-content-between align-items-start list-group-item-action"
                    >
                      <div class="ms-2 me-auto">
                        <div class="fw-bold">
                          <i class="fa-solid fa-calendar-days"></i>
                          {{ $t("users.table.agreement-expiration") }}
                        </div>
                      </div>
                      {{ format_date(user?.agreement_expiration).slice(0, 10) }}
                    </li>

                    <li
                      class="list-group-item d-flex justify-content-between align-items-start list-group-item-action"
                    >
                      <div class="ms-2 me-auto">
                        <div class="fw-bold">
                          <i class="fa-solid fa-shop"></i>
                          {{ $t("users.market") }}
                        </div>
                      </div>
                      {{ user?.market?.name || "-" }}
                    </li>
                  </ul>
                </div>
                <div class="col-md-6">
                  <ul class="list-group">
                    <li
                      class="list-group-item d-flex justify-content-between align-items-start list-group-item-action"
                    >
                      <div class="ms-2 me-auto">
                        <div class="fw-bold">
                          <i class="fa-solid fa-user"></i>
                          {{ $t("users.table.nation") }}
                        </div>
                      </div>
                      {{ user?.nation }}
                    </li>
                    <li
                      class="list-group-item d-flex justify-content-between align-items-start list-group-item-action"
                    >
                      <div class="ms-2 me-auto">
                        <div class="fw-bold">
                          <i class="fa-solid fa-location-dot"></i>
                          {{ $t("users.address") }}
                        </div>
                      </div>
                      {{ user?.address }}
                    </li>
                    <li
                      class="list-group-item d-flex justify-content-between align-items-start list-group-item-action"
                    >
                      <div class="ms-2 me-auto">
                        <div class="fw-bold">
                          <i class="fa-solid fa-phone"></i>
                          {{ $t("users.table.phone") }}
                        </div>
                      </div>
                      <div>
                        <a
                          :href="`tel:+${phone.number}`"
                          v-for="(phone, i) in user?.phones"
                          :key="phone"
                          >{{ phone.number }}
                          <span v-if="user?.phones.length - 1 > i">, </span></a
                        >
                      </div>
                    </li>
                    <li
                      class="list-group-item d-flex justify-content-between align-items-start list-group-item-action"
                      v-if="!$route.query.market_id"
                    >
                      <div class="ms-2 me-auto">
                        <div class="fw-bold">
                          <i class="fa-solid fa-coins"></i>
                          {{ $t("users.profile.wage") }}
                        </div>
                      </div>
                      <div>
                        <span
                          v-if="!user?.plant_users?.length"
                          :class="
                            user?.salary > 0 ? 'text-success' : 'text-danger'
                          "
                        >
                          {{
                            Intl.NumberFormat().format(user?.salary) +
                            " " +
                            $t("currency")
                          }}
                        </span>
                        <span
                          v-else
                          class="d-block"
                          v-for="plant in user?.plant_users"
                          :key="plant"
                          >{{ plant.plant.name }} -
                          <span class="text-success">
                            {{
                              Intl.NumberFormat().format(plant.wage) +
                              " " +
                              $t("currency")
                            }}
                          </span>
                        </span>
                      </div>
                    </li>
                    <li
                      class="list-group-item d-flex justify-content-between align-items-start list-group-item-action"
                    >
                      <div class="ms-2 me-auto">
                        <div class="fw-bold">
                          <i class="fa-solid fa-calendar"></i>
                          {{ $t("plant.shift") }}
                        </div>
                      </div>
                      <div>
                        {{ user?.shift?.name }}
                      </div>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          class="tab-pane fade"
          id="pills-attandance"
          role="tabpanel"
          aria-labelledby="pills-attandance-tab"
        >
          <div class="row">
            <div class="col-md-8 col-lg-9 col-sm-7 col-12 mb-1">
              <div class="card shadow-sm">
                <div class="card-body">
                  <div class="table-responsive p-0 mb-2">
                    <table class="table table-bordered align-items-center mb-0">
                      <thead>
                        <tr>
                          <th style="width: 40px"></th>
                          <th>{{ $t("attandance.type.title") }}</th>
                          <th>{{ $t("attandance.wage") }}</th>
                          <th>{{ $t("attandance.plant") }}</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr
                          v-for="(attandance, i) in attandances"
                          :key="attandance"
                        >
                          <td>{{ i + 1 }}</td>
                          <td>
                            <span
                              class="text-success"
                              v-if="attandance.type == 'entry'"
                              >{{ $t("attandance.type.entry") }}-<span
                                class="text-dark"
                                >({{ attandance.authorizator }})</span
                              >
                              <p class="m-0 text-sm">
                                {{ format_date(attandance.datetime) }}
                              </p>
                            </span>
                            <span
                              class="text-danger"
                              v-else-if="attandance.type == 'exit'"
                              >{{ $t("attandance.type.exit") }}-<span
                                class="text-dark"
                                >({{ attandance.authorizator }})</span
                              >
                              <p class="m-0 text-sm">
                                {{ format_date(attandance.datetime) }}
                              </p></span
                            >
                            <span class="text-danger" v-else>{{
                              $t("attandance.type.did-not-come")
                            }}</span>
                          </td>
                          <td>
                            {{
                              Intl.NumberFormat().format(attandance.wage) +
                              ` ${$t("currency")}`
                            }}
                          </td>
                          <td>
                            {{ attandance.plant?.name }}
                          </td>
                        </tr>
                      </tbody>
                      <tfoot v-if="!attandances.length">
                        <tr>
                          <td colspan="4">
                            <data-notfound :data="attandances" />
                          </td>
                        </tr>
                      </tfoot>
                    </table>
                  </div>
                  <pagination
                    :page="page"
                    :pages="pages"
                    :limit="limit"
                    @get="get_attandance"
                  />
                </div>
              </div>
            </div>
            <div class="col-md-4 col-lg-3 col-sm-5 col-12 mb-1">
              <div class="card shadow-sm">
                <div class="card-header border-bottom">
                  <h5 class="card-title mb-0">{{ $t("weekdays.title") }}</h5>
                </div>
                <form @submit.prevent="create_holiday()">
                  <div class="card-body border-bottom">
                    <div
                      class="form-check d-flex align-items-center my-1"
                      v-for="day in weekdays"
                      :key="day"
                    >
                      <input
                        class="form-check-input"
                        type="checkbox"
                        :id="day"
                        v-model="holidays"
                        :value="day"
                      />
                      <label
                        class="form-check-label fs-6 m-0 p-0 ms-2"
                        :for="day"
                        >{{ $t(`weekdays.${day}`) }}</label
                      >
                    </div>
                  </div>
                  <div class="card-footer">
                    <button
                      class="btn btn-sm bg-gradient-primary mb-0 w-100"
                      type="submit"
                    >
                      {{ $t("save") }}
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
        <div
          class="tab-pane fade"
          id="pills-wage"
          role="tabpanel"
          aria-labelledby="pills-wage-tab"
          v-if="role != 'analyst'"
        >
          <div class="card shadow-sm">
            <div class="card-header border-bottom mx-3 px-0 py-3">
              <h5 class="card-title mb-0">
                {{ $t("users.profile.wage-period") }}
              </h5>
            </div>
            <div class="card-body">
              <div class="table-responsive p-0 mb-2">
                <table class="table table-bordered align-items-center mb-0">
                  <thead>
                    <tr>
                      <th style="width: 40px"></th>
                      <th>{{ $t("expense.date") }}</th>
                      <th>{{ $t("expense.money") }}</th>
                      <th>{{ $t("expense.kassa") }}</th>
                      <th>{{ $t("expense.comment") }}</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(wage, i) in wages" :key="wage">
                      <td>{{ i + 1 }}</td>
                      <td>
                        {{ format_date(wage.time) }}
                      </td>
                      <td>
                        {{
                          `${Intl.NumberFormat().format(wage.money)} ${$t(
                            "currency"
                          )}`
                        }}
                      </td>
                      <td>{{ wage.kassa.name }}</td>
                      <td>{{ wage.comment }}</td>
                    </tr>
                  </tbody>
                  <tfoot v-if="!wages.length">
                    <tr>
                      <td colspan="5">
                        <data-notfound :data="wages" />
                      </td>
                    </tr>
                  </tfoot>
                </table>
              </div>
              <pagination
                :page="wage.page"
                :pages="wage.pages"
                :limit="wage.limit"
                @get="get_paid_wage"
              />
            </div>
          </div>
        </div>
        <!-- <div
          class="tab-pane fade"
          id="pills-setting"
          role="tabpanel"
          aria-labelledby="pills-setting-tab"
        >
          <kpi :cycles="cycles" ref="kpi" />
        </div> -->
      </div>
    </div>
    <div id="image-viewer">
      <span class="close" @click="image_view()">&times;</span>
      <img class="modal-content" id="full-image" />
    </div>
  </main>
</template>

<script>
import * as api from "@/server/api";
import setNavPills from "@/assets/js/nav-pills.js";
import codes from "@/language/CountryPhoneCode.json";
import pagination from "@/components/pagination.vue";
import DataNotfound from "@/components/dataNotfound.vue";
// import Kpi from "@/components/modals/Kpi.vue";

const body = document.getElementsByTagName("body")[0];

export default {
  components: {
    pagination,
    DataNotfound, // Kpi
  },
  name: "profile",
  data() {
    return {
      showMenu: false,
      user_id: null,
      body: {
        name: "",
        address: "",
        market_id: 0,
        nation: "",
        birthdate: "",
        specialty: "",
        passport: "",
        workbegindate: "",
        agreement_expiration: "",
        username: "",
        password: "",
        disabled: false,
        phones: [{ value: "", dial_code: "+998" }],
        role: "",
        salary: 0,
        shift_id: 0,
      },
      image: api.image,
      phone_codes: codes,
      user: {},
      update: false,
      // attandance
      attandances: [],
      pages: 0,
      page: 1,
      limit: 10,
      cycles: [],
      markets: [],
      wage: {
        page: 1,
        pages: 0,
        limit: 10,
        from_time: "",
        to_time: "",
      },
      wages: [],
      format_date: api.format_date,
      role: localStorage["role"],
      weekdays: [
        "monday",
        "tuesday",
        "thursday",
        "wednesday",
        "friday",
        "saturday",
        "sunday",
      ],
      holidays: [],
      shifts: [],
    };
  },
  methods: {
    get() {
      api.get_user(this.user_id).then((res) => {
        res.data.phones.forEach((element) => {
          if (element.number.slice(0, 4) == "+998") {
            element.dial_code = "+998";
            let num = element.number.split("+998");
            element.value = num[1];
          } else if (element.number.slice(0, 4) == "+992") {
            element.dial_code = "+992";
            let num = element.number.split("+992");
            element.value = num[1];
          } else if (element.number.slice(0, 4) == "+996") {
            element.dial_code = "+996";
            let num = element.number.split("+996");
            element.value = num[1];
          } else if (element.number.slice(0, 4) == "+993") {
            element.dial_code = "+993";
            let num = element.number.split("+993");
            element.value = num[1];
          } else if (element.number.slice(0, 2) == "+7") {
            element.dial_code = "+7";
            let num = element.number.split("+7");
            element.value = num[1];
          } else {
            element.dial_code = "";
            element.value = element.number;
          }
        });
        this.body = {
          name: res.data.name,
          market_id: res.market_id,
          address: res.data.address,
          nation: res.data.nation,
          birthdate: res.data.birthDate,
          specialty: res.data.specialty,
          passport: "",
          workbegindate: res.data.workBeginDate,
          agreement_expiration: res.data.agreement_expiration,
          username: res.data.username,
          password: "",
          disabled: res.data.disabled,
          role: res.data.role,
          phones: res.data.phones,
          salary: res.data.salary,
          shift_id: res.data.shift_id,
        };
        this.cycles = res.data.user_cycles;
        this.user = res.data;
        this.holidays = [];
        res.data.holidays.forEach((element) => {
          this.holidays.push(element.week_day.toLowerCase());
        });
      });
    },
    image_view(src) {
      if (src) {
        document.getElementById("full-image").src = src;
        document.getElementById("image-viewer").style.display = "block";
      } else {
        document.getElementById("image-viewer").style.display = "none";
      }
    },
    edit(body) {
      let data = this.form_data(body);
      api
        .put_user(data, this.user_id)
        .then(() => {
          this.update = false;
          api
            .success()
            .then(() => (this.body.disabled ? this.$router.go(-1) : ""));
          this.get();
        })
        .catch(() => {
          if (this.body.disabled == true) {
            this.body.disabled = false;
          }
        });
    },
    form_data(body) {
      var phones = body.phones
        .map(function (phone) {
          return phone.dial_code + "" + phone.value;
        })
        .join(";");
      let data = new FormData();
      data.append("name", body.name);
      data.append("nation", body.nation);
      data.append("birthdate", body.birthdate);
      data.append("specialty", body.specialty);
      if (body.passport) {
        data.append("passport", body.passport);
      }
      if (body.market_id) {
        data.append("market_id", body.market_id);
      }
      data.append("workbegindate", body.workbegindate);
      data.append("agreement_expiration", body.agreement_expiration);
      data.append("username", body.username);
      data.append("address", body.address);
      data.append("password", body.password);
      data.append("disabled", this.$route.query.id ? body.disabled : false);
      data.append("phones", phones);
      data.append("role", body.role);
      data.append("salary", body.salary);
      data.append("shift_id", body.shift_id);
      return data;
    },
    get_markets() {
      api.markets("", 0, 1, 1000).then((res) => {
        this.markets = res.data.data;
        this.get_shifts();
      });
    },
    get_attandance(page, limit) {
      api
        .get_attandances(page, limit, this.search, 0, this.user_id)
        .then((res) => {
          this.pages = res.data.page_count;
          this.page = res.data.current_page;
          this.limit = res.data.page_limit;
          this.attandances = res.data.data;
        });
    },
    get_paid_wage(page, limit) {
      api
        .get_expenses(
          page,
          limit,
          this.wage.from_date,
          this.wage.to_date,
          0,
          "user_salary",
          this.user_id,
          0
        )
        .then((res) => {
          this.wage.pages = res.data.page;
          this.wage.page = res.data.current_page;
          this.wage.limit = res.data.limit;
          this.wages = res.data.data;
        });
    },
    create_holiday() {
      for (let index = 0; index < this.holidays.length; index++) {
        this.holidays[index] =
          this.holidays[index].charAt(0).toUpperCase() +
          this.holidays[index].slice(1);
      }
      api.create_holiday(this.user_id, this.holidays).then(() => {
        this.get();
      });
    },
    get_shifts() {
      api.get_shifts(1, 100).then((res) => {
        this.shifts = res.data.data;
      });
    },
  },
  mounted() {
    this.$store.state.isAbsolute = true;
    setNavPills();
  },
  beforeMount() {
    this.$store.state.imageLayout = "profile-overview";
    body.classList.add("profile-overview");
  },
  beforeUnmount() {
    this.$store.state.isAbsolute = false;
    this.$store.state.imageLayout = "default";
    this.$store.state.layout = "default";
    body.classList.remove("profile-overview");
  },
  created() {
    if (this.$route.query.id) {
      this.user_id = this.$route.query.id;
    } else {
      this.user_id = localStorage["user_id"];
    }
    this.get();
  },
};
</script>
