<template>
  <div
    class="modal fade"
    id="add-plant"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-plant-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-plant-label">
            {{ $t("plant.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('add', body)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("plant.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="body.name"
                    :placeholder="$t('plant.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="kpi">{{ $t("cycle.kpi") }}</label>
                  <div class="input-group">
                    <input
                      id="kpi"
                      type="number"
                      step="any"
                      class="form-control"
                      v-model="body.kpi"
                      @focus="body.kpi == 0 ? (body.kpi = null) : body.kpi"
                      @focusout="body.kpi == null ? (body.kpi = 0) : body.kpi"
                      :placeholder="$t('cycle.kpi')"
                      required
                    />
                    <span class="input-group-text">{{ $t("currency") }}</span>
                  </div>
                </div>
              </div>
              <div class="col-md-12">
                <h6 class="ms-1">{{ $t("plant.plant_users") }}</h6>
                <hr class="m-0" />
                <div class="row">
                  <div class="col-md-3">
                    <div class="form-group">
                      <label for="admin">{{ $t("plant.role.title") }}</label>
                      <select
                        id="admin"
                        class="form-select"
                        v-model="body.admin"
                        @change="get(false)"
                      >
                        <option value="true">
                          {{ $t("plant.role.admin") }}
                        </option>
                        <option value="false">
                          {{ $t("plant.role.worker") }}
                        </option>
                      </select>
                    </div>
                  </div>
                  <div class="col-md-9">
                    <div class="row">
                      <div class="col">
                        <div class="form-group">
                          <label for="shift">{{ $t("plant.shift") }}</label>
                          <select
                            id="shift"
                            class="form-select"
                            v-model="body.shift_id"
                          >
                            <option
                              :value="shift.id"
                              v-for="shift in shifts"
                              :key="shift"
                            >
                              {{ shift.name }}
                            </option>
                          </select>
                        </div>
                      </div>
                      <div class="col">
                        <label for="kpi">{{ $t("plant.name") }}</label>
                        <div class="dropdown d-flex">
                          <button
                            class="form-control border-secondary-custom shadow-none mb-0 w-100"
                            data-bs-toggle="dropdown"
                            aria-expanded="false"
                            :disabled="!body.admin"
                          >
                            {{ plant_user }}
                          </button>
                          <ul
                            class="dropdown-menu w-100"
                            aria-labelledby="dropdownMenuButton1"
                          >
                            <input
                              type="text"
                              class="form-control"
                              :placeholder="$t('search')"
                              v-model="search"
                              @keyup="
                                get(false);
                                $store.dispatch('setloading', false);
                              "
                            />
                            <div
                              class="scroll-25"
                              @scroll="
                                scroll();
                                $store.dispatch('setloading', false);
                              "
                            >
                              <li
                                @click="
                                  body.user_id = user.id;
                                  plant_user = users.find(
                                    (e) => body.user_id == e.id
                                  ).name;
                                "
                                v-for="user in users"
                                :key="user"
                              >
                                <a class="dropdown-item"> {{ user.name }} </a>
                              </li>
                            </div>
                            <data-notfound :data="users" class="text-lg" />
                          </ul>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="col-6">
                    <div class="form-group">
                      <label for="wage">{{ $t("plant.wage") }}</label>
                      <div class="input-group">
                        <input
                          id="wage"
                          type="number"
                          step="any"
                          @focus="
                            body.wage == 0 ? (body.wage = null) : body.wage
                          "
                          @focusout="
                            body.wage == null ? (body.wage = 0) : body.wage
                          "
                          class="form-control"
                          v-model="body.wage"
                          :placeholder="$t('plant.wage')"
                        />
                        <span class="input-group-text">
                          {{ $t("currency") }}
                        </span>
                      </div>
                    </div>
                  </div>
                  <div class="col-6">
                    <div class="form-group">
                      <label for="wage_period">{{
                        $t("plant.wage_period")
                      }}</label>
                      <div class="input-group">
                        <select
                          id="wage_period"
                          class="form-select"
                          v-model="body.wage_period"
                        >
                          <option value="hourly">
                            {{ $t("plant.hourly") }}
                          </option>
                          <option value="daily">
                            {{ $t("plant.daily") }}
                          </option>
                          <option value="weekly">
                            {{ $t("plant.weekly") }}
                          </option>
                          <option value="monthly">
                            {{ $t("plant.monthly") }}
                          </option>
                        </select>
                        <button
                          class="btn bg-gradient-success mb-0"
                          type="button"
                          @click="
                            body.plant_users.push({
                              user_id: body.user_id,
                              shift_id: body.shift_id,
                              admin: body.admin,
                              wage: body.wage,
                              wage_period: body.wage_period,
                              name: users.find((e) => body.user_id == e.id)
                                .name,
                            });
                            body.user_id = null;
                            body.shift_id = null;
                            body.admin = '';
                            body.wage = 0;
                            body.wage_period = 'hourly';
                            plant_user = $t('plant.user');
                          "
                          :disabled="
                            !body.admin ||
                            !body.shift_id ||
                            !body.user_id ||
                            !body.wage ||
                            !body.wage_period
                          "
                        >
                          <i class="fas fa-circle-check"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                  <div class="col-md-12 my-2">
                    <ul class="list-group">
                      <li
                        v-for="(user, i) in body.plant_users"
                        :key="user"
                        class="list-group-item list-group-item-action d-flex justify-content-between align-items-center"
                      >
                        <div>
                          <span class="d-block">
                            {{ user.name }}
                          </span>
                          <span class="d-block">
                            {{ $t("plant.wage") }}:
                            {{
                              wage_period(
                                Intl.NumberFormat().format(user.wage_period)
                              ) + `(${user.wage} ${$t("currency")})`
                            }}
                          </span>
                        </div>
                        <div>
                          <span
                            class="badge badge-primary badge-pill text-capitalize mx-1"
                            >{{
                              user.admin
                                ? $t("plant.role.admin")
                                : $t("plant.role.worker")
                            }}</span
                          >
                          <span
                            class="badge badge-danger badge-pill cursor-pointer"
                            @click="body.plant_users.splice(i, 1)"
                            ><i class="fas fa-circle-xmark"></i
                          ></span>
                        </div>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button
              type="submit"
              class="btn bg-gradient-primary"
              :disabled="!body.name || !body.plant_users.length"
            >
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="update-plant"
    tabindex="-1"
    role="dialog"
    aria-labelledby="update-plant-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="update-plant-label">
            {{ $t("plant.update") }}
          </h5>
        </div>
        <form @submit.prevent="$emit('put', data)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="name">{{ $t("plant.name") }}</label>
                  <input
                    id="name"
                    type="text"
                    class="form-control"
                    v-model="data.name"
                    :placeholder="$t('plant.name')"
                    required
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="kpi">{{ $t("cycle.kpi") }}</label>
                  <div class="input-group">
                    <input
                      id="kpi"
                      type="number"
                      step="any"
                      class="form-control"
                      v-model="data.kpi"
                      :placeholder="$t('cycle.kpi')"
                      required
                    />
                    <span class="input-group-text">{{ $t("currency") }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-put
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("update") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";

export default {
  components: { dataNotfound },
  props: ["item", "shifts"],
  emits: ["add", "put"],
  data() {
    return {
      body: {
        name: "",
        kpi: 0,
        user_id: null,
        shift_id: null,
        admin: "",
        wage: 0,
        wage_period: "hourly",
        plant_users: [
          // user_id INT
          // shift_id INT
          // admin BOOL
          // wage INT
          // wage_period STR
        ],
      },
      plant_user: this.$t("plant.user"),
      pages: 0,
      page: 1,
      search: "",
      users: [],
    };
  },
  methods: {
    get(scroll) {
      this.page = scroll ? this.page : 1;
      api
        .get_users(
          scroll ? this.page + 1 : this.page,
          10,
          this.search,
          false,
          this.body.admin == "true" ? "plant_admin" : "worker",
          false,
          0
        )
        .then((res) => {
          this.pages = res.data.page_count;
          this.page = res.data.current_page;
          this.users = scroll
            ? this.users.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll() {
      let div = document.querySelector(".scroll-25");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.page < this.pages) {
          this.get(true);
        }
      }
    },
    wage_period(period) {
      let pr = "";
      if (period == "hourly") {
        pr = this.$t("plant.hourly");
      } else if (period == "daily") {
        pr = this.$t("plant.daily");
      } else if (period == "weekly") {
        pr = this.$t("plant.weekly");
      } else {
        pr = this.$t("plant.monthly");
      }
      return pr;
    },
  },
  computed: {
    data() {
      return this.item;
    },
  },
};
</script>
