<template>
  <div
    class="modal fade"
    id="component-item"
    tabindex="-1"
    role="dialog"
    aria-labelledby="component-item-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="component-item-label">
            {{ $t("component-item.title") }}
          </h5>
          <button
            type="button"
            class="btn shadow-none mb-0"
            data-bs-toggle="modal"
            data-bs-target="#component"
          >
            <i class="fas fa-xmark"></i>
          </button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-lg-4 col-6 my-1">
              <div class="dropdown">
                <button
                  class="btn border border-secondary-custom mb-0 shadow-none w-100"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  {{
                    body.name
                      ? body.name
                      : product.type == "product"
                      ? $t("component-item.product")
                      : $t("component-item.material")
                  }}
                </button>
                <ul
                  class="dropdown-menu w-100"
                  aria-labelledby="dropdownMenuButton1"
                >
                  <input
                    type="text"
                    class="form-control"
                    :placeholder="$t('search')"
                    v-model="product.search"
                    @keyup="
                      get_product(product.type, product.type_id, false);
                      $store.dispatch('setloading', false);
                    "
                  />
                  <div
                    class="scroll-25"
                    @scroll="
                      scroll();
                      $store.dispatch('setloading', false);
                    "
                  >
                    <li
                      @click="
                        product.type == 'product'
                          ? (body.product_id = item.id)
                          : (body.material_id = item.id);
                        body.name = item.name;
                        body.olchov = item.olchov;
                      "
                      v-for="item in product.products"
                      :key="item"
                    >
                      <a class="dropdown-item">{{ item.name }} </a>
                    </li>
                  </div>
                  <data-notfound :data="product.products" class="text-lg" />
                </ul>
              </div>
            </div>
            <div class="col-lg-4 col-6 my-1">
              <div class="form-group">
                <select class="form-select" v-model="body.main" required>
                  <option value="" disabled>
                    {{ $t("component-item.main.title") }}
                  </option>
                  <option value="true">
                    {{ $t("component-item.main.true") }}
                  </option>
                  <option value="false">
                    {{ $t("component-item.main.false") }}
                  </option>
                </select>
              </div>
            </div>
            <div class="col-lg-4 col-12 my-1">
              <div class="d-flex">
                <div class="input-group border-05">
                  <input
                    type="number"
                    class="form-control"
                    v-model="body.value"
                    @focus="body.value == 0 ? (body.value = null) : body.value"
                    @focusout="
                      body.value == null ? (body.value = 0) : body.value
                    "
                    :placeholder="$t('component-item.value')"
                    step="any"
                  />
                  <span class="input-group-text border-05-right">{{
                    product.type == "product" ? body.olchov.name : body.olchov
                  }}</span>
                  <button
                    class="btn bg-gradient-success mb-0 mx-1 border-05"
                    type="button"
                    :disabled="
                      product.type == 'product'
                        ? !body.product_id ||
                          !body.value ||
                          !body.component_id ||
                          !body.main
                        : !body.material_id ||
                          !body.value ||
                          !body.component_id ||
                          !body.main
                    "
                    @click="add()"
                  >
                    <i class="fas fa-circle-check"></i>
                  </button>
                </div>
              </div>
            </div>
            <div class="col-md-12 my-2">
              <div class="list-group">
                <div
                  v-for="component_item in component_items"
                  :key="component_item"
                  class="list-group-item list-group-item-action"
                >
                  <div
                    class="d-flex justify-content-between align-items-center"
                  >
                    <div>
                      {{
                        component_item.product
                          ? component_item.product.name
                          : component_item.material.name
                      }}
                      <span class="text-primary">
                        ({{
                          component_item.main
                            ? $t("component-item.main.true")
                            : $t("component-item.main.false")
                        }})
                      </span>
                    </div>
                    <div class="d-flex align-items-center">
                      <span
                        class="badge badge-primary badge-pill text-capitalize rounded-pill"
                        >{{ component_item.value }}
                        {{
                          component_item.product
                            ? component_item.product?.olchov?.name
                            : component_item.material?.olchov?.name
                        }}</span
                      >
                      <button
                        class="btn btn-sm px-3 bg-gradient-danger mx-1 mb-0"
                        type="button"
                        @click="remove(component_item.id)"
                      >
                        <i class="fas fa-circle-xmark"></i>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <data-notfound :data="component_items" />
              <pagination
                :page="page"
                :pages="pages"
                :limit="limit"
                @get="get"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "../pagination.vue";

export default {
  components: { dataNotfound, Pagination },
  name: "component-item",
  data() {
    return {
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      component_items: [],
      product: {
        pages: 0,
        page: 1,
        search: "",
        products: [],
        type: "",
        type_id: null,
      },
      body: {
        material_id: 0,
        product_id: 0,
        component_id: 0,
        value: 0,
        main: "",
        name: "",
        olchov: "",
      },
    };
  },
  methods: {
    get(page, limit, id) {
      if (id) {
        this.body.component_id = id;
      }
      api
        .get_component_items(page, limit, this.search, this.body.component_id)
        .then((res) => {
          this.pages = res.data.page_count;
          this.page = res.data.current_page;
          this.limit = res.data.page_limit;
          this.component_items = res.data.data;
        });
    },
    get_product(type, id, scroll) {
      this.product.type = type;
      this.product.type_id = id;
      this.product.page = scroll ? this.product.page : 1;
      if (type == "product") {
        api
          .get_products(
            scroll ? this.product.page + 1 : this.product.page,
            10,
            this.product.search,
            id,
            false
          )
          .then((res) => {
            this.product.pages = res.data.page_count;
            this.product.page = res.data.current_page;
            this.product.products = scroll
              ? this.product.products.concat(res.data.data)
              : res.data.data;
          });
      } else {
        api
          .get_materials(
            scroll ? this.product.page + 1 : this.product.page,
            10,
            this.product.search,
            id,
            false
          )
          .then((res) => {
            this.product.pages = res.data.page_count;
            this.product.page = res.data.current_page;
            this.product.products = scroll
              ? this.product.products.concat(res.data.data)
              : res.data.data;
          });
      }
    },
    scroll() {
      let div = document.querySelector(".scroll-25");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.product.page < this.product.pages) {
          this.get_product(this.product.type, this.product.type_id, true);
        }
      }
    },
    add() {
      api.create_component_item(this.body).then(() => {
        this.body.material_id = 0;
        this.body.product_id = 0;
        this.body.main = "";
        this.body.name = "";
        this.body.olchov = "";
        this.body.value = 0;
        api.success();
        this.get(this.page, this.limit, this.body.component_id);
      });
    },
    remove(id) {
      api.delete_component_item(id).then(() => {
        api.success();
        this.get(this.page, this.limit, this.body.component_id);
      });
    },
  },
};
</script>
