<template>
  <div class="container-fluid py-4">
    <div class="card">
      <div class="card-header" v-if="role != 'seller_admin'">
        <h6 class="mb-0">{{ market?.name }}</h6>
      </div>
      <div class="card-body">
        <div class="row">
          <div
            class="col-lg-3 col-md-4 col-sm-6 my-2"
            v-for="item in routers"
            :key="item"
          >
            <router-link :to="item.path" class="card shadow-sm py-2">
              <div class="card-body">
                <h1 class="text-center">
                  <i class="p-1" :class="item.icon"></i>
                </h1>
                <h5 class="text-center">
                  {{ $t(item.name) }}
                </h5>
              </div>
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
export default {
  name: "market",
  data() {
    return {
      role: localStorage.getItem("role"),
      market: null,
      routers: [
        {
          name: "routers.order",
          icon: "fa fa-receipt",
          path: `order?market_id=${this.$route.query.id}`,
        },
        {
          name: "routers.pre-order",
          icon: "fa fa-receipt",
          path: `pre-orders?market_id=${this.$route.query.id}`,
        },
        {
          name: "routers.orders-history",
          icon: "fa fa-history",
          path: `orders-history?market_id=${this.$route.query.id}`,
        },
        {
          name: "routers.return",
          icon: "fa fa-undo",
          path: `return?market_id=${this.$route.query.id}`,
        },
        {
          name: "routers.galery",
          icon: "fa fa-images",
          path: `galery?market_id=${this.$route.query.id}`,
        },
        {
          name: "routers.cashier",
          icon: "fa fa-cash-register",
          path: `cashreg?market_id=${this.$route.query.id}`,
        },
        {
          name: "routers.customer",
          icon: "fas fa-users",
          path: `customer?market_id=${this.$route.query.id}`,
        },
        {
          name: "routers.loan",
          icon: "ni ni-box-2",
          path: `loan?market_id=${this.$route.query.id}`,
        },
        {
          name: "routers.expense",
          icon: "ni ni-credit-card",
          path: `expense?market_id=${this.$route.query.id}`,
        },
        {
          name: "routers.users",
          icon: "fas fa-users",
          path: `users?market_id=${this.$route.query.id}`,
        },
        {
          name: "routers.warehouse",
          icon: "fas fa-warehouse",
          path: `market-warehouse?market_id=${this.$route.query.id}`,
        },
        {
          name: "routers.statistika",
          icon: "fas fa-chart-pie",
          path: `statistic?market_id=${this.$route.query.id}`,
        },
      ],
    };
  },
  created() {
    this.getMarket();
  },
  methods: {
    getMarket() {
      api.markets("", this.$route.query.id, 1, 1).then((res) => {
        this.market = res.data;
      });
    },
  },
};
</script>

<style scoped>
p {
  margin: 0;
}
</style>
