<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">{{ $t("spare.title") }}</h6>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col-md-6 my-1">
                <button
                  class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
                  data-bs-toggle="modal"
                  data-bs-target="#add-spare"
                  @click="
                    $refs.spare.get_plants();
                    $refs.spare.product.page = 1;
                    $refs.spare.product_type.page = 1;
                    $refs.spare.product.name = $t('norm.products');
                    $refs.spare.product_type.name = $t('norm.product_types');
                  "
                >
                  {{ $t("spare.add-button") }}
                </button>
              </div>
              <div class="col-md-6 my-1">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    get(1, limit);
                    $store.dispatch('setloading', false);
                  "
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-md-4 col-lg-4 col-sm-6 my-2"
            v-for="(spare, i) in spare_parts"
            :key="spare"
          >
            <div class="card h-100 shadow-sm border">
              <div class="card-body pb-2">
                <h6 class="mb-0">
                  <b> {{ i + 1 }}.</b>
                </h6>
                <hr class="my-1" />
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-box"></i>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{ $t("spare.name") }}: {{ spare.product?.name }}
                  </h6>
                </div>
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-box"></i>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{ $t("spare.plant") }}: {{ spare.plant?.name }}
                  </h6>
                </div>
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-box"></i>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{ $t("spare.machine_name") }}: {{ spare.machine_name }}
                  </h6>
                </div>
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-calendar-days"></i>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{ $t("spare.date") }}:
                    {{ format_date(spare.to_date).slice(0, 10) }}
                    <!-- {{ $t("cycle.day") }} -->
                  </h6>
                </div>
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-calendar-check"></i>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{ $t("spare.updated_date") }}: {{ spare.updated_date }}
                  </h6>
                </div>
                <div class="row my-1">
                  <div class="col">
                    <button
                      class="btn bg-gradient-warning btn-sm mb-0 w-100"
                      data-bs-toggle="modal"
                      data-bs-target="#update-spare"
                      @click="
                        $refs.spare.product.page = 1;
                        $refs.spare.product_type.page = 1;
                        $refs.spare.get_plants();
                        $refs.spare.product.name = spare.product?.name;
                        $refs.spare.product_type.name =
                          spare.product?.product_type?.name;
                        $refs.spare.item = {
                          id: spare.id,
                          product_id: spare.product_id,
                          product_type_id: spare.product?.product_type_id,
                          plant_id: spare.plant_id,
                          machine_name: spare.machine_name,
                          updated_date: spare.updated_date,
                          to_date: spare.to_date,
                        };
                      "
                    >
                      <i class="fas fa-pen"></i>
                    </button>
                  </div>
                  <div class="col">
                    <button
                      class="btn bg-gradient-primary btn-sm mb-0 w-100"
                      @click="updated_date(spare.id)"
                    >
                      <i class="fas fa-rotate"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="spare_parts" />
            <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
          </div>
        </div>
      </div>
    </div>
  </div>
  <spare-part-modals ref="spare" />
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import Pagination from "@/components/pagination.vue";
import SparePartModals from "@/components/modals/SparePartModals.vue";

export default {
  components: { dataNotfound, Pagination, SparePartModals },
  name: "spare-part",
  data() {
    return {
      spare_parts: [],
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      format_date: api.format_date,
    };
  },
  methods: {
    get(page, limit) {
      api.get_spares(page, limit, this.search).then((res) => {
        this.pages = res.data.page_count;
        this.page = res.data.current_page;
        this.limit = res.data.page_limit;
        this.spare_parts = res.data.data;
      });
    },
    updated_date(id) {
      api.update_date(id).then(() => {
        api.success();
        this.get(this.page, this.limit);
      });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>
