<template>
  <h4>
    <span class="text-white">{{
      current_date.getDate() + "-" + months[current_date.getMonth()] + " "
    }}</span>
    <span class="text-danger">
      {{ weekdays[current_date.getDay()] }}
    </span>
    <span class="text-white"> ({{ current_date.getFullYear() }})</span>
  </h4>
</template>

<script>
export default {
  name: "current-date",
  data() {
    return {
      months: [
        this.$t("months.january"),
        this.$t("months.february"),
        this.$t("months.march"),
        this.$t("months.april"),
        this.$t("months.may"),
        this.$t("months.june"),
        this.$t("months.july"),
        this.$t("months.august"),
        this.$t("months.september"),
        this.$t("months.october"),
        this.$t("months.november"),
        this.$t("months.december"),
      ],
      weekdays: [
        this.$t("weekdays.sunday"),
        this.$t("weekdays.monday"),
        this.$t("weekdays.tuesday"),
        this.$t("weekdays.thursday"),
        this.$t("weekdays.wednesday"),
        this.$t("weekdays.friday"),
        this.$t("weekdays.saturday"),
      ],
      current_date: new Date(),
    };
  },
};
</script>
