<template>
  <div
    class="modal fade"
    id="plant-cycle"
    tabindex="-1"
    role="dialog"
    aria-labelledby="plant-cycle-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="plant-cycle-label">
            {{ $t("plant-cycle.title") }}
          </h5>
        </div>
        <form @submit.prevent="add()">
          <div class="modal-body">
            <div class="row">
              <div class="col-10">
                <div class="form-group">
                  <select class="form-select" v-model="body.plant_id" required>
                    <option
                      :value="plant.id"
                      v-for="plant in plants"
                      :key="plant"
                    >
                      {{ plant.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-2">
                <button
                  class="btn bg-gradient-success mb-0"
                  type="submit"
                  :disabled="!body.plant_id"
                >
                  <i class="fas fa-circle-check"></i>
                </button>
              </div>
              <hr class="my-2" />
              <div class="col-md-12">
                <ul class="list-group">
                  <li
                    v-for="cycle in plant_cycles"
                    :key="cycle"
                    class="list-group-item list-group-item-action d-flex justify-content-between align-items-start"
                  >
                    {{ cycle.plant.name }}
                    <span
                      class="badge bg-gradient-danger rounded-pill cursor-pointer"
                      @click="
                        put({
                          id: cycle.id,
                          cycle_id: cycle.cycle_id,
                          plant_id: cycle.plant_id,
                          disabled: true,
                          hidden: false,
                        })
                      "
                      ><i class="fas fa-xmark"></i
                    ></span>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";

export default {
  name: "plant-cycle",
  data() {
    return {
      body: {
        cycle_id: null,
        plant_id: null,
        disabled: false,
        hidden: false,
      },
      plant_cycles: [],
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      plant_id: 0,
      plants: [],
    };
  },
  methods: {
    get(page, limit, id) {
      this.body.cycle_id = id;
      api
        .get_plant_cycles(page, limit, this.search, this.plant_id, id)
        .then((res) => {
          this.pages = res.data.page_count;
          this.page = res.data.current_page;
          this.limit = res.data.page_limit;
          this.plant_cycles = res.data.data;
          this.get_plants();
        });
    },
    get_plants() {
      api.get_plants(1, 1000, "").then((res) => {
        this.plants = res.data.data;
      });
    },
    add() {
      api.create_plant_cycle(this.body).then(() => {
        this.body.plant_id = null;
        api.success();
        this.get(this.page, this.limit, this.body.cycle_id);
      });
    },
    put(body) {
      api.put_plant_cycle(body).then(() => {
        api.success();
        this.get(this.page, this.limit, this.body.cycle_id);
      });
    },
  },
};
</script>
