                    </span>
                    <h4 class="mb-0 mt-auto">
                      <span>
                        {{ $t("indicator.kassa_balance") }}
                      </span>
                    </h4>
                  </router-link>
                </div>
              </div>
            </div>
          </div>
          <div class="col-12 my-2" v-if="indicator != null">
            <hr class="mx-2" />
            <div class="row">
              <div class="col-md-6 col-lg-4 col-sm-6 my-2">
                <div class="card shadow-sm border border-primary border-2">
                  <router-link
                    :to="{
                      path: '/supplies_statistic',
                    }"
                  >
                    <div class="card-body">
                      <h6 class="d-flex justify-content-between mb-0">
                        <span>
                          {{ $t("indicator.supply") }}
                        </span>
                        <span>
                          {{ Intl.NumberFormat().format(indicator?.supply) }}
                          {{ $t("currency") }}
                        </span>
                      </h6>
                    </div>
                  </router-link>
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6 my-2">
                <div class="card shadow-sm border border-primary border-2">
                  <router-link
                    :to="{
                      path: '/producted',
                    }"
                  >
                    <div class="card-body">
                      <h6 class="d-flex justify-content-between mb-0">
                        <span>
                          {{ $t("indicator.production") }}
                        </span>
                        <span>
                          {{
                            Intl.NumberFormat().format(indicator?.production)
                          }}
                          {{ $t("currency") }}
                        </span>
                      </h6>
                    </div>
                  </router-link>
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6 my-2">
                <div class="card shadow-sm border border-primary border-2">
                  <router-link
                    :to="{
                      path: '/expense_statistic',
                    }"
                  >
                    <div class="card-body">
                      <h6 class="d-flex justify-content-between mb-0">
                        <span>
                          {{ $t("indicator.sum_expense") }}
                        </span>
                        <span>
                          {{
                            Intl.NumberFormat().format(indicator?.sum_expense)
                          }}
                          {{ $t("currency") }}
                        </span>
                      </h6>
                    </div>
                  </router-link>
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6 my-2">
                <div class="card shadow-sm border border-primary border-2">
                  <router-link
                    :to="{
                      path: '/income',
                    }"
                  >
                    <div class="card-body">
                      <h6 class="d-flex justify-content-between mb-0">
                        <span>
                          {{ $t("indicator.sum_income") }}
                        </span>
                        <span>
                          {{
                            Intl.NumberFormat().format(indicator?.sum_income)
                          }}
                          {{ $t("currency") }}
                        </span>
                      </h6>
                    </div>
                  </router-link>
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6 my-2">
                <div class="card shadow-sm border border-primary border-2">
                  <router-link
                    :to="{
                      path: '/trade',
                    }"
                  >
                    <div class="card-body">
                      <h6 class="d-flex justify-content-between mb-0">
                        <span>
                          {{ $t("indicator.sum_trade_price") }}
                        </span>
                        <span>
                          {{
                            Intl.NumberFormat().format(
                              indicator?.sum_trade_price
                            )
                          }}
                          {{ $t("currency") }}
                        </span>
                      </h6>
                    </div>
                  </router-link>
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6 my-2">
                <div class="card shadow-sm border border-primary border-2">
                  <router-link
                    :to="{
                      path: '/product_total',
                    }"
                  >
                    <div class="card-body">
                      <h6 class="d-flex justify-content-between mb-0">
                        <span>
                          {{ $t("indicator.warehouse") }}
                        </span>
                        <span>
                          {{ Intl.NumberFormat().format(indicator?.warehouse) }}
                          {{ $t("currency") }}
                        </span>
                      </h6>
                    </div>
                  </router-link>
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6 my-2">
                <div class="card shadow-sm border border-primary border-2">
                  <router-link
                    :to="{
                      path: '/supplier_loans',
                    }"
                  >
                    <div class="card-body">
                      <h6 class="d-flex justify-content-between mb-0">
                        <span>
                          {{ $t("indicator.supplier_loans") }}
                        </span>
                        <span>
                          {{
                            Intl.NumberFormat().format(
                              indicator?.supplier_loans
                            )
                          }}
                          {{ $t("currency") }}
                        </span>
                      </h6>
                    </div>
                  </router-link>
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6 my-2">
                <div class="card shadow-sm border border-primary border-2">
                  <router-link
                    :to="{
                      path: '/customer_loans',
                    }"
                  >
                    <div class="card-body">
                      <h6 class="d-flex justify-content-between mb-0">
                        <span>
                          {{ $t("indicator.customer_loans") }}
                        </span>
                        <span>
                          {{
                            Intl.NumberFormat().format(
                              indicator?.customer_loans
                            )
                          }}
                          {{ $t("currency") }}
                        </span>
                      </h6>
                    </div>
                  </router-link>
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6 my-2">
                <div class="card shadow-sm border border-primary border-2">
                  <router-link
                    :to="{
                      path: '/material_statistic',
                    }"
                  >
                    <div class="card-body">
                      <h6 class="d-flex justify-content-between mb-0">
                        <span>
                          {{ $t("indicator.material") }}
                        </span>
                        <span>
                          {{
                            Intl.NumberFormat().format(
                              indicator?.sum_needy_material_for_orders
                            )
                          }}
                          {{ $t("currency") }}
                        </span>
                      </h6>
                    </div>
                  </router-link>
                </div>
              </div>
              <div class="col-md-6 col-lg-4 col-sm-6 my-2">
                <div class="card shadow-sm border border-primary border-2">
                  <router-link
                    :to="{
                      path: '/need_material_statistic',
                    }"
                  >
                    <div class="card-body">
                      <h6 class="d-flex justify-content-between mb-0">
                        <span>
                          {{ $t("indicator.need_material") }}
                        </span>
                        <span>
                          {{
                            Intl.NumberFormat().format(
                              indicator?.sum_needy_material_for_plant
                            )
                          }}
                          {{ $t("currency") }}
                        </span>
                      </h6>
                    </div>
                  </router-link>
                </div>
              </div>
            </div>
          </div>
          <div
            class="col-md-4 col-lg-4 col-sm-6 my-2"
            v-for="item in orders"
            :key="item"
          >
            <div
              class="card h-100 shadow-sm border border-2"
              :class="
                count_date(
                  new Date().toLocaleDateString(),
                  item.delivery_date
                ) > 10
                  ? 'border-success'
                  : count_date(
                      new Date().toLocaleDateString(),
                      item.delivery_date
                    ) <= 10 &&
                    count_date(
                      new Date().toLocaleDateString(),
                      item.delivery_date
                    ) > 5
                  ? 'border-warning'
                  : 'border-danger'
              "
              @click="
                order = item;
                item.delivery_date == '0000-00-00'
                  ? false
                  : getTrades(1, 25, true);
              "
            >
              <div class="card-body pb-2">
                <h6 class="mb-0 d-flex justify-content-between">
                  <b> {{ item.id }}.</b>
                  <span>{{ format_date(item.time) }}</span>
                </h6>
                <hr class="my-1" />
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-user"></i>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{ item.customer?.name }}
                  </h6>
                </div>
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-phone"></i>
                  </div>
                  <h6 class="ms-2 mb-0">
                    <a :href="`tel:+${item.customer?.phones[0]?.number}`"
                      >{{ item.customer?.phones[0]?.number }}
                    </a>
                  </h6>
                </div>
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-calendar"></i>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{ item.delivery_date }}
                  </h6>
                </div>
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-shop"></i>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{ item.market?.name }}
                  </h6>
                </div>
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-coins"></i>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{ Intl.NumberFormat().format(item.order_balance) }}
                    {{ $t("currency") }}
                  </h6>
                </div>
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-percent"></i>
                  </div>
                  <h6 class="ms-2 mb-0">
                    {{ Intl.NumberFormat().format(item.discount) }}
                    {{ $t("currency") }}
                  </h6>
                </div>
                <div class="d-flex">
                  <div class="icon h6 mb-0 text-center justify-content-center">
                    <i class="fas fa-coins text-success"></i>
                  </div>
                  <h6 class="ms-2 mb-0 text-success">
                    {{ Intl.NumberFormat().format(item.sum_income) }}
                    {{ $t("currency") }}
                  </h6>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="orders" />
          </div>
        </div>
      </div>
    </div>
  </div>

  <button
    data-bs-toggle="modal"
    data-bs-target="#order-status-modal"
    order-status-modal-button
    v-show="false"
  ></button>
  <div
    class="modal fade"
    id="order-status-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="order-status-modal-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="order-status-modal-label">
            {{ $t("pre-order.title") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row gap-1">
            <div class="col-12">
              <div class="table-responsive" style="max-height: 60vh">
                <ul
                  class="list-group"
                  v-if="trades.data.length && trade_items.length"
                >
                  <li
                    class="list-group-item"
                    v-for="item in trades.data"
                    :key="item"
                  >
                    <p class="mb-2">
                      <img
                        :src="image + '/products/' + item.Trades.product.image"
                        :alt="item.Trades.product.image"
                      />
                      {{ item.Trades.product.name }}
                    </p>
                    <ul class="list-group">
                      <li
                        class="list-group-item"
                        v-for="item1 in item.Trades.trade_items"
                        :key="item1"
                      >
                        <div class="row">
                          <div class="col-10">
                            <p class="m-0">
                              <img
                                v-if="item1.component_item.product"
                                :src="
                                  image +
                                  '/products/' +
                                  item1.component_item.product?.image
                                "
                                :alt="item1.component_item.product?.image"
                              />
                              <span class="p-2">
                                {{
                                  item1.component_item.product
                                    ? item1.component_item.product?.name
                                    : item1.component_item.material?.name
                                }}
                              </span>
                            </p>
                            <p class="m-0">
                              {{
                                item1.component_item.product
                                  ? item1.trade_items_size.length
                                    ? (item1.trade_items_size[0].size1
                                        ? `${$t(
                                            "pre-order.placeholders.width"
                                          )}: ${
                                            item1.trade_items_size[0].size1
                                          }cm`
                                        : ``) +
                                      (item1.trade_items_size[0].size2
                                        ? `, ${$t(
                                            "pre-order.placeholders.height"
                                          )}: ${
                                            item1.trade_items_size[0].size2
                                          }cm`
                                        : ``) +
                                      (item1.trade_items_size[0].comment
                                        ? `, ${$t(
                                            "pre-order.placeholders.comment"
                                          )}: ${
                                            item1.trade_items_size[0].comment
                                          }`
                                        : ``)
                                    : ``
                                  : item1.trade_items_size.length
                                  ? (item1.trade_items_size[0].size1
                                      ? `${$t(
                                          "pre-order.placeholders.width"
                                        )}: ${
                                          item1.trade_items_size[0].size1
                                        }cm`
                                      : ``) +
                                    (item1.trade_items_size[0].size2
                                      ? `, ${$t(
                                          "pre-order.placeholders.height"
                                        )}: ${
                                          item1.trade_items_size[0].size2
                                        }cm`
                                      : ``) +
                                    (item1.trade_items_size[0].comment
                                      ? `, ${$t(
                                          "pre-order.placeholders.comment"
                                        )}: ${
                                          item1.trade_items_size[0].comment
                                        }`
                                      : ``)
                                  : ``
                              }}
                            </p>
                            <hr class="m-2" />
                            <p>
                              <i class="fa px-2 fa-check"></i>
                              <span>
                                {{
                                  findTradeItem(item1)?.sum_product_store +
                                  findTradeItem(item1)?.sum_material_store +
                                  " / " +
                                  item1.quantity +
                                  " " +
                                  (item1.component_item.product
                                    ? item1.component_item.product?.olchov.name
                                    : item1.component_item.material?.olchov
                                        .name)
                                }}
                              </span>
                              <progress
                                :max="item1.quantity"
                                :value="
                                  findTradeItem(item1)?.sum_product_store +
                                  findTradeItem(item1)?.sum_material_store
                                    ? findTradeItem(item1)?.sum_product_store +
                                      findTradeItem(item1)?.sum_material_store
                                    : 0
                                "
                              ></progress>
                            </p>
                            <p v-if="findTradeItem(item1)?.sum_cycle_store">
                              <i class="fa px-2 fa-recycle"></i>
                              <span>
                                {{
                                  findTradeItem(item1)?.sum_cycle_store +
                                  " / " +
                                  item1.quantity +
                                  " " +
                                  (item1.component_item.product
                                    ? item1.component_item.product?.olchov.name
                                    : item1.component_item.material?.olchov
                                        .name)
                                }}
                              </span>
                              <progress
                                :max="item1.quantity"
                                :value="findTradeItem(item1)?.sum_cycle_store"
                              ></progress>
                            </p>
                          </div>
                          <div
                            class="col-2 d-flex align-items-center justify-content-center"
                          >
                            <button
                              class="btn btn-sm bg-gradient-primary m-0 px-2 py-1"
                              @click="
                                trade_item = item1;
                                getTradeItemStatus(item1.id);
                              "
                            >
                              <i class="fa fa-eye"></i>
                            </button>
                          </div>
                        </div>
                      </li>
                    </ul>
                  </li>
                </ul>
              </div>
            </div>
            <div class="col-12">
              <pagination
                :page="trades.current_page"
                :pages="trades.pages"
                :limit="trades.limit"
                @get="getTrades"
              />
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-order-status-modal
          >
            {{ $t("close") }}
          </button>
        </div>
      </div>
    </div>
  </div>

  <button
    data-bs-toggle="modal"
    data-bs-target="#trade-status-modal"
    trade-status-modal-button
    v-show="false"
  ></button>
  <div
    class="modal fade"
    id="trade-status-modal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="trade-status-modal-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="trade-status-modal-label">
            {{ $t("pre-order.title") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row gap-1">
            <div class="col-12">
              <div class="table-responsive" style="max-height: 60vh">
                <div class="d-flex">
                  <img
                    :src="
                      image +
                      '/products/' +
                      (trade_item?.component_item?.product
                        ? trade_item?.component_item?.product?.image
                        : trade_item?.component_item?.material?.image)
                    "
                    :alt="
                      trade_item?.component_item?.product
                        ? trade_item?.component_item?.product?.image
                        : trade_item?.component_item?.material?.image
                    "
                  />
                  <strong class="p-2">
                    <p class="m-0">
                      {{
                        trade_item?.component_item?.product
                          ? trade_item?.component_item?.product?.name
                          : trade_item?.component_item?.material?.name
                      }}
                    </p>
                    <p class="m-0">
                      <i class="fa px-2 fa-check"></i>
                      {{
                        trade_item_status?.ready +
                        " " +
                        trade_item_status?.olchov_name
                      }}
                    </p>
                  </strong>
                </div>
                <ul class="list-group">
                  <li
                    class="list-group-item"
                    v-for="item in trade_item_status?.cycles"
                    :key="item"
                  >
                    <i class="fa px-2 fa-recycle"></i> {{ item.cycle }}
                    <ul class="list-group">
                      <li
                        class="list-group-item d-flex justify-content-between"
                        v-for="item1 in item.inSections"
                        :key="item1"
                      >
                        <span>
                          <i class="fa px-2 fa-industry"></i>
                          {{ item1.plant_name }}
                        </span>
                        <span>
                          {{ item1.value + " " + item1.olchov_name }}
                        </span>
                      </li>
                    </ul>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-order-status-modal
          >
            {{ $t("close") }}
          </button>
        </div>
      </div>
    </div>
  </div>

  <!-- filter -->
  <div class="modal fade" id="filter">
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title">
            {{ $t("filter") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-6">
              <div class="form-group">
                <label for="from_date">{{ $t("from_date") }}</label>
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="from_date"
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("to_date") }}</label>
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="to_date"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-sm bg-gradient-danger"
            data-bs-dismiss="modal"
            @click="
              from_date = '';
              to_date = '';
              indicators();
            "
          >
            <i class="fas fa-trash"></i>
          </button>
          <button
            class="btn btn-sm bg-gradient-secondary"
            data-bs-dismiss="modal"
            close-filter
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn btn-sm bg-gradient-primary"
            data-bs-dismiss="modal"
            @click="indicators()"
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import pagination from "@/components/pagination.vue";
export default {
  components: { dataNotfound, pagination },
  name: "admin-statistic",
  data() {
    return {
      image: api.image,
      orders: [],
      order: null,
      trades: {
        current_page: 1,
        pages: 1,
        limit: 25,
        data: [],
      },
      trade_items: [],
      trade_item: null,
      trade_item_status: null,
      count_date: api.countDays,
      indicator: null,
      from_date: new Date(new Date().getFullYear(), new Date().getMonth(), 1)
        .toISOString()
        .substring(0, 10),
      to_date: new Date(
        new Date().getFullYear(),
        new Date().getMonth(),
        new Date().getDate() + 1
      )
        .toISOString()
        .substring(0, 10),
      format_date: api.format_date,
    };
  },
  methods: {
    indicators() {
      api.indicators_index(this.from_date, this.to_date).then((res) => {
        this.indicator = res.data;
        this.get();
      });
    },
    get() {
      api.get_orders_table().then((res) => {
        this.orders = res.data;
        this.get_balance();
      });
    },
    get_balance() {
      this.orders.forEach((element) => {
        api.trades(element.id, false, 1, 1, 25).then((res) => {
          element.order_balance = res.data;
        });
      });
    },
    getTrades(page, limit, next) {
      api.trades(this.order.id, false, 0, page, limit).then((res) => {
        this.trades = res.data;
        this.getTradeStatus(next);
      });
    },
    getTradeStatus(next) {
      api.trade_status(this.order.id).then((res) => {
        this.trade_items = res.data;
        if (next) document.querySelector("[order-status-modal-button]").click();
      });
    },
    findTradeItem(trade_item) {
      return this.trade_items.find((item) => {
        return (
          item.trade_id == trade_item.trade_id &&
          item.trade_item_id == trade_item.id
        );
      });
    },
    getTradeItemStatus(id) {
      api.trade_item_status(id).then((res) => {
        this.trade_item_status = res.data;
        document.querySelector("[trade-status-modal-button]").click();
      });
    },
  },
  created() {
    this.indicators();
  },
};
</script>

