<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">{{ $t("olchov.title") }}</h6>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col-md-6 my-1">
                <button
                  class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
                  data-bs-toggle="modal"
                  data-bs-target="#add-olchov"
                >
                  {{ $t("olchov.add-button") }}
                </button>
              </div>
              <div class="col-md-6 my-1">
                <input
                  type="search"
                  :placeholder="$t('search')"
                  class="form-control form-control-sm"
                  v-model="search"
                  @keyup="
                    get(1, limit);
                    $store.dispatch('setloading', false);
                  "
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-lg-4 col-md-6 col-sm-6 my-2"
            v-for="olchov in olchovs"
            :key="olchov"
          >
            <div class="card card-stats h-100 shadow-sm">
              <div class="card-body">
                <div class="row">
                  <div
                    class="col cursor-pointer"
                    data-bs-toggle="modal"
                    data-bs-target="#proportions"
                    @click="olchov_id = olchov.id"
                  >
                    <h6 class="card-title text-uppercase text-muted mb-0">
                      {{ olchov.name }}
                    </h6>
                  </div>
                  <div class="col">
                    <p
                      class="mb-0 text-sm cursor-pointer text-warning float-end"
                      @click="
                        item = {
                          id: olchov.id,
                          name: olchov.name,
                        }
                      "
                      data-bs-toggle="modal"
                      data-bs-target="#update-olchov"
                    >
                      <b><i class="fas fa-pen"></i> {{ $t("update") }}</b>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 mt-1">
            <data-notfound :data="olchovs" />
            <pagination :page="page" :pages="pages" :limit="limit" @get="get" />
          </div>
        </div>
      </div>
    </div>
  </div>
  <olchov-modals @add="add" :item="item" @put="put" />
  <olchov-proportion :id="olchov_id" :olchovs="olchovs" />
</template>

<script>
import * as api from "@/server/api";
import pagination from "@/components/pagination.vue";
import DataNotfound from "@/components/dataNotfound.vue";
import OlchovModals from "@/components/modals/OlchovModals.vue";
import OlchovProportion from "./OlchovProportion.vue";

export default {
  components: { pagination, DataNotfound, OlchovModals, OlchovProportion },
  name: "Olchov",
  data() {
    return {
      olchovs: [],
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      item: {},
      olchov_id: null,
    };
  },
  methods: {
    get(page, limit) {
      api.get_olchovs(page, limit, this.search).then((res) => {
        this.pages = res.data.page_count;
        this.page = res.data.current_page;
        this.limit = res.data.page_limit;
        this.olchovs = res.data.data;
      });
    },
    add(body) {
      api.create_olchov(body).then(() => {
        body.name = "";
        api.success("close");
        this.get(this.page, this.limit);
      });
    },
    put(body) {
      api.put_olchov(body).then(() => {
        api.success("close-put");
        this.get(this.page, this.limit);
      });
    },
  },
  created() {
    this.get(this.page, this.limit);
  },
};
</script>
