<template>
  <div
    class="modal fade"
    id="filter-loan"
    tabindex="-1"
    role="dialog"
    aria-labelledby="filter-loan-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title" id="filter-loan-label">
            {{ $t("filter") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-6">
              <div class="form-group">
                <label for="from_date">{{ $t("from_date") }}</label>
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="filter_body.from_date"
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("to_date") }}</label>
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="filter_body.to_date"
                />
              </div>
            </div>
            <div class="col-md-12">
              <div class="form-group">
                <label for="status">{{ $t("loan.status.title") }}</label>
                <select
                  id="status"
                  class="form-select"
                  v-model="filter_body.status"
                >
                  <option value="true">{{ $t("loan.status.true") }}</option>
                  <option value="false">{{ $t("loan.status.false") }}</option>
                </select>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-sm bg-gradient-danger"
            @click="
              filter_body = {
                status: false,
                from_date: '',
                to_date: '',
              };
              $emit('loan-filter', filter_body, false);
            "
          >
            <i class="fas fa-trash"></i>
          </button>
          <button
            class="btn btn-sm bg-gradient-secondary"
            data-bs-dismiss="modal"
            loan-filter
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn btn-sm bg-gradient-primary"
            @click="$emit('loan-filter', filter_body, true)"
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="filter-order"
    tabindex="-1"
    role="dialog"
    aria-labelledby="filter-order-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header border-bottom">
          <h5 class="modal-title" id="filter-order-label">
            {{ $t("filter") }}
          </h5>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-6">
              <div class="form-group">
                <label for="from_date">{{ $t("from_date") }}</label>
                <input
                  id="from_date"
                  type="date"
                  class="form-control"
                  v-model="filter_order.from_date"
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="to_date">{{ $t("to_date") }}</label>
                <input
                  id="to_date"
                  type="date"
                  class="form-control"
                  v-model="filter_order.to_date"
                />
              </div>
            </div>
            <div class="col-md-12">
              <div class="form-group">
                <label for="status">{{ $t("loan.status.title") }}</label>
                <select
                  id="status"
                  class="form-select"
                  v-model="filter_order.status"
                >
                  <option value="true">{{ $t("customer.true") }}</option>
                  <option value="false">{{ $t("customer.false") }}</option>
                </select>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-sm bg-gradient-danger"
            @click="
              filter_order = {
                status: false,
                from_date: '',
                to_date: '',
              };
              $emit('order-filter', filter_order, false);
            "
          >
            <i class="fas fa-trash"></i>
          </button>
          <button
            class="btn btn-sm bg-gradient-secondary"
            data-bs-dismiss="modal"
            order-filter
          >
            {{ $t("close") }}
          </button>
          <button
            class="btn btn-sm bg-gradient-primary"
            @click="$emit('order-filter', filter_order, true)"
          >
            {{ $t("save") }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "loan-history",
  emits: ["loan-filter", "order-filter"],
  data() {
    return {
      filter_body: {
        to_date: "",
        from_date: "",
        status: false,
      },
      filter_order: {
        to_date: "",
        from_date: "",
        status: false,
      },
    };
  },
};
</script>
