<template>
  <div class="py-4 container-fluid">
    <div class="card">
      <div class="card-header pb-0">
        <div class="row">
          <div class="col-md-6 my-1">
            <h6 class="mb-0">{{ $t("galery.title") }}</h6>
          </div>
          <div class="col-md-6">
            <div class="row">
              <div class="col-md-6 my-1"></div>
              <div class="col-md-6 my-1">
                <button
                  class="btn btn-sm bg-gradient-primary float-end mb-0 w-100"
                  data-bs-toggle="modal"
                  data-bs-target="#add-galery"
                >
                  {{ $t("galery.add-button") }}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-0">
        <div class="row">
          <div
            class="col-md-3 col-lg-3 col-sm-6"
            v-for="galery in galeries"
            :key="galery"
          >
            <div class="card">
              <div class="position-relative">
                <img
                  @click="image_view(`${image}/galery/${galery.file}`)"
                  class="card-img-top cursor-pointer"
                  :src="`${image}/galery/${galery.file}`"
                  alt="Image"
                  height="160"
                  style="object-fit: cover"
                />
                <button
                  class="btn btn-sm mb-0 bg-gradient-danger px-3 position-absolute"
                  style="top: 5%; right: 3%"
                  @click="warning(galery.id)"
                >
                  <i class="fas fa-trash"></i>
                </button>
              </div>
              <div class="card-body pt-1 border-top">
                <p class="card-text" v-if="!galery.show_more">
                  {{ galery.comment.substring(0, 29)
                  }}<span
                    v-if="galery.comment.length > 29"
                    class="cursor-pointer"
                    @click="galery.show_more = !galery.show_more"
                    >...
                  </span>
                </p>
                <p
                  class="card-text"
                  @click="galery.show_more = !galery.show_more"
                  v-else
                >
                  {{ galery.comment }}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

  <div
    class="modal fade"
    id="add-galery"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-galery-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-galery-label">
            {{ $t("galery.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="add()">
          <div class="modal-body">
            <div class="form-group">
              <label for="image">{{ $t("galery.file") }}</label>
              <input
                type="file"
                @change="body.file = $event.target.files[0]"
                id="image"
                accept="image/png, image/jpg, image/jpeg"
                class="form-control"
                required
              />
            </div>
            <div class="form-group">
              <label for="comment">{{ $t("galery.comment") }}</label>
              <textarea
                id="comment"
                class="form-control"
                v-model="body.comment"
                required
              ></textarea>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>

  <div id="image-viewer">
    <span class="close" @click="image_view()">&times;</span>
    <img class="modal-content" id="full-image" />
  </div>
</template>

<script>
import * as api from "@/server/api";
import Swal from "sweetalert2";

export default {
  name: "galery",
  data() {
    return {
      galeries: [],
      item: {},
      body: {
        file: "",
        comment: "",
      },
      image: api.image,
    };
  },
  methods: {
    get() {
      api.get_galery(this.$route.query.market_id).then((res) => {
        res.data.forEach((element) => {
          element.show_more = false;
        });
        this.galeries = res.data;
      });
    },
    add() {
      api.create_galery(api.getFormData(this.body)).then(() => {
        this.body.file = "";
        this.body.comment = "";
        api.success("close");
        this.get();
      });
    },
    warning(id) {
      Swal.fire({
        title: this.$t("delete_text"),
        showCancelButton: true,
        confirmButtonText: this.$t("confirm"),
        cancelButtonText: this.$t("cancel"),
      }).then((result) => {
        if (result.isConfirmed) {
          this.remove(id);
        }
      });
    },
    remove(id) {
      api.delete_galery(id).then(() => {
        api.success();
        this.get();
      });
    },
    image_view(src) {
      if (src) {
        document.getElementById("full-image").src = src;
        document.getElementById("image-viewer").style.display = "block";
      } else {
        document.getElementById("image-viewer").style.display = "none";
      }
    },
  },
  created() {
    this.get();
  },
};
</script>
