<template>
  <div
    class="modal fade"
    id="component"
    tabindex="-1"
    role="dialog"
    aria-labelledby="component-label"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-xl" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="component-label">
            {{ $t("component.title") }}
          </h5>
          <button
            type="button"
            class="btn shadow-none py-0 mb-0"
            data-bs-toggle="modal"
            data-bs-target="#component"
          >
            <i class="fas fa-xmark"></i>
          </button>
        </div>
        <form @submit.prevent="$emit('add', body)">
          <div class="modal-body">
            <div class="row">
              <div class="col-md-4">
                <div class="form-group">
                  <label for="type">{{ $t("component.type") }}</label>
                  <select
                    id="type"
                    class="form-select"
                    v-model="body.type"
                    @change="
                      body.olchov = '';
                      product.search = '';
                      body.name = '';
                      get_product(false);
                    "
                  >
                    <option value="product">
                      {{ $t("component.product_type") }}
                    </option>
                    <option value="material">
                      {{ $t("component.material_type") }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-8">
                <div class="row">
                  <div class="col-md-6">
                    <label>{{ $t("component.name") }}</label>
                    <div class="dropdown">
                      <div class="input-group-custom">
                        <input
                          type="text"
                          class="form-control"
                          data-bs-toggle="dropdown"
                          aria-expanded="false"
                          v-model="body.name"
                          :disabled="!body.type"
                        />
                        <ul
                          class="dropdown-menu w-100"
                          aria-labelledby="dropdownMenuButton1"
                        >
                          <input
                            type="text"
                            class="form-control"
                            :placeholder="$t('search')"
                            v-model="product.search"
                            @keyup="
                              get_product(false);
                              $store.dispatch('setloading', false);
                            "
                          />
                          <div
                            class="scroll-25"
                            @scroll="
                              scroll();
                              $store.dispatch('setloading', false);
                            "
                          >
                            <li
                              @click="
                                body.name =
                                  body.type == 'product'
                                    ? item.name
                                    : item.Material_Type?.name;
                                body.olchov =
                                  body.type == 'product'
                                    ? item.olchov?.name
                                    : item.Material_Type.olchov?.name;
                              "
                              v-for="item in product.items"
                              :key="item"
                            >
                              <a class="dropdown-item">
                                {{
                                  body.type == "product"
                                    ? item.name
                                    : item.Material_Type?.name
                                }}
                              </a>
                            </li>
                          </div>
                          <data-notfound
                            :data="product.items"
                            class="text-lg"
                          />
                        </ul>
                      </div>
                    </div>
                  </div>
                  <div class="col-md-6">
                    <label>{{ $t("component-item.value") }}</label>
                    <div class="input-group border-05">
                      <input
                        type="number"
                        class="form-control"
                        v-model="body.value"
                        @focus="
                          body.value == 0 ? (body.value = null) : body.value
                        "
                        @focusout="
                          body.value == null ? (body.value = 0) : body.value
                        "
                        :placeholder="$t('component-item.value')"
                        step="any"
                      />
                      <span class="input-group-text border-05-right">{{
                        body.olchov
                      }}</span>
                      <button
                        class="btn bg-gradient-success mb-0 mx-1 rounded-2"
                        type="button"
                        :disabled="!body.name || !body.type || !body.value"
                        @click="add"
                      >
                        <i class="fas fa-circle-check"></i>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-md-12 mt-2">
                <div class="list-group">
                  <div
                    v-for="component in components"
                    :key="component"
                    class="list-group-item list-group-item-action"
                  >
                    <div
                      class="d-flex justify-content-between align-items-center"
                    >
                      {{
                        component.product_type
                          ? `${component.product_type.name}(${component.value} ${component.product_type.olchov?.name})`
                          : `${component.material_type.name}(${component.value} ${component.material_type.olchov?.name})`
                      }}
                      <div class="d-flex align-items-center">
                        <span
                          class="badge badge-primary badge-pill text-capitalize"
                          >{{
                            component.product_type
                              ? $t("cycle.product_type")
                              : $t("cycle.material_type")
                          }}</span
                        >
                        <button
                          class="btn btn-sm px-3 bg-gradient-danger mx-1 mb-0"
                          type="button"
                          @click="remove(component.id)"
                        >
                          <i class="fas fa-circle-xmark"></i>
                        </button>
                        <button
                          class="btn btn-sm px-3 bg-gradient-primary mb-0"
                          type="button"
                          @click="
                            $refs.componentItem.body = {
                              material_id: 0,
                              product_id: 0,
                              component_id: 0,
                              value: component.value,
                              main: '',
                              name: '',
                              olchov: '',
                            };
                            $refs.componentItem.get(1, 10, component.id);
                            $refs.componentItem.get_product(
                              component.product_type ? 'product' : 'material',
                              component.product_type_id == 0
                                ? component.material_type_id
                                : component.product_type_id,
                              false
                            );
                          "
                          data-bs-toggle="modal"
                          data-bs-target="#component-item"
                        >
                          <i class="fas fa-eye"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
                <data-notfound :data="components" />
                <pagination
                  :page="page"
                  :pages="pages"
                  :limit="limit"
                  @get="get"
                />
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
  <component-item-modals ref="componentItem" />
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "@/components/dataNotfound.vue";
import ComponentItemModals from "./ComponentItemModals.vue";
import Pagination from "../pagination.vue";

export default {
  name: "component-modal",
  components: { dataNotfound, ComponentItemModals, Pagination },
  props: ["detail"],
  data() {
    return {
      body: {
        type: "",
        name: "",
        cycle_id: null,
        value: 0,
        olchov: null,
      },
      product: {
        pages: 0,
        page: 1,
        search: "",
        items: [],
      },
      pages: 0,
      page: 1,
      limit: 10,
      search: "",
      components: [],
    };
  },
  methods: {
    get_product(scroll) {
      this.product.page = scroll ? this.product.page : 1;
      if (this.body.type == "product") {
        api
          .get_product_types(
            scroll ? this.product.page + 1 : this.product.page,
            10,
            this.product.search
          )
          .then((res) => {
            this.product.pages = res.data.page_count;
            this.product.page = res.data.current_page;
            this.product.items = scroll
              ? this.product.items.concat(res.data.data)
              : res.data.data;
          });
      } else {
        api
          .get_material_types(
            scroll ? this.product.page + 1 : this.product.page,
            10,
            this.product.search
          )
          .then((res) => {
            this.product.pages = res.data.page_count;
            this.product.page = res.data.current_page;
            this.product.items = scroll
              ? this.product.items.concat(res.data.data)
              : res.data.data;
          });
      }
    },
    scroll() {
      let div = document.querySelector(".scroll-25");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.product.page < this.product.pages) {
          this.get_product(true);
        }
      }
    },
    get(page, limit, id) {
      if (id) {
        this.body.cycle_id = id;
      }
      api
        .get_components(page, limit, this.search, this.body.cycle_id)
        .then((res) => {
          this.pages = res.data.page_count;
          this.page = res.data.current_page;
          this.limit = res.data.page_limit;
          this.components = res.data.data;
        });
    },
    add() {
      api.create_component(this.body).then(() => {
        this.body.name = "";
        this.body.type = "";
        this.body.value = 0;
        api.success();
        this.get(this.page, this.limit, this.body.cycle_id);
      });
    },
    remove(id) {
      api.delete_component(id).then(() => {
        api.success();
        this.get(this.page, this.limit, this.body.cycle_id);
      });
    },
  },
};
</script>
