<template>
  <div
    class="modal fade"
    id="add-spare"
    tabindex="-1"
    role="dialog"
    aria-labelledby="add-spare-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-spare-label">
            {{ $t("spare.add-button") }}
          </h5>
        </div>
        <form @submit.prevent="create()">
          <div class="modal-body">
            <div class="row">
              <div class="col-12 my-1">
                <div class="dropdown">
                  <button
                    class="form-control border-secondary-custom shadow-none mb-0 w-100"
                    data-bs-toggle="dropdown"
                    type="button"
                    aria-expanded="false"
                    @click="get_product_type(false)"
                  >
                    {{ product_type.name }}
                  </button>
                  <ul
                    class="dropdown-menu w-100"
                    aria-labelledby="dropdownMenuButton1"
                  >
                    <input
                      type="text"
                      class="form-control"
                      :placeholder="$t('search')"
                      v-model="product_type.search"
                      @keyup="
                        get_product_type(false);
                        $store.dispatch('setloading', false);
                      "
                    />
                    <div
                      class="scroll-25 product-type"
                      @scroll="
                        scroll_product_type();
                        $store.dispatch('setloading', false);
                      "
                    >
                      <li
                        @click="
                          body.product_type_id = item.id;
                          product_type.name = item.name;
                          get_product(false);
                        "
                        v-for="item in product_types"
                        :key="item"
                      >
                        <a class="dropdown-item">
                          {{ item.name }}
                        </a>
                      </li>
                    </div>
                    <data-notfound :data="product_types" class="text-lg" />
                  </ul>
                </div>
              </div>
              <div class="col-12 my-1">
                <div class="dropdown">
                  <button
                    class="form-control border-secondary-custom shadow-none mb-0 w-100"
                    data-bs-toggle="dropdown"
                    type="button"
                    aria-expanded="false"
                    :disabled="!body.product_type_id"
                  >
                    {{ product.name }}
                  </button>
                  <ul
                    class="dropdown-menu w-100"
                    aria-labelledby="dropdownMenuButton1"
                  >
                    <input
                      type="text"
                      class="form-control"
                      :placeholder="$t('search')"
                      v-model="product.search"
                      @keyup="
                        get_product(false);
                        $store.dispatch('setloading', false);
                      "
                    />
                    <div
                      class="scroll-25 product"
                      @scroll="
                        scroll_product();
                        $store.dispatch('setloading', false);
                      "
                    >
                      <li
                        @click="
                          body.product_id = item.id;
                          product.name = item.name;
                        "
                        v-for="item in products"
                        :key="item"
                      >
                        <a class="dropdown-item"> {{ item.name }} </a>
                      </li>
                    </div>
                    <data-notfound :data="products" class="text-lg" />
                  </ul>
                </div>
              </div>
              <div class="col-12">
                <div class="form-group">
                  <label for="plant">{{ $t("supply.plant") }}</label>
                  <select
                    id="plant"
                    class="form-select"
                    v-model="body.plant_id"
                    required
                  >
                    <option
                      :value="plant.id"
                      v-for="plant in plants"
                      :key="plant"
                    >
                      {{ plant.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-12">
                <div class="form-group">
                  <label for="machine_name">{{
                    $t("spare.machine_name")
                  }}</label>
                  <input
                    id="machine_name"
                    type="text"
                    class="form-control"
                    v-model="body.machine_name"
                    :placeholder="$t('spare.machine_name')"
                    required
                  />
                </div>
              </div>
              <div class="col-12">
                <div class="form-group">
                  <label for="to_date">{{ $t("spare.date") }}</label>
                  <input
                    id="to_date"
                    type="date"
                    class="form-control"
                    v-model="body.to_date"
                    :placeholder="$t('spare.date')"
                    required
                  />
                </div>
              </div>
              <div class="col-12">
                <div class="form-group">
                  <label for="updated_date">{{
                    $t("spare.updated_date")
                  }}</label>
                  <input
                    id="updated_date"
                    type="date"
                    class="form-control"
                    v-model="body.updated_date"
                    :placeholder="$t('spare.updated_date')"
                    required
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close
            >
              {{ $t("close") }}
            </button>
            <button
              type="submit"
              class="btn bg-gradient-primary"
              :disabled="!body.product_type_id || !body.product_id"
            >
              {{ $t("add") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="update-spare"
    tabindex="-1"
    role="dialog"
    aria-labelledby="update-spare-label"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="update-spare-label">
            {{ $t("update") }}
          </h5>
        </div>
        <form @submit.prevent="update()">
          <div class="modal-body">
            <div class="row">
              <div class="col-12 my-1">
                <div class="dropdown">
                  <button
                    class="form-control border-secondary-custom shadow-none mb-0 w-100"
                    data-bs-toggle="dropdown"
                    type="button"
                    aria-expanded="false"
                    @click="get_product_type(false)"
                  >
                    {{ product_type.name }}
                  </button>
                  <ul
                    class="dropdown-menu w-100"
                    aria-labelledby="dropdownMenuButton1"
                  >
                    <input
                      type="text"
                      class="form-control"
                      :placeholder="$t('search')"
                      v-model="product_type.search"
                      @keyup="
                        get_product_type(false);
                        $store.dispatch('setloading', false);
                      "
                    />
                    <div
                      class="scroll-25 product-type"
                      @scroll="
                        scroll_product_type();
                        $store.dispatch('setloading', false);
                      "
                    >
                      <li
                        @click="
                          item.product_type_id = itemm.id;
                          product_type.name = itemm.name;
                          get_product(false);
                        "
                        v-for="itemm in product_types"
                        :key="itemm"
                      >
                        <a class="dropdown-item">
                          {{ itemm.name }}
                        </a>
                      </li>
                    </div>
                    <data-notfound :data="product_types" class="text-lg" />
                  </ul>
                </div>
              </div>
              <div class="col-12 my-1">
                <div class="dropdown">
                  <button
                    class="form-control border-secondary-custom shadow-none mb-0 w-100"
                    data-bs-toggle="dropdown"
                    type="button"
                    aria-expanded="false"
                    :disabled="!item.product_type_id"
                    @click="item.product_type_id ? get_product() : ''"
                  >
                    {{ product.name }}
                  </button>
                  <ul
                    class="dropdown-menu w-100"
                    aria-labelledby="dropdownMenuButton1"
                  >
                    <input
                      type="text"
                      class="form-control"
                      :placeholder="$t('search')"
                      v-model="product.search"
                      @keyup="
                        get_product(false);
                        $store.dispatch('setloading', false);
                      "
                    />
                    <div
                      class="scroll-25 product"
                      @scroll="
                        scroll_product();
                        $store.dispatch('setloading', false);
                      "
                    >
                      <li
                        @click="
                          item.product_id = itemm.id;
                          product.name = itemm.name;
                        "
                        v-for="itemm in products"
                        :key="itemm"
                      >
                        <a class="dropdown-item"> {{ itemm.name }} </a>
                      </li>
                    </div>
                    <data-notfound :data="products" class="text-lg" />
                  </ul>
                </div>
              </div>
              <div class="col-12">
                <div class="form-group">
                  <label for="plant">{{ $t("supply.plant") }}</label>
                  <select
                    id="plant"
                    class="form-select"
                    v-model="item.plant_id"
                    required
                  >
                    <option
                      :value="plant.id"
                      v-for="plant in plants"
                      :key="plant"
                    >
                      {{ plant.name }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-12">
                <div class="form-group">
                  <label for="machine_name">{{
                    $t("spare.machine_name")
                  }}</label>
                  <input
                    id="machine_name"
                    type="text"
                    class="form-control"
                    v-model="item.machine_name"
                    :placeholder="$t('spare.machine_name')"
                    required
                  />
                </div>
              </div>
              <div class="col-12">
                <div class="form-group">
                  <label for="to_date">{{ $t("spare.date") }}</label>
                  <input
                    id="to_date"
                    type="date"
                    class="form-control"
                    v-model="item.to_date"
                    :placeholder="$t('spare.date')"
                    required
                  />
                </div>
              </div>
              <div class="col-12">
                <div class="form-group">
                  <label for="updated_date">{{
                    $t("spare.updated_date")
                  }}</label>
                  <input
                    id="updated_date"
                    type="date"
                    class="form-control"
                    v-model="item.updated_date"
                    :placeholder="$t('spare.updated_date')"
                    required
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn bg-gradient-secondary"
              data-bs-dismiss="modal"
              close-put
            >
              {{ $t("close") }}
            </button>
            <button type="submit" class="btn bg-gradient-primary">
              {{ $t("update") }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/server/api";
import dataNotfound from "../dataNotfound.vue";

export default {
  components: { dataNotfound },
  name: "spare-part-modals",
  data() {
    return {
      body: {
        product_id: 0,
        product_type_id: 0,
        plant_id: 0,
        machine_name: "",
        updated_date: "",
        to_date: "",
      },
      item: {
        id: null,
        product_id: 0,
        product_type_id: 0,
        plant_id: 0,
        machine_name: "",
        updated_date: "",
        to_date: "",
      },
      plants: [],
      product_types: [],
      products: [],
      product: {
        pages: 0,
        page: 1,
        search: "",
        name: this.$t("norm.products"),
      },
      product_type: {
        pages: 0,
        page: 1,
        search: "",
        name: this.$t("norm.product_types"),
      },
    };
  },
  methods: {
    create() {
      api.create_spare(this.body).then(() => {
        this.body.machine_name = "";
        this.body.plant_id = 0;
        this.body.product_id = 0;
        this.body.product_type_id = 0;
        this.body.to_date = "";
        this.body.updated_date = "";
        this.product.name = this.$t("norm.products");
        this.product_type.name = this.$t("norm.product_types");
        api.success("close");
        this.$parent.get(this.$parent.page, this.$parent.limit);
      });
    },
    update() {
      api.update_spare(this.item).then(() => {
        api.success("close-put");
        this.$parent.get(this.$parent.page, this.$parent.limit);
      });
    },
    get_product(scroll) {
      this.product.page = scroll ? this.product.page : 1;
      api
        .get_products(
          scroll ? this.product.page + 1 : this.product.page,
          10,
          this.product.search,
          this.body.product_type_id,
          false
        )
        .then((res) => {
          this.product.pages = res.data.page_count;
          this.product.page = res.data.current_page;
          this.products = scroll
            ? this.products.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll_product() {
      let div = document.querySelector(".product");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.product.page < this.product.pages) {
          this.get_product(true);
        }
      }
    },
    get_product_type(scroll) {
      this.product_type.page = scroll ? this.product_type.page : 1;
      api
        .get_product_types(
          scroll ? this.product_type.page + 1 : this.product_type.page,
          10,
          this.product_type.search,
          this.body.product_type_id,
          false
        )
        .then((res) => {
          this.product_type.pages = res.data.page_count;
          this.product_type.page = res.data.current_page;
          this.product_types = scroll
            ? this.product_types.concat(res.data.data)
            : res.data.data;
        });
    },
    scroll_product_type() {
      let div = document.querySelector(".product-type");
      if (div.scrollTop + div.clientHeight >= div.scrollHeight - 1) {
        if (this.product_type.page < this.product_type.pages) {
          this.get_product_type(true);
        }
      }
    },
    get_plants() {
      api.get_plants(1, 1000, "").then((res) => {
        this.plants = res.data.data;
      });
    },
  },
};
</script>
